import {
    FdMisc
} from '../../FdMisc/FdMisc';

/*
 * 高度量测工具。
 * @private
 * @alias FdAnalysisHeight
 * @class
 */
class FdAnalysisHeight {
    constructor(viewer) {
        this._viewer = viewer;

        this._data = [];
        this._points = [];
        this._labels = [];
        this._vLines = [];
        this._bInSurvey = false;

        this._event = new FreeDo.Event();
        this._eventHelper = new FreeDo.EventHelper();

        this._tmpPoint = undefined;
        this._tmpPosition = undefined;
        this._tmpLabel = undefined;
        this._tmpVLine = undefined;
    }
}

/*
 * 添加点
 * @param {Cartesian3} position 位置
 */
FdAnalysisHeight.prototype.addPoint = function(position) {
    var posC = Freedo.FdMisc.FdGeometric.tc(position);
    if (!Freedo.defined(this._viewer.scene.globe))
        posC = {
            x: position[0],
            y: position[1],
            z: position[2]
        };

    var tmpPoint = this._viewer.entities.add({
        position: posC,
        point: {
            color: Freedo.Color.YELLOW,
            pixelSize: 5,
            //保证不被遮挡
            disableDepthTestDistance: Number.POSITIVE_INFINITY,
            show: true
        }
    });

    var round = Freedo.FdMisc.FdMath.round
    var labelEntity;
    if (this._viewer.scene.globe)
        labelEntity = this.createLabel(posC, '高度：' + round(position[2], 4));
    else
        labelEntity = this.createLabel(posC, 'X：' + round(position[0], 8) + '\n' + 'Y：' + round(position[1], 8) + '\n' + 'Z：' + round(position[2], 4));

    if (this._viewer.scene.globe) {
        var zPt = Freedo.FdMisc.FdGeometric.tc([position[0], position[1], 0])
        var vLine = this._viewer.scene.primitives.add(this.createLinePrimitive([posC, zPt]));
        this._vLines.push(vLine);
    }

    this._labels.push(labelEntity);
    this._points.push(tmpPoint);

    var data = {};
    data.pointG = [round(position[0], 8), round(position[1], 8), round(position[2], 8)];
    data.pointC = posC;
    this._data.push(data);

    this._event.raiseEvent('HeightPoint', {
        point: position
    });

    this._event.raiseEvent('DataChanged', data);
    this._viewer.scene.requestRender();
}

/**
 * 创建多段线
 * @private
 * @param {Object} pts 点组
 * @returns 返回线段对象
 */
FdAnalysisHeight.prototype.createLinePrimitive = function(pts) {
    return new Freedo.Primitive({
        geometryInstances: new Freedo.GeometryInstance({
            geometry: new Freedo.PolylineGeometry({
                positions: pts,
                width: 2.0,
                vertexFormat: Freedo.PolylineMaterialAppearance.VERTEX_FORMAT
            })
        }),
        appearance: new Freedo.PolylineMaterialAppearance({
            material: new Freedo.Material({
                fabric: {
                    type: 'PolylineDash',
                    uniforms: {
                        color: new Freedo.Color(1.0, 0, 0, 1.0)
                    }
                }
            }),
            renderState: new Freedo.RenderState({
                polygonOffset: {
                    enabled: true,
                    factor: -100.0,
                    units: -100.0,
                },
                depthTest: {
                    enabled: true,
                    func: Freedo.WebGLConstants.LEQUAL,
                }
            }),
        }),
        depthFailAppearance: new Freedo.PolylineMaterialAppearance({
            material: new Freedo.Material({
                fabric: {
                    type: 'PolylineDash',
                    uniforms: {
                        color: new Freedo.Color(1.0, 0.5, 0.5, 0.5)
                    }
                }
            }),
        }),
    });
}

FdAnalysisHeight.prototype.callbackPosition = function() {
    this._viewer.scene.requestRender();
    return Freedo.FdMisc.FdGeometric.tc(this._tmpPosition);
}

FdAnalysisHeight.prototype.callbackPositionHalf = function() {
    this._viewer.scene.requestRender();
    return Freedo.FdMisc.FdGeometric.tc(this._tmpPosition);
}

FdAnalysisHeight.prototype.callbackVLine = function() {
    this._viewer.scene.requestRender();
    var zpos = Freedo.clone(this._tmpPosition, true);
    zpos[2] = 0;
    return [Freedo.FdMisc.FdGeometric.tc(this._tmpPosition), Freedo.FdMisc.FdGeometric.tc(zpos)];
}

FdAnalysisHeight.prototype.callbackVLineText = function() {
    this._viewer.scene.requestRender();
    var round = Freedo.FdMisc.FdMath.round;
    return '高程：' + round(this._tmpPosition[2], 2);
}

/*
 * 添加点
 * @param {Cartesian3} position 位置
 */
FdAnalysisHeight.prototype.updateTmpPoint = function() {
    if (this._tmpPoint == undefined) {
        var c = new Freedo.CallbackProperty(this.callbackPosition.bind(this), false);
        this._tmpPoint = this._viewer.entities.add({
            position: c,
            point: {
                color: Freedo.Color.YELLOW,
                pixelSize: 5,
                //保证不被遮挡
                disableDepthTestDistance: Number.POSITIVE_INFINITY,
                show: true
            }
        });
    }

    if (this._tmpLabel == undefined) {
        var cp = new Freedo.CallbackProperty(this.callbackPositionHalf.bind(this), false);
        var cpText = new Freedo.CallbackProperty(this.callbackVLineText.bind(this), false);
        this._tmpLabel = this._viewer.entities.add({
            position: cp,
            label: {
                style: Freedo.LabelStyle.FILL_AND_OUTLINE,
                text: cpText,
                show: true,
                showBackground: true,
                backgroundColor: new Freedo.Color(0.165, 0.165, 0.165, 1),
                font: '16px 黑体',
                horizontalOrigin: Freedo.HorizontalOrigin.LEFT,
                verticalOrigin: Freedo.VerticalOrigin.BOTTOM,
                pixelOffset: new Freedo.Cartesian2(10, -10),
                disableDepthTestDistance: Number.POSITIVE_INFINITY
            }
        });
    }

    if (this._tmpVLine == undefined) {
        var c = new Freedo.CallbackProperty(this.callbackVLine.bind(this), false);
        this._tmpVLine = this._viewer.entities.add({
            polyline: {
                positions: c,
                width: 2,
                material: Freedo.Color.RED,
                depthFailMaterial: Freedo.Color.RED
            }
        });
    }

    this._viewer.scene.requestRender();
}

/**
 * 创建Label
 * @private
 * @param {Object} pos 位置
 * @param {String} text 文字
 * @returns 返回对象
 */
FdAnalysisHeight.prototype.createLabel = function(pos, text) {
    return this._viewer.entities.add({
        position: pos,
        label: {
            style: Freedo.LabelStyle.FILL_AND_OUTLINE,
            text: text,
            show: true,
            showBackground: true,
            backgroundColor: new Freedo.Color(0.165, 0.165, 0.165, 1),
            font: '16px 黑体',
            horizontalOrigin: Freedo.HorizontalOrigin.LEFT,
            verticalOrigin: Freedo.VerticalOrigin.BOTTOM,
            pixelOffset: new Freedo.Cartesian2(1, 1),
            disableDepthTestDistance: Number.POSITIVE_INFINITY
        }
    });
}

/**
 * 注册事件捕捉
 * @param {FdAnalysisHeight~FdAnalysisHeightCallback} listener 当有事件触发时被执行的函数。
 * @param {Object} [scope] listener函数执行时的绑定的对象。
 * @returns {Freedo.Event~RemoveCallback} 返回一个函数，调用该函数可以取消监听。
 */
FdAnalysisHeight.prototype.on = function(listener, scope) {
        return this._eventHelper.add(this._event, listener, scope);
    }
    /**
     * @callback FdAnalysisHeight~FdAnalysisHeightCallback
     * @param {String} eventType 事件类型有：Point（点量测）
     * @param {Object} eventArg Point类型事件参数包含相关信息
     */

FdAnalysisHeight.prototype.handleLeftClick = function(movement) {
    if (this._bInSurvey == false)
        return;

    //var position = Freedo.FdMisc.FdGeometric.pickPositionG(this._viewer, movement.position);
    if (this._tmpPosition) {
        this.addPoint(this._tmpPosition);
    }
};

FdAnalysisHeight.prototype.handleMouseMove = function(movement) {
    if (this._bInSurvey == false)
        return;

    var mPos = movement.endPosition;
    mPos.x -= 0;
    mPos.x -= 0;
    var pos = Freedo.FdMisc.FdGeometric.pickPositionG(this._viewer, mPos);
    if (pos)
        this._tmpPosition = pos;

    this.updateTmpPoint();
}

/**
 * 获取数据
 */
FdAnalysisHeight.prototype.getData = function() {
    return {
        height: Freedo.clone(this._data, true)
    }
}

/**
 * 设置数据
 */
FdAnalysisHeight.prototype.setData = function(v) {
    for (var k in v) {
        this.addPoint(v[k].pointG);
    }
}

/**
 * 销毁所有资源
 */
FdAnalysisHeight.prototype.run = function(type) {
    this._hanlder = new Freedo.ScreenSpaceEventHandler(this._viewer.scene.canvas);
    this._hanlder.setInputAction(this.handleLeftClick.bind(this), Freedo.ScreenSpaceEventType.LEFT_CLICK);
    this._hanlder.setInputAction(this.handleMouseMove.bind(this), Freedo.ScreenSpaceEventType.MOUSE_MOVE);
    this._bInSurvey = true;
}

FdAnalysisHeight.prototype.delTmpEntities = function() {
    this._viewer.entities.remove(this._tmpLabel);
    this._viewer.entities.remove(this._tmpPoint);
    this._viewer.entities.remove(this._tmpVLine);

    this._tmpPoint = undefined;
    this._tmpPosition = undefined;
    this._tmpLabel = undefined;
    this._tmpVLine = undefined;

    this._viewer.scene.requestRender();
};

/**
 * 重置
 */
FdAnalysisHeight.prototype.reset = function() {
    this._bInSurvey = false;
    this._hanlder.removeInputAction(Freedo.ScreenSpaceEventType.LEFT_CLICK);

    this.delTmpEntities();
};

/**
 * 销毁
 */
FdAnalysisHeight.prototype.removeAll = function() {
    for (var k in this._points) {
        this._viewer.entities.remove(this._points[k]);
    }
    for (var k in this._labels) {
        this._viewer.entities.remove(this._labels[k]);
    }
    for (var k in this._vLines) {
        this._viewer.scene.primitives.remove(this._vLines[k]);
    }
    this._points = [];
    this._data = [];

    this.delTmpEntities();
};

/**
 * 销毁
 */
FdAnalysisHeight.prototype.dispose = function() {
    this.reset();
    this.removeAll();
};

export {
    FdAnalysisHeight
};