import {
	FdBezierCurveAlgorithm
} from './Analysis/FdBezierCurveAlgorithm';

import {
	FdKeyboard
} from '../FdTools/FdKeyboard';

import {
	FdMisc
} from '../FdMisc/FdMisc';

/**
 * 路径动画类
 * 可用PageUp、PageDown按键控制前进和后退
 * @alias FdMicroApp.FdPathAnimator
 * @class
 * @private
 */
class FdPathAnimator {
	constructor(viewer) {
		this._viewer = viewer;

		// 辅助变量
		this._points = {};
		this._ptsSmoothed = [];

		//引用
		this._event = new FreeDo.Event();
		this._eventHelper = new FreeDo.EventHelper();

		this._kb = new FdKeyboard(viewer);
		this._kbCallback = undefined;
		this._bKBActive = false;

		this._lines = [];

		//播放功能需要用到的变量
		this._bStop = true;
		this._bPause = true;

		//0 初始状态，1 手动暂停 2 手动恢复
		this._bPauseMannul = 0;

		this._bRun = false;

		//飞行姿态控制
		this._dPitch = 15;
		this._dOffsetH = 0;
		this._dHeight = 100;
		this._dSpeed = 1000; //每秒XX米
		this._bLock = 1;

		this._lastTS = 0;
		this._timeUsed = 0;
		this._totalLen = 0;
		this._passedLen = 0;

		this._dHeading = 0;
		this._pathPL = undefined;

		this._smoothRate = 2;
		this._nearRate = 4;

		this.addKBLsnr();

		//路径显示与否
		this._showPath = false;
		this._loop = false;
		this._pathViewMode = false;

		this._aTmpPoint = [];
		this._centerPt = undefined;
	}
}

/**
 * 开始播放动画
 */
FdPathAnimator.prototype.start = function() {
	if(!this._bRun) {
		var that = this;
		this._bRun = true;
		this._viewer.scene.preRender.addEventListener(function() {
			that.tick();
		});
	}

	if(!this._bStop && this._bPauseMannul != 1)
		return;

	if(this._bStop) {
		this._bStop = false;
		this._bPause = false;
		this._bPauseMannul = 2;
		this._startTime = 0;
		this._timeUsed = 0;
	}
	if(this._bPauseMannul == 1) {
		this._bPauseMannul = 2;
		console.log(this._bPauseMannul);
	}

	this._event.raiseEvent('StatusChanged', {
		status: 'play'
	});
	this._kb.setFocus();
}

/**
 * 激活键盘手动操作功能
 *
 */
FdPathAnimator.prototype.enable = function() {
	this._bKBActive = true;
	this._kb.enable();
}

/**
 * 禁用键盘手动操作功能
 *
 */
FdPathAnimator.prototype.disable = function() {
	this._bKBActive = false;
	this._kb.disable();
}

/**
 * 获取当前键盘手动操作功能的状态
 * @returns {Boolean} true为激活状态，false为未激活状态
 */
FdPathAnimator.prototype.getKeyboardStatus = function() {
	return this._bKBActive;
}

FdPathAnimator.prototype.addKBLsnr = function() {
	var that = this;
	this._kbCallback = this._kb.on(function(eventType, attach) {
		if(eventType === 'timer') {
			that.onKeyboard(attach.metaData);
			return;
			if(!that._bRun && (attach.metaData[38] || attach.metaData[40])) {
				that.start();
				that._bPauseMannul = 1;
			}
		}
	});
}

/*
 * 键盘事件响应
 */
FdPathAnimator.prototype.onKeyboard = function(metaData) {
	if(!this._bKBActive)
		return;

	if(this._points.length == 0)
		return;

	if(metaData[33]) {
		//PageUp键
		this._bPauseMannul = 1;
		var len = 33.3 / 1000 * this._dSpeed;
		this.kbtick(len);
	}
	if(metaData[34]) {
		//PageDown键
		this._bPauseMannul = 1;
		var len = -33.3 / 1000 * this._dSpeed;
		this.kbtick(len);
	}
}

/**
 * 停止播放
 */
FdPathAnimator.prototype.stop = function() {
	if(this._bStop)
		return;
	this._bStop = true;
	this._event.raiseEvent('StatusChanged', {
		status: 'stop'
	});
	this._kb.setFocus();
	if(this._centerPt) {
		this._viewer.entities.remove(this._centerPt);
		this._centerPt = undefined;
	}
    this._viewer.scene.requestRender();
    this._viewer.camera.lookAtTransform(Freedo.Matrix4.IDENTITY);
	//this._project.getCamera().unTrack();
}

/**
 * 暂停播放
 */
FdPathAnimator.prototype.pause = function() {
	if(this._bStop)
		return;
	this._bPauseMannul = 1;
	this._event.raiseEvent('StatusChanged', {
		status: 'pause'
	});
	this._kb.setFocus();
}

/**
 * 获取动画播放速度
 * @returns {Number} 单位米/秒
 */
FdPathAnimator.prototype.getSpeed = function() {
	return this._dSpeed;
}

/**
 * 设置动画播放速度
 * @param {Number} s 单位米/秒
 */
FdPathAnimator.prototype.setSpeed = function(s) {
	this._dSpeed = s;
}

/**
 * 设置路径贴近率
 * @param {Number} rate 贴近率，建议1-100之间
 */
FdPathAnimator.prototype.setNearRate = function(rate) {
    this._nearRate = rate;
}

/**
 * 获取俯仰角
 * @returns {Number} 单位度
 */
FdPathAnimator.prototype.getPitch = function() {
	return this._dPitch;
}

/**
 * 设置俯仰角
 * @param {Number} pitch 单位度
 */
FdPathAnimator.prototype.setPitch = function(pitch) {
	this._dPitch = pitch * -1;
}

/**
 * 设置路径高度偏移
 * @param {Number} offsetH 单位米
 */
FdPathAnimator.prototype.setOffsetH = function(offsetH) {
	this._dOffsetH = offsetH;
	this.smoothPath();
}

/**
 * 设置
 * @param {Number} pitch 单位度
 */
FdPathAnimator.prototype.setPathViewMode = function(pathView) {
	this._pathViewMode = pathView;
}

/**
 * 设置路径平滑度
 * @param {Number} r 平滑度，建议1-11之间，数字越大越平滑
 */
FdPathAnimator.prototype.setSmoothRate = function(r) {
	this._smoothRate = r;
	this.smoothPath();
}

/**
 * 获取视角高度
 * @returns {Number} 单位米
 */
FdPathAnimator.prototype.getHeight = function() {
	return this._dHeight;
}

/**
 * 设置视角距离
 * @param {Number} range 单位米
 */
FdPathAnimator.prototype.setHeight = function(range) {
	this._dHeight = range;
}

/**
 * 设置偏航角
 * @param {Number} heading 单位度
 */
FdPathAnimator.prototype.setHeading = function(heading) {
	this._dHeading = heading;
}

/**
 * 设置路径是否可见
 * @param {Number} h 单位米
 */
FdPathAnimator.prototype.setPathShow = function(show) {
	if(this._showPath !== show) {
		this._event.raiseEvent('PathStatusChanged', {
			status: this._showPath
		});
	}
	this._showPath = show;
	this.showPath();
}

/**
 * 获取当前播放状态
 * @returns {String} 返回状态字符串：play，pause，stop
 */
FdPathAnimator.prototype.getStatus = function() {
	if(this._bStop)
		return 'stop';
	else if(this._bPauseMannul == 1)
		return 'pause';
	else
		return 'play';
	this._kb.setFocus();
}

/**
 * 显示路径
 * @param {Boolean} show true显示，false隐藏
 */
FdPathAnimator.prototype.showPath = function() {
	if(this._showPath) {
		if(this._pathPL !== undefined) {
			this._viewer.entities.remove(this._pathPL);
			this._pathPL = undefined;
		}

		for(var key in this._aTmpPoint) {
			this._viewer.entities.remove(this._aTmpPoint[key]);
		}
		this._aTmpPoint = [];

		for(var i = 0; i < this._points.length; i++) {
			var newPos = Freedo.Cartesian3.fromDegrees(this._points[i][0], this._points[i][1], this._points[i][2] + this._dOffsetH);

			var tmpPoint = this._viewer.entities.add({
				position: newPos,
				point: {
					color: Freedo.Color.RED,
					pixelSize: 10,
					//保证不被遮挡
					disableDepthTestDistance: Number.POSITIVE_INFINITY,
					show: true
				}
			});

			this._aTmpPoint.push(tmpPoint);
		}

		this._pathPL = this._viewer.entities.add({
			name: 'MeasureLine',
			polyline: {
				positions: this._ptsSmoothed,
				width: 3,
				material: Freedo.Color.YELLOW,
				//加这个之后将使得本线条无法求交
				depthFailMaterial: Freedo.Color.YELLOW
			}
		});
	} else {
		this._viewer.entities.remove(this._pathPL);
		this._pathPL = undefined;

		for(var key in this._aTmpPoint) {
			this._viewer.entities.remove(this._aTmpPoint[key]);
		}
	}

	this._kb.setFocus();
	this._viewer.scene.requestRender();
}

/**
 * 计算水平面上矢量的朝向
 * @param {Freedo.Cartesian3} ptSXYZ 起点
 * @param {Freedo.Cartesian3} ptSXYZ 终点
 * @return 返回值1/2/3/4 分别表示对应的象限
 */
FdPathAnimator.prototype.calVectorOri = function(ptSXYZ, ptEXYZ) {
	var ptS = FreeDo.Cartographic.fromCartesian(ptSXYZ);
	var ptE = FreeDo.Cartographic.fromCartesian(ptEXYZ);
	if(ptE.longitude > ptS.longitude && ptE.latitude > ptS.latitude)
		return 1;
	if(ptE.longitude > ptS.longitude && ptE.latitude < ptS.latitude)
		return 2;
	if(ptE.longitude < ptS.longitude && ptE.latitude < ptS.latitude)
		return 3;
	if(ptE.longitude < ptS.longitude && ptE.latitude > ptS.latitude)
		return 4;
}

/**
 * 计算水平面上矢量的偏航角
 * @param {Freedo.Cartesian3} ptSXYZ 起点
 * @param {Freedo.Cartesian3} ptEXYZ 终点
 * @return 返回值为偏航角，单位为弧度
 */
FdPathAnimator.prototype.calVectorHeading = function(ptSXYZ, ptEXYZ) {
	//先转换成经纬度
	var ptS = FreeDo.Cartographic.fromCartesian(ptSXYZ);
	var ptE = FreeDo.Cartographic.fromCartesian(ptEXYZ);
	return Math.atan((ptE.longitude - ptS.longitude) / (ptE.latitude - ptS.latitude));
}

/*
 * 计算控制点
 */
FdPathAnimator.prototype.calcCP = function(p0p, p1p, p2p) {
	var p0,
		p1,
		p2;
	p0 = p0p.position;
	p1 = p1p.position;
	p2 = p2p.position;

	var cp = [];
	var v1 = FreeDo.Cartesian3.subtract(p0, p1, new FreeDo.Cartesian3());
	var v2 = FreeDo.Cartesian3.subtract(p2, p1, new FreeDo.Cartesian3());

	var axis = FreeDo.Cartesian3.cross(v1, v2, new FreeDo.Cartesian3());

	var v1n = FreeDo.Cartesian3.normalize(v1, new FreeDo.Cartesian3());
	var v2n = FreeDo.Cartesian3.normalize(v2, new FreeDo.Cartesian3());
	var mv = FreeDo.Cartesian3.add(v1n, v2n, new FreeDo.Cartesian3());

	var quat1 = Freedo.Quaternion.fromAxisAngle(axis, Freedo.Math.PI_OVER_TWO, new Freedo.Quaternion());
	var quat2 = Freedo.Quaternion.fromAxisAngle(axis, -1 * Freedo.Math.PI_OVER_TWO, new Freedo.Quaternion());

	var m31 = Freedo.Matrix3.fromQuaternion(quat1, new Freedo.Matrix3());
	var m32 = Freedo.Matrix3.fromQuaternion(quat2, new Freedo.Matrix3());

	var mv1 = Freedo.Matrix3.multiplyByVector(m31, mv, new Freedo.Cartesian3());
	var mv2 = Freedo.Matrix3.multiplyByVector(m32, mv, new Freedo.Cartesian3());

	var mv1n = FreeDo.Cartesian3.normalize(mv1, new FreeDo.Cartesian3());
	var mv2n = FreeDo.Cartesian3.normalize(mv2, new FreeDo.Cartesian3());

	var len1 = FreeDo.Cartesian3.distance(p0, p1);
	var len2 = FreeDo.Cartesian3.distance(p2, p1);
	var len = 1;
	if(len1 < len2) {
		len = len1 / this._nearRate; //this._smoothRate;
	} else
		len = len2 / this._nearRate; // this._smoothRate;

	mv1n = FreeDo.Cartesian3.multiplyByScalar(mv1n, len, new FreeDo.Cartesian3());
	mv2n = FreeDo.Cartesian3.multiplyByScalar(mv2n, len, new FreeDo.Cartesian3());

	var cp0 = FreeDo.Cartesian3.add(p1, mv1n, new FreeDo.Cartesian3());
	var cp1 = FreeDo.Cartesian3.add(p1, mv2n, new FreeDo.Cartesian3());

	var dis1 = FreeDo.Cartesian3.distance(p0, cp0);
	var dis2 = FreeDo.Cartesian3.distance(p0, cp1);

	if(dis1 < dis2)
		return [cp0, cp1];
	else
		return [cp1, cp0];
}

/*
 * 平滑化
 * @param {Boolean} pts 需要进行平滑化的点组
 * @returns 平滑后的点组，[Cartesian3]
 */
FdPathAnimator.prototype.smoothPolyline = function(pts) {
	var aPt = [];
	if(pts.length < 3) {
		for(var i = 0; i < pts.length; i++) {
			aPt.push(pts[i].position);
		}
	} else {
		for(var i = 0; i <= pts.length - 2; i++) {
			var ca = {},
				cb = {},
				pa = {},
				pb = {};
			if(i == 0) {
				ca = pts[0];
				cb.position = this.calcCP(pts[0], pts[1], pts[2])[0];
				pa = pts[0];
				pb = pts[1];
			} else if(i == pts.length - 2) {
				ca.position = this.calcCP(pts[i - 1], pts[i], pts[i + 1])[1];
				cb = pts[i + 1];
				pa = pts[i];
				pb = pts[i + 1];
			} else {
				ca.position = this.calcCP(pts[i - 1], pts[i], pts[i + 1])[1];
				cb.position = this.calcCP(pts[i], pts[i + 1], pts[i + 2])[0];
				pa = pts[i];
				pb = pts[i + 1];
			}
			var bz = new FdBezierCurveAlgorithm(pa, pb, ca, cb);

			/*
			this._viewer.entities.add({
			position: ca.position,
			ellipsoid: {
			material: Freedo.Color.YELLOW.withAlpha(1),
			radii: new Freedo.Cartesian3(5, 5, 5)
			}
			});
			this._viewer.entities.add({
			position: cb.position,
			ellipsoid: {
			material: Freedo.Color.RED.withAlpha(1),
			radii: new Freedo.Cartesian3(5, 5, 5)
			}
			});
			 */

			var segPts = bz.getVertexForCurve(this._smoothRate);
			aPt = aPt.concat(segPts);
		}
	}

	//把重复的相邻点删除掉
	var newPts = [];
	for(var i = 0; i < aPt.length; i++) {
		var o = {};
		o = aPt[i];
		if(newPts.length > 0) {
			var len = FreeDo.Cartesian3.distance(newPts[newPts.length - 1], o);
			if(len > 0.0001)
				newPts.push(o);
		} else
			newPts.push(o);
	}
	return newPts;
}

/**
 * 计算矢量的Heading值
 */
FdPathAnimator.prototype.calcHeading = function(i) {
	var heading = this.calVectorHeading(this._ptsSmoothed[i], this._ptsSmoothed[i + 1]);

	var vXX = this.calVectorOri(this._ptsSmoothed[i], this._ptsSmoothed[i + 1]);

	heading = Freedo.Math.toDegrees(heading);
	if(vXX == 1)
		heading = heading + 0;
	if(vXX == 2)
		heading = heading + 180;
	if(vXX == 3)
		heading = heading + 180;
	if(vXX == 4)
		heading = heading + 360;

	return heading;
}

/**
 * 平滑处理
 */
FdPathAnimator.prototype.smoothPath = function() {
	if(!this._points)
		return;

	var pts = [];
	for(var i = 0; i < this._points.length; i++) {
		var newPos = Freedo.Cartesian3.fromDegrees(this._points[i][0], this._points[i][1], this._points[i][2] + this._dOffsetH);
		pts.push({
			position: newPos
		});
	}

	this._ptsSmoothed = this.smoothPolyline(pts);

	this.showPath();

	this._totalLen = Freedo.FdMisc.FdGeometric.calculateLength(this._ptsSmoothed);
	this._kb.setFocus();
}

/**
 * 平滑处理
 */
FdPathAnimator.prototype.getTotalLength = function() {
	return this._totalLen;
}

/**
 * 设置路径点数组
 * @param {Array} points 经纬度高度格式的点组
 */
FdPathAnimator.prototype.setPoints = function(points) {
	this._points = points;
	this.smoothPath();
}

/**
 * 设置是否循环播放
 * @param {Boolean} loop true为循环播放
 */
FdPathAnimator.prototype.setLoop = function(loop) {
	this._loop = loop;
}

/**
 * 注册事件捕捉
 * @param {FdPathAnimator~FdPathAnimatorCallback} listener 当有事件触发时被执行的函数。
 * @param {Object} [scope] listener函数执行时的绑定的对象。
 * @returns {Freedo.Event~RemoveCallback} 返回一个函数，调用该函数可以取消监听。
 */
FdPathAnimator.prototype.on = function(listener, scope) {
	return this._eventHelper.add(this._event, listener, scope);
}

/**
 * @callback FdPathAnimator~FdPathAnimatorCallback
 * @param {String} eventType 事事件类型有：Finished,StatusChanged,PathStatusChanged
 * @param {Object} eventArg 事件参数中可以得到相关信息
 */

FdPathAnimator.prototype.callbackCenter = function(time, result) {
	result = this._center;
	return result;
}

/**
 * 跳转到两个点中间距离起点指定距离的地方
 */
FdPathAnimator.prototype.gotoSubByLength = function(i, subLen, totalLen) {
	var preHeading = 0;
	var nextHeading = 0;

	var heading = 0;
	var pitch = 0;

	preHeading = this.calcHeading(i);
	if(i == this._ptsSmoothed.length - 2)
		nextHeading = preHeading;
	else
		nextHeading = this.calcHeading(i + 1);

	var heading = preHeading + subLen / totalLen * (nextHeading - preHeading);

	if(preHeading + 180 < nextHeading) {
		//跨越0°位置，从右到左
		var angle = 360 - nextHeading + preHeading;
		if(subLen / totalLen < preHeading / angle) {
			heading = preHeading - (subLen / totalLen) / (preHeading / angle) * preHeading
			if(heading < 0)
				heading += 360;
		} else {
			heading = 360 - (subLen / totalLen - preHeading / angle) / ((360 - nextHeading) / angle) * (360 - nextHeading);
		}
	}

	if(preHeading - 180 > nextHeading) {
		//跨越0°位置，从左到右
		var angle = 360 - preHeading + nextHeading;
		if(subLen / totalLen < (360 - preHeading) / angle) {
			heading = preHeading + (subLen / totalLen) / ((360 - preHeading) / angle) * (360 - preHeading);
			if(heading > 360)
				heading -= 360;
		} else {
			heading = (subLen / totalLen - (360 - preHeading) / angle) / (nextHeading / angle) * nextHeading;
		}
	}

	var position = {};
	position.x = this._ptsSmoothed[i].x + subLen / totalLen * (this._ptsSmoothed[i + 1].x - this._ptsSmoothed[i].x);
	position.y = this._ptsSmoothed[i].y + subLen / totalLen * (this._ptsSmoothed[i + 1].y - this._ptsSmoothed[i].y);
	position.z = this._ptsSmoothed[i].z + subLen / totalLen * (this._ptsSmoothed[i + 1].z - this._ptsSmoothed[i].z);

	var pt = FreeDo.Cartographic.fromCartesian(position);
	var ptLon = FreeDo.Math.toDegrees(pt.longitude);
	var ptLat = FreeDo.Math.toDegrees(pt.latitude);

	var center = Freedo.Cartesian3.fromDegrees(ptLon, ptLat, pt.height);
	this._center = center;

	if(!this._centerPt) {
		var centerCBP = new Freedo.CallbackProperty(this.callbackCenter.bind(this), false);
		this._centerPt = this._viewer.entities.add({
			position: centerCBP,
			point: {
				color: Freedo.Color.GREEN,
				pixelSize: 10,
				//保证不被遮挡
				disableDepthTestDistance: Number.POSITIVE_INFINITY,
				show: false
			}
		});
	}

	heading = Freedo.Math.toRadians(heading + this._dHeading);
	pitch = Freedo.Math.toRadians(this._dPitch);

    if(this._dHeight < 1)
        this._dHeight = 100;
	this._viewer.camera.lookAt(center, new Freedo.HeadingPitchRange(heading, pitch, this._dHeight));
	this._viewer.scene.requestRender();
}

/**
 * 跳转到两个点中间距离起点指定距离的地方
 */
FdPathAnimator.prototype.gotoSubByLengthUsePathViewMode = function(i, subLen, totalLen) {
	var position = {};
	position.x = this._ptsSmoothed[i].x + subLen / totalLen * (this._ptsSmoothed[i + 1].x - this._ptsSmoothed[i].x);
	position.y = this._ptsSmoothed[i].y + subLen / totalLen * (this._ptsSmoothed[i + 1].y - this._ptsSmoothed[i].y);
	position.z = this._ptsSmoothed[i].z + subLen / totalLen * (this._ptsSmoothed[i + 1].z - this._ptsSmoothed[i].z);

	var pt = FreeDo.Cartographic.fromCartesian(position);
	var ptLon = FreeDo.Math.toDegrees(pt.longitude);
	var ptLat = FreeDo.Math.toDegrees(pt.latitude);

	var newPos = Freedo.Cartesian3.fromDegrees(ptLon, ptLat, pt.height + this._dHeight);

	var direction = FreeDo.Cartesian3.subtract(this._ptsSmoothed[i + 1], this._ptsSmoothed[i], new FreeDo.Cartesian3());
	var normal = Freedo.Cartesian3.normalize(direction, new Freedo.Cartesian3());
	var up = Freedo.Cartesian3.normalize(newPos, new Freedo.Cartesian3());
	this._viewer.camera.setView({
		destination: newPos,
		orientation: {
			direction: normal,
			up: up
		}
	});
	newPos = null;
	direction = null;
	up = null;
}

/**
 * 跳转到整条路径中距离起点指定距离的地方
 */
FdPathAnimator.prototype.gotoByLength = function(passedLen) {
	var preLen = 0;
	var len = 0;
	for(var i = 0; i < this._ptsSmoothed.length - 1; i++) {
		len = FreeDo.Cartesian3.distance(this._ptsSmoothed[i], this._ptsSmoothed[i + 1]);
		if(passedLen > preLen && passedLen < preLen + len) {
			if(this._pathViewMode)
				this.gotoSubByLengthUsePathViewMode(i, passedLen - preLen, len);
			else
				this.gotoSubByLength(i, passedLen - preLen, len);
		    break;
		}
		preLen += len;
	}
}

/**
 * 定时执行
 */
FdPathAnimator.prototype.tick = function() {
	var ts = new Date().getTime();
	if(this._bStop) {
		this._lastTS = 0;
		this._timeUsed = 0;
		return;
	}
	if(!this._bStop && !this._bPause && Freedo.defined(ts) && (this._bPauseMannul == 0 || this._bPauseMannul == 2)) {
		if(this._lastTS == 0) {
			this._lastTS = ts;
		} else {
			//累计用时
			this._timeUsed += ts - this._lastTS;

			//应该走完的距离
			this._passedLen = this._timeUsed / 1000 * this._dSpeed;
			
			if(this._passedLen < this._totalLen){
				this.gotoByLength(this._passedLen);
		    }
			else {
				//重新播放
				this.stop();
				this._event.raiseEvent('Finished', {
					status: 'Finished'
				});
				if(this._loop)
					this.start();
			}
		}
	}

	this._lastTS = ts;
}

/**
 * 按键定时执行
 */
FdPathAnimator.prototype.kbtick = function(len) {
	this._timeUsed += (33) * len / Math.abs(len);;

	this._passedLen += len;

	if(this._passedLen <= 0) {
		this._passedLen = 0;
		this._timeUsed = 0;
		console.log('重新播放:' + this._passedLen);
	}

	if(this._passedLen < this._totalLen)
		this.gotoByLength(this._passedLen);
	else {
		this._passedLen = 0;
		this._timeUsed = 0;
	}
}

/**
 * 删除漫游路径
 */
FdPathAnimator.prototype.clear = function() {
	for(var key in this._lines) {
		this._viewer.entities.remove(this._lines[key]);
	}

	if(this._pathPL) {
		this._viewer.entities.remove(this._pathPL);
		this._pathPL = undefined;
	}

	for(var key in this._aTmpPoint) {
		this._viewer.entities.remove(this._aTmpPoint[key]);
	}
	this._aTmpPoint = [];

	if(this._centerPt) {
		this._viewer.entities.remove(this._centerPt);
		this._centerPt = undefined;
	}
	this._viewer.scene.requestRender();
}

/**
 * 重置
 */
FdPathAnimator.prototype.reset = function() {
	this.clear();
	this._bStop = true;
	this._points = undefined;
}

/**
 * 销毁所有资源
 */
FdPathAnimator.prototype.dispose = function() {
	this.reset();
}

export {
	FdPathAnimator
};