/**
 * 地面挖洞功能
 *
 * @alias FdTools.FdEraseGroundManager
 * @param {Freedo.Viewer} viewer 视景器
 * @class
 *
 * @example
 * var container = document.getElementById('freedoContainer');
 * 
 * var earseGroudManager = new Freedo.FdTools.FdEraseGroundManager(viewer);
 * 
 * var points = [
 *     new Freedo.Cartesian3.fromRadians(2.0303325298072714, 0.4873716058277563, 52.69139335005104),
 *     new Freedo.Cartesian3.fromRadians(2.030319963427302, 0.48738285295499395, 55.34687523086545),
 *     new Freedo.Cartesian3.fromRadians(2.03028628513872, 0.4873924498939492, 44.66087555229469),
 *     new Freedo.Cartesian3.fromRadians(2.03026872726754, 0.4873898657279729, 44.236593776935734),
 *     new Freedo.Cartesian3.fromRadians(2.030262094002657, 0.4873739129711501, 45.97863005368587),
 *     new Freedo.Cartesian3.fromRadians(2.030274276720867, 0.4873593189830798, 47.8334224833019),
 *     new Freedo.Cartesian3.fromRadians(2.0303002959620713, 0.4873574933398821, 50.549889179969426),
 *     new Freedo.Cartesian3.fromRadians(2.0303325298072714, 0.4873716058277563, 52.69139335005104)
 *  ];
 * 
 * eraseGroudManager.eraseGroudByPointsArray(points, [0.0, 0.0, 0.0, 1.0]);
 * 
 */
class FdEraseGroundManager {

    constructor(viewer) {
        this._viewer = viewer;
        this._scene = this._viewer.scene;

        // 辅助变量
        this._egs = [];

        //this.oldV = undefined;
        //this._holeColor = [0.0, 0.0, 0.0, 1.0];

        this._event = new Freedo.Event();
        this._eventHelper = new Freedo.EventHelper();

        this._options = {};
    }

    /**
     * 地形挖洞选项
     * @type {Object}
     * 
     * @example
     * 
     * var eraseGroudOptions = eraseGroudManager.eraseGroudManager ;
     * for(var key in eraseGroudOptions){
     *      var eraseGroudOption = eraseGroudOptions[key] ;
     *      var points = eraseGroudOption.points ; //洞的控制点，世界坐标 {Freedo.Cartesian3[]}
     *      var color = eraseGroudOption.color ; //挖的洞的颜色 {Number[]} 如 [157 / 255, 226 / 255, 53 / 255]
     * }
     * 
     */
    get eraseGroudOptions() {
        return this._options;
    }

    // /**
    //  * 地形挖洞颜色
    //  * @memberof FdEraseGroundManager.prototype
    //  * @type {Number[]}
    //  * 
    //  */
    // get holeColor() {
    //     return this._holeColor;
    // }

    // /**
    //  * 地形挖洞颜色
    //  * @memberof FdEraseGroundManager.prototype
    //  * @type {Number[]}
    //  * 
    //  * @example
    //  * 
    //  * eraseGroudManager.holeColor = [157 / 255, 226 / 255, 53 / 255,1.0] ;
    //  * 
    //  */
    // set holeColor(value) {
    //     this._holeColor = value;
    // }

    /**
     * 直接输入点坐标做地形挖洞
     * 
     * @param {Freedo.Cartesian3[]} points 洞的控制点，世界坐标
     * @param {Number[]} [color] 挖的洞的颜色，如 [157 / 255, 226 / 255, 53 / 255]
     */
    eraseGroudByPointsArray(points, color) {
        this._options['id_' + this._egs.length] = {
            points: points,
            color: Freedo.clone(color, true)
        };
        var pri = new Freedo.GroundErasePrimitive({
            geometryInstances: new Freedo.GeometryInstance({
                geometry: new Freedo.PolygonGeometry({
                    polygonHierarchy: new Freedo.PolygonHierarchy(points)
                }),
                attributes: {
                    color: Freedo.ColorGeometryInstanceAttribute.fromColor(new Freedo.Color(color[0], color[1], color[2], 1)) // 洞的颜色 (0, 0, 0)表示黑色
                }
            }),
            classificationType: Freedo.ClassificationType.TERRAIN,
            debugShowShadowVolume: false
        });

        var er = this._scene.groundPrimitives.add(pri);
        this._egs.push(er);

        this._event.raiseEvent('dataAdded', {
            points: points,
            color: color,
            erasePrimitive: pri
        });

        this._scene.requestRender();
    }

    /**
     * 事件类型有：dataAdded,dataRemoved
     * @param {FdTools.FdEraseGroundManager~FdEraseGroundManagerCallback} listener 当有事件触发时被执行的函数。
     * @param {Object} [scope] listener函数执行时的绑定的对象。
     * @returns {Freedo.Event~RemoveCallback} 返回一个函数，调用该函数可以取消监听。
     */
    on(listener, scope) {
        return this._eventHelper.add(this._event, listener, scope);
    };
    /**
     * @callback FdTools.FdEraseGroundManager~FdEraseGroundManagerCallback
     * @param {String} eventType 事件类型有：dataAdded(添加了一个地形挖洞)，dataRemoved(清空了所有的地形挖洞)
     * @param {Object} eventArg dataAdded事件的参数为刚添加的对象的封装，参考{@link FdMicroApp.FdEraseGroundHandler#getData} ，dataRemoved不带参数
     */

    /**
     * @private
     */
    reset() {
        for (var key in this._egs) {
            this._scene.groundPrimitives.remove(this._egs[key]);
        }

        this._options = {};

        this._event.raiseEvent('dataRemoved', {});
        this._scene.requestRender();
        //this._scene.globe.depthTestAgainstTerrain = this.oldV;
    }

    /**
     * 销毁所有资源
     * 
     */
    dispose() {
        this.reset();
        this._eventHelper.removeAll();
    }
}

export { FdEraseGroundManager }