import {
    FdMisc
} from '../../FdMisc/FdMisc';

import {
    FdPolylineEditor
} from '../../FdMicroApp/Analysis/FdPolylineEditor';
/**
 * BillBoard类
 * @alias FdModel.FdBillboard
 * @class
 *
 * @example
 * var viewer = Freedo.FdApp.createDefaultViewer('freedoContainer');
 * var mm = new Freedo.FdModel.FdModelManager(viewer);
 * //如果option参数中不包含position属性，则会自动激活点选创建功能
 * mm.add('Billboard', {
        position:[longitude, latitude, height],
        height: height,
        width: width,
        divID: 'divTest'//对应的div层的ID
    });
    <div id="divTest" style="z-index: 999; width: 400px; height: 100px; font-size: x-large;">
        <p style="background-color: blue; margin: 0px; margin-top: 10px; text-align: center;">500kV#1主变A相避雷器
        </p>
        <p style="margin: 2px; text-align: center; font-size: x-large;">
            <span style=" color: green; margin-right: 100px;">●</span>
            <span style=" color: orangered; margin-right: 100px;">●</span>
            <span style=" color: burlywood;">●</span>
        </p>
        <p style="margin: 2px; text-align: center; font-size: x-large;">
            <span style=" color: green; margin-right: 70px;">状态</span>
            <span style=" color: orangered; margin-right: 70px;">风险</span>
            <span style=" color: burlywood;">监测</span>
        </p>
    </div>
 */
class FdBillboard {
    constructor(viewer, id, option) {
        this._viewer = viewer;
        this._id = id;
        this._data = option;
        this._board = undefined;
        this._arrow = undefined;
        this._canvas = undefined;
        this._ple = new FdPolylineEditor(viewer);

        this._event = new Freedo.Event();
        this._eventHelper = new Freedo.EventHelper();

        this.create();
    }

    /**
     * 获取实体的属性数据
     */
    get option() {
        return Freedo.clone(this.getData(), true);
    }
}

/**
 * 创建
 * @private
 */
FdBillboard.prototype.create = function() {
    if(!Freedo.defined(this._data)) {
        console.error("FdBillboard.prototype.create：参数无效");
        return;
    }
    if(!Freedo.defined(this._id)) {
        this._id = Freedo.createGuid();
    }
    if(!Freedo.defined(this._data.rotate)) {
        this._data.rotate = 0;
    }
    if(!this._data.position) {
        this._ple.run();
        this.addLsnr();
    } else {
        this.build();
    }
}

FdBillboard.prototype.createAreaPrimitive = function(pts, option) {
    option = option || {};
    var N;
    var r = new Freedo.Cartesian3();
    var v1 = Freedo.Cartesian3.subtract(pts[1], pts[0], new Freedo.Cartesian3());
    var v2 = Freedo.Cartesian3.subtract(pts[2], pts[1], new Freedo.Cartesian3());
    N = Freedo.Cartesian3.cross(v1, v2, new Freedo.Cartesian3());
    N = Freedo.Cartesian3.normalize(N, new Freedo.Cartesian3());

    var angle = Freedo.Cartesian3.angleBetween(N, Freedo.Cartesian3.UNIT_Z);
    var axis = Freedo.Cartesian3.cross(N, Freedo.Cartesian3.UNIT_Z, new Freedo.Cartesian3());
    var quat = Freedo.Quaternion.fromAxisAngle(axis, angle, new Freedo.Quaternion());
    var rMatrix = Freedo.Matrix4.fromTranslationQuaternionRotationScale(new Freedo.Cartesian3(), quat, new Freedo.Cartesian3(1, 1, 1));
    var newN = Freedo.Matrix4.multiplyByPointAsVector(rMatrix, N, new Freedo.Cartesian3());

    var newPts = [];
    for(var k in pts) {
        var newPt = Freedo.Matrix4.multiplyByPointAsVector(rMatrix, pts[k], new Freedo.Cartesian3());
        newPts.push(newPt);
    }

    var geo;
    if( /*viewer.scene.globe*/ false) {

    } else {
        var positions = [];
        for(var i = 0; i < pts.length; i++) {
            positions.push(pts[i].x * 1);
            positions.push(pts[i].y * 1);
            positions.push(pts[i].z * 1);
        }

        var index = Freedo.PolygonPipeline.triangulate(newPts);

        var positionsFloat64 = new Float64Array(positions);
        geo = new Freedo.Geometry({
            attributes: {
                position: new Freedo.GeometryAttribute({
                    componentDatatype: Freedo.ComponentDatatype.DOUBLE,
                    componentsPerAttribute: 3,
                    values: positionsFloat64
                })
            },
            indices: new Uint16Array(index),
            primitiveType: Freedo.PrimitiveType.TRIANGLES,
            boundingSphere: Freedo.BoundingSphere.fromVertices(positionsFloat64)
        });
    }

    var color;
    if(!option.color)
        color = new Freedo.Color(1.0, 1.0, 1.0, 1.0);
    return new Freedo.Primitive({
        geometryInstances: new Freedo.GeometryInstance({
            geometry: geo,
            attributes: {
                color: Freedo.ColorGeometryInstanceAttribute.fromColor(color)
            }
        }),
        appearance: new Freedo.PerInstanceColorAppearance({
            flat: true,
            translucent: true
        }),
        asynchronous: false
    });
}

FdBillboard.prototype.callbackM = function(time, result) {
    if(this._canvas) result = this._canvas;
    var ran = Math.random();
    if(ran > 0.9) {
        this._viewer.scene.requestRender();
    }
    return result;
}

FdBillboard.prototype.refresh = function() {
    this._viewer.scene.requestRender();
}

/**
 * div内容更新以后，需要调用update来更新
 */
FdBillboard.prototype.update = function() {
    var divID = this._data.divID;
    var that = this;
    html2canvas(document.getElementById(divID), {
        backgroundColor: '#ffffff'
    }).then(function(canvas) {
        that._canvas = canvas;
    });
}

/**
 * 构建实体
 * @private
 */
FdBillboard.prototype.build = function() {
    var option = this._data;
    var position = option.position;

    var width = option.width;
    var height = option.height;
    var offHeight = position[2];

    this.update();

    if(!Freedo.defined(width) || !Freedo.defined(height)) {
        console.error('FdBillboard.prototype.build：width或height参数无效')
        return;
    }

    var pos = FdMisc.FdGeometric.tc(position);
    var posGra = Freedo.Cartographic.fromCartesian(pos);
    var rio = FdMisc.FdGeometric.getLongAndLatRatio(posGra);
    this._data.positionC = Freedo.clone(pos, true);

    var mCBP = new Freedo.CallbackProperty(this.callbackM.bind(this), false);
    
    var heading = Freedo.Math.toRadians(option.rotate);
    
    this._board = viewer.entities.add({
        name: 'boardEntity',
        wall: {
            positions: Freedo.Cartesian3.fromDegreesArrayHeights([
                position[0] - Freedo.Math.toDegrees(width * rio[0] / 2) * Math.cos(heading), position[1] - Freedo.Math.toDegrees(width * rio[1] / 2) * Math.sin(heading), height + offHeight,
                position[0] + Freedo.Math.toDegrees(width * rio[0] / 2)* Math.cos(heading), position[1] + Freedo.Math.toDegrees(width * rio[1] / 2) * Math.sin(heading), height + offHeight
            ]),
            material: new Freedo.ImageMaterialProperty({
                image: mCBP
            }),
            minimumHeights: [offHeight + width / 10.0, offHeight + width / 10.0],
            outline: true
        }
    });

    var arrowPolygonPts = [];
    arrowPolygonPts = Freedo.Cartesian3.fromDegreesArrayHeights([
        position[0], position[1], offHeight,
        position[0] + Freedo.Math.toDegrees(width * rio[0] / 10.0) * Math.cos(heading), position[1] + Freedo.Math.toDegrees(width * rio[1] / 10.0) * Math.sin(heading), width / 10.0 + offHeight,
        position[0] - Freedo.Math.toDegrees(width * rio[0] / 10.0) * Math.cos(heading), position[1] - Freedo.Math.toDegrees(width * rio[1] / 10.0) * Math.sin(heading), width / 10.0 + offHeight,
        position[0], position[1], offHeight
    ])
    this._arrow = viewer.scene.primitives.add(this.createAreaPrimitive(arrowPolygonPts));
    this._viewer.scene.requestRender();
}

FdBillboard.prototype.addLsnr = function() {
    if(this._lsner)
        this._lsner();
    var that = this;
    this._lsner = this._ple.on(function(eventType, param) {
        console.log(eventType);
        if(eventType == 'cancel') {
            this.cancel();
        }
        if(eventType == 'PointAdded') {
            that._data.position = Freedo.clone(param.position, true);
            that._ple.cancel();
            that._lsner();
            that.build();
        }
        if(eventType == 'Transformed') {
            var pg = param.positionG;
            var position = Freedo.Cartesian3.fromRadians(pg.longitude, pg.latitude, pg.height);
            that._data.position = Freedo.FdMisc.FdGeometric.tg(position);
            that.clear();
            that.build();
        }
    });
}

/**
 * 注册事件捕捉
 * @param {FdModel.FdBillboard~FdBillboardCallback} listener 当有事件触发时被执行的函数。
 * @param {Object} [scope] listener函数执行时的绑定的对象。
 * @returns {Freedo.Event~RemoveCallback} 返回一个函数，调用该函数可以取消监听。
 */
FdBillboard.prototype.on = function(listener, scope) {
    return this._eventHelper.add(this._event, listener, scope);
}

/**
 * @callback FdModel.FdBillboard~FdBillboardCallback
 * @param {String} eventType 事件类型有：Start,Cancel
 * @param {Object} eventArg Start/Cancel无返回值
 */

/**
 * 开始进行节点编辑
 */
FdBillboard.prototype.startEdit = function() {
    console.info('开始编辑');
    this.addLsnr();
    this._ple.startEdit([this._data.positionC]);
}

/**
 * 结束编辑
 */
FdBillboard.prototype.endEdit = function() {
    this._ple.endEdit();
}

/**
 * 清理
 */
FdBillboard.prototype.clear = function() {
    this._viewer.entities.remove(this._board);
    this._viewer.scene.primitives.remove(this._arrow);
    this._board = undefined;
    this._arrow = undefined;
    this._viewer.scene.requestRender();
}

/**
 * 删除
 */
FdBillboard.prototype.remove = function() {
    this.clear();
    this._data = undefined;
    this._viewer.scene.requestRender();
}

/**
 * 显示
 */
FdBillboard.prototype.show = function() {
    if(this._board) {
        this._board.show = true;
    }
    if(this._arrow) {
        this._arrow.show = true;
    }
}

/**
 * 隐藏
 */
FdBillboard.prototype.hide = function() {
    if(this._board) {
        this._board.show = false;
    }
    if(this._arrow) {
        this._arrow.show = false;
    }
}

/**
 * 获取数据
 * @returns {Object} 返回值格式：
 * [{
        id: GUID,
        divID: 'divid'
        position: [Cartesian3],
        width: 宽度,
        height: 高度
   }...] 
 */
FdBillboard.prototype.getData = function() {
    if(!this._data)
        return undefined;
    var d = Freedo.clone(this._data, true);
    return {
        id: this._id,
        type: 'Billboard',
        option: d
    }
}

/**
 * 重置
 */
FdBillboard.prototype.reset = function() {
    if(this._lsner)
        this._lsner();
    this._lsner = undefined;
    this._ple.cancel();
}

/**
 * 销毁所有资源
 */
FdBillboard.prototype.dispose = function() {
    this.reset();
    this.remove();
}

export {
    FdBillboard
};