/*
一、左键点击拖拽：rotate
1.点击在模型上，以拾取到的空间点为参考点
2.点击在空白处，以视口中心点拾取到模型上的点，或指定的旋转参考点，或世界坐标系原点为参考点
左右拖动，绕过参考点并垂直于地面的轴旋转
上下拖动，绕过参考点并垂直于视线方向的水平轴旋转

二、右键、中键点击拖动：pan
在垂直于当前视线的平面内移动（保持参考点在光标下）
当光标拾取到模型时，以拾取点为参考点
当光标未拾取到模型时，以光标在过世界坐标系原点的平面上的拾取点为参考点

三、滚轮滚动（当前光标拾取到的点为参考点）：zoom
1.光标在模型上时，参考点选择光标拾取点
2.光标不在模型上，以视口中心点拾取到模型上的点，或指定的缩放参考点，或世界坐标系原点为参考点
向上滚动，保持视线方向不变，拉近与参考点的距离
向下滚动，保持视线方向不变，拉远与参考点的距离


注：
不能使用Camera.lookAt
使用模型的minimumPixelSize将导致zoom目标不准

*/
var Cesium = Freedo;

function ScreenSpaceCameraController(scene) {
    if (!Cesium.defined(scene)) {
        throw new DeveloperError('scene is required.');
    }
    
    this.enableCollisionDetection = false;

    this.minimumZoomDistance = 1.0;
    this.maximumZoomDistance = Number.POSITIVE_INFINITY;

    this.maximumMovementRatio = 0.1;

    this.enableInputs = true;
    this.enableRotate = true;
    this.enablePan = true;
    this.enableZoom = true;

    this.inertiaRotate = 0.5;
    this.inertiaPan = 0.5;
    this.inertiaZoom = 0.5;

    this.defaultZoomReference = undefined;
    this.defaultRotateReference = undefined;

    this.rotateEventTypes = Cesium.CameraEventType.LEFT_DRAG;
    this.panEventTypes = [Cesium.CameraEventType.RIGHT_DRAG, Cesium.CameraEventType.MIDDLE_DRAG];
    this.zoomEventTypes = [Cesium.CameraEventType.WHEEL, Cesium.CameraEventType.PINCH];

    this._scene = scene;
    this._aggregator = new Cesium.CameraEventAggregator(scene.canvas);

    this._zoomFactor = 5;
    this._rotateFactor = 0.01;
}

function decay(time, coefficient) {
    if (time < 0) {
        return 0.0;
    }

    var tau = (1.0 - coefficient) * 25.0;
    return Math.exp(-tau * time);
}

function sameMousePosition(movement) {
    return Cesium.Cartesian2.equalsEpsilon(movement.startPosition, movement.endPosition, Cesium.Math.EPSILON14);
}

var inertiaMaxClickTimeThreshold = 0.4;

function maintainInertia(aggregator, type, modifier, decayCoef, action, object, lastMovementName) {
    var movementState = object[lastMovementName];
    if (!Cesium.defined(movementState)) {
        movementState = object[lastMovementName] = {
            startPosition : new Cesium.Cartesian2(),
            endPosition : new Cesium.Cartesian2(),
            motion : new Cesium.Cartesian2(),
            active : false
        };
    }

    var ts = aggregator.getButtonPressTime(type, modifier);
    var tr = aggregator.getButtonReleaseTime(type, modifier);

    var threshold = ts && tr && ((tr.getTime() - ts.getTime()) / 1000.0);
    var now = new Date();
    var fromNow = tr && ((now.getTime() - tr.getTime()) / 1000.0);

    if (ts && tr && threshold < inertiaMaxClickTimeThreshold) {
        var d = decay(fromNow, decayCoef);

        if (!movementState.active) {
            var lastMovement = aggregator.getLastMovement(type, modifier);
            if (!Cesium.defined(lastMovement) || sameMousePosition(lastMovement)) {
                return;
            }

            movementState.motion.x = (lastMovement.endPosition.x - lastMovement.startPosition.x) * 0.5;
            movementState.motion.y = (lastMovement.endPosition.y - lastMovement.startPosition.y) * 0.5;

            movementState.startPosition = Cesium.Cartesian2.clone(lastMovement.startPosition, movementState.startPosition);

            movementState.endPosition = Cesium.Cartesian2.multiplyByScalar(movementState.motion, d, movementState.endPosition);
            movementState.endPosition = Cesium.Cartesian2.add(movementState.startPosition, movementState.endPosition, movementState.endPosition);

            movementState.active = true;
        } else {
            movementState.startPosition = Cesium.Cartesian2.clone(movementState.endPosition, movementState.startPosition);

            movementState.endPosition = Cesium.Cartesian2.multiplyByScalar(movementState.motion, d, movementState.endPosition);
            movementState.endPosition = Cesium.Cartesian2.add(movementState.startPosition, movementState.endPosition, movementState.endPosition);

            movementState.motion = Cesium.Cartesian2.clone(Cesium.Cartesian2.ZERO, movementState.motion);
        }

        // If value from the decreasing exponential function is close to zero,
        // the end coordinates may be NaN.
        if (isNaN(movementState.endPosition.x) || isNaN(movementState.endPosition.y) || Cesium.Cartesian2.distance(movementState.startPosition, movementState.endPosition) < 0.5) {
            movementState.active = false;
            return;
        }

        if (!aggregator.isButtonDown(type, modifier)) {
            var startPosition = aggregator.getStartMousePosition(type, modifier);
            action(object, startPosition, movementState);
        }
    } else {
        movementState.active = false;
    }
}

var scratchEventTypeArray = [];

function reactToInput(controller, enabled, eventTypes, action, inertiaConstant, inertiaStateName) {
    if (!Cesium.defined(eventTypes)) {
        return;
    }

    var aggregator = controller._aggregator;

    if (!Cesium.isArray(eventTypes)) {
        scratchEventTypeArray[0] = eventTypes;
        eventTypes = scratchEventTypeArray;
    }

    var length = eventTypes.length;
    for (var i = 0; i < length; ++i) {
        var eventType = eventTypes[i];
        var type = Cesium.defined(eventType.eventType) ? eventType.eventType : eventType;
        var modifier = eventType.modifier;

        var movement = aggregator.isMoving(type, modifier) && aggregator.getMovement(type, modifier);
        var startPosition = aggregator.getStartMousePosition(type, modifier);

        if (controller.enableInputs && enabled) {
            if (movement) {
                action(controller, startPosition, movement);
            } 
            else if (inertiaConstant < 1.0) {
                maintainInertia(aggregator, type, modifier, inertiaConstant, action, controller, inertiaStateName);
            }
        }
    }
}

var windowPositionScratch = new Cesium.Cartesian2();
var mouseIntersectionScratch = new Cesium.Cartesian3();
var targetScratch = new Cesium.Cartesian3();
var directionScratch = new Cesium.Cartesian3();
var negateDirectionScratch = new Cesium.Cartesian3();
var pickRayScratch = new Cesium.Ray();
var planeScratch = new Cesium.Plane(new Cesium.Cartesian3(0,0,1), 1);
var deltaScratch = new Cesium.Cartesian2();

var atScratch = new Cesium.Cartesian3();
var leftScratch = new Cesium.Cartesian3();
var upScratch = new Cesium.Cartesian3();
var positionScratch = new Cesium.Cartesian3();

var lastStartPosition = new Cesium.Cartesian2();
var rotateReferencePosition = new Cesium.Cartesian3();

function getYawAngle(camera) {
    var at = camera.direction;
    var up = camera.up;
    var yaw = 0;
    if (at.z < -0.99999) {
        yaw = Math.atan2(up.y, up.x);
    } else if (at.z > 0.99999) {
        yaw = Math.atan2(-up.y, -up.x);
    } else {
        yaw = Math.atan2(at.y, at.x);
    }
    if (isNaN(yaw)) {
        throw new Cesium.DeveloperError('');
    }
    return yaw;
}

function getPitchAngle(camera) {
    var at = camera.direction;
    var pitch = 0;
    if (at.z < -0.99999) {
        pitch = -Cesium.Math.PI_OVER_TWO;
    } else if (at.z > 0.99999) {
        pitch = Cesium.Math.PI_OVER_TWO;
    } else {
        var d = Math.sqrt(at.x*at.x+at.y*at.y);
        pitch = Math.atan(at.z/d);
    }
    if (isNaN(pitch)) {
        throw new Cesium.DeveloperError('');
    }
    return pitch;
}

function getDirections(yaw, pitch, at, left, up) {
    at.x = Math.cos(pitch) * Math.cos(yaw);
    at.y = Math.cos(pitch) * Math.sin(yaw);
    at.z = Math.sin(pitch);
    Cesium.Cartesian3.normalize(at, at);

    var angle = yaw + Cesium.Math.PI_OVER_TWO;
    left.x = Math.cos(angle);
    left.y = Math.sin(angle);
    left.z = 0;
    Cesium.Cartesian3.normalize(left, left);

    Cesium.Cartesian3.cross(at, left, up);
    Cesium.Cartesian3.normalize(up, up);
}

function rotate3D(controller, startPosition, movement) {
    var scene = controller._scene;
    var camera = scene.camera;
    var canvas = scene.canvas;

    if (sameMousePosition(movement)) {
        return;
    }

    if (!Cesium.Cartesian2.equalsEpsilon(lastStartPosition, startPosition, Cesium.Math.EPSILON14)) {
        Cesium.Cartesian2.clone(startPosition, lastStartPosition);
        var intersection = scene.pickPosition(movement.startPosition, rotateReferencePosition);
        if (!Cesium.defined(intersection)) {
            var windowPosition = windowPositionScratch;
            windowPosition.x = canvas.clientWidth / 2;
            windowPosition.y = canvas.clientHeight / 2;
            intersection = scene.pickPosition(windowPosition, rotateReferencePosition);
            if (!Cesium.defined(intersection)) {
                if (Cesium.defined(controller.defaultRotateReference)) {
                    Cesium.Cartesian3.clone(controller.defaultRotateReference, rotateReferencePosition);
                } else {
                    Cesium.Cartesian3.clone(Cesium.Cartesian3.ZERO, rotateReferencePosition);
                }
            }
        }
    }

    var delta = Cesium.Cartesian2.subtract(movement.endPosition, movement.startPosition, deltaScratch);
    var yawAngleDelta = -delta.x * controller._rotateFactor;
    var pitchAngleDelta = -delta.y * controller._rotateFactor;

    var yawAngle = getYawAngle(camera);
    var pitchAngle = getPitchAngle(camera);

    var newYawAngle = yawAngle + yawAngleDelta;
    var newPitchAngle = pitchAngle + pitchAngleDelta;
    newPitchAngle = Cesium.Math.clamp(newPitchAngle, -Cesium.Math.PI_OVER_TWO, Cesium.Math.PI_OVER_TWO);

    var direction = Cesium.Cartesian3.subtract(camera.position, rotateReferencePosition, directionScratch);
    var distance = Cesium.Cartesian3.magnitude(direction);

    getDirections(yawAngle, pitchAngle, atScratch, leftScratch, upScratch);
    var d1 = Cesium.Cartesian3.dot(direction, atScratch);
    var d2 = Cesium.Cartesian3.dot(direction, leftScratch);
    var d3 = Cesium.Cartesian3.dot(direction, upScratch);

    getDirections(newYawAngle, newPitchAngle, atScratch, leftScratch, upScratch);
    Cesium.Cartesian3.multiplyByScalar(atScratch, d1, targetScratch);
    Cesium.Cartesian3.add(rotateReferencePosition, targetScratch, positionScratch);

    Cesium.Cartesian3.multiplyByScalar(leftScratch, d2, targetScratch);
    Cesium.Cartesian3.add(targetScratch, positionScratch, positionScratch);

    Cesium.Cartesian3.multiplyByScalar(upScratch, d3, targetScratch);
    var position = Cesium.Cartesian3.add(targetScratch, positionScratch, positionScratch);

    Cesium.Cartesian3.clone(position, camera.position);
    Cesium.Cartesian3.normalize(atScratch, camera.direction);
    Cesium.Cartesian3.normalize(upScratch, camera.up);
    Cesium.Cartesian3.cross(atScratch, upScratch, camera.right);
}

function pan3D(controller, startPosition, movement) {
    var scene = controller._scene;
    var camera = scene.camera;
    var canvas = scene.canvas;

    if (sameMousePosition(movement)) {
        return;
    }

    var negateDirection = Cesium.Cartesian3.negate(camera.direction, negateDirectionScratch);
    var intersection = scene.pickPosition(movement.startPosition, mouseIntersectionScratch);
    if (Cesium.defined(intersection)) {
        var plane = Cesium.Plane.fromPointNormal(intersection, negateDirection, planeScratch);
        var ray = camera.getPickRay(movement.endPosition, pickRayScratch);
        var target = Cesium.IntersectionTests.rayPlane(ray, plane, targetScratch);
        if (Cesium.defined(target)) {
            var direction = Cesium.Cartesian3.subtract(intersection, target, directionScratch);
            camera.move(direction, 1);
            return;
        }
    }

    var plane = Cesium.Plane.fromPointNormal(Cesium.Cartesian3.ZERO, negateDirection, planeScratch);
    var ray = camera.getPickRay(movement.startPosition, pickRayScratch);
    intersection = Cesium.IntersectionTests.rayPlane(ray, plane, mouseIntersectionScratch);
    ray = camera.getPickRay(movement.endPosition, pickRayScratch);
    target = Cesium.IntersectionTests.rayPlane(ray, plane, targetScratch);
    if (Cesium.defined(intersection) && Cesium.defined(target)) {
        var direction = Cesium.Cartesian3.subtract(intersection, target, directionScratch);
        camera.move(direction, 1);
        return;
    }
}

function zoom3D(controller, startPosition, movement) {
    if (Cesium.defined(movement.distance)) {
        movement = movement.distance;
    }

    var scene = controller._scene;
    var camera = scene.camera;
    var canvas = scene.canvas;

    var diff = movement.endPosition.y - movement.startPosition.y;
    if (Math.abs(diff) < Cesium.Math.EPSILON14) {
        return;
    }

    var rangeWindowRatio = diff / canvas.clientHeight;
    rangeWindowRatio = Math.min(rangeWindowRatio, controller.maximumMovementRatio);

    var intersection = scene.pickPosition(startPosition, mouseIntersectionScratch);
    if (!Cesium.defined(intersection)) {
        var windowPosition = windowPositionScratch;
        windowPosition.x = canvas.clientWidth / 2;
        windowPosition.y = canvas.clientHeight / 2;
        intersection = scene.pickPosition(windowPosition, mouseIntersectionScratch);
        if (!Cesium.defined(intersection)) {
            if (Cesium.defined(controller.defaultZoomReference)) {
                intersection = Cesium.Cartesian3.clone(controller.defaultZoomReference, mouseIntersectionScratch);
            } else {
                intersection = Cesium.Cartesian3.clone(Cesium.Cartesian3.ZERO, mouseIntersectionScratch);
            }
        }
    }

    var direction = Cesium.Cartesian3.subtract(intersection, camera.position, directionScratch);
    var distance = Cesium.Cartesian3.magnitude(direction);
    direction = Cesium.Cartesian3.normalize(direction, directionScratch);

    distance = distance * rangeWindowRatio * controller._zoomFactor;
    camera.move(direction, distance);
}

function update3D(controller) {
    reactToInput(controller, controller.enableRotate, controller.rotateEventTypes, rotate3D, controller.inertiaRotate, '_lastInertiaSpinMovement');
    reactToInput(controller, controller.enablePan, controller.panEventTypes, pan3D, controller.inertiaPan, '_lastInertiaPanMovement');
    reactToInput(controller, controller.enableZoom, controller.zoomEventTypes, zoom3D, controller.inertiaZoom, '_lastInertiaZoomMovement');
}

ScreenSpaceCameraController.prototype.update = function() {
    var scene = this._scene;
    var mode = scene.mode;
    if (mode === Cesium.SceneMode.SCENE3D) {
        update3D(this);
    }

    this._aggregator.reset();
};

ScreenSpaceCameraController.prototype.isDestroyed = function() {
    return false;
};

ScreenSpaceCameraController.prototype.destroy = function() {
    this._tweens.removeAll();
    this._aggregator = this._aggregator && this._aggregator.destroy();
    return destroyObject(this);
};

var FdScreenSpaceCameraController = ScreenSpaceCameraController;

export {
    FdScreenSpaceCameraController
};