import {
    FdClipPlaneEditor
} from './FdClipPlaneEditor';

import {
    FdMisc
} from '../FdMisc/FdMisc';

/**
 * 裁剪功能类
 * 支持调整裁剪面的位置和姿态
 * @alias FdMicroApp.FdClipPlane
 * @class
 * @example
 * var viewer = Freedo.FdApp.createDefaultViewer('freedoContainer');
 * var tileset = viewer.scene.primitives.add(new Freedo.FreedoPModelset({
 *  url : '../../sampleData/文化室2018z'
 * }));
 * tileset.readyPromise.then(function(tileset) {
 *  viewer.camera.viewBoundingSphere(tileset.boundingSphere, new Freedo.HeadingPitchRange(0, -0.5, 0));
 *  var cp = new Freedo.FdMicroApp.FdClipPlane(viewer, tileset);
 *  cp.startClip('CLIPXY');
 * }); 
 */
class FdClipPlane {
    constructor(viewer, tileset, isPlaneMode) {

        this._viewer = viewer;
        this._tileset = tileset;

        this._event = new Freedo.Event();
        this._eventHelper = new Freedo.EventHelper();

        this._mani = new FdClipPlaneEditor(viewer, isPlaneMode);

        this._selectedAxis = '';
        this._transType = 'CLIP';
        this._mouseDown12Arg = {};
        this._axisZoom = 1;

        this._isPlaneMode = isPlaneMode;

        this._t12 = {
            TMoveX: 0,
            TMoveY: 0,
            TMoveZ: 0,
            THeading: 0,
            TPitch: 0,
            TRoll: 0,
            TScaleX: 1,
            TScaleY: 1,
            TScaleZ: 1,
            TBaseX: 0,
            TBaseY: 0,
            TBaseZ: 0
        }

        this._q4 = new Freedo.Quaternion(0, 0, 0, 1);
        this._lsner = undefined;
    }

    get clipOptions() {
        return {
            type: 'CLIPXY',
            Q4: this._q4,
            TMoveX: this._t12.TMoveX,
            TMoveY: this._t12.TMoveY,
            TMoveZ: this._t12.TMoveZ,
            TPitch: this._t12.TPitch,
            TRoll: this._t12.TRoll
        }
    }

    set clipOptions(v) {
        if(!Freedo.defined(v)) {
            return;
        }

        if(Freedo.defined(v.type)) {
            this.start(v.type, v);
        }
    }
}

/**
 * 设置需要进行操作的场景ID，开始前需先进行设置
 * @private
 * @param {Array} aSceneObjectId 场景ID数组。
 */
FdClipPlane.prototype.setSceneObjectIDs = function(aSceneObjectId) {
    this._aSceneObjectId = FreeDo.clone(aSceneObjectId, true);
}

FdClipPlane.prototype.calcSceneBS = function() {
    //this._sceneSphere = this._tileset.boundingSphere;
    var boundingSpheres = [];
    for(var key in this._tileset) {
        var tileset = this._tileset[key];
        var bs = tileset.boundingSphere;
        boundingSpheres.push(bs);
    }
    this._sceneSphere = FreeDo.BoundingSphere.fromBoundingSpheres(boundingSpheres, new FreeDo.BoundingSphere());
}

/**
 * 返回当前裁剪面类型
 * @returns {String} 返回CLIPYZ、CLIPXY、CLIPXYNEG、CLIPXZ分别对应不同的初始操作面。
 */
FdClipPlane.prototype.getType = function() {
    return this._transType;
}

/**
 * 激活场景裁剪
 * @param {String} type 类型，CLIPYZ、CLIPXY、CLIPXYNEG、CLIPXZ分别对应不同的初始操作面。
 * @param {Object} data 初始化数据
 */
FdClipPlane.prototype.start = function(type, data) {
    if(!type)
        type = 'CLIPXY';
    this.startClip(type, data);
}

/**
 * 激活场景裁剪
 * @param {String} type 类型，CLIPYZ、CLIPXY、CLIPXYNEG、CLIPXZ分别对应不同的初始操作面。
 * @private
 */
FdClipPlane.prototype.startClip = function(type, data) {
    this._transType = type;
    this._bInClip = true;

    this.calcSceneBS();

    var that = this;

    if(this._lsner) {
        this._lsner();
        this._lsner = undefined;
        this._q4 = new Freedo.Quaternion(0, 0, 0, 1);
    }

    this._lsner = this._mani.on(function(eventType, param) {
        if(eventType == 'Transform') {
            that.onTransformStart();
        }

        if(eventType == 'Transformed') {
            that.onTransformEnd();
        }

        if(eventType == 'Transforming') {
            that.onTransforming(param);
        }
    });

    this._mani.startClip('CLIPXY');

    if(type == 'CLIPXY' || type == 'CLIPXYNEG' || type == 'CLIPYZ' || type == 'CLIPXZ')
        this._selectedAxis = 'Z_AXIS';
    else {
        console.warn('不支持此种类型：' + type)
    }

    if(!Freedo.defined(data))
        this.reset12Args();
    else {
        this._t12.TMoveX = data.TMoveX;
        this._t12.TMoveY = data.TMoveY;
        this._t12.TMoveZ = data.TMoveZ;
        //this._t12.TPitch = data.TPitch;
        //this._t12.TRoll = data.TRoll;
        this._q4 = Freedo.clone(data.Q4);
    }

    if(type == 'CLIPYZ') {
        this._t12.TPitch = 90;
    }
    if(type == 'CLIPXZ') {
        this._t12.TRoll = 90;
    }
    if(type == 'CLIPXYNEG') {
        this._t12.TPitch = 180;
    }

    this.onTransformStart();

    var t12 = Freedo.clone(this._mouseDown12Arg, true);

    this.updatePlane(t12);

    //这里不要轻易修改
    if(type == 'CLIPYZ' || type == 'CLIPXZ' || type == 'CLIPXYNEG') {
        var longi, lat, height;
        var posGraphic = Freedo.Cartographic.fromCartesian(this._sceneSphere.center);
        if(posGraphic) {
            longi = posGraphic.longitude;
            lat = posGraphic.latitude;
            height = posGraphic.height;
        }

        if(this._isPlaneMode) {
            longi = this._sceneSphere.center.x;
            lat = this._sceneSphere.center.y;
            height = this._sceneSphere.center.z;
        }

        if(type == 'CLIPXYNEG' || type == 'CLIPYZ')
            this._selectedAxis = 'XZ_CIRCLE';
        if(type == 'CLIPXZ')
            this._selectedAxis = 'YZ_CIRCLE';

        var m3 = new Freedo.Matrix3();
        var q4 = new Freedo.Quaternion();
        var m4 = this.getMatrix(this._q4, this._t12.TMoveX, this._t12.TMoveY, this._t12.TMoveZ,
            this._t12.THeading, this._t12.TPitch, this._t12.TRoll, longi, lat, height, m3, q4);
        Freedo.Quaternion.multiply(this._q4, q4, this._q4);
    }

    //这里不要轻易修改
    if(type == 'CLIPYZ') {
        this._t12.TPitch = 0;
    }
    if(type == 'CLIPXZ') {
        this._t12.TRoll = 0;
    }
    if(type == 'CLIPXYNEG') {
        this._t12.TPitch = 0;
    }

    this._mani.show(this._sceneSphere);

    this._event.raiseEvent('Start', {});
}

FdClipPlane.prototype.getMatrix = function(TQ4, TMoveX, TMoveY, TMoveZ, THeading, TPitch, TRoll, xCenter, yCenter, zCenter, m3, outQ4) {
    var center = Freedo.Cartesian3.fromRadians(xCenter, yCenter, zCenter);
    var nextPos = Freedo.Cartesian3.fromRadians(xCenter + TMoveX, yCenter + TMoveY, zCenter + TMoveZ);

    if(this._isPlaneMode) {
        center = new Freedo.Cartesian3(xCenter, yCenter, zCenter);
        nextPos = new Freedo.Cartesian3(xCenter + TMoveX, yCenter + TMoveY, zCenter + TMoveZ);
    }
    var q4 = Freedo.clone(TQ4, true);

    TPitch = Freedo.Math.toRadians(TPitch);
    TRoll = Freedo.Math.toRadians(TRoll);

    var tMatrix;
    var tMatrix4;
    var quat4;
    if(this._selectedAxis == 'XZ_CIRCLE') {
        var quat = Freedo.Quaternion.fromAxisAngle(new FreeDo.Cartesian3(1, 0, 0), TRoll, new Freedo.Quaternion());
        tMatrix = Freedo.Matrix4.fromTranslationQuaternionRotationScale(new Freedo.Cartesian3(), quat, new Freedo.Cartesian3(1, 1, 1));

        var r = Freedo.Matrix4.getRotation(tMatrix, new Freedo.Matrix3());
        var v4 = Freedo.Matrix3.multiplyByVector(r, new FreeDo.Cartesian3(0, -1, 0), new Freedo.Cartesian3());
        v4 = new FreeDo.Cartesian3(0, -1, 0);
        quat4 = Freedo.Quaternion.fromAxisAngle(v4, TPitch, new Freedo.Quaternion());
    } else {
        var quat = Freedo.Quaternion.fromAxisAngle(new FreeDo.Cartesian3(0, -1, 0), TPitch, new Freedo.Quaternion());
        tMatrix = Freedo.Matrix4.fromTranslationQuaternionRotationScale(new Freedo.Cartesian3(), quat, new Freedo.Cartesian3(1, 1, 1));

        var r = Freedo.Matrix4.getRotation(tMatrix, new Freedo.Matrix3());
        var v4 = Freedo.Matrix3.multiplyByVector(r, new FreeDo.Cartesian3(1, 0, 0), new Freedo.Cartesian3());
        v4 = new FreeDo.Cartesian3(1, 0, 0);
        quat4 = Freedo.Quaternion.fromAxisAngle(v4, TRoll, new Freedo.Quaternion());
    }

    Freedo.Quaternion.multiply(q4, quat4, q4);

    for(var key in quat4) {
        if(outQ4)
            outQ4[key] = quat4[key];
    }

    tMatrix4 = Freedo.Matrix4.fromTranslationQuaternionRotationScale(new Freedo.Cartesian3(0, 0, 0), q4, new Freedo.Cartesian3(1, 1, 1));

    var orgEnuMatrix = Freedo.Transforms.eastNorthUpToFixedFrame(center);
    if(this._isPlaneMode) {
        orgEnuMatrix = Freedo.Matrix4.fromTranslation(center);
    }
    var orgEnuMatrixInverse = Freedo.Matrix4.inverse(orgEnuMatrix, new Freedo.Matrix4());
    var nextEnuMatrix = Freedo.Transforms.eastNorthUpToFixedFrame(nextPos);
    if(this._isPlaneMode) {
        nextEnuMatrix = Freedo.Matrix4.fromTranslation(nextPos);
    }

    // 从旧点变换到原点操作，然后变换到新点
    var xMatrix = Freedo.Matrix4.multiply(tMatrix, orgEnuMatrixInverse, new Freedo.Matrix4());
    var xMatrix2 = Freedo.Matrix4.multiply(tMatrix4, xMatrix, new Freedo.Matrix4());
    var fMatrix = Freedo.Matrix4.multiply(nextEnuMatrix, xMatrix2, new Freedo.Matrix4());

    var tm3 = Freedo.Matrix4.getRotation(fMatrix, new Freedo.Matrix3());
    for(var key in tm3) {
        if(m3)
            m3[key] = tm3[key];
    }
    return fMatrix;
}

FdClipPlane.prototype.calcAxisZoom = function() {
    if(this._bInClip === false) {
        this._axisZoom = 1;
        return;
    }

    var startPt = this._sceneSphere.center;
    var endPt = this._viewer.camera.positionWC;
    var dist1 = 1,
        dist2 = 1;

    dist1 = Freedo.Cartesian3.distance(startPt, endPt) / 6;
    dist2 = this._viewer.camera.getPixelSize(this._sceneSphere, 100, 100);

    this._axisZoom = dist1;

    if(this._axisZoom > 400000)
        this._axisZoom = 400000;
    if(this._axisZoom < 20)
        this._axisZoom = 20
}

FdClipPlane.prototype.onTransforming = function(param) {
    var selectedAxis = param.SelectedAxis;
    this._selectedAxis = selectedAxis;

    var tM = [0, 0, 0];
    var TScaleX = 1,
        TScaleY = 1,
        TScaleZ = 1,
        THeading = 0,
        TPitch = 0,
        TRoll = 0;

    this.calcAxisZoom();

    var t12 = Freedo.clone(this._mouseDown12Arg, true);

    var a = Freedo.FdMisc.FdGeometric.calcAngle(param.MouseDownStartXY, param.MouseDownXY, param.MouseDownStartXY, param.MouseMoveXY);

    if(selectedAxis === 'Z_AXIS') {
        var dis = Math.sqrt(Math.pow(param.MouseDownXY.y - param.MouseMoveXY.y, 2) + Math.pow(param.MouseDownXY.x - param.MouseMoveXY.x, 2)) * this._axisZoom / 200;
        if(dis == 0)
            return;
        var vv0 = [param.MouseDownEndXY.x - param.MouseDownStartXY.x, param.MouseDownEndXY.y - param.MouseDownStartXY.y];
        var vv1 = [param.MouseMoveXY.x - param.MouseDownXY.x, param.MouseMoveXY.y - param.MouseDownXY.y];
        var c = (vv0[0] * vv1[0] + vv0[1] * vv1[1]) / (Math.sqrt(Math.pow(vv0[0], 2) + Math.pow(vv0[1], 2)) * Math.sqrt(Math.pow(vv1[0], 2) + Math.pow(vv1[1], 2)))
        var angle = Math.acos(c);

        var centerGra, upPt, v; //头顶向量
        if(this._isPlaneMode) {
            centerGra = {};
            centerGra.longitude = this._sceneSphere.center.x;
            centerGra.latitude = this._sceneSphere.center.y;
            centerGra.height = this._sceneSphere.center.z;
            upPt = new FreeDo.Cartesian3(this._sceneSphere.center.x, this._sceneSphere.center.y, this._sceneSphere.center.z + 1);
            v = FreeDo.Cartesian3.subtract(upPt, this._sceneSphere.center, new FreeDo.Cartesian3()); //头顶向量
        } else {
            centerGra = Freedo.Cartographic.fromCartesian(this._sceneSphere.center);
            upPt = FreeDo.Cartesian3.fromRadians(centerGra.longitude, centerGra.latitude, centerGra.height + 1);
            v = FreeDo.Cartesian3.subtract(upPt, this._sceneSphere.center, new FreeDo.Cartesian3()); //头顶向量
        }

        var m3 = new Freedo.Matrix3(); //旋转矩阵

        this.getMatrix(this._q4, this._t12.TMoveX, this._t12.TMoveY, this._t12.TMoveZ,
            this._t12.THeading, this._t12.TPitch, this._t12.TRoll,
            centerGra.longitude, centerGra.latitude, centerGra.height, m3);

        var v2 = Freedo.Matrix3.multiplyByVector(m3, v, new Freedo.Cartesian3()); //旋转之后的向量

        var orgEnuMatrix = Freedo.Transforms.eastNorthUpToFixedFrame(this._sceneSphere.center);
        if(this._isPlaneMode){
            orgEnuMatrix = Freedo.Matrix4.fromTranslation(this._sceneSphere.center);
        }
        var orgEnuMatrixInverse = Freedo.Matrix4.inverse(orgEnuMatrix, new Freedo.Matrix4());

        m3 = Freedo.Matrix4.getRotation(orgEnuMatrixInverse, new Freedo.Matrix3()); //回到原点需要进行的旋转矩阵
        var v3 = Freedo.Matrix3.multiplyByVector(m3, v2, new Freedo.Cartesian3()); //坐标轴对齐，让XYZ分别对应纬度精度高度

        var rio = [1, 1];
        if(this._isPlaneMode){
            rio = [1, 1];
        }
        else{
            rio = Freedo.FdMisc.FdGeometric.getLongAndLatRatio(centerGra)
        }
        tM[0] = (rio[1] * dis * v3.x * Math.cos(angle));
        tM[1] = (rio[0] * dis * v3.y * Math.cos(angle));
        tM[2] = (dis * v3.z * Math.cos(angle));
    }

    if(selectedAxis === 'XZ_CIRCLE') {
        var vY = FreeDo.Cartesian3.subtract(param.Ori.yAxis[1], param.Ori.yAxis[0], new FreeDo.Cartesian3());
        var areaOri = Freedo.Math.toDegrees(FreeDo.Cartesian3.angleBetween(vY, param.Ori.rayDirection));
        if(areaOri > 90)
            TPitch = a;
        else
            TPitch = -1 * a;
    }

    if(selectedAxis === 'YZ_CIRCLE') {
        var vX = FreeDo.Cartesian3.subtract(param.Ori.xAxis[1], param.Ori.xAxis[0], new FreeDo.Cartesian3());
        var areaOri = Freedo.Math.toDegrees(FreeDo.Cartesian3.angleBetween(vX, param.Ori.rayDirection));
        if(areaOri > 90)
            TRoll = -1 * a;
        else
            TRoll = a;
    }
    var t12m = {
        TMoveX: tM[0],
        TMoveY: tM[1],
        TMoveZ: tM[2],
        THeading: 0,
        TPitch: TPitch,
        TRoll: TRoll,
        TScaleX: 1,
        TScaleY: 1,
        TScaleZ: 1
    }

    t12.TMoveX += t12m.TMoveX;
    t12.TMoveY += t12m.TMoveY;
    t12.TMoveZ += t12m.TMoveZ;

    t12.TRoll = t12m.TRoll;
    t12.TPitch = t12m.TPitch;

    this._t12.TMoveX = t12.TMoveX;
    this._t12.TMoveY = t12.TMoveY;
    this._t12.TMoveZ = t12.TMoveZ;

    this._t12.TPitch = t12.TPitch;
    this._t12.TRoll = t12.TRoll;

    this.refineArgs();

    this._event.raiseEvent('Cliping', this.clipOptions);
    this._event.raiseEvent('DataChanged', this.clipOptions);
    this.updatePlane(this._t12);
    this._viewer.scene.requestRender();
}

FdClipPlane.prototype.getNewPlane = function(m4) {
    // 定义原始clipplane
    var plane = new Freedo.Plane(new Freedo.Cartesian3(0.0, 0.0, 1), 0);

    // 得到当前位置的ENU坐标系
    var transform = Freedo.Transforms.eastNorthUpToFixedFrame(this._sceneSphere.center);
    if(this._isPlaneMode) {
        var transform = Freedo.Matrix4.fromTranslation(this._sceneSphere.center);
    }
    Freedo.Matrix4.multiply(m4, transform, transform);

    Freedo.Plane.transform(plane, transform, plane);
    return plane;
}

FdClipPlane.prototype.updatePlane = function(t12) {
    var longi,
        lat,
        height;

    var posGraphic = Freedo.Cartographic.fromCartesian(this._sceneSphere.center);
    if(posGraphic) {
        longi = posGraphic.longitude;
        lat = posGraphic.latitude;
        height = posGraphic.height;
    }
    if(this._isPlaneMode) {
        longi = this._sceneSphere.center.x;
        lat = this._sceneSphere.center.y;
        height = this._sceneSphere.center.z;
    }

    //this._mani.updateSceneSphere(this._sceneSphere);

    var normal = undefined;
    var plane = undefined;
    var m3 = new Freedo.Matrix3();
    var m4 = new Freedo.Matrix4();

    if(this._selectedAxis === 'Z_AXIS' || this._selectedAxis === 'XZ_CIRCLE' || this._selectedAxis === 'YZ_CIRCLE') {
        var g, delta;
        if(this._isPlaneMode) {
            g = Freedo.clone(this._sceneSphere.center, true);
            g.z -= 1;
            var delta = Freedo.clone(g, true);
        } else {
            g = Freedo.Cartographic.fromCartesian(this._sceneSphere.center);
            g.height -= 1;
            var delta = Freedo.Cartesian3.fromRadians(g.longitude, g.latitude, g.height);
        }
        var v = FreeDo.Cartesian3.subtract(delta, this._sceneSphere.center, new FreeDo.Cartesian3());

        m4 = this.getMatrix(this._q4, t12.TMoveX, t12.TMoveY, t12.TMoveZ, t12.THeading, t12.TPitch, t12.TRoll, longi, lat, height, m3);
        var v2 = Freedo.Matrix3.multiplyByVector(m3, v, new Freedo.Cartesian3());
        if(isNaN(v2.x)) {
            console.warn('出现异常v2');
            return;
        }
        normal = Freedo.Cartesian3.normalize(v2, new Freedo.Cartesian3());
    }

    if(normal !== undefined) {
        var plane = this.getNewPlane(m4);

        var clippingPlanes = [
            plane
        ];

        for(var key in this._tileset) {
            var tileset = this._tileset[key];
            tileset.clippingPlanes = new Freedo.ClippingPlaneCollection({
                planes: clippingPlanes,
                edgeWidth: 1.0,
                useGltfModelMatrix: false //我们自己加的属性
            })
        }
    }

    this._mani.updateMatrix4(m4);
}

/*
 * 对参数进行格式化
 */
FdClipPlane.prototype.refineArgs = function() {
    this._t12.TMoveX = Freedo.FdMisc.FdMath.round(this._t12.TMoveX, 8);
    this._t12.TMoveY = Freedo.FdMisc.FdMath.round(this._t12.TMoveY, 8);
    this._t12.TMoveZ = Freedo.FdMisc.FdMath.round(this._t12.TMoveZ, 6);

    this._t12.TPitch = Freedo.FdMisc.FdMath.round(this._t12.TPitch, 4);
    this._t12.TRoll = Freedo.FdMisc.FdMath.round(this._t12.TRoll, 4);
}

/*
 * 获取数据
 * @returns {Object} 数据格式如下（经纬度单位为度，高度、长度、距离单位为米）：
 * {
 *     type: 类型,
 *     TMoveX: 经度方向平移量,
 *     TMoveY: 纬度方向平移量,
 *     TMoveZ: 高度方向平移量,
 *     TPitch: 俯仰角,
 *     TRoll: 翻滚角
 * }
 */
FdClipPlane.prototype.getData = function() {
    return {
        type: this._transType,
        TMoveX: this._t12.TMoveX,
        TMoveY: this._t12.TMoveY,
        TMoveZ: this._t12.TMoveZ,
        TPitch: this._t12.TPitch,
        TRoll: this._t12.TRoll
    }
}

/*
 * 设置数据
 * @param {Object} tileset PModel数据
 */
FdClipPlane.prototype.setData = function(tileset) {
    this._tileset = tileset;
}

FdClipPlane.prototype.reset12Args = function() {
    this._t12.TMoveX = 0;
    this._t12.TMoveY = 0;
    this._t12.TMoveZ = 0;
    this._t12.TPitch = 0;
    this._t12.TRoll = 0;
}

FdClipPlane.prototype.onTransformStart = function(movement) {
    this._event.raiseEvent('ClipStart', {});
    this.refineArgs();
    this._mouseDown12Arg = Freedo.clone(this._t12, true);
}

FdClipPlane.prototype.onTransformEnd = function(movement) {
    var longi,lat,height;
    
    if(this._isPlaneMode){
        longi = this._sceneSphere.center.x;
        lat = this._sceneSphere.center.y;
        height = this._sceneSphere.center.z;
    }
    else{
        var posGraphic = Freedo.Cartographic.fromCartesian(this._sceneSphere.center);
        longi = posGraphic.longitude;
        lat = posGraphic.latitude;
        height = posGraphic.height;
    }

    var q4 = new Freedo.Quaternion();
    var m4 = this.getMatrix(this._q4, this._t12.TMoveX, this._t12.TMoveY, this._t12.TMoveZ, 
        this._t12.THeading, this._t12.TPitch, this._t12.TRoll, 
        longi, lat, height, new Freedo.Matrix3(), q4);
    Freedo.Quaternion.multiply(this._q4, q4, this._q4);

    this.refineArgs();

    this._event.raiseEvent('ClipEnd', FreeDo.clone(this._t12, true));
}

/**
 * 恢复裁剪面的初始位置
 */
FdClipPlane.prototype.backToOriginPlane = function() {
    this.start(this._transType);
    this._event.raiseEvent('ClipReset', FreeDo.clone(this._t12, true));
}

/**
 * 退出场景裁剪
 * @private
 */
FdClipPlane.prototype.cancelClip = function() {
    this.reset();
    this._event.raiseEvent('Cancel', {});
}

/**
 * 退出场景裁剪
 */
FdClipPlane.prototype.cancel = function() {
    this.cancelClip();
}

/**
 * 注册事件捕捉
 * @param {FdMicroApp.FdClipPlane~FdClipPlaneCallback} listener 当有事件触发时被执行的函数。
 * @param {Object} [scope] listener函数执行时的绑定的对象。
 * @returns {Freedo.Event~RemoveCallback} 返回一个函数，调用该函数可以取消监听。
 *
 */
FdClipPlane.prototype.on = function(listener, scope) {
    return this._eventHelper.add(this._event, listener, scope);
}

/**
 * @callback FdMicroApp.FdClipPlane~FdClipPlaneCallback
 * @param {String} eventType 事件类型有以下几种：Start（裁剪开始）/Cancel（裁剪取消）/ClipReset（恢复裁剪面到初始位置）/Clip（裁剪面即将开始变化）/Cliping（裁剪面正在发生变化）/Cliped（裁剪面变化完成）
 * @param {Object} eventArg Start/Cancel无返回值，其他事件返回值是包含各个参数数据的对象
 */

/**
 * 重置
 */
FdClipPlane.prototype.reset = function() {
    this._bInClip = false;
    this._sceneSphere = null;
    this.reset12Args();
    this._mani.reset();
    if(this._lsner)
        this._lsner();
    this._lsner = undefined;
    this._q4 = new Freedo.Quaternion(0, 0, 0, 1);
}

/**
 * 销毁所有资源
 */
FdClipPlane.prototype.dispose = function() {
    this.reset();
}

export {
    FdClipPlane
};