import {
    FdTransformManipulator
} from './FdTransformManipulator';

import {
    FdMisc
} from '../../FdMisc/FdMisc';

/**
 * 模型编辑
 * @alias FdMicroApp.FdTransformHelper
 * @class
 *
 * @example
 * var viewer = Freedo.FdApp.createDefaultViewer('freedoContainer');
 * var tileset = viewer.scene.primitives.add(new Freedo.FreedoPModelset({
 *     url : '../../sampleData/文化室2018z'
 * }));
 * tileset.readyPromise.then(function(tileset) {
 *  viewer.camera.viewBoundingSphere(tileset.boundingSphere, new Freedo.HeadingPitchRange(0, -0.5, 0));
 *  var th = new Freedo.FdMicroApp.FdTransformHelper(viewer);
 *  th.setData([tileset]);
 *  th.start('MOVE');
 * });
 */
class FdTransformHelper {
    constructor(viewer) {
        this._viewer = viewer;

        this._bInTransform = false;
        this._transType = 'MOVE'; //MOVE ROTATE SCALE

        this._event = new Freedo.Event();
        this._eventHelper = new Freedo.EventHelper();

        this._sceneSphere = {};
        this._basePt = undefined;

        this._t12 = {
            TMoveX: 0,
            TMoveY: 0,
            TMoveZ: 0,
            DMoveX: 0,
            DMoveY: 0,
            DMoveZ: 0,
            THeading: 0,
            TPitch: 0,
            TRoll: 0,
            TScaleX: 1,
            TScaleY: 1,
            TScaleZ: 1,
            TBaseX: 0,
            TBaseY: 0,
            TBaseZ: 0
        }

        this._aPModelSet = [];

        this._mani = new FdTransformManipulator(viewer);
        this._maniCallBack = undefined;

        this._mouseDown12Arg = {};
        this._mouseDownMM = FreeDo.Matrix4.IDENTITY;
        this._selectedAxis = '';

        this._centerDis = {};

        this._inMoveTo = false;

        this.PositionType = {
            NONE: 0,
            ON_MODEL: 1,
            ON_TERRAIN: 2,
            ON_ELLIPSOID: 3
        };

        this._axisZoom = 1;
    }
    
    get tranOptions(){
        var mm = [];
        for(var key in this._aPModelSet) {
            var tileset = this._aPModelSet[key];
            mm = tileset.modelMatrix;
        }
        return {
            modelMatrix: mm
        }
    }
    
    set tranOptions(v){
        if(!Freedo.defined(v)){
            return;
        }
        
        //this._t12 = Freedo.clone(v.data, true);
        
        //this.start(v.type);
        
        //console.log(this._sceneSphere.center);
        
        for(var key in this._aPModelSet) {
            var tileset = this._aPModelSet[key];

            tileset.modelMatrix = Freedo.clone(v.modelMatrix, true);
            /*
             * this.applyTransformToTileset(this._aPModelSet[key], Freedo.Math.toRadians(t12.DMoveX), Freedo.Math.toRadians(t12.DMoveY), t12.DMoveZ,
                t12.THeading, t12.TPitch, t12.TRoll, t12.TScaleX, t12.TScaleY, t12.TScaleZ,
                Freedo.Math.toRadians(this._basePt.longitude), Freedo.Math.toRadians(this._basePt.latitude), this._basePt.height);
            */
        }
        
        //this.calcSceneBS();
        
        //if(this._aPModelSet.length > 0) {
        //    this._mani.updateSceneSphere(this._sceneSphere);
        //}
    }
}

/**
 * 设置参数值
 * @param {Array} aPModelSet PModelSet的数组
 */
FdTransformHelper.prototype.setData = function(aPModelSet) {
    this._aPModelSet = aPModelSet;
}

/**
 * 隐藏操作轴
 */
FdTransformHelper.prototype.hideAxis = function() {
    this._transType = '';
}

/*
 * 设置坐标系大小，数字越大，坐标系越小
 * @param {Number} size 系数
 */
FdTransformHelper.prototype.setAxisSize = function(size) {
    this._mani.setAxisSize(size);
}

/**
 * 设置参数值
 * @param {String} argName 参数名
 * @param {String} argValue 参数值
 */
FdTransformHelper.prototype.setArg = function(argName, argValue) {
    switch(argName) {
        case 'basePtX':
            this._t12.TBaseX = argValue;
            break;
        case 'basePtY':
            this._t12.TBaseY = argValue;
            break;
        case 'basePtZ':
            this._t12.TBaseZ = argValue;
            break;

        case 'translateX':
            this._t12.DMoveX = argValue;
            break;
        case 'translateY':
            this._t12.DMoveY = argValue;
            break;
        case 'translateZ':
            this._t12.DMoveZ = argValue;
            break;

        case 'rotateX':
            this._t12.THeading = argValue;
            break;
        case 'rotateY':
            this._t12.TPitch = argValue;
            break;
        case 'rotateZ':
            this._t12.TRoll = argValue;
            break;

        case 'scaleX':
            this._t12.TScaleX = argValue;
            break;
        case 'scaleY':
            this._t12.TScaleY = argValue;
            break;
        case 'scaleZ':
            this._t12.TScaleZ = argValue;
            break;
    }

    this._t12.TMoveX = this._t12.TBaseX + this._t12.DMoveX;
    this._t12.TMoveY = this._t12.TBaseY + this._t12.DMoveY;
    this._t12.TMoveZ = this._t12.TBaseZ + this._t12.DMoveZ;

    var tr = Freedo.Math.toRadians;
    var td = Freedo.Math.toDegrees;
    var newM4 = this.getMatrix(tr(this._t12.DMoveX), tr(this._t12.DMoveY), this._t12.DMoveZ, this._t12.THeading, this._t12.TPitch, this._t12.TRoll, this._t12.TScaleX, this._t12.TScaleY, this._t12.TScaleZ, Freedo.Math.toRadians(this._t12.TBaseX), Freedo.Math.toRadians(this._t12.TBaseY), this._t12.TBaseZ);

    var cartBase = Freedo.Cartesian3.fromDegrees(this._t12.TBaseX, this._t12.TBaseY, this._t12.TBaseZ);
    var cart3 = Freedo.Matrix4.multiplyByPoint(newM4, cartBase, new Freedo.Cartesian3());
    var gra = Freedo.Cartographic.fromCartesian(cart3);

    this._t12.TMoveX = td(gra.longitude);
    this._t12.TMoveY = td(gra.latitude);
    this._t12.TMoveZ = gra.height;

    this._mani.updatePosition(this._t12.TMoveX, this._t12.TMoveY, this._t12.TMoveZ);
    this._mani.updateBasePt(this._t12.TBaseX, this._t12.TBaseY, this._t12.TBaseZ);
    this._sceneSphere.center = Freedo.Cartesian3.fromDegrees(this._t12.TMoveX, this._t12.TMoveY, this._t12.TMoveZ);
}

FdTransformHelper.prototype.getMatrix = function(TMoveX, TMoveY, TMoveZ, THeading, TPitch, TRoll, TScaleX, TScaleY, TScaleZ, xCenter, yCenter, zCenter) {
    var center = Freedo.Cartesian3.fromRadians(xCenter, yCenter, zCenter);
    var nextPos = Freedo.Cartesian3.fromRadians(xCenter + TMoveX, yCenter + TMoveY, zCenter + TMoveZ);

    THeading = Freedo.Math.toRadians(THeading);
    TPitch = Freedo.Math.toRadians(TPitch);
    TRoll = Freedo.Math.toRadians(TRoll);

    var quat = Freedo.Quaternion.fromHeadingPitchRoll(new Freedo.HeadingPitchRoll(THeading, TPitch, TRoll));
    var scale = new Freedo.Cartesian3(TScaleX, TScaleY, TScaleZ);
    var tMatrix = Freedo.Matrix4.fromTranslationQuaternionRotationScale(new Freedo.Cartesian3(), quat, scale);

    var orgEnuMatrix = Freedo.Transforms.eastNorthUpToFixedFrame(center);
    var orgEnuMatrixInverse = Freedo.Matrix4.inverse(orgEnuMatrix, new Freedo.Matrix4());
    var nextEnuMatrix = Freedo.Transforms.eastNorthUpToFixedFrame(nextPos);

    // 从旧点变换到原点操作，然后变换到新点
    var xMatrix = Freedo.Matrix4.multiply(tMatrix, orgEnuMatrixInverse, new Freedo.Matrix4());
    var fMatrix = Freedo.Matrix4.multiply(nextEnuMatrix, xMatrix, new Freedo.Matrix4());

    //tileset.modelMatrix = fMatrix;
    return Freedo.clone(fMatrix, true);
}

/**
 * 根据参数得到变换矩阵
 * @param {Object} tileSet 需要进行操作的模型
 * @param {Number} TMoveX 从原点计算的经度偏移量，单位弧度
 * @param {Number} TMoveY 从原点计算的纬度偏移量，单位弧度
 * @param {Number} TMoveZ 从原点计算的最终高程值，单位是米
 * @param {Number} THeading Heading，单位是角度
 * @param {Number} TPitch Pitch，单位是角度
 * @param {Number} TRoll Roll，单位是角度
 * @param {Number} TScaleX X方向上的缩放系数
 * @param {Number} TScaleY X方向上的缩放系数
 * @param {Number} TScaleZ X方向上的缩放系数
 * @param {Number} [XCenter] 指定操作点的经度，单位弧度，可选参数，如果不指定默认使用原点的经度
 * @param {Number} [YCenter] 指定操作点的纬度，单位弧度，可选参数，如果不指定默认使用原点的纬度
 * @param {Number} [ZCenter] 指定操作点的高程，单位米，可选参数，如果不指定默认使用原点的高程
 */
FdTransformHelper.prototype.getMatrixOfTileset = function(tileSet, TMoveX, TMoveY, TMoveZ, THeading, TPitch, TRoll, TScaleX, TScaleY, TScaleZ, xCenter, yCenter, zCenter) {
    if(tileSet === undefined)
        return undefined;

    if(xCenter === undefined) {
        var centerGra = this.getTilesetBasePt(tileSet);

        xCenter = centerGra.longitude;
        yCenter = centerGra.latitude;
        zCenter = centerGra.height;
    }
    var center = Freedo.Cartesian3.fromRadians(xCenter, yCenter, zCenter);
    var nextPos = Freedo.Cartesian3.fromRadians(xCenter + TMoveX, yCenter + TMoveY, zCenter + TMoveZ);

    THeading = Freedo.Math.toRadians(THeading);
    TPitch = Freedo.Math.toRadians(TPitch);
    TRoll = Freedo.Math.toRadians(TRoll);

    var quat = Freedo.Quaternion.fromHeadingPitchRoll(new Freedo.HeadingPitchRoll(THeading, TPitch, TRoll));
    var scale = new Freedo.Cartesian3(TScaleX, TScaleY, TScaleZ);
    var tMatrix = Freedo.Matrix4.fromTranslationQuaternionRotationScale(new Freedo.Cartesian3(), quat, scale);

    var orgEnuMatrix = Freedo.Transforms.eastNorthUpToFixedFrame(center);
    var orgEnuMatrixInverse = Freedo.Matrix4.inverse(orgEnuMatrix, new Freedo.Matrix4());
    var nextEnuMatrix = Freedo.Transforms.eastNorthUpToFixedFrame(nextPos);

    // 从旧点变换到原点操作，然后变换到新点
    var xMatrix = Freedo.Matrix4.multiply(tMatrix, orgEnuMatrixInverse, new Freedo.Matrix4());
    var fMatrix = Freedo.Matrix4.multiply(nextEnuMatrix, xMatrix, new Freedo.Matrix4());

    //tileset.modelMatrix = fMatrix;
    return Freedo.clone(fMatrix, true);
}

/**
 * 激活模型编辑
 * @param {String} type 编辑类型，MOVE，SCALE， ROTATE
 * @private
 */
FdTransformHelper.prototype.startTransform = function(type, data) {
    this._bInTransform = true;
    this._transType = type;

    if(this._aPModelSet.length > 0) {
        if(this.checkConflict())
            return;

        this._basePt = undefined;

        this.calcSceneBS();

        for(var key in this._aPModelSet) {
            var tileset = this._aPModelSet[key];
            this._mouseDownMat = FreeDo.clone(tileset.modelMatrix, true);
        }
    }

    var that = this;
    this._maniCallBack = this._mani.on(function(eventType, param) {
        if(eventType == 'Transform') {
            that.onTransform();
        }

        if(eventType == 'Transformed') {
            that.onTransformed();
        }

        if(eventType == 'GotoPoint') {
            that.onGotoPoint(param);
        }

        if(eventType == 'Transforming') {
            that.onTransforming(param);
        }
    });

    if(this._transType.indexOf('MOVETO') >= 0)
        this._inMoveTo = true;

    this._mani.startTransform(this._transType);

    this._event.raiseEvent('Start', {});

    if(this._aPModelSet.length > 0) {
        this._mani.updateSceneSphere(this._sceneSphere);
        this._mani.updatePosition(this._basePt.longitude, this._basePt.latitude, this._basePt.height);
        this._mani.updateBasePt(this._basePt.longitude, this._basePt.latitude, this._basePt.height);
    }
}

/**
 * 激活模型编辑
 * @param {String} type 编辑类型，MOVE，SCALE， ROTATE
 */
FdTransformHelper.prototype.start = function(type, data) {
    this.startTransform(type, data);
}

/**
 * 获取数据
 * @returns {Object} 格式如下：
 * [{
 *     tileset: tileset对象,
 *     modelMatrix: 变换矩阵
 * }...]
 */
FdTransformHelper.prototype.getData = function() {
    var data = [];
    for(var key in this._aPModelSet) {
        var tileset = this._aPModelSet[key];
        data.push({
            modelMatrix: FreeDo.clone(tileset.modelMatrix, true),
            tileset: tileset
        });
    }
    return data;
}

FdTransformHelper.prototype.checkConflict = function() {
    var time = 0;
    var i = 0;
    for(var key in this._aPModelSet) {
        var tileset = this._aPModelSet[key];
        var tm = this._aPModelSet[key]['transformTime'];
        if(i == 0) {
            if(tm === undefined)
                time = undefined;
            else
                time = Freedo.clone(tm, true);
        } else {
            if(tm !== time) {
                alert('编辑状态存在冲突无法继续！');
                this.cancelTransform();
                return true;
            }
        }

        i++;
    }
    return false;
}

FdTransformHelper.prototype.initTransform = function() {
    var time = 0;

    var i = 0;
    for(var key in this._aPModelSet) {
        var tileset = this._aPModelSet[key];
        var tm = this._aPModelSet[key]['transformTime'];
        if(i == 0) {
            if(tm === undefined)
                time = undefined;
            else
                time = Freedo.clone(tm, true);
        } else {
            if(tm !== time) {
                console.log('部分模型存在变换，部分没有，请注意');
            }
        }

        i++;
    }

    for(var key in this._aPModelSet) {
        var tileset = this._aPModelSet[key];

        var time = this._aPModelSet[key]['transformTime'];
        if(time == undefined || time == 0) {
            this.resetTransform();
        }

        var tm = this._aPModelSet[key]['transformMatrix'];
        if(tm !== undefined) {
            this._t12 = FreeDo.clone(tm, true);
        }

        this._event.raiseEvent('InitTransform', FreeDo.clone(this._t12, true));

    }
}

//暂时用不到
FdTransformHelper.prototype.updateBasePt = function() {
    this._basePt = {
        longitude: Freedo.Math.toRadians(this._t12.TMoveX),
        latitude: Freedo.Math.toRadians(this._t12.TMoveY),
        height: this._t12.TMoveZ
    };
}

FdTransformHelper.prototype.getModelBS = function(model) {
    var basePt = Freedo.Matrix4.getTranslation(model.modelMatrix, new Freedo.Cartesian3());
    var bs = model.boundingSphere;
    var newBS = new Freedo.BoundingSphere(basePt, bs.radius);
    return newBS;
}

FdTransformHelper.prototype.calcSceneBS = function() {
    var boundingSpheres = [];
    for(var key in this._aPModelSet) {
        var tileset = this._aPModelSet[key];
        if(tileset === undefined)
            console.log('FdTransform：无效数据');

        var bs = tileset.boundingSphere;
        if(tileset instanceof Freedo.Model) {
            //gltf模型以基点为包围球的中心点来算
            bs = this.getModelBS(tileset);
        }
        else if(tileset.point){
            bs = new Freedo.BoundingSphere(tileset.position.getValue(), 10);
        }

        boundingSpheres.push(bs);

        /*  
        if(this._basePt === undefined) {
            this._basePt = this._aPModelSet[key]['basePoint'];
        }
        */
    }

    this._sceneSphere = FreeDo.BoundingSphere.fromBoundingSpheres(boundingSpheres, new FreeDo.BoundingSphere());

    if(this._basePt === undefined) {
        this._basePt = Freedo.Cartographic.fromCartesian(this._sceneSphere.center);
        this._basePt.longitude = Freedo.Math.toDegrees(this._basePt.longitude);
        this._basePt.latitude = Freedo.Math.toDegrees(this._basePt.latitude);
    }
}

FdTransformHelper.prototype.calcXYZDist = function(long1, lat1, height1, long2, lat2, height2) {
    return [long2 - long1, lat2 - lat1, height2 - height1];
    var TMoveX = (new Freedo.EllipsoidGeodesic(new FreeDo.Cartographic(long1, lat1, height1), new FreeDo.Cartographic(long2, lat1, height1))).surfaceDistance;
    if(long2 < long1)
        TMoveX *= -1;
    var TMoveY = (new Freedo.EllipsoidGeodesic(new FreeDo.Cartographic(long1, lat1, height1), new FreeDo.Cartographic(long1, lat2, height1))).surfaceDistance;
    if(lat2 < lat1)
        TMoveY *= -1;
    var TMoveZ = height2 - height1;
    return [TMoveX, TMoveY, TMoveZ];
}

/*
 * 计算缩放系数
 */
FdTransformHelper.prototype.calcAxisZoom = function() {
    if(this._bInClip === false) {
        this._axisZoom = 1;
        return;
    }

    var startPt = this._sceneSphere.center;
    var endPt = this._viewer.camera.positionWC;
    var dist1 = 1,
        dist2 = 1;

    dist1 = Freedo.Cartesian3.distance(startPt, endPt) / 6;
    dist2 = this._viewer.camera.getPixelSize(this._sceneSphere, 10, 10);

    this._axisZoom = dist1;

    if(this._viewer.scene._mode === Freedo.SceneMode.COLUMBUS_VIEW)
        this._axisZoom = this._viewer.camera.positionWC.x / 6;

    if(this._axisZoom > 400000)
        this._axisZoom = 400000;
    if(this._axisZoom < 1)
        this._axisZoom = 1;
}

FdTransformHelper.prototype.onTransforming = function(param) {
    var mouseDownPos = param.MouseDownPos;
    var mouseMovePos = param.MouseMovePos;
    var selectedAxis = param.SelectedAxis;
    var fixedAxis = param.FixedAxis;
    this._selectedAxis = selectedAxis;

    var tM = [0, 0, 0];
    var TScaleX = 1,
        TScaleY = 1,
        TScaleZ = 1,
        THeading = 0,
        TPitch = 0,
        TRoll = 0;

    this.calcAxisZoom();

    var startPt3 = undefined;
    if(Freedo.defined(mouseDownPos))
        startPt3 = Freedo.Cartesian3.fromRadians(mouseDownPos.longitude, mouseDownPos.latitude, mouseDownPos.height);

    var endPt3 = undefined;
    if(Freedo.defined(mouseMovePos))
        endPt3 = Freedo.Cartesian3.fromRadians(mouseMovePos.longitude, mouseMovePos.latitude, mouseMovePos.height);

    var dis1 = 0;
    var dis2 = 0;
    if(startPt3)
        dis1 = Freedo.Cartesian3.distance(startPt3, this._sceneSphere.center);
    if(endPt3)
        dis2 = Freedo.Cartesian3.distance(endPt3, this._sceneSphere.center);

    var t12 = Freedo.clone(this._mouseDown12Arg, true);

    if(this._aPModelSet.length > 0) {
        this.calcSceneBS();
    }

    if(selectedAxis === 'XY') {
        tM = this.calcXYZDist(mouseDownPos.longitude, mouseDownPos.latitude, mouseDownPos.height,
            mouseMovePos.longitude, mouseMovePos.latitude, mouseDownPos.height);
    }

    if(selectedAxis === 'YZ') {
        tM = this.calcXYZDist(mouseDownPos.longitude, mouseDownPos.latitude, mouseDownPos.height,
            mouseDownPos.longitude, mouseMovePos.latitude, mouseMovePos.height);
    }

    if(selectedAxis === 'XZ') {
        tM = this.calcXYZDist(mouseDownPos.longitude, mouseDownPos.latitude, mouseDownPos.height,
            mouseMovePos.longitude, mouseDownPos.latitude, mouseMovePos.height);
    }

    //if (selectedAxis === 'XY_CIRCLE')
    //offset = FreeDo.Cartesian3.fromRadians(mouseMovePos.longitude, mouseMovePos.latitude, mouseDownPos.height);

    if(fixedAxis === 'X') {
        tM = this.calcXYZDist(mouseDownPos.longitude, mouseDownPos.latitude, mouseDownPos.height,
            mouseMovePos.longitude, mouseDownPos.latitude, mouseDownPos.height);
    }

    if(fixedAxis === 'Y') {
        tM = this.calcXYZDist(mouseDownPos.longitude, mouseDownPos.latitude, mouseDownPos.height,
            mouseDownPos.longitude, mouseMovePos.latitude, mouseDownPos.height);
    }

    if(fixedAxis === 'Z') {
        tM[0] = 0;
        tM[1] = 0;
        tM[2] = -1 * (param.MouseMoveXY.y - param.MouseDownXY.y) * this._axisZoom / 100;
    }

    if(this._transType.indexOf('SCALE') >= 0 && selectedAxis === 'XY_QCIRCLE') {
        var scale = dis2 / dis1;
        TScaleX = scale;
        TScaleY = scale;
        TScaleZ = scale;
    }

    var centerGra = {};
    //2018年5月15日：貌似无用，setArg可能会用到
    //centerGra.longitude = this._t12.TBaseX;
    //centerGra.latitude = this._t12.TBaseY;
    //centerGra.height = this._t12.TBaseZ;

    if(this._transType.indexOf('ROTATE') >= 0 && selectedAxis === 'XY_CIRCLE') {
        centerGra = Freedo.Cartographic.fromCartesian(this._sceneSphere.center);
        var xpos = FreeDo.Cartesian3.fromRadians(centerGra.longitude + 0.01, centerGra.latitude, centerGra.height);
        var v0 = FreeDo.Cartesian3.subtract(xpos, this._sceneSphere.center, new FreeDo.Cartesian3());
        var v1 = FreeDo.Cartesian3.subtract(startPt3, this._sceneSphere.center, new FreeDo.Cartesian3());
        var v2 = FreeDo.Cartesian3.subtract(endPt3, this._sceneSphere.center, new FreeDo.Cartesian3());
        var v20 = Freedo.Math.toDegrees(FreeDo.Cartesian3.angleBetween(v2, v0));
        var v10 = Freedo.Math.toDegrees(FreeDo.Cartesian3.angleBetween(v1, v0));
        //console.log(v20 + ' ==== ' + v10);
        if(mouseDownPos.latitude < centerGra.latitude)
            v10 = 360 - v10;
        if(mouseMovePos.latitude < centerGra.latitude)
            v20 = 360 - v20;

        THeading = v10 - v20;
    }

    if(this._transType.indexOf('ROTATE') >= 0 && selectedAxis === 'XZ_CIRCLE') {
        if(!Freedo.defined(startPt3))
            return;
        centerGra = Freedo.Cartographic.fromCartesian(this._sceneSphere.center);
        var xpos = FreeDo.Cartesian3.fromRadians(centerGra.longitude + 0.01, centerGra.latitude, centerGra.height);
        var v0 = FreeDo.Cartesian3.subtract(xpos, this._sceneSphere.center, new FreeDo.Cartesian3());
        var v1 = FreeDo.Cartesian3.subtract(startPt3, this._sceneSphere.center, new FreeDo.Cartesian3());
        var v2 = FreeDo.Cartesian3.subtract(endPt3, this._sceneSphere.center, new FreeDo.Cartesian3());
        var v20 = Freedo.Math.toDegrees(FreeDo.Cartesian3.angleBetween(v2, v0));
        var v10 = Freedo.Math.toDegrees(FreeDo.Cartesian3.angleBetween(v1, v0));
        //console.log(v20 + ' ==== ' + v10);
        if(mouseDownPos.height < centerGra.height)
            v10 = 360 - v10;
        if(mouseMovePos.height < centerGra.height)
            v20 = 360 - v20;

        TPitch = v20 - v10;
    }

    if(this._transType.indexOf('ROTATE') >= 0 && selectedAxis === 'YZ_CIRCLE') {
        if(!Freedo.defined(startPt3))
            return;
        centerGra = Freedo.Cartographic.fromCartesian(this._sceneSphere.center);
        var xpos = FreeDo.Cartesian3.fromRadians(centerGra.longitude, centerGra.latitude + 0.01, centerGra.height);
        var v0 = FreeDo.Cartesian3.subtract(xpos, this._sceneSphere.center, new FreeDo.Cartesian3());
        var v1 = FreeDo.Cartesian3.subtract(startPt3, this._sceneSphere.center, new FreeDo.Cartesian3());
        var v2 = FreeDo.Cartesian3.subtract(endPt3, this._sceneSphere.center, new FreeDo.Cartesian3());
        var v20 = Freedo.Math.toDegrees(FreeDo.Cartesian3.angleBetween(v2, v0));
        var v10 = Freedo.Math.toDegrees(FreeDo.Cartesian3.angleBetween(v1, v0));
        //console.log(v20 + ' ==== ' + v10);
        if(mouseDownPos.height < centerGra.height)
            v10 = 360 - v10;
        if(mouseMovePos.height < centerGra.height)
            v20 = 360 - v20;

        TRoll = v20 - v10;
    }

    var t12m = {
        TMoveX: tM[0],
        TMoveY: tM[1],
        TMoveZ: tM[2],
        THeading: THeading,
        TPitch: TPitch,
        TRoll: TRoll,
        TScaleX: TScaleX,
        TScaleY: TScaleY,
        TScaleZ: TScaleZ
    }

    // t12.TMoveX += Freedo.Math.toDegrees(t12m.TMoveX);
    // t12.TMoveY += Freedo.Math.toDegrees(t12m.TMoveY);
    // t12.TMoveZ += t12m.TMoveZ;

    t12.DMoveX += Freedo.Math.toDegrees(t12m.TMoveX);
    t12.DMoveY += Freedo.Math.toDegrees(t12m.TMoveY);
    t12.DMoveZ += t12m.TMoveZ;
    t12.THeading += t12m.THeading;
    t12.TPitch += t12m.TPitch;
    t12.TRoll += t12m.TRoll;
    t12.TScaleX *= t12m.TScaleX;
    t12.TScaleY *= t12m.TScaleY;
    t12.TScaleZ *= t12m.TScaleZ;

    //centerGra = this.getTilesetBasePt();

    // t12.TMoveX = Freedo.Math.toRadians(t12.TMoveX) - centerGra.longitude;
    // t12.TMoveY = Freedo.Math.toRadians(t12.TMoveY) - centerGra.latitude;
    // t12.TMoveZ = t12.TMoveZ - centerGra.height;

    // this._t12.TMoveX = Freedo.Math.toDegrees(centerGra.longitude + t12.TMoveX);
    // this._t12.TMoveY = Freedo.Math.toDegrees(centerGra.latitude + t12.TMoveY);
    // this._t12.TMoveZ = centerGra.height + t12.TMoveZ;

    this._t12.DMoveX = t12.DMoveX;
    this._t12.DMoveY = t12.DMoveY;
    this._t12.DMoveZ = t12.DMoveZ;

    this._t12.THeading = t12.THeading;
    this._t12.TPitch = t12.TPitch;
    this._t12.TRoll = t12.TRoll;

    this._t12.TScaleX = t12.TScaleX;
    this._t12.TScaleY = t12.TScaleY;
    this._t12.TScaleZ = t12.TScaleZ;

    this.refineArgs();
    this._event.raiseEvent('Transforming', FreeDo.clone(this._t12, true));
    
    if(this._aPModelSet.length > 0) {
        for(var key in this._aPModelSet) {
            centerGra = this.getTilesetBasePt(this._aPModelSet[key]);
            var tileset = this._aPModelSet[key];
            this.applyTransformToTileset(this._aPModelSet[key], Freedo.Math.toRadians(t12.DMoveX), Freedo.Math.toRadians(t12.DMoveY), t12.DMoveZ,
                t12.THeading, t12.TPitch, t12.TRoll, t12.TScaleX, t12.TScaleY, t12.TScaleZ,
                Freedo.Math.toRadians(this._basePt.longitude), Freedo.Math.toRadians(this._basePt.latitude), this._basePt.height);
            this._event.raiseEvent('DataChanged', FreeDo.clone(this._aPModelSet[key].modelMatrix, true));
        }

        this._t12.TMoveX = Freedo.Math.toDegrees(centerGra.longitude) + t12.DMoveX;
        this._t12.TMoveY = Freedo.Math.toDegrees(centerGra.latitude) + t12.DMoveY;
        this._t12.TMoveZ = centerGra.height + t12.DMoveZ;

        this.calcSceneBS();
        this._mani.updateSceneSphere(this._sceneSphere);
        //console.log(this._sceneSphere.center);
    }
}

/*
 * 对参数进行格式化
 */
FdTransformHelper.prototype.refineArgs = function() {
    for(var key in this._aPModelSet) {
        var tileset = this._aPModelSet[key];

        var scale = FreeDo.Matrix4.getScale(tileset.modelMatrix, new FreeDo.Cartesian3());

        if(this._t12.THeading > 360)
            this._t12.THeading -= 360;
        if(this._t12.THeading < -360)
            this._t12.THeading += 360;

        if(this._t12.TPitch > 360)
            this._t12.TPitch -= 360;
        if(this._t12.TPitch < -360)
            this._t12.TPitch += 360;

        if(this._t12.TRoll > 360)
            this._t12.TRoll -= 360;
        if(this._t12.TRoll < -360)
            this._t12.TRoll += 360;

        this._t12.TMoveX = Freedo.FdMisc.FdMath.round(this._t12.TMoveX, 8);
        this._t12.TMoveY = Freedo.FdMisc.FdMath.round(this._t12.TMoveY, 8);
        this._t12.TMoveZ = Freedo.FdMisc.FdMath.round(this._t12.TMoveZ, 6);

        this._t12.THeading = Freedo.FdMisc.FdMath.round(this._t12.THeading, 4);
        this._t12.TPitch = Freedo.FdMisc.FdMath.round(this._t12.TPitch, 4);
        this._t12.TRoll = Freedo.FdMisc.FdMath.round(this._t12.TRoll, 4);

//      this._t12.TScaleX = Freedo.FdMisc.FdMath.round(scale.x, 4);
//      this._t12.TScaleY = Freedo.FdMisc.FdMath.round(scale.y, 4);
//      this._t12.TScaleZ = Freedo.FdMisc.FdMath.round(scale.z, 4);
        //现在又改成了分阶段
        this._t12.TScaleX = 1;
        this._t12.TScaleY = 1;
        this._t12.TScaleZ = 1;
    }
}

FdTransformHelper.prototype.onGotoPoint = function(para) {
    //console.log('选点类型：' + para.type);
    if(para.type != this.PositionType.NONE && this._inMoveTo) {
        var posG = Freedo.Cartographic.fromCartesian(para.position);

        if(posG !== undefined) {
            var t12 = FreeDo.clone(this._t12, true);
            t12.TMoveX = posG.longitude;
            t12.TMoveY = posG.latitude;
            //取到的地形选点本身就是高度不对
            //t12.TMoveZ = posG.height;

            for(var key in this._aPModelSet) {
                var centerGra = this.getTilesetBasePt(this._aPModelSet[key]);

                var disLong = t12.TMoveX - centerGra.longitude;
                var disLat = t12.TMoveY - centerGra.latitude;
                var disHeiht = t12.TMoveZ - centerGra.height;

                this.applyTransformToTileset(this._aPModelSet[key], parseFloat(disLong), parseFloat(disLat), parseFloat(disHeiht),
                    parseFloat(t12.THeading), parseFloat(t12.TPitch), parseFloat(t12.TRoll),
                    parseFloat(t12.TScaleX), parseFloat(t12.TScaleY), parseFloat(t12.TScaleZ),
                    Freedo.Math.toRadians(this._t12.TBaseX), Freedo.Math.toRadians(this._t12.TBaseY), this._t12.TBaseZ);
            }

            t12.TMoveX = Freedo.Math.toDegrees(t12.TMoveX);
            t12.TMoveY = Freedo.Math.toDegrees(t12.TMoveY);
            t12.TMoveZ = t12.TMoveZ;

            this._t12 = FreeDo.clone(t12, true);

            this.refineArgs();

            if(this._transType !== '') {
                this.calcSceneBS();
                this._mani.updateSceneSphere(this._sceneSphere);
            }

            this._event.raiseEvent('Transformed', FreeDo.clone(this._t12, true));
        }

        this._inMoveTo = false;
    }
}

FdTransformHelper.prototype.onTransform = function(movement) {
    this._event.raiseEvent('Transform', {});
    this.reset12Args();
    this._mouseDown12Arg = Freedo.clone(this._t12, true);
    for(var key in this._aPModelSet) {
        var tileset = this._aPModelSet[key];
        this._mouseDownMM = Freedo.clone(tileset.modelMatrix, true);
        break;
    }
}

FdTransformHelper.prototype.onTransformed = function(movement) {
    this.refineArgs();
    this._mouseDownMM = FreeDo.Matrix4.IDENTITY;
    this._event.raiseEvent('Transformed', FreeDo.clone(this._t12, true));
}

FdTransformHelper.prototype.getTilesetBasePt = function(tileSet) {
    if(tileSet === undefined)
        return undefined;
    
    var isGeo = false;

    var initMatrix4;
    if(tileSet._root && tileSet._root.transform)
        initMatrix4 = Freedo.clone(tileSet._root.transform, true);
    else if(tileSet instanceof Freedo.Model)
        initMatrix4 = Freedo.clone(tileSet.modelMatrix, true);
    else{
        initMatrix4 = Freedo.clone(tileSet.modelMatrix, true);
        isGeo = true;
    }
        

    var initTran = FreeDo.Matrix4.getTranslation(initMatrix4, new FreeDo.Cartesian3());
    var centerGra = Freedo.Cartographic.fromCartesian(initTran);
    if(isGeo){
        centerGra = Freedo.Cartographic.fromCartesian(tileSet.boundingSphere.center);
    }
    return centerGra;
}

/**
 * 根据参数进行模型变换
 * @param {Object} tileSet 需要进行操作的模型
 * @param {Number} TMoveX 从原点计算的经度偏移量，单位弧度
 * @param {Number} TMoveY 从原点计算的纬度偏移量，单位弧度
 * @param {Number} TMoveZ 从原点计算的最终高程值，单位是米
 * @param {Number} THeading Heading，单位是角度
 * @param {Number} TPitch Pitch，单位是角度
 * @param {Number} TRoll Roll，单位是角度
 * @param {Number} TScaleX X方向上的缩放系数
 * @param {Number} TScaleY X方向上的缩放系数
 * @param {Number} TScaleZ X方向上的缩放系数
 * @param {Number} [XCenter] 指定操作点的经度，单位弧度
 * @param {Number} [YCenter] 指定操作点的纬度，单位弧度
 * @param {Number} [ZCenter] 指定操作点的高程，单位米
 */
FdTransformHelper.prototype.applyTransformToTileset = function(tileSet, TMoveX, TMoveY, TMoveZ, THeading, TPitch, TRoll, TScaleX, TScaleY, TScaleZ, xCenter, yCenter, zCenter) {

    var modelMatrix = this.getMatrixOfTileset(tileSet, TMoveX, TMoveY, TMoveZ, THeading, TPitch, TRoll, TScaleX, TScaleY, TScaleZ, xCenter, yCenter, zCenter);
    if(tileSet instanceof Freedo.Model) {
        //var newMatrix = Freedo.Matrix4.multiply(modelMatrix, tileSet.initModelMatrix, new Freedo.Matrix4());
        var newMatrix = Freedo.Matrix4.multiply(modelMatrix, this._mouseDownMM, new Freedo.Matrix4());
        tileSet.modelMatrix = newMatrix;
    } else{
        var newMatrix = Freedo.Matrix4.multiply(modelMatrix, this._mouseDownMM, new Freedo.Matrix4());
        tileSet.modelMatrix = newMatrix;
    }
}

/**
 * 整体变换场景模型
 * @private
 * @param {Number} TMoveX 期望偏移到的经度，单位是度
 * @param {Number} TMoveY 期望偏移到的纬度，单位是度
 * @param {Number} TMoveZ 最终高程值，单位是米
 * @param {Number} THeading Heading，单位是角度
 * @param {Number} TPitch Pitch，单位是角度
 * @param {Number} TRoll Roll，单位是角度
 * @param {Number} TScaleX X方向上的缩放系数
 * @param {Number} TScaleY X方向上的缩放系数
 * @param {Number} TScaleZ X方向上的缩放系数
 */
FdTransformHelper.prototype.applyTransform = function(TMoveX, TMoveY, TMoveZ, THeading, TPitch, TRoll, TScaleX, TScaleY, TScaleZ) {
    if(this.checkConflict())
        return;

    this.calcSceneBS();

    try {
        TMoveX = parseFloat(TMoveX);
        TMoveY = parseFloat(TMoveY);
        TMoveZ = parseFloat(TMoveZ);
        THeading = parseFloat(THeading);
        TPitch = parseFloat(TPitch);
        TRoll = parseFloat(TRoll);
        TScaleX = parseFloat(TScaleX);
        TScaleY = parseFloat(TScaleY);
        TScaleZ = parseFloat(TScaleZ);
    } catch(e) {
        return;
    }

    if(isNaN(TMoveX) || isNaN(TMoveY) || isNaN(TMoveZ) ||
        isNaN(THeading) || isNaN(TPitch) || isNaN(TRoll) ||
        isNaN(TScaleX) || isNaN(TScaleY) || isNaN(TScaleZ))
        return;

    for(var key in this._aPModelSet) {
        var centerGra = this.getTilesetBasePt(this._aPModelSet[key]);;

        var disLong = Freedo.Math.toRadians(TMoveX) - centerGra.longitude;
        var disLat = Freedo.Math.toRadians(TMoveY) - centerGra.latitude;
        var disHeiht = TMoveZ - centerGra.height;

        this.applyTransformToTileset(this._aPModelSet[key], parseFloat(disLong), parseFloat(disLat), parseFloat(disHeiht),
            parseFloat(THeading), parseFloat(TPitch), parseFloat(TRoll),
            parseFloat(TScaleX), parseFloat(TScaleY), parseFloat(TScaleZ),
            Freedo.Math.toRadians(this._basePt.longitude), Freedo.Math.toRadians(this._basePt.latitude), this._basePt.height);
    }

    this._t12.TMoveX = TMoveX;
    this._t12.TMoveY = TMoveY;
    this._t12.TMoveZ = TMoveZ;

    this._t12.THeading = THeading;
    this._t12.TPitch = TPitch;
    this._t12.TRoll = TRoll;

    this._t12.TScaleX = TScaleX;
    this._t12.TScaleY = TScaleY;
    this._t12.TScaleZ = TScaleZ;

    this.refineArgs();

    if(this._transType !== '') {
        this.calcSceneBS();
        if(this._mani)
            this._mani.updateSceneSphere(this._sceneSphere);
    }

    this._event.raiseEvent('Transformed', FreeDo.clone(this._t12, true));
}

/**
 * 整体变换场景模型
 * @private
 * @param {Number} DMoveX 经度偏移量，单位是弧度
 * @param {Number} DMoveY 纬度偏移量，单位是弧度
 * @param {Number} DMoveZ 高程偏移量，单位是米
 * @param {Number} THeading Heading，单位是角度
 * @param {Number} TPitch Pitch，单位是角度
 * @param {Number} TRoll Roll，单位是角度
 * @param {Number} TScaleX X方向上的缩放系数
 * @param {Number} TScaleY X方向上的缩放系数
 * @param {Number} TScaleZ X方向上的缩放系数
 */
FdTransformHelper.prototype.applyTransformDelta = function(DMoveX, DMoveY, DMoveZ, THeading, TPitch, TRoll, TScaleX, TScaleY, TScaleZ) {
    if(this.checkConflict())
        return;

    this.calcSceneBS();

    try {
        DMoveX = parseFloat(DMoveX);
        DMoveY = parseFloat(DMoveY);
        DMoveZ = parseFloat(DMoveZ);
        THeading = parseFloat(THeading);
        TPitch = parseFloat(TPitch);
        TRoll = parseFloat(TRoll);
        TScaleX = parseFloat(TScaleX);
        TScaleY = parseFloat(TScaleY);
        TScaleZ = parseFloat(TScaleZ);
    } catch(e) {
        return;
    }

    if(isNaN(DMoveX) || isNaN(DMoveY) || isNaN(DMoveZ) ||
        isNaN(THeading) || isNaN(TPitch) || isNaN(TRoll) ||
        isNaN(TScaleX) || isNaN(TScaleY) || isNaN(TScaleZ))
        return;

    
    for(var key in this._aPModelSet) {
        var centerGra = this.getTilesetBasePt(this._aPModelSet[key]);

        this.applyTransformToTileset(this._aPModelSet[key], DMoveX, DMoveY, DMoveZ,
            parseFloat(THeading), parseFloat(TPitch), parseFloat(TRoll),
            parseFloat(TScaleX), parseFloat(TScaleY), parseFloat(TScaleZ),
            Freedo.Math.toRadians(this._basePt.longitude), Freedo.Math.toRadians(this._basePt.latitude), this._basePt.height);
    }
    
    this._t12.DMoveX = DMoveX;
    this._t12.DMoveY = DMoveY;
    this._t12.DMoveZ = DMoveZ;

    this._t12.TMoveX = DMoveX + this._basePt.longitude;
    this._t12.TMoveY = DMoveY + this._basePt.latitude;
    this._t12.TMoveZ = DMoveZ + this._basePt.height;

    this._t12.THeading = THeading;
    this._t12.TPitch = TPitch;
    this._t12.TRoll = TRoll;

    this._t12.TScaleX = TScaleX;
    this._t12.TScaleY = TScaleY;
    this._t12.TScaleZ = TScaleZ;

    this.refineArgs();

    if(this._transType !== '') {
        this.calcSceneBS();
        this._mani.updateSceneSphere(this._sceneSphere);
    }

    this._event.raiseEvent('Transformed', FreeDo.clone(this._t12, true));
}

FdTransformHelper.prototype.reset12Args = function() {
    if(this.checkConflict())
        return;

    var centerGra = undefined;
    for(var key in this._aPModelSet) {
        var centerGra = this.getTilesetBasePt(this._aPModelSet[key]);
        break;
    }
    if(centerGra == undefined) {
        return;
    }

    this._t12.DMoveX = 0;
    this._t12.DMoveY = 0;
    this._t12.DMoveZ = 0;
    this._t12.TMoveX = Freedo.Math.toDegrees(centerGra.longitude);
    this._t12.TMoveY = Freedo.Math.toDegrees(centerGra.latitude);
    this._t12.TMoveZ = centerGra.height;
    this._t12.THeading = 0;
    this._t12.TPitch = 0;
    this._t12.TRoll = 0;
    this._t12.TScaleX = 1;
    this._t12.TScaleY = 1;
    this._t12.TScaleZ = 1;

    this.refineArgs();

    this._basePt = undefined;
}

/**
 * 重置场景的变换矩阵
 */
FdTransformHelper.prototype.resetTransform = function() {
    this.reset12Args();
    for(var key in this._aPModelSet) {
        var tileset = this._aPModelSet[key];
        //this._sceneMgr.setSceneObjectProperty(this._aPModelSet[key], 'initialMatrix', FreeDo.clone(FreeDo.Matrix4.IDENTITY, true));
        //this._sceneMgr.setSceneObjectProperty(this._aPModelSet[key], 'transformMatrix', FreeDo.clone(this._t12, true));
        //this._sceneMgr.setSceneObjectProperty(this._aPModelSet[key], 'transformTime', 0);
        //this._sceneMgr.setSceneObjectProperty(this._aPModelSet[key], 'basePoint', this._basePt);
        if(tileset instanceof Freedo.Model) {
            tileset.modelMatrix = FreeDo.clone(tileset.initModelMatrix, true);
        } else
            tileset.modelMatrix = FreeDo.clone(FreeDo.Matrix4.IDENTITY, true);
        
        this._event.raiseEvent('DataChanged', FreeDo.clone(tileset.modelMatrix, true));
    }

    this.calcSceneBS();

    if(this._bInTransform) {
        this._mani.updateSceneSphere(this._sceneSphere);
    }

    this._event.raiseEvent('resetTransform', FreeDo.clone(this._t12, true));
}

