/**
 * 退出模型编辑
 * @private
 */
FdTransformHelper.prototype.cancelTransform = function() {
    this.reset();
}

/**
 * 退出模型编辑
 */
FdTransformHelper.prototype.cancel = function() {
    this.reset();
}

/**
 * 注册事件捕捉
 * @param {FdMicroApp.FdTransformHelper~FdTransformHelperCallback} listener 当有事件触发时被执行的函数。
 * @param {Object} [scope] listener函数执行时的绑定的对象。
 * @returns {Freedo.Event~RemoveCallback} 返回一个函数，调用该函数可以取消监听。
 */
FdTransformHelper.prototype.on = function(listener, scope) {
    return this._eventHelper.add(this._event, listener, scope);
}

/**
 * @callback FdMicroApp.FdTransformHelper~FdTransformHelperCallback
 * @param {String} eventType 事件类型有：Start，Cancel，Transform，Transforming，Transformed
 * @param {Object} eventArg Start/Cancel无返回值，其他事件返回值是包含各个参数数据的对象
 */

/**
 * 重置
 */
FdTransformHelper.prototype.reset = function() {
    this._bInTransform = false;
    this._mani.reset();
    this._transType = '';
    if(this._maniCallBack)
        this._maniCallBack();
    this._event.raiseEvent('Cancel', {});
}

/**
 * 销毁所有资源
 */
FdTransformHelper.prototype.dispose = function() {
    this.reset();
}

export {
    FdTransformHelper
};