import { FdCommon } from './FdCommon';

/**
 * 影像服务
 * @namespace
 * @alias FdServer.FdImagerylayersServer
 */
class FdImagerylayersServer {}

/**
 * 处理获取到的影像的XML文件，返回当前影像的添加url、name、format等信息
 * @param {XML} metadata 需要处理的XML文件
 * @param {String} token 用户权限token
 * @private
 */
function _parseXmlMetadata(metadata, token) {
    let result = {};
    let xmlMetadata = metadata;
    let xmlDom = xmlMetadata.documentElement;
    let contents = xmlDom.getElementsByTagName('Contents');
    let resourceUrls = contents[0].getElementsByTagName('ResourceURL');
    let format = contents[0].getElementsByTagName('Format')[0].innerHTML;
    let name = contents[0].getElementsByTagName('ows:Title')[0].innerHTML;
    result.name = name;
    result.format = format;
    let crs = contents[0].getElementsByTagName('ows:SupportedCRS')[0].innerHTML;
    let tilingScheme;
    if (crs.includes('4326')) {
        tilingScheme = 'Geographic';
    } else if (crs.includes('3857')) {
        tilingScheme = 'WebMercator';
    }
    result.tilingScheme = tilingScheme;
    let len = resourceUrls.length;
    let urls = [];
    let imgUrl;
    for (let i = 0; i < len; i++) {
        let url = resourceUrls[0].getAttribute('template');
        urls.push(url);
    }
    for (let i = 0; i < len; i++) {
        if (urls[i].includes('{TileMatrix}/{TileCol}/{TileRow}')) {
            let tempUrl = urls[i];
            let url1 = tempUrl.replace('{TileMatrix}', '{z}');
            let url2 = url1.replace('{TileCol}', '{x}');
            let url3 = url2.replace('{TileRow}', '{y}');
            imgUrl = url3;
            break;
        }
    }
    result.imgUrl = `${imgUrl}?token=${token}`;
    return result;
}

/**
 * 获取用户的影像列表
 * @param {String} baseURL 域名
 * @param {String} token 用户权限token
 * @param {FdServer.FdImagerylayersServer~ResponseCallback} response 函数完成回调函数
 */
FdImagerylayersServer.getImagerylayers = async function(baseURL, token, callback) {
    let url = baseURL + `/cloudlink/freeserver-admin/admin/layer/get?token=${token}`;
    let response = [];
    try {
        response = await FdCommon.ajaxJson({
            url: url,
            data: {
                servicetype: 'WMTS'
            },
            type: 'POST',
            dataType: 'json'
        })
    } catch (error) {
        console.log('获取列表失败');
    }
    callback(response)
}

/**
 * 获取某个影像图层
 * @param {String} url 请求地址url
 * @param {String} token 用户权限token
 * @param {FdServer.FdImagerylayersServer~ResponseCallback} response 函数完成回调函数
 */
FdImagerylayersServer.getImagerylayer = async function(capaurl, token, callback) {
    let opt = {};
    let pmtsUrl = capaurl + '?token=' + token;
    try {
        let response = await FdCommon.ajaxJson({
            url: pmtsUrl,
            type: 'GET',
            dataType: 'XML'
        })
        let res = _parseXmlMetadata(response, token);
        opt = {
            'name': res.name,
            'type': 'UrlTemplate',
            'iconUrl': '//res.gbim360.com/lib/freedo/rel/1.5.7/Freedo/Widgets/Images/ImageryProviders/blackMarble.png',
            'layerOption': {
                'show': true,
                'alpha': 1.0
            },
            'providerOptions': {
                'url': res.imgUrl,
                'maximumLevel': 20,
                'tilingScheme': res.tilingScheme
            }
        };
    } catch (error) {
        console.log('获取错误');
    }
    callback(opt);
}

/**
 * FdImagerylayersServer相关的方法事件回调函数
 * @callback FdServer.FdImagerylayersServer~ResponseCallback
 * @param {Object} response 调用请求获取的返回值
 */

export {
    FdImagerylayersServer
}