let mobx = require('mobx');
import { getFreedoXPath } from '../FdApp/getFreedoXPath';

const defaultImage = getFreedoXPath() + 'Assets/Textures/pin/pin.png';


/**
 * 标签管理器
 * @alias FdTools.FdLabelsManager
 * @param {Freedo.Viewer} viewer 视景器
 * @param {Object} data 具体标签数据，格式如下：
 * {
 *     collection: [
 *         {position: '', text: '', image: '', show: true},
 *         {position: '', text: '', image: '', show: true},
 *     ],
 *     show: true
 * }
 *  
 * @example
 * var viewer = Freedo.FdApp.createDefaultViewer('freedoContainer');
 * 
 * var labels = new Freedo.FdLabelsManager(viewer, {
 *     collection: [
 *         {image: '', position: [116, 40], text: '123中国abc'},
 *         {image: '', position: [116, 20], text: '456北京bj'},
 *     ],
 *     show: true
 * });
 * 
 * // 另外一种形式的初始化
 * //var labels = new Freedo.FdLabelsManager(viewer);
 * 
 * // 隐藏/显示所有标签
 * labels.data.show = false;
 * 
 * // 增加标签
 * labels.data.collection.push({
 *     position: [110, 42],
 *     text: '标签内容',
 *     show: true,
 *     image: ''
 * });
 * 
 * // 删除标签
 * labels.data.collection.pop(); 或者 labels.data.collection.splice(3, 1);
 * 
 * // 修改标签位置
 * labels.data.collection[1].position = [116, 30];
 * 
 */
class FdLabelsManager {
    constructor(viewer, data) {
        this._viewer = viewer;

        this._event = new Freedo.Event();
        this._eventHelper = new Freedo.EventHelper();

        let collection = viewer.scene.primitives.add(new Freedo.PrimitiveCollection());
        let billboards = collection.add(new Freedo.BillboardCollection());
        let points = collection.add(new Freedo.PointPrimitiveCollection());
        let texts = collection.add(new Freedo.LabelCollection());

        this._collection = collection;
        this._billboards = billboards;
        this._points = points;
        this._texts = texts;

        this._setData(data);
    }

    set data(value) {
        throw new Error('FdLabelsManager不支持直接赋值！');
    }

    /**
     * 获取data来进行修改，操作方式如下
     * 
     * @example
     * // 隐藏/显示所有标签
     * labels.data.show = false;
     * 
     * // 增加标签
     * labels.data.collection.push({
     *     position: [110, 42],
     *     text: '标签内容'
     * });
     */
    get data() {
        return this._data;
    }

    /**
     * 增加标签
     * @param {Object} options 标签属性对象
     * @param {String} [options.image] 标签附带的图标路径
     * @param {Number[]} options.position 标签所在位置，数组形式，按顺序分别表示经度、纬度和高度，其中经度和纬度的单位是度数，高度的单位是米
     * @param {String} [options.text] 标签文本内容
     * @param {Boolean} [options.show] 标签的可见性
     * 
     * @example
     * addLabel({text: '123', position: [116, 40, 100]});
     * 
     * @private
     */
    addLabel(options) {
        let { image = '', position, text = '', show = true } = options;
        this.data.collection.push({ image, position, text, show });
        return this.data.collection.length - 1;
    }

    /**
     * 删除标签
     * @param {Number} index 
     * @example
     * removeLabel(2);
     * 
     * @private
     */
    removeLabel(index) {
        this.data.collection.splice(index, 1);
    }

    /**
     * 设置标签
     * @param {Number} index 
     * @param {Object} options 标签属性对象
     * @param {String} [options.image] 标签附带的图标路径
     * @param {Number[]} options.position 标签所在位置，数组形式，按顺序分别表示经度、纬度和高度，其中经度和纬度的单位是度数，高度的单位是米
     * @param {String} [options.text] 标签文本内容
     * @param {Boolean} [options.show] 标签的可见性
     * 
     * @example
     * setLabel(2, {show: false, text: '456'});
     * 
     * @private
     */
    setLabel(index, options) {
        Object.assign(this.data.collection[index], options);
    }

    /**
     * 注册捕捉FdLabelPointsManager类的回调函数
     * @param {FdTools.FdLabelsManager~FdLabelsChangedCallback} listener 当有事件触发时被执行的函数，事件类型有以下几种：Changed
     * @param {Object} [scope] listener函数执行时的绑定的对象
     * @returns {Freedo.Event~RemoveCallback} 返回一个函数，调用该函数可以取消监听
     * 
     * @example
     * 
     * g_labels.on(function(eventType) {
     *    writeToLocalStorage(g_labels.data);
     * });
     *
     */
    on(listener, scope) {
        return this._eventHelper.add(this._event, listener, scope);
    }

    /**
     * @callback FdTools.FdLabelsManager~FdLabelsChangedCallback
     * @param {String} eventType 事件类型有以下几种：Changed
     */

    /**
     * 销毁标签管理器，以及所有的标签
     */
    dispose() {
        this._viewer.scene.primitives.remove(this._collection);
        this._collection = undefined;
        this._billboards = undefined;
        this._points = undefined;
        this._texts = undefined;

        this._data = undefined;
        this._mobxSpy();
        this._mobxSpy = undefined;

        this._event.raiseEvent('Changed');

        this._eventHelper.removeAll();
        this._eventHelper = undefined;
        this._event = undefined;

        this._viewer.scene.requestRender();
    }

    // data的形式
    // {
    //     collection: [
    //         {position: '', text: '', image: ''},
    //         {position: '', text: '', image: ''},
    //     ],
    //     show: true
    // }
    _setData(data = {}) {
        if (typeof data.show === 'undefined') {
            data.show = true;
        }

        if (typeof data.collection === 'undefined') {
            data.collection = [];
        }

        data.collection.forEach(element => {
            if (typeof element.show === 'undefined') {
                element.show = true;
            }
        })

        let mobxData = mobx.observable(data);
        // if (typeof mobxData.show === 'undefined') {
        //     //mobxData.show = true;
        //     mobx.extendObservable(data, {show: true});
        // }

        // mobx.reaction(function () {
        //     return mobxData.data;
        // }, function (data) {
        //     console.log(data);
        //     manager._event.raiseEvent('Changed');
        // });

        mobxData.collection.forEach((element) => {
            element.label = this._addLabel(element);
        })

        this._data = mobxData;
        this._collection.show = data.show;

        let manager = this;
        this._mobxSpy = mobx.spy(function(spyReport) {
            if (spyReport.type === 'splice') {
                if (spyReport.object === manager._data.collection) {
                    if (spyReport.addedCount > 0) {
                        spyReport.added.forEach(function(element) {
                            element.label = manager._addLabel(element);
                            setTimeout(() => { manager._event.raiseEvent('Changed') }, 0);
                        });
                    }

                    if (spyReport.removedCount > 0) {
                        spyReport.removed.forEach(function(element) {
                            let label = element.label;
                            manager._removeLabel(label);
                            // manager._event.raiseEvent('Changed');
                            setTimeout(() => { manager._event.raiseEvent('Changed') }, 0);
                        });
                    }
                }
            }

            if (spyReport.type === 'update') {
                if (spyReport.object === manager._data) {
                    if (spyReport.key === 'show') {
                        manager._collection.show = spyReport.newValue;
                        manager._viewer.scene.requestRender();
                        manager._event.raiseEvent('Changed');
                    } else if (spyReport.key === 'collection') {
                        manager._data.collection.forEach(element => {
                            manager._removeLabel(element.label);
                        })

                        spyReport.newValue.forEach(element => {
                            manager._addLabel(element);
                        });
                        // manager._event.raiseEvent('Changed');
                        setTimeout(() => { manager._event.raiseEvent('Changed') }, 0);
                    }
                } else if (typeof spyReport.object.label !== 'undefined') {
                    if (manager._containsLabel(spyReport.object.label)) {
                        let label = spyReport.object.label;

                        manager._setLabel(label, {
                            [spyReport.key]: spyReport.newValue
                        });
                        // manager._event.raiseEvent('Changed');
                        setTimeout(() => { manager._event.raiseEvent('Changed') }, 0);
                    }
                }
            }
        });
    }

    _addLabel({ position, text = '', image = '', show = true }) {
        let pos = Freedo.Cartesian3.fromDegrees(position[0], position[1], position[2]);
        const maxDistance = 20000000;

        if (typeof image === 'undefined' || image === '') {
            image = defaultImage;
        }

        let billboard = this._billboards.add({
            position: pos,
            image: image,
            show: show,
            pixelOffset: new Freedo.Cartesian2(0, -1),
            horizontalOrigin: Freedo.HorizontalOrigin.CENTER,
            verticalOrigin: Freedo.VerticalOrigin.BOTTOM,
            width: 32,
            height: 32,
            id: undefined,
            distanceDisplayCondition: new Freedo.DistanceDisplayCondition(0, maxDistance),
            disableDepthTestDistance: Number.POSITIVE_INFINITY,
        });

        let point = this._points.add({
            show: false,
            position: pos,
            pixelSize: 3.0,
            color: Freedo.Color.YELLOW,
            outlineColor: Freedo.Color.BLACK,
            outlineWidth: 1.0,
            id: undefined,
            distanceDisplayCondition: new Freedo.DistanceDisplayCondition(0, maxDistance),
            disableDepthTestDistance: Number.POSITIVE_INFINITY,
        });

        let textPrimitive = this._texts.add({
            show: show,
            position: pos,
            text: text,
            font: '16px sans-serif',
            fillColor: Freedo.Color.WHITE,
            outlineColor: Freedo.Color.BLACK,
            outlineWidth: 1.0,
            showBackground: false,
            style: Freedo.LabelStyle.FILL_AND_OUTLINE,
            pixelOffset: new Freedo.Cartesian2(18, -16),
            horizontalOrigin: Freedo.HorizontalOrigin.LEFT,
            verticalOrigin: Freedo.VerticalOrigin.CENTER,
            id: undefined,
            distanceDisplayCondition: new Freedo.DistanceDisplayCondition(0, maxDistance),
            disableDepthTestDistance: Number.POSITIVE_INFINITY,
        });

        this._viewer.scene.requestRender();
        return { text: textPrimitive, point, billboard };
    }

    _setLabel(label, { position, text, image, show }) {
        if (typeof position !== 'undefined') {
            let pos = Freedo.Cartesian3.fromDegrees(position[0], position[1], position[2]);
            label.billboard.position = pos;
            label.point.position = pos;
            label.text.position = pos;
        }

        if (typeof show !== 'undefined') {
            label.billboard.show = show;
            //label.point.show = show;
            label.text.show = show;
        }

        if (typeof text !== 'undefined') {
            label.text.text = text;
        }

        if (typeof image !== 'undefined' && image) {
            label.billboard.image = image;
        }
        this._viewer.scene.requestRender();
    }

    _removeLabel(label) {
        this._points.remove(label.point);
        this._texts.remove(label.text);
        this._billboards.remove(label.billboard);
        this._viewer.scene.requestRender();
    }

    _containsLabel(label) {
        return this._points.contains(label.point);
    }
}

export { FdLabelsManager };