import {
	FdMisc
} from '../../FdMisc/FdMisc';

/**
 * 场景变换操作器类
 * @alias FdTransformManipulator
 * @class
 * @private
 */
class FdTransformManipulator {
	constructor(viewer) {
		this._viewer = viewer;
		this._bInTransform = false;
		this._transType = 'MOVE'; //MOVE ROTATE SCALE
		this._bInRun = false;

		this._xAxisPL = undefined;
		this._yAxisPL = undefined;
		this._zAxisPL = undefined;

		this._XYPlane = undefined;
		this._YZPlane = undefined;
		this._XZPlane = undefined;

		this._XYCircle = undefined;
		this._XZCircle = undefined;
		this._YZCircle = undefined;

		this._basePt = undefined;
		this._basePtOriPosition = undefined;
		this._basePtOri = undefined;

		this._selectedAxis = '';

		this._mouseDownPos = undefined;
		this._initSphere = undefined;

		this._useDepthPicking = true;
		this._pickTranslucentDepth = true;

		this._fixedAxis = '';
		this._DISPERRAD = 111000;

		this._event = new Freedo.Event();
		this._eventHelper = new Freedo.EventHelper();

		this._oldPt = undefined;
		this._started = false;

		this._mouseDown = false;

		this._lastDis = -1;

		this._alpha = 0.5;
		this._axisZoom = 1;
		this._axisSize = 4;

		this.PositionType = {
			NONE: 0,
			ON_MODEL: 1,
			ON_TERRAIN: 2,
			ON_ELLIPSOID: 3
		};

		this._handler = undefined;

		this._mouseDownXY = undefined;
		
		this._xPts = undefined;
	}
}

/**
 * 注册事件捕捉
 * @param {FdTransformManipulator~FdTransformManipulatorCallback} listener 当有事件触发时被执行的函数。
 * @param {Object} [scope] listener函数执行时的绑定的对象。
 * @returns {Freedo.Event~RemoveCallback} 返回一个函数，调用该函数可以取消监听。
 */
FdTransformManipulator.prototype.on = function(listener, scope) {
	return this._eventHelper.add(this._event, listener, scope);
}

/**
 * @callback FdTransformManipulator~FdTransformManipulatorCallback
 * @param {String} eventType 事件类型有：Transform（操作开始），Transforming（操作中），Transformed（操作后）
 * @param {Object} eventArg 事件参数包含相关信息
 */

/**
 * 激活场景变换
 * @param {String} type 变换类型，MOVE，SCALE， ROTATE。
 * @private
 */
FdTransformManipulator.prototype.startTransform = function(type) {
	this._transType = type;
	if(this._bInTransform)
		return;
	this._bInTransform = true;
	this.addListeners();
	this._bInRun = true;
}

/**
 * 激活场景变换
 * @param {String} type 变换类型，MOVE，SCALE， ROTATE。
 * @private
 */
FdTransformManipulator.prototype.start = function(type) {
	this.startTransform(type);
}

FdTransformManipulator.prototype.createLinePrimitive = function(pts) {
    return new Freedo.Primitive({
        geometryInstances: new Freedo.GeometryInstance({
            geometry: new Freedo.PolylineGeometry({
                positions: pts,
                width: 6.0,
                vertexFormat: Freedo.PolylineMaterialAppearance.VERTEX_FORMAT
            })
        }),
        appearance: new Freedo.PolylineMaterialAppearance({
            material: new Freedo.Material({
                fabric: {
                    type: 'PolylineArrow',
                    uniforms: {
                        color: new Freedo.Color(1.0, 0.3, 0.3, 1.0)
                    }
                }
            }),
            renderState: new Freedo.RenderState({
                polygonOffset: {
                    enabled: true,
                    factor: -100.0,
                    units: -100.0,
                },
                depthTest: {
                    enabled: true,
                    func: Freedo.WebGLConstants.LEQUAL,
                }
            }),
        }),
        depthFailAppearance: new Freedo.PolylineMaterialAppearance({
            material: new Freedo.Material({
                fabric: {
                    type: 'PolylineDash',
                    width: 1.0,
                    uniforms: {
                        color: new Freedo.Color(1.0, 0.3, 0.3, 0.5)
                    }
                }
            }),
        }),
    });
}

/**
 * 重绘坐标轴
 * @private
 */
FdTransformManipulator.prototype.redraw = function() {
	this.calcAxisZoom();
}

FdTransformManipulator.prototype.updateSceneSphere = function(sceneSphere) {
	this._initSphere = FreeDo.clone(sceneSphere, true);
	if(this._xAxisPL === undefined)
		this.showAxis();
}

FdTransformManipulator.prototype.updatePosition = function(longitude, latitude, height) {
	var center = Freedo.Cartesian3.fromDegrees(longitude, latitude, height);
	this._initSphere = {
		center: center
	};
	if(this._xAxisPL === undefined)
		this.showAxis();
}

FdTransformManipulator.prototype.updateBasePt = function(longitude, latitude, height) {
	this._basePtOriPosition = Freedo.Cartesian3.fromDegrees(longitude, latitude, height);
}

/**
 * 退出模型编辑
 * @private
 */
FdTransformManipulator.prototype.cancelTransform = function() {
	this.reset();
}

/**
 * 退出模型编辑
 */
FdTransformManipulator.prototype.cancel = function() {
	this.reset();
}

FdTransformManipulator.prototype.addListeners = function() {
	this._handler = new Freedo.ScreenSpaceEventHandler(this._viewer.scene.canvas);
	this._handler.setInputAction(this.onLeftDown.bind(this), FreeDo.ScreenSpaceEventType.LEFT_DOWN);
	this._handler.setInputAction(this.onMouseMove.bind(this), FreeDo.ScreenSpaceEventType.MOUSE_MOVE);
	this._handler.setInputAction(this.onLeftUp.bind(this), FreeDo.ScreenSpaceEventType.LEFT_UP);
	this._handler.setInputAction(this.onWheel.bind(this), FreeDo.ScreenSpaceEventType.WHEEL);
	this._handler.setInputAction(this.onClick.bind(this), FreeDo.ScreenSpaceEventType.LEFT_CLICK);

	var that = this;
	//this._project.getCamera().on(function(eventType, param) {
	//	if(eventType == 'Changed') {
	//		that.onWheel();
	//	}
	//});
	this._viewer.scene.camera.changed.addEventListener(FdTransformManipulator.prototype.onWheel, this);

	window.addEventListener('keydown', this.onKeyDown.bind(this));
}

FdTransformManipulator.prototype.calcFixedAxis = function() {
	if(this._selectedAxis === 'X_AXIS') {
		this._fixedAxis = 'X';
		return;
	}

	if(this._selectedAxis === 'Y_AXIS') {
		this._fixedAxis = 'Y';
		return;
	}

	if(this._selectedAxis === 'Z_AXIS') {
		this._fixedAxis = 'Z';
		return;
	}
}

/*
 * 计算缩放系数
 */
FdTransformManipulator.prototype.calcAxisZoom = function() {
	if(this._bInTransform === false) {
		this._axisZoom = 1;
		return;
	}

	if(!this._initSphere)
		return;

	var startPt = this._initSphere.center;
	var endPt = this._viewer.camera.positionWC;
	var dist = 1;

	dist = Freedo.Cartesian3.distance(startPt, endPt) / this._axisSize;

	this._axisZoom = dist;

	if(this._viewer.scene._mode === Freedo.SceneMode.COLUMBUS_VIEW)
		this._axisZoom = this._viewer.camera.positionWC.x / 3;

	if(this._axisZoom > 400000)
		this._axisZoom = 400000;
}

/*
 * 设置坐标系大小，数字越大，坐标系越小
 * @param {Number} size 系数
 */
FdTransformManipulator.prototype.setAxisSize = function(size) {
    this._axisSize = size;
}

FdTransformManipulator.prototype.onWheel = function() {
	if(this._bInTransform === false)
		return;
	this.calcAxisZoom();
}

FdTransformManipulator.prototype.onKeyDown = function() {
	if(this._bInTransform === false)
		return;

	if(window.event.keyCode === 27) {
		if(this._selectedAxis !== '') {
			this._event.raiseEvent('OnKeyDown', {
				keyCode: window.event.keyCode
			});
			this._selectedAxis = '';
			this.onLeftUp();
		}
	}
}

FdTransformManipulator.prototype.onMouseMove = function(movement) {
	//console.log(this._selectedAxis);
	if(this._selectedAxis != '') {
		var moveGraphic = undefined;

		if(this._selectedAxis === 'XY_QCIRCLE' || this._selectedAxis === 'XY_CIRCLE' ||
			this._selectedAxis === 'X_AXIS' || this._selectedAxis === 'Y_AXIS' || this._selectedAxis === 'Z_AXIS')
			moveGraphic = this.pickPositionOnEllipsoid(movement.endPosition);

		if(this._selectedAxis === 'YZ' || this._selectedAxis === 'YZ_CIRCLE')
			moveGraphic = this.pickPositionOnPlane(movement.endPosition, 'YZ');

		if(this._selectedAxis === 'XZ' || this._selectedAxis === 'XZ_CIRCLE' /* || this._selectedAxis === 'Z_AXIS'*/ )
			moveGraphic = this.pickPositionOnPlane(movement.endPosition, 'XZ');

		/*
		if (this._mouseDownPos == undefined) {
		console.log('_mouseDownPos 为空');
		this._mouseDownPos = FreeDo.clone(moveGraphic, true);
		if (this._transType.indexOf('MOVE') >= 0)
		this.calcFixedAxis();
		return;
		}
		if (moveGraphic === undefined) {
		console.log('moveGraphic 为空');
		return;
		}
		 */

		/*
		var startPt3 = Freedo.Cartesian3.fromRadians(this._mouseDownPos.longitude, this._mouseDownPos.latitude, this._mouseDownPos.height);
		var endPt3 = Freedo.Cartesian3.fromRadians(moveGraphic.longitude, moveGraphic.latitude, moveGraphic.height); ;
		var dis1 = Freedo.Cartesian3.distance(startPt3, this._initSphere.center);
		var dis2 = Freedo.Cartesian3.distance(endPt3, this._initSphere.center);
		if (Math.abs(moveGraphic.longitude - this._mouseDownPos.longitude) > 1.0 / this._DISPERRAD && this._selectedAxis === 'YZ') {
		this._mouseDownPos = undefined;
		console.log('出现异常2');
		return;
		}

		if (Math.abs(moveGraphic.latitude - this._mouseDownPos.latitude) > 1.0 / this._DISPERRAD && this._selectedAxis === 'XZ') {
		this._mouseDownPos = undefined;
		console.log('出现异常3');
		return;
		}
		 */

		this._event.raiseEvent('Transforming', {
			MouseDownPos: this._mouseDownPos,
			MouseMovePos: moveGraphic,
			MouseDownXY: this._mouseDownXY,
			MouseMoveXY: movement.endPosition,
			SelectedAxis: this._selectedAxis,
			FixedAxis: this._fixedAxis,
		});

		return;
	}
}

FdTransformManipulator.prototype.onClick = function(movement) {
	var pos = this.pickPosition(movement.position);

	this._event.raiseEvent('GotoPoint', pos);
}

FdTransformManipulator.prototype.onLeftDown = function(movement) {
	if(!this._bInTransform)
		return;

	//this.calcAxisZoom();

	this._mouseDown = true;
	this._oldPt = undefined;
	this._started = false;
	this._lastDis = -1;

	var pickedFeature = this._viewer.scene.pick(movement.position);
	if(!FreeDo.defined(pickedFeature)) {
		return;
	}

	if(FreeDo.defined(pickedFeature.id) && FreeDo.defined(pickedFeature.id._name)) {
		var pickName = pickedFeature.id._name;
		if((pickName === 'X_AXIS' || pickName === 'Y_AXIS' || pickName === 'Z_AXIS') && this._transType.indexOf('MOVE') >= 0) {
			pickedFeature.id.polyline.material.color = new Freedo.Color(1, 1, 0, 1);
		}
		if(pickName === 'YZ' || pickName === 'XZ') {
			pickedFeature.id.wall.material.color = new Freedo.Color(1, 0, 0, 0.2);
		}

		if(pickName === 'XY_QCIRCLE') {
			pickedFeature.id.polygon.material.color = new Freedo.Color(1, 1, 0, 1);
		}

		if(pickName === 'XY_CIRCLE' || pickName === 'YZ_CIRCLE' || pickName === 'XZ_CIRCLE') {
			pickedFeature.id.polyline.material.color = new Freedo.Color(1, 1, 0, 1);
		}

		if(pickName === 'XY_CIRCLE' || pickName === 'XZ_CIRCLE' || pickName === 'YZ_CIRCLE' ||
			pickName === 'XY_QCIRCLE' || pickName === 'XZ' || pickName === 'YZ' ||
			pickName === 'X_AXIS' || pickName === 'Y_AXIS' || pickName === 'Z_AXIS') {
			this._selectedAxis = FreeDo.clone(pickName, true);
			this._viewer.scene.screenSpaceCameraController.enableRotate = false;
			this._viewer.scene.screenSpaceCameraController.enableLook = false;
			//this._useDepthPicking = FreeDo.clone(this._viewer.scene.useDepthPicking, true);
			//this._pickTranslucentDepth = FreeDo.clone(this._viewer.scene.pickTranslucentDepth, true);
			//this._viewer.scene.useDepthPicking = true;
			//this._viewer.scene.pickTranslucentDepth = true;
			this._mouseDownPos = undefined;

			this._event.raiseEvent('Transform', {});
		}
	}

	if(this._selectedAxis === 'XY_QCIRCLE' || this._selectedAxis === 'XY_CIRCLE' ||
		this._selectedAxis === 'X_AXIS' || this._selectedAxis === 'Y_AXIS') {
		this._mouseDownPos = this.pickPositionOnEllipsoid(movement.position);
		if(this._transType.indexOf('MOVE') >= 0)
			this.calcFixedAxis();
	}

	if(this._selectedAxis === 'YZ' || this._selectedAxis === 'YZ_CIRCLE') {
		this._mouseDownPos = this.pickPositionOnPlane(movement.position, 'YZ');
		if(this._transType.indexOf('MOVE') >= 0)
			this.calcFixedAxis();
	}

	if(this._selectedAxis === 'XZ' || this._selectedAxis === 'Z_AXIS' || this._selectedAxis === 'XZ_CIRCLE') {
		this._mouseDownPos = this.pickPositionOnPlane(movement.position, 'XZ');
		if(this._transType.indexOf('MOVE') >= 0)
			this.calcFixedAxis();
	}

	if(this._fixedAxis === 'X') {
		this._xAxisPL.polyline.material.color = FreeDo.Color.YELLOW;
	}
	if(this._fixedAxis === 'Y') {
		this._yAxisPL.polyline.material.color = FreeDo.Color.YELLOW;
	}
	if(this._fixedAxis === 'Z') {
		this._zAxisPL.polyline.material.color = FreeDo.Color.YELLOW;
	}

	this._mouseDownXY = movement.position;
	//console.log(this._mouseDownPos);
}

FdTransformManipulator.prototype.backToEnableRotate = function(movement) {
	if(this._selectedAxis === '') {
		this._viewer.scene.screenSpaceCameraController.enableRotate = true;
		this._viewer.scene.screenSpaceCameraController.enableLook = true;
		//this._viewer.scene.useDepthPicking = FreeDo.clone(this.useDepthPicking, true);
		//this._viewer.scene.pickTranslucentDepth = FreeDo.clone(this.pickTranslucentDepth, true);
	}
}

FdTransformManipulator.prototype.onLeftUp = function(movement) {
	//要给个延迟，不能直接调用
	setTimeout(this.backToEnableRotate.bind(this), 500);

	this._mouseDownPos = undefined;
	this._mouseDown = false;

	if(this._selectedAxis === 'XY_QCIRCLE') {
		this._XYPlane.polygon.material.color = new Freedo.Color(0, 0, 1, this._alpha);
	}
	if(this._selectedAxis === 'XZ') {
		this._XZPlane.wall.material.color = new Freedo.Color(1, 0, 1, this._alpha);
	}
	if(this._selectedAxis === 'YZ') {
		this._YZPlane.wall.material.color = new Freedo.Color(0, 1, 1, this._alpha)
	}

	if(this._selectedAxis === 'XY_CIRCLE') {
		this._XYCircle.polyline.material.color = new Freedo.Color(0, 0, 1, this._alpha)
	}

	if(this._selectedAxis === 'XZ_CIRCLE') {
		this._XZCircle.polyline.material.color = new Freedo.Color(0, 1, 0, this._alpha)
	}

	if(this._selectedAxis === 'YZ_CIRCLE') {
		this._YZCircle.polyline.material.color = new Freedo.Color(1, 0, 0, this._alpha)
	}

	if(this._fixedAxis === 'X') {
		this._xAxisPL.polyline.material.color = FreeDo.Color.RED;
	}
	if(this._fixedAxis === 'Y') {
		this._yAxisPL.polyline.material.color = FreeDo.Color.GREEN;
	}
	if(this._fixedAxis === 'Z') {
		this._zAxisPL.polyline.material.color = FreeDo.Color.BLUE;
	}

	if(this._selectedAxis !== '') {
		this._event.raiseEvent('Transformed', {});
	}
	this.calcAxisZoom();

	this._selectedAxis = '';
	this._fixedAxis = '';
}

FdTransformManipulator.prototype.clearAxis2 = function() {
    if(this._xAxisPL != undefined) {
        this._viewer.entities.remove(this._basePt);
        this._viewer.entities.remove(this._basePtOri);
        
        this._viewer.scene.primitives.remove(this._xAxisPL);
        this._viewer.scene.primitives.remove(this._yAxisPL);
        this._viewer.scene.primitives.remove(this._zAxisPL);
        
        this._viewer.entities.remove(this._XZPlane);
        this._viewer.entities.remove(this._YZPlane);
        this._viewer.entities.remove(this._XYPlane);
        
        this._viewer.entities.remove(this._XYCircle);
        this._viewer.entities.remove(this._XZCircle);
        this._viewer.entities.remove(this._YZCircle);
    }
    this._basePt = undefined;
    this._basePtOri = undefined;
    this._xAxisPL = undefined;
    this._yAxisPL = undefined;
    this._zAxisPL = undefined;
    this._XZPlane = undefined;
    this._YZPlane = undefined;
    this._XYPlane = undefined;
    this._XYCircle = undefined;
    this._XZCircle = undefined;
    this._YZCircle = undefined;
    
    this._selectedAxis = '';
}

FdTransformManipulator.prototype.clearAxis = function() {
	if(this._xAxisPL != undefined) {
		this._viewer.entities.remove(this._basePt);
		this._viewer.entities.remove(this._basePtOri);
		
		this._viewer.entities.remove(this._xAxisPL);
		this._viewer.entities.remove(this._yAxisPL);
		this._viewer.entities.remove(this._zAxisPL);
		
		this._viewer.entities.remove(this._XZPlane);
		this._viewer.entities.remove(this._YZPlane);
		this._viewer.entities.remove(this._XYPlane);
		
		this._viewer.entities.remove(this._XYCircle);
		this._viewer.entities.remove(this._XZCircle);
		this._viewer.entities.remove(this._YZCircle);
	}
	this._basePt = undefined;
	this._basePtOri = undefined;
	this._xAxisPL = undefined;
	this._yAxisPL = undefined;
	this._zAxisPL = undefined;
	this._XZPlane = undefined;
	this._YZPlane = undefined;
	this._XYPlane = undefined;
	this._XYCircle = undefined;
	this._XZCircle = undefined;
	this._YZCircle = undefined;
	
	this._selectedAxis = '';
}

var position = undefined;
var length = undefined;
var posGraphic = undefined;
var longitude = undefined;
var latitude = undefined;
var height = undefined;

FdTransformManipulator.prototype.calcCallbackArgs = function() {
	position = this._initSphere.center;
	posGraphic = Freedo.Cartographic.fromCartesian(position);
	longitude = Freedo.Math.toDegrees(posGraphic.longitude);
	latitude = Freedo.Math.toDegrees(posGraphic.latitude);
	height = posGraphic.height;
	length = this._axisZoom;
	this._viewer.scene.requestRender();
}

FdTransformManipulator.prototype.pickPositionOnEllipsoid = function(position) {
	var centerGraphic = Freedo.Cartographic.fromCartesian(this._initSphere.center);

	var des = Freedo.clone(Freedo.Ellipsoid.WGS84, true);
	des.radii.x += centerGraphic.height;
	des.radii.y += centerGraphic.height;
	des.radii.z += centerGraphic.height;
	var newdes = new Freedo.Ellipsoid(des.radii.x, des.radii.y, des.radii.z);
	var cartesian = this._viewer.camera.pickEllipsoid(position, newdes, new Freedo.Cartesian3());
	if(cartesian === undefined)
		return undefined;
	var moveGraphic = Freedo.Cartographic.fromCartesian(cartesian);
	return moveGraphic;
}

FdTransformManipulator.prototype.pickPositionOnPlane = function(position, axis) {
	this.calcCallbackArgs();
	//console.log('pickPositionOnPlane');
	//默认是YZ
	var tdis = this._axisZoom * 0.00001;
	var p0 = Freedo.Cartesian3.fromRadians(posGraphic.longitude - tdis, posGraphic.latitude, -2000);
	var p1 = Freedo.Cartesian3.fromRadians(posGraphic.longitude, posGraphic.latitude, posGraphic.height + length * 0.95 + 1000);
	var p2 = Freedo.Cartesian3.fromRadians(posGraphic.longitude + tdis, posGraphic.latitude, -2000);
	if(axis === 'YZ') {
		p0 = Freedo.Cartesian3.fromRadians(posGraphic.longitude, posGraphic.latitude - tdis, -2000);
		p1 = Freedo.Cartesian3.fromRadians(posGraphic.longitude, posGraphic.latitude, posGraphic.height + length * 0.95 + 1000);
		p2 = Freedo.Cartesian3.fromRadians(posGraphic.longitude, posGraphic.latitude + tdis, -2000);
	}

	var ray = this._viewer.camera.getPickRay(position);
	//var mouseDownPos = this.pickPositionOnEllipsoid(position);
	//var startPt3 = Freedo.Cartesian3.fromRadians(mouseDownPos.longitude, mouseDownPos.latitude, mouseDownPos.height);

	var x = 0;
	var y = 0;
	var z = 0;
	var centerGraphic = Freedo.Cartographic.fromCartesian(this._initSphere.center);
	x = Math.cos(centerGraphic.longitude);
	y = Math.sin(centerGraphic.longitude);

	var plane = new Freedo.Plane.fromPointNormal(new Freedo.Cartesian3(0, 0, 0), new Freedo.Cartesian3(x, y, z));

	var end = {};
	end.x = ray.origin.x + ray.direction.x * 1000000;
	end.y = ray.origin.y + ray.direction.y * 1000000;
	end.z = ray.origin.z + ray.direction.z * 1000000;

	if(0) {
		var op = {
			polyline: {
				positions: [p1, p2],
				width: 6,
				material: Freedo.Color.RED
			}
		}
		this._viewer.entities.add(op);
		op = {
			polyline: {
				positions: [p1, p0],
				width: 6,
				material: Freedo.Color.RED
			}
		}
		this._viewer.entities.add(op);
	}

	var cartesian = Freedo.IntersectionTests.lineSegmentTriangle(ray.origin, end, p0, p1, p2);
	if(cartesian === undefined)
		return undefined;
	if(0) {
		this._viewer.entities.add({
			position: cartesian,
			ellipsoid: {
				material: Freedo.Color.YELLOW.withAlpha(0.9),
				radii: new Freedo.Cartesian3(1, 1, 1)
			}
		});
	}
	var moveGraphic = Freedo.Cartographic.fromCartesian(cartesian);
	return moveGraphic;
}

FdTransformManipulator.prototype.pickPosition = function(windowPosition) {
	var cartesian,
		type = this.PositionType.NONE;

	//尝试在模型上选点
	var pickedObjects,
		entity;
	if(this._viewer.scene.pickPositionSupported) {
		var pickedObjects = this._viewer.scene.drillPick(windowPosition);
		if(Freedo.defined(pickedObjects) && pickedObjects.length > 0) {
			for(var i = 0; i < pickedObjects.length; ++i) {
				if(pickedObjects[i] instanceof Freedo.FreedoPModelFeature) {
					//if (Freedo.defined(pickedObjects[i])) {
					//this._useDepthPicking = FreeDo.clone(this._viewer.scene.useDepthPicking, true);
					//this._pickTranslucentDepth = FreeDo.clone(this._viewer.scene.pickTranslucentDepth, true);

					//this._viewer.scene.useDepthPicking = true;
					//this._viewer.scene.pickTranslucentDepth = true;

					cartesian = this._viewer.scene.pickPosition(windowPosition);

					//this._viewer.scene.useDepthPicking = FreeDo.clone(this._useDepthPicking, true);
					//this._viewer.scene.pickTranslucentDepth = FreeDo.clone(this._pickTranslucentDepth, true);

					if(Freedo.defined(cartesian)) {
						type = this.PositionType.ON_MODEL;
						break;
					}
				}
			}
		}
	}

	//尝试在地形上拾取点
	if(type === this.PositionType.NONE) {
		this._useDepthPicking = FreeDo.clone(this._viewer.scene.useDepthPicking, true);
		this._pickTranslucentDepth = FreeDo.clone(this._viewer.scene.pickTranslucentDepth, true);

		//this._viewer.scene.useDepthPicking = true;
		//this._viewer.scene.pickTranslucentDepth = true;

		if(this._viewer.scene.pickPositionSupported) {
			cartesian = this._viewer.scene.pickPosition(windowPosition, new Freedo.Cartesian3());
			var posGraphic = Freedo.Cartographic.fromCartesian(cartesian);
			//console.log(posGraphic);
			if(Freedo.defined(cartesian))
				type = this.PositionType.ON_TERRAIN;
		}

		this._viewer.scene.useDepthPicking = FreeDo.clone(this._useDepthPicking, true);
		this._viewer.scene.pickTranslucentDepth = FreeDo.clone(this._pickTranslucentDepth, true);
	}

	if(type === this.PositionType.NONE) {
		cartesian = this._viewer.camera.pickEllipsoid(windowPosition, this._viewer.scene.globe.ellipsoid);
		if(Freedo.defined(cartesian))
			type = this.PositionType.ON_ELLIPSOID;
	}

	return {
		type: type,
		position: cartesian
	};
}

FdTransformManipulator.prototype.callbackXY = function(time, result) {
	this.calcCallbackArgs();

	var rio = Freedo.FdMisc.FdGeometric.getLongAndLatRatio(posGraphic);

	var widthLong = FreeDo.Math.toDegrees(length * 0.5 * rio[0]);
	var widthLat = FreeDo.Math.toDegrees(length * 0.5 * rio[1]);
	result = Freedo.Cartesian3.fromDegreesArray([longitude, latitude,
		longitude + widthLong, latitude,
		longitude + widthLong, latitude + widthLat,
		longitude, latitude + widthLat,
		longitude, latitude
	]);

	return result;
}

FdTransformManipulator.prototype.callbackQXY = function(time, result) {
	this.calcCallbackArgs();
	var aPos = [];

	var rio = Freedo.FdMisc.FdGeometric.getLongAndLatRatio(posGraphic);
	var widthLong = FreeDo.Math.toDegrees(length * 0.55 * rio[0]);
	var widthLat = FreeDo.Math.toDegrees(length * 0.55 * rio[1]);

	aPos.push(Freedo.Cartesian3.fromRadians(posGraphic.longitude, posGraphic.latitude, posGraphic.height));

	for(var i = 0; i <= Math.PI / 2; i = i + 0.001) {
		var longi = longitude + widthLong * Math.cos(i);
		var lat = latitude + widthLat * Math.sin(i);
		var pt = Freedo.Cartesian3.fromDegrees(longi, lat, height);
		aPos.push(pt);
	}
	result = aPos;

	return result;
}

FdTransformManipulator.prototype.callbackXYHalfCircle = function(time, result) {
	this.calcCallbackArgs();
	var aPos = [];

	var rio = Freedo.FdMisc.FdGeometric.getLongAndLatRatio(posGraphic);

	var widthLong = FreeDo.Math.toDegrees(length * 0.65 * rio[0]);
	var widthLat = FreeDo.Math.toDegrees(length * 0.65 * rio[1]);

	for(var i = 0; i <= Math.PI / 2; i = i + 0.001) {
		var longi = longitude + widthLong * Math.cos(i);
		var lat = latitude + widthLat * Math.sin(i);
		var pt = Freedo.Cartesian3.fromDegrees(longi, lat, height);
		aPos.push(pt);
	}
	result = aPos;

	return result;
}

FdTransformManipulator.prototype.callbackXZHalfCircle = function(time, result) {
	this.calcCallbackArgs();

	var rio = Freedo.FdMisc.FdGeometric.getLongAndLatRatio(posGraphic);

	var widthLong = FreeDo.Math.toDegrees(length * 0.65 * rio[0]);
	var widthLat = FreeDo.Math.toDegrees(length * 0.65 * rio[1]);

	var aPos = [];

	for(var i = Math.PI / 2; i >= 0; i = i - 0.001) {
		var longi = longitude + widthLong * Math.cos(i);
		var lat = latitude;
		var h = height + length * 0.65 * Math.sin(i);
		var pt = Freedo.Cartesian3.fromDegrees(longi, lat, h);
		aPos.push(pt);
	}

	result = aPos;

	return result;
}

FdTransformManipulator.prototype.callbackYZHalfCircle = function(time, result) {
	this.calcCallbackArgs();

	var rio = Freedo.FdMisc.FdGeometric.getLongAndLatRatio(posGraphic);
	var widthLong = FreeDo.Math.toDegrees(length * 0.65 * rio[0]);
	var widthLat = FreeDo.Math.toDegrees(length * 0.65 * rio[1]);

	var aPos = [];

	for(var i = Math.PI / 2; i >= 0; i = i - 0.001) {
		var longi = longitude;
		var lat = latitude + widthLat * Math.cos(i);
		var h = height + length * 0.65 * Math.sin(i);
		var pt = Freedo.Cartesian3.fromDegrees(longi, lat, h);
		aPos.push(pt);
	}
	result = aPos;

	return result;
}

FdTransformManipulator.prototype.callbackXYShow = function(time, result) {
	this.calcCallbackArgs();
	result = true;
	if(this._selectedAxis == 'XY_CIRCLE')
		result = false;

	return result;
}

//XY厚度
FdTransformManipulator.prototype.callbackXYH = function(time, result) {
	return height + 0.1;
}

FdTransformManipulator.prototype.callbackXZ = function(time, result) {
	this.calcCallbackArgs();

	var rio = Freedo.FdMisc.FdGeometric.getLongAndLatRatio(posGraphic);
	var widthLong = FreeDo.Math.toDegrees(length * 0.5 * rio[0]);

	result = Freedo.Cartesian3.fromDegreesArray([longitude, latitude, longitude + widthLong, latitude]);
	return result;
}

FdTransformManipulator.prototype.callbackXZMinimumHeights = function(time, result) {
	this.calcCallbackArgs();
	result = [height, height];
	return result;
}

FdTransformManipulator.prototype.callbackXZMaximumHeights = function(time, result) {
	this.calcCallbackArgs();
	result = [height + length * 0.5, height + length * 0.5];
	return result;
}

FdTransformManipulator.prototype.callbackYZ = function(time, result) {
	this.calcCallbackArgs();
	result = Freedo.Cartesian3.fromDegreesArray([longitude, latitude,
		longitude, latitude + length * 0.5 / this._DISPERRAD
	]);
	return result;
}

FdTransformManipulator.prototype.callbackXYCCBP = function(time, result) {
	this.calcCallbackArgs();
	result = Freedo.Cartesian3.fromDegrees(longitude, latitude);
	return result;
}

FdTransformManipulator.prototype.callbackX = function(time, result) {
	this.calcCallbackArgs();
	var rio = Freedo.FdMisc.FdGeometric.getLongAndLatRatio(posGraphic);
	var widthLong = FreeDo.Math.toDegrees(length * rio[0]);

	result = Freedo.Cartesian3.fromDegreesArrayHeights([longitude, latitude, height,
		longitude + widthLong, latitude, height
	]);
	return result;
}

FdTransformManipulator.prototype.callbackY = function(time, result) {
	this.calcCallbackArgs();
	result = Freedo.Cartesian3.fromDegreesArrayHeights([longitude, latitude, height,
		longitude, latitude + length / this._DISPERRAD, height
	]);
	return result;
}

FdTransformManipulator.prototype.callbackZ = function(time, result) {
	this.calcCallbackArgs();
	result = Freedo.Cartesian3.fromDegreesArrayHeights([longitude, latitude, height,
		longitude, latitude, height + length
	]);
	return result;
}

FdTransformManipulator.prototype.callbackH = function(time, result) {
	this.calcCallbackArgs();
	result = height;
	return result;
}

FdTransformManipulator.prototype.callbackBasePt = function(time, result) {
	this.calcCallbackArgs();
	result = position.clone();
	return result;
}

FdTransformManipulator.prototype.callbackBasePtOri = function(time, result) {
	//this.calcCallbackArgs();
	result = this._basePtOriPosition.clone();
	return result;
}

