	var zCP = new Freedo.CallbackProperty(this.callbackZ.bind(this), false);

	var hCP = new Freedo.CallbackProperty(this.callbackH.bind(this), false);
	var basePtCP = new Freedo.CallbackProperty(this.callbackBasePt.bind(this), false);
