/**
 * 重置
 */
FdTransformManipulator.prototype.reset = function() {
	if(this._handler !== undefined && !this._handler.isDestroyed())
		this._handler.destroy();
    this._viewer.scene.camera.changed.removeEventListener(FdTransformManipulator.prototype.onWheel, this);
	this._bInTransform = false;
	this.clearAxis();
	this._mouseDownPos = undefined;
	this._initSphere = undefined;
	this._fixedAxis = '';
}

/**
 * 销毁所有资源
 */
FdTransformManipulator.prototype.dispose = function() {
	this.reset();
}

export {
	FdTransformManipulator
};