var scratchInterpolatePoint = new Freedo.Cartesian3();
var scratchOffsetPoint = new Freedo.Cartesian3();

function interpolate(cartographic0, cartographic1, maxStep) {
    var result = [];
    var cartesian0 = Freedo.Cartesian3.fromRadians(cartographic0.longitude, cartographic0.latitude, cartographic0.height);
    var cartesian1 = Freedo.Cartesian3.fromRadians(cartographic1.longitude, cartographic1.latitude, cartographic1.height);
    var length = Freedo.Cartesian3.distance(cartesian0, cartesian1);
    if (length > maxStep) {
        var num = length / maxStep;
        for (var i = 0; i < num - 1; ++i) {
            //var cartesian = new Freedo.Cartesian3();
            Freedo.Cartesian3.lerp(cartesian0, cartesian1, (i + 1) / num, scratchInterpolatePoint);
            var cartographic = Freedo.Cartographic.fromCartesian(scratchInterpolatePoint);
            result.push(cartographic);
        }
    }
    return result;
}

function cloneLayers(layers, result) {
    if (!Freedo.defined(result)) {
        result = [];
    }
    for (var i = 0; i < layers.length; i++) {
        result.push({
            name: layers[i].name,
            depth: layers[i].depth,
            thickness: layers[i].thickness
        });
    }
    return result;
}

function getOffsetPoint(cartesian, normal, offset) {
    var point = new Freedo.Cartesian3();
    Freedo.Cartesian3.multiplyByScalar(normal, offset, point);
    Freedo.Cartesian3.add(cartesian, point, point);
    return point;
}

function getSimulateDrill(scope, cartographic, refRadius) {
    var refDrills = [];
    //var cartesian = Freedo.Cartesian3.fromRadians(cartographic.longitude, cartographic.latitude, cartographic.height);
    var cartesianOnZeroHeight = Freedo.Cartesian3.fromRadians(cartographic.longitude, cartographic.latitude, 0);
    var totalWeight = 0;
    for (var i = 0; i < scope._drillDataArray.length; i++) {
        var drill = scope._drillDataArray[i];
        var distance = Freedo.Cartesian3.distance(drill.cartesianOnZeroHeight, cartesianOnZeroHeight);
        if (distance <= refRadius) {
            refDrills.push({
                name: drill.name,
                waterDepth: drill.waterDepth,
                layers: cloneLayers(drill.layers), //Array.from(drill.layers),
                weight: 1.0 / distance,
                pos: drill.pos
            });
            totalWeight += 1.0 / distance;
        }
    }

    var refDrillsNum = refDrills.length;
    if (refDrillsNum < 1) {
        // 没有在参考距离范围内的钻孔数据，使用最近的钻孔点数据
        var minDis = Number.MAX_VALUE;
        var minDisDrillIndex = -1;
        for (var i = 0; i < scope._drillDataArray.length; i++) {
            var distance = Freedo.Cartesian3.distance(scope._drillDataArray[i].cartesianOnZeroHeight, cartesianOnZeroHeight);
            if (distance <= minDis) {
                minDisDrillIndex = i;
            }
        }

        return {
            waterDepth: scope._drillDataArray[minDisDrillIndex].waterDepth,
            drillsInfo: [{
                drillName: scope._drillDataArray[minDisDrillIndex].name,
                drillPos: scope._drillDataArray[minDisDrillIndex].pos,
                layers: scope._drillDataArray[minDisDrillIndex].layers
            }],
            layers: cloneLayers(scope._drillDataArray[minDisDrillIndex].layers) //Array.from(scope._drillDataArray[minDisDrillIndex].layers)
        };
    }

    var waterDepth = 0;
    var drillInfo = [];
    for (var i = 0; i < refDrillsNum; i++) {
        refDrills[i].weight /= totalWeight;
        waterDepth += refDrills[i].waterDepth * refDrills[i].weight;

        drillInfo.push({
            drillName: refDrills[i].name,
            drillPos: refDrills[i].pos,
            layers: Freedo.clone(refDrills[i].layers)
        });
    }

    var layers = [];

    while (true) {

        var hasLayer = false;
        for (var i = 0; i < refDrillsNum; i++) {
            if (refDrills[i].layers.length > 0) {
                hasLayer = true;
                break;
            }
        }

        if (!hasLayer) {
            break;
        }

        var layerMap = new Map;
        for (var i = 0; i < refDrillsNum; i++) {
            if (refDrills[i].layers.length > 0) {
                var topLayerName = refDrills[i].layers[0].name;
                if (!layerMap.has(topLayerName)) {
                    layerMap.set(topLayerName, {
                        weight: refDrills[i].weight,
                        //drillName: refDrills[i].name,
                        //drillPos: refDrills[i].pos,
                        thickness: refDrills[i].layers[0].thickness * refDrills[i].weight
                    });
                } else {
                    layerMap.set(topLayerName, {
                        weight: layerMap.get(topLayerName).weight + refDrills[i].weight,
                        //drillName: layerMap.get(topLayerName).drillName + refDrills[i].name,
                        //drillPos: layerMap.get(topLayerName).drillPos + refDrills[i].pos,
                        thickness: layerMap.get(topLayerName).thickness + refDrills[i].layers[0].thickness * refDrills[i].weight
                    });
                }
            }
        }
        var weight = 0;
        var layerName;
        var thickness = 0;
        layerMap.forEach(function(value, key) {
            if (weight < value.weight) {
                weight = value.weight;
                layerName = key;
                thickness = value.thickness;
            }
        });

        for (var i = 0; i < refDrillsNum; i++) {
            if (refDrills[i].layers.length > 0 && refDrills[i].layers[0].name === layerName) {
                refDrills[i].layers.shift();
            }
        }

        layers.push({
            name: layerName,
            thickness: thickness,
            depth: thickness + (layers.length > 0 ? layers[layers.length - 1].depth : 0)
        });
    }

    return {
        waterDepth: waterDepth,
        drillsInfo: drillInfo,
        pos: cartographic,
        layers: layers
    };
}


export { interpolate, getOffsetPoint, getSimulateDrill };