import {
    FdPolylineEditor
} from './Analysis/FdPolylineEditor';

import {
    FdGeologySimulationManager
} from '../FdTools/FdGeologySimulationManager/FdGeologySimulationManager';
/**
 * 地质模拟鼠标交互类
 *
 * @alias FdMicroApp.FdGeologySimulationHandler
 * @class
 * @param {Freedo.Viewer} viewer 视景器
 * @param {String | FdTools.FdGeologySimulationManager} geologySimulationManager 原始Json文件字符串 | 地质模拟管理器对象
 * @param {String} [textureDir] 地质模拟纹理目录
 *
 *
 *
 * @example
 * var viewer = Freedo.FdApp.createDefaultViewer('freedoContainer');
 *
 *  var geologySimulationManager;
 *  var geologySimulationHandel
 *  Freedo.loadJson('./static/GeologySimulation/drills.json').then(function(jsonData) {
 *      geologySimulationManager = new Freedo.FdTools.FdGeologySimulationManager(scene ,jsonData);
 *      geologySimulationHandel = new FdGeologySimulationHandle(viewer ,geologySimulationManager);
 * 
 *      //or
 * 
 *     geologySimulationHandel = new FdGeologySimulationHandle(viewer ,JSON.stringify(jsonData) );
 *  }).otherwise(function(error) {
 *      console.log('Load drills file failed.');
 *  });
 *
 *  var screenSpaceEventHandler = new Freedo.ScreenSpaceEventHandler(viewer.canvas);
 *  screenSpaceEventHandler.setInputAction(function(movement){
 *      if(Freedo.defined(geologySimulationHandel)){
 *          geologySimulationHandel.start() ;
 *      }
 *  }, Freedo.ScreenSpaceEventType.MIDDLE_CLICK);
 *
 */
class FdGeologySimulationHandler {

    constructor(viewer, geologySimulationManager, textureDir) {
        if (!Freedo.defined(geologySimulationManager)) {
            throw new Freedo.DeveloperError('geologySimulationManager Is Required!');
        }

        this._viewer = viewer;
        this._scene = this._viewer.scene;

        this._ple = new FdPolylineEditor(viewer);
        this._ple.setAutoDelLines(true);

        if (typeof geologySimulationManager === 'string') {
            this._geologySimulationManager = new FdTools.FdGeologySimulationManager(viewer.scene, JSON.parse(geologySimulationManager), textureDir);
        } else {
            this._geologySimulationManager = geologySimulationManager;
        }

        this._data = [];

        this._event = new Freedo.Event();
        this._eventHelper = new Freedo.EventHelper();

        var that = this;
        this._geologySimulationManager.on(function(eventType, param) {
            if (eventType === 'dataAdded') {
                that._data.push(param);
                that._event.raiseEvent('dataChanged', param);
            }
        });
    }

    /**
     * 地质模拟管理对象
     * @type {FdTools.FdGeologySimulationManager}
     */
    get geologySimulationManager() {
        return this._geologySimulationManager;
    };


    /**
     * 获取已经添加的地质模拟的相关参数的数组
     * @returns {Object[]} geologyObjectArray 已经添加的地质模拟的相关参数的数组
     * @param {Freedo.Cartographics[]} geologyObjectArray[i].points 输入的多边形的点
     * @param {Freedo.GroundErasePrimitive} geologyObjectArray[i].erasePritive 地形挖洞的对象
     * @param {Freedo.Primitive[]} geologyObjectArray[i].geologyPrimitives 添加的地质面对象数组
     * 
     */
    getData() {
        return this._data;
    }

    /**
     * 事件类型有：start,cancel,dispose,dataChanged
     * @param {FdMicroApp.FdGeologySimulationHandler~FdGeologySimulationHandlerCallback} listener 当有事件触发时被执行的函数。
     * @param {Object} [scope] listener函数执行时的绑定的对象。
     * @returns {Freedo.Event~RemoveCallback} 返回一个函数，调用该函数可以取消监听。
     * 
     */
    on(listener, scope) {
        return this._eventHelper.add(this._event, listener, scope);
    }

    /**
     * @callback FdMicroApp.FdGeologySimulationHandler~FdGeologySimulationHandlerCallback
     * @param {String} eventType 事件类型有：start（开始地质模拟选点），cancel（结束地质模拟选点），dispose（销毁对象），dataChanged（添加完地质模拟对象）
     * @param {Object} eventArg dataChanged的参数为刚添加的对象的封装，参考{@link FdMicroApp.FdGeologySimulationHandler#getData};start/cancel/dispose事件不带参数
     */


    /**
     * 开始进行绘制地质模拟多边形的交互操作
     *
     */
    start() {
        this._ple.run();

        if (this._lsner) {
            this._lsner();
            this._lsner = undefined;
        }

        var that = this;

        this._lsner = this._ple.on(function(eventType, param) {
            if (eventType === 'PLAdd') {
                that.onPLAdd(param);
            }
        });

        this._event.raiseEvent('start', {});
    }

    /**
     * 取消绘制地质模拟多边形的交互操作
     *
     */
    cancel() {
        this._ple.cancel();

        this._event.raiseEvent('cancel', {});
    }

    /**
     * 新添加多段线之后的响应函数
     * @private
     */
    onPLAdd(param) {
        if (!param.polyline)
            return;

        if (param.polyline.pts.length === 2) {
            this._ple.delResult();
            return;
        }

        var cartesians = param.polyline.pts;

        var cartographics = [];
        for (var i = 0; i < cartesians.length; i++) {
            cartographics.push(Freedo.Cartographic.fromCartesian(cartesians[i]));
        }
        this._geologySimulationManager.addGeologySimulation(cartographics, 1, 5, 5, undefined, true);
    }

    /**
     * 重置
     * @private
     */
    reset() {
        if (this._lsner)
            this._lsner();
        this._lsner = undefined;
        this._data = [];
        //this._geologySimulationManager.removeAllGeologySimulations();
        this._ple.cancel();
    }

    /**
     * 重置
     * 
     */
    dispose() {
        this.reset();
        this._event.raiseEvent('dispose', {});
        this._eventHelper.removeAll();
    }
}

export {
    FdGeologySimulationHandler
};