 function look(camera, angle) {
     var pos = camera.positionWC;
     var oldTransform = Freedo.Matrix4.clone(camera.transform, oldTransform);
     camera.lookAtTransform(Freedo.Matrix4.IDENTITY);
     camera.look(pos, Freedo.Math.toRadians(angle));
     camera.lookAtTransform(oldTransform);
 }

 /**
  * 以当前相机世界坐标为中心点向左旋转相机
 *
  * @alias FdCamera.lookLeft
  * @param {Freedo.Camera} camera 场景相机对象
  * @param {Number} angle 相机旋转角度，单位为度
  */
 function lookLeft(camera, angle) {
     look(camera, -angle);
     //_getCurrentCameraInfo(camera, this._currentCameraInfo);
     //this._event.raiseEvent('Changed', this._currentCameraInfo);
 }

 /**
  * 以当前相机世界坐标为中心点向右旋转相机
 *
  * @alias FdCamera.lookRight
  * @param {Freedo.Camera} camera 场景相机对象
  * @param {Number} angle 相机旋转角度，单位为度
  */
 function lookRight(camera, angle) {
     look(camera, angle);
     // _getCurrentCameraInfo(camera, this._currentCameraInfo);
     // this._event.raiseEvent('Changed', this._currentCameraInfo);
 }

 export { lookLeft, lookRight };