import {
    FdPathAnimator
} from './FdPathAnimator';

import {
    FdMisc
} from '../FdMisc/FdMisc';

import {
    FdPolylineEditor
} from './Analysis/FdPolylineEditor';

/**
 * 路径管理器
 * @alias FdMicroApp.FdPathsManager
 * @class
 * 
 * @example 
 * var viewer = Freedo.FdApp.createDefaultViewer('freedoContainer');
 * var pathsManager = new FdPathsManager(viewer, []);
 * pathsManager.setSpeed(20);
 * pathsManager.setLoop(false);
 * pathsManager.setKBStatus(true);
 * pathsManager.setSmoothRate(5);
 * pathsManager.setHeight(0);
 * pathsManager.setHeading(0);
 * pathsManager.setPitch(45);
 * //设置高度偏移，就是在实际拾取到的点的高度的基础上增加一个值
 * pathsManager.setOffsetH(50);
 * pathsManager.setPathViewMode(true);
 * pathsManager.setPathShow(true);
 * currentGroupId = pathsManager.createGroup('Group_' + new Date().getTime());
 * //准备进行选点操作
 * pathsManager.preAppendPathPoint();
 */

class FdPathsManager {
    constructor(viewer, pathsOpts) {
        // 将这个变量也引用过去
        this._pathsOpts = pathsOpts;

        this._pa = new FdPathAnimator(viewer);
        this._pa.on((eventType, params) => {
            this.pathAnimatorEventCallback(eventType, params);
        });

        this._ple = new FdPolylineEditor(viewer);
        this._ple.setAutoLoop(false);
        this._ple.setAutoDelLines(true);

        this._event = new Freedo.Event();
        this._eventHelper = new Freedo.EventHelper();

        this._currentPlayingGroupID = undefined;
        this._currentGroupID = undefined;

        var that = this;

        this._lsner = this._ple.on(function(eventType, param) {
            if(eventType == 'PointAdded') {
                that.appendPathPoint(that._currentGroupID, param);
            }

            if(eventType == 'PLAdd') {
                that._ple.cancel();
                that.reloadPA();
                that._event.raiseEvent('DataChanged', that._pathsOpts[that._currentGroupID]);
            }

            if(eventType == 'EditEnd') {
                var list = that._pathsOpts[that._currentGroupID].list;
                for(var k in param.pts) {
                    var pos = FdMisc.FdGeometric.tg(param.pts[k]);
                    list[k].position = pos;
                }
                that.reloadPA(that._currentGroupID);
                that._event.raiseEvent('DataChanged', that._pathsOpts[that._currentGroupID]);
            }
        });
    }

    get pathsOptions() {
        return this._pathsOpts;
    }

    set pathsOptions(v) {
        if(!Freedo.defined(v)) {
            return;
        }

        this._pathsOpts = v;
    }
}

/**
 * 转发FdPathAnimator的事件
 * @param {String} eventType 事件名称
 * @param {Object} param 事件参数
 * @private
 */
FdPathsManager.prototype.pathAnimatorEventCallback = function(eventType, param) {
    this._event.raiseEvent(eventType, param);
}

/**
 * 给定组名称，创建组
 * @param {String} groupName 组的名称
 * @returns {String} 返回组ID
 */
FdPathsManager.prototype.createGroup = function(groupName, groupID) {
    if(typeof groupID === 'undefined') {
        groupID = Freedo.createGuid();
    }

    this._pathsOpts[groupID] = {
        name: groupName,
        smooth: 5,
        list: [],
    };

    this._currentGroupID = groupID;

    return groupID;
}

/**
 * 删除组
 * @param {String} groupID 组ID
 */
FdPathsManager.prototype.removeGroup = function(groupID) {
    if(typeof(this._pathsOpts[groupID]) == 'undefined') {
        return false;
    }

    if(this._currentGroupID === groupID) {
        this._pa.reset();
        this._currentGroupID = undefined;
    }

    if(this._currentPlayingGroupID === groupID) {

    }

    delete this._pathsOpts[groupID];
    this._event.raiseEvent('GroupsChanged');

    return true;
}

/**
 * 获组的名字
 * @param {String} groupID 组ID
 * @returns {String} 组名称
 */
FdPathsManager.prototype.getGroupName = function(groupID) {
    if(typeof(this._pathsOpts[groupID]) == 'undefined') {
        return '';
    }

    return this._pathsOpts[groupID].name;
}

/**
 * 设置组的名字
 * @param {String} groupID 组ID
 * @param {String} groupName 组名称
 */
FdPathsManager.prototype.setGroupName = function(groupID, groupName) {
    if(typeof(this._pathsOpts[groupID]) == 'undefined') {
        return '';
    }

    this._pathsOpts[groupID].name = groupName;
}

/**
 * 获组的平滑度
 * @param {String} groupID 组ID
 * @returns {Number} 平滑度
 */
FdPathsManager.prototype.getGroupSmooth = function(groupID) {
    if(typeof(this._pathsOpts[groupID]) == 'undefined') {
        return -1;
    }

    if(!this._pathsOpts[groupID].smooth)
        return 5;

    return this._pathsOpts[groupID].smooth;
}

/**
 * 获组的平滑度
 * @param {String} groupID 组ID
 * @returns {Boolean} true为设置成功
 */
FdPathsManager.prototype.setGroupSmooth = function(groupID, value) {
    if(typeof(this._pathsOpts[groupID]) == 'undefined') {
        return false;
    }

    this._pathsOpts[groupID].smooth = value;
    this._pa.setSmoothRate(value);
    return true;
}

FdPathsManager.prototype.getGroupOptions = function(groupID) {
    if(typeof(this._pathsOpts[groupID]) == 'undefined') {
        return undefined;
    }

    var groupOptions = Freedo.clone(this._pathsOpts[groupID], true);
    return groupOptions;
}

/**
 * 删除所有组
 */
FdPathsManager.prototype.removeAllGroups = function() {
    for(var groupID in this._pathsOpts) {
        delete this._pathsOpts[groupID];
    }
    this._currentPlayingGroupID = undefined;

    this._event.raiseEvent('GroupsChanged');
    return true;
}

/**
 * 获取所有组的ID
 * @returns 返回一个包含组ID的数组
 */
FdPathsManager.prototype.getAllGroupIDs = function() {
    var allGroupIDs = [];
    for(var groupID in this._pathsOpts) {
        allGroupIDs.push(groupID);
    }

    return allGroupIDs;
}

/**
 * 准备增加点
 *
 */
FdPathsManager.prototype.preAppendPathPoint = function() {
    if(!Freedo.defined(this._currentGroupID) || this._currentGroupID.length == 0)
        return;
    var list = this._pathsOpts[this._currentGroupID].list;
    if(list && list.length >= 1) {
        var lastPt = list[list.length - 1];
        this._ple.setStartPt(Freedo.FdMisc.FdGeometric.tc(lastPt.position));
    }

    this._ple.run();
}

/**
 * 结束增加点
 *
 */
FdPathsManager.prototype.endAppendPathPoint = function() {
    this._ple.cancel();

    if(this._lsner) {
        this._lsner();
        this._lsner = undefined;
    }
}

/**
 * 在当前组列表的末尾增加一个点
 * @param {String} groupID 组ID
 * @param {Object} options 组选项
 */
FdPathsManager.prototype.appendPathPoint = function(groupID, options) {
    if(typeof(this._pathsOpts[groupID]) == 'undefined') {
        return false;
    }

    var pathOpt = Freedo.clone(options, true);
    pathOpt.name = '路径点_' + new Date().getTime();
    this._pathsOpts[groupID].list.push(pathOpt);
    this._event.raiseEvent('GroupChanged', groupID);

    return true;
}

/**
 * 在组列表的某个位置插入一个点
 * @param {String} groupID 组ID
 * @param {Object} options 点选项，目前只有position
 * @param {Number[]} options.position [经度,纬度,高度]单位为度度米
 */
FdPathsManager.prototype.insertPathPoint = function(groupID, options, index) {
    if(typeof(this._pathsOpts[groupID]) == 'undefined') {
        return false;
    }

    var pathOpt = Freedo.clone(options, true);
    this._pathsOpts[groupID].list.splice(index, 0, pathOpt);
    this._event.raiseEvent('GroupChanged', groupID);

    return true;
}

/**
 * 删除组中的点
 * @param {String} groupID 组ID
 * @param {number} index 指定组列表中的位置
 */
FdPathsManager.prototype.removePathPoint = function(groupID, index) {
    if(typeof(this._pathsOpts[groupID]) === 'undefined') {
        return false;
    }

    if(typeof(this._pathsOpts[groupID].list[index]) === 'undefined') {
        return false;
    }

    this._pathsOpts[groupID].list.splice(index, 1);
    this._event.raiseEvent('GroupChanged', groupID);

    this.reloadPA(groupID);

    return true;
}

/**
 * 获取组中点的数量
 * @param {String} groupID 组ID
 * @returns {Number} 数量
 */
FdPathsManager.prototype.getPathsLength = function(groupID) {
    if(typeof(this._pathsOpts[groupID]) === 'undefined') {
        throw new Error('getPathsLength error!');
    }

    return this._pathsOpts[groupID].list.length;
}

/**
 * 设置点
 * @param {String} groupID 组ID
 * @param {number} index 指定组列表中的位置
 * @param {Object} options 点选项，目前只有position
 * @param {Number[]} options.position [经度,纬度,高度]单位为度度米
 */
FdPathsManager.prototype.setPathPoint = function(groupID, index, options) {
    if(typeof(this._pathsOpts[groupID]) === 'undefined') {
        return false;
    }

    if(typeof(this._pathsOpts[groupID].list[index]) === 'undefined') {
        return false;
    }

    var pathOpt = Freedo.clone(options, true);

    this._pathsOpts[groupID].list[index] = pathOpt;

    //this._event.raiseEvent('PathChanged', groupID);

    return true;
}

/**
 * 获取点属性
 * @param {String} groupID 组ID
 * @param {number} index 指定组列表中的位置
 * @returns {Object} options 点选项，目前只有position
 * @returns {Number[]} options.position [经度,纬度,高度]单位为度度米
 */
FdPathsManager.prototype.getPathPoint = function(groupID, index) {
    if(typeof(this._pathsOpts[groupID]) === 'undefined') {
        throw new Error('getPath error');
    }

    if(typeof(this._pathsOpts[groupID].list[index]) === 'undefined') {
        return new Error('getPath error');
    }

    var options = this._pathsOpts[groupID].list[index];
    var pathOpt = Freedo.clone(options, true);
    return pathOpt;
}

/**
 * 飞入某个点
 * @param {String} groupID 组ID
 * @param {number} index 指定组列表中的位置
 */
FdPathsManager.prototype.flyTo = function(groupID, index) {
    if(typeof(this._pathsOpts[groupID]) === 'undefined') {
        return false;
    }

    if(typeof(this._pathsOpts[groupID].list[index]) === 'undefined') {
        return false;
    }

    var options = this._pathsOpts[groupID].list[index];

    var len = this._pa.getTotalLength();
    var bs = new Freedo.BoundingSphere(Freedo.FdMisc.FdGeometric.tc(options.position), len / 400);

    //this._project.getCamera().flyToBoundingSphere(bs);

    return true;
}

/**
 * 跳入到点
 * @param {String} groupID 组ID
 * @param {number} index 指定组列表中的位置
 */
FdPathsManager.prototype.goto = function(groupID, index) {
    if(typeof(this._pathsOpts[groupID]) === 'undefined') {
        return false;
    }

    if(typeof(this._pathsOpts[groupID].list[index]) === 'undefined') {
        return false;
    }

    var options = this._pathsOpts[groupID].list[index];
    var camInfo = options.cameraInfo;
    if(typeof camInfo === 'undefined') {
        return false;
    }

    //this._project.getCamera().gotoByCameraInfo(options.cameraInfo);

    return true;
}

/**
 * 开始播放
 */
FdPathsManager.prototype.start = function() {
    this._pa.start();
}

/**
 * 暂停正在播放的组
 */
FdPathsManager.prototype.pause = function() {
    this._pa.pause();
}

/**
 * 停止正在播放的组。
 */
FdPathsManager.prototype.stop = function() {
    this._pa.stop();
}

/**
 * 获取组的播放状态
 * @private
 * @returns {String} 播放状态可能是 Playing/Stopped/Paused
 */
FdPathsManager.prototype.getGroupPlayingStatus = function() {

}

/**
 * 获取正在播放的组ID
 * @returns {String} 组ID
 */
FdPathsManager.prototype.getGroupPlayingGroupID = function() {
    return this._currentPlayingGroupID;
}

/**
 * 设置当前组ID
 * @param {String} 组ID
 */
FdPathsManager.prototype.setCurrentGroupID = function(id) {
    this._currentGroupID = id;
    this.reloadPA(id);
}

/**
 * 开始进行节点编辑
 * @param {String} 组ID
 */
FdPathsManager.prototype.startEdit = function(id) {
    this._currentGroupID = id;

    var list = this._pathsOpts[this._currentGroupID].list;
    var pts = [];
    for(var k in list) {
        var pos = FdMisc.FdGeometric.tc(list[k].position);
        pts.push(pos);
    }
    /*this._ple.pleOptions = [{
        pts: pts
    }];*/
    this._ple.startEdit(pts);
}

/**
 * 编辑上一个节点
 */
FdPathsManager.prototype.editPre = function() {
    this._ple.editPre();
}

/**
 * 编辑下一个节点
 */
FdPathsManager.prototype.editNext = function() {
    this._ple.editNext();
}

/**
 * 编辑下一个节点
 */
FdPathsManager.prototype.endEdit = function() {
    this._ple.endEdit();
}

/**
 * 重新加载路径动画
 * @param {String} id 组ID
 * @private
 */
FdPathsManager.prototype.reloadPA = function(id) {
    if(!id)
        id = this._currentGroupID;
    var g = this._pathsOpts[id];
    if(typeof(g) === 'undefined') {
        throw new Error('getPath error');
    }

    if(typeof(g.list) === 'undefined') {
        return new Error('getPath error');
    }

    var aPts = [];
    for(let key in g.list) {
        aPts.push(g.list[key].position);
    }

    this._pa.setPoints(aPts);
}

/**
 * 是否显示路径
 * @param {Boolean} show true为显示
 */
FdPathsManager.prototype.setPathShow = function(show) {
    this._pa.setPathShow(show);
}

/**
 * 设置是否循环播放
 * @param {Boolean} loop true为循环播放
 */
FdPathsManager.prototype.setLoop = function(loop) {
    this._pa.setLoop(loop);
}

/**
 * 设置是否开启键盘操作
 * @param {Boolean} enable true为开启
 */
FdPathsManager.prototype.setKBStatus = function(enable) {
    if(enable)
        this._pa.enable();
    else
        this._pa.disable();
}

/**
 * 获取俯仰角
 * @returns {Number} 单位度
 */
FdPathsManager.prototype.getPitch = function() {
    return this._pa.getPitch();
}

/**
 * 设置俯仰角
 * @param {Number} pitch 单位度
 */
FdPathsManager.prototype.setPitch = function(pitch) {
    this._pa.setPitch(pitch);
}

/**
 * 设置路径视角模式
 * @param {Number} true为路径视角模式
 */
FdPathsManager.prototype.setPathViewMode = function(pathView) {
    this._pa.setPathViewMode(pathView);
}

/**
 * 获取动画播放速度
 * @returns {Number} 单位米/秒
 */
FdPathsManager.prototype.getSpeed = function() {
    this._pa.getSpeed();
}

/**
 * 设置动画播放速度
 * @param {Number} s 单位米/秒
 */
FdPathsManager.prototype.setSpeed = function(s) {
    this._pa.setSpeed(s);
}

/**
 * 设置路径平滑度
 * @param {Number} r 平滑度，建议1-10之间，数字越小越平滑
 */
FdPathsManager.prototype.setSmoothRate = function(r) {
    this._pa.setSmoothRate(r);
}

/**
 * 获取视角高度
 * @returns {Number} 单位米
 */
FdPathsManager.prototype.getHeight = function() {
    return this._pa.getHeight();
}

/**
 * 设置视角距离
 * @param {Number} h 单位米
 */
FdPathsManager.prototype.setHeight = function(h) {
    this._pa.setHeight(h);
}

/**
 * 设置偏航角
 * @param {Number} h 单位米
 */
FdPathsManager.prototype.setHeading = function(h) {
    this._pa.setHeading(h);
}

/**
 * 设置路径贴近率
 * @param {Number} rate 贴近率，建议1-100之间
 */
FdPathsManager.prototype.setNearRate = function(rate) {
    this._pa.setNearRate(rate);
}

/**
 * 设置路径高度偏移
 * @param {Number} offsetH 单位米
 */
FdPathsManager.prototype.setOffsetH = function(offsetH) {
    this._pa.setOffsetH(offsetH);
}

/**
 * 获取数据
 * @returns {Object[]} 返回值格式（经纬度单位为度，高度为米）：
 * {
 *     编号1:{
 *         list: [{name: 名称,position: [经度,纬度,高度]}...]
 *         name: 道路名称,
 *         smooth: 平滑度
 *     }
 *  
 *     编号2:{
 *         list: [{name: 名称,position: [经度,纬度,高度]}...]
 *         name: 道路名称,
 *         smooth: 平滑度
 *     }
 * }
 */
FdPathsManager.prototype.getData = function() {
    return Freedo.clone(this._pathsOpts, true);
}

/**
 * 注册事件捕捉
 * @param {FdMicroApp.FdPathsManager~FdPathsManagerCallback} listener 当有事件触发时被执行的函数。
 * @param {Object} [scope] listener函数执行时的绑定的对象。
 * @returns {Freedo.Event~RemoveCallback} 返回一个函数，调用该函数可以取消监听。
 */
FdPathsManager.prototype.on = function(listener, scope) {
    return this._eventHelper.add(this._event, listener, scope);
}

/**
 * @callback FdMicroApp.FdPathsManager~FdPathsManagerCallback
 * @param {String} eventType 事件类型有：GroupsChanged
 * @param {Object} eventArg GroupsChanged事件类型对应的返回值为该组的ID
 */

/**
 * 重置
 */
FdPathsManager.prototype.reset = function() {
    this.removeAllGroups();
    this._pa.reset();
    if(this._lsner) {
        this._lsner();
        this._lsner = undefined;
    }
}

/**
 * 销毁所有资源
 */
FdPathsManager.prototype.dispose = function() {
    this.reset();
    this._eventHelper.removeAll();
}

export {
    FdPathsManager
};