import {
    FdMisc
} from '../../FdMisc/FdMisc';

import {
    FdPolylineEditor
} from '../../FdMicroApp/Analysis/FdPolylineEditor';

/**
 * TextBox2D类
 * @alias FdModel.FdTextBox2D
 * @class
 *
 * @example
 * var viewer = Freedo.FdApp.createDefaultViewer('freedoContainer');
 * var mm = new Freedo.FdModel.FdModelManager(viewer);
 * //如果option参数中不包含position属性，则会自动激活点选创建功能
 * mm.add('TextBox2D', {
        position:[longitude, latitude, height],
        height: height,
        width: width,
        text: '示例Text'
    });
 */
class FdTextBox2D {
    constructor(viewer, id, option) {
        this._viewer = viewer;
        this._id = id;
        this._data = option;

        this._basePtCo = viewer.scene.primitives.add(new Freedo.PointPrimitiveCollection());
        this._basePt = undefined;
        this._canvas = undefined;

        this._ple = new FdPolylineEditor(viewer);

        this._event = new Freedo.Event();
        this._eventHelper = new Freedo.EventHelper();

        this.create();
    }

    /**
     * 获取实体的属性数据
     */
    get option() {
        return Freedo.clone(this.getData(), true);
    }
}

/**
 * 创建
 * @private
 */
FdTextBox2D.prototype.create = function() {
    if(!Freedo.defined(this._data)) {
        console.error("FdTextBox2D.prototype.create：参数无效");
        return;
    }
    
    if(!Freedo.defined(this._id)) {
        this._id = Freedo.createGuid();
    }
    
        
    if(!Freedo.defined(this._data.paddingLeft)){
        this._data.paddingLeft = 50;
    }
    
    if(!Freedo.defined(this._data.moveTop)){
        this._data.moveTop = 50;
    }

    if(!this._data.position) {
        this._ple.run();
        this.addLsnr();
    } else {

        this.build();
    }
}

FdTextBox2D.prototype.refresh = function() {
    this._viewer.scene.requestRender();
}

/**
 * 修改Text内容之后，需调用update来更新
 */
FdTextBox2D.prototype.update = function() {
    var pt = this._basePt.computeScreenSpacePosition(viewer.scene, new Freedo.Cartesian2());
    if(!Freedo.defined(pt))
        return false;
        
    this._canvas.style.left = (pt.x + 0) + "px";
    this._canvas.style.top = (pt.y - this._data.moveTop) + "px";
                
    var context = this._canvas.getContext("2d");
    context.clearRect(0, 0, this._canvas.width, this._canvas.height);
    context.lineWidth = 2;
    context.strokeStyle = 'white';

    context.shadowColor = 'rgba(0, 0, 0, 0.8)';
    context.shadowOffsetX = 1;
    context.shadowOffsetY = 1;
    context.shadowBlur = 10;

    var x1 = this._data.paddingLeft;
    var y1 = 1;
    var radius = 10;
    var width = this._data.width - x1;
    var height = this._data.height - y1;
    
    context.beginPath();
    // 移动到左上角
    context.moveTo(x1, y1 + radius*2);
    // 添加一条连接到右上角的线段
    context.lineTo(2, this._data.moveTop - y1);
    
    // 移动到左上角
    context.moveTo(x1 + radius, y1);
    // 添加一条连接到右上角的线段
    context.lineTo(x1 + width - radius, y1);
    // 添加一段圆弧
    context.arcTo(x1 + width, y1, x1 + width, y1 + radius, radius);
    // 添加一条连接到右下角的线段
    context.lineTo(x1 + width, y1 + height - radius);
    // 添加一段圆弧
    context.arcTo(x1 + width, y1 + height, x1 + width - radius, y1 + height, radius);
    // 添加一条连接到左下角的线段
    context.lineTo(x1 + radius, y1 + height);
    // 添加一段圆弧
    context.arcTo(x1, y1 + height, x1, y1 + height - radius, radius);
    // 添加一条连接到左上角的线段
    context.lineTo(x1, y1 + radius);
    // 添加一段圆弧
    context.arcTo(x1, y1, x1 + radius, y1, radius);
    context.closePath();

    context.stroke();

    context.font = '16px 雅黑';
    context.lineWidth = 1.0;
    context.fillStyle = 'white';

    context.textAlign = "left";
    
    var ta = this._data.text.split('|');
    for(var i = 0; i < ta.length; i++){
        context.fillText(ta[i], x1 + 10, y1 + 20*(i+1));
    }
}

/**
 * 构建实体
 * @private
 */
FdTextBox2D.prototype.build = function() {
    var option = this._data;
    var position = option.position;

    var width = option.width;
    var height = option.height;

    if(!Freedo.defined(width) || !Freedo.defined(height)) {
        console.error('FdTextBox2D.prototype.build：width或height参数无效')
        return;
    }

    var pos = FdMisc.FdGeometric.tc(position);
    var posGra = Freedo.Cartographic.fromCartesian(pos);
    var rio = FdMisc.FdGeometric.getLongAndLatRatio(posGra);
    this._data.positionC = Freedo.clone(pos, true);

    const maxDistance = 20000000;
    this._basePt = this._basePtCo.add({
        position: pos,
        pixelSize: 10,
        show: true,
        color: Freedo.Color.YELLOW,
        //distanceDisplayCondition: new Freedo.DistanceDisplayCondition(0, maxDistance),
        disableDepthTestDistance: Number.POSITIVE_INFINITY
    });

    if(this._canvas) {
        document.body.removeChild(this._canvas);
        this._canvas = undefined;
    }

    this._canvas = document.createElement('canvas');
    this._canvas.id = this._id;

    this._canvas.style.position = "absolute";
    this._canvas.style.display = "block";
    this._canvas.style.zIndex = "999";
    this._canvas.width = option.width + 10;
    this._canvas.height = option.height + 10;
    this._canvas.style.pointerEvents = "none";

    this._canvas.addEventListener("click",function (event) {  
        console.log("click");
    },false); 

    this._canvas.ondrag = function(e) {
        console.log("ondrag");
        //e.preventDefault();
        //return true;
        //e.preventDefault();
    }

    document.body.appendChild(this._canvas);

    this.update();

    this._viewer.scene.requestRender();
}

FdTextBox2D.prototype.addLsnr = function() {
    if(this._lsner)
        this._lsner();
    var that = this;
    this._lsner = this._ple.on(function(eventType, param) {
        console.log(eventType);
        if(eventType == 'cancel') {
            this.cancel();
        }
        if(eventType == 'PointAdded') {
            that._data.position = Freedo.clone(param.position, true);
            that._ple.cancel();
            that._lsner();
            that.build();
        }
        if(eventType == 'Transformed') {
            var pg = param.positionG;
            var position = Freedo.Cartesian3.fromRadians(pg.longitude, pg.latitude, pg.height);
            that._data.position = Freedo.FdMisc.FdGeometric.tg(position);
            that.clear();
            that.build();
        }
    });
}

/**
 * 注册事件捕捉
 * @param {FdModel.FdTextBox2D~FdTextBox2DCallback} listener 当有事件触发时被执行的函数。
 * @param {Object} [scope] listener函数执行时的绑定的对象。
 * @returns {Freedo.Event~RemoveCallback} 返回一个函数，调用该函数可以取消监听。
 */
FdTextBox2D.prototype.on = function(listener, scope) {
    return this._eventHelper.add(this._event, listener, scope);
}

/**
 * @callback FdModel.FdTextBox2D~FdTextBox2DCallback
 * @param {String} eventType 事件类型有：Start,Cancel
 * @param {Object} eventArg Start/Cancel无返回值
 */

/**
 * 开始进行节点编辑
 */
FdTextBox2D.prototype.startEdit = function() {
    console.info('开始编辑');
    this.addLsnr();
    this._ple.startEdit([this._data.positionC]);
}

/**
 * 结束编辑
 */
FdTextBox2D.prototype.endEdit = function() {
    this._ple.endEdit();
}

/**
 * 清理
 */
FdTextBox2D.prototype.clear = function() {
    this._basePtCo.remove(this._basePt);
    if(this._canvas) {
        document.body.removeChild(this._canvas);
        this._canvas = undefined;
    }
    this._viewer.scene.requestRender();
}

/**
 * 删除
 */
FdTextBox2D.prototype.remove = function() {
    this.clear();
    this._data = undefined;
    this._viewer.scene.requestRender();
}

/**
 * 显示
 */
FdTextBox2D.prototype.show = function() {

}

/**
 * 隐藏
 */
FdTextBox2D.prototype.hide = function() {

}

/**
 * 设置文字
 */
FdTextBox2D.prototype.setText = function(text) {
    this.clear();
    this._data.text = text;
    this.build();
}

/**
 * 获取数据
 * @returns {Object} 返回值格式：
 * [{
        id: GUID,
        divID: 'divid'
        position: [Cartesian3],
        width: 宽度,
        height: 高度
   }...] 
 */
FdTextBox2D.prototype.getData = function() {
    if(!this._data)
        return undefined;
    var d = Freedo.clone(this._data, true);
    return {
        id: this._id,
        type: 'TextBox2D',
        option: d
    }
}

/**
 * 重置
 */
FdTextBox2D.prototype.reset = function() {
    if(this._lsner)
        this._lsner();
    this._lsner = undefined;
    this._ple.cancel();
}

/**
 * 销毁所有资源
 */
FdTextBox2D.prototype.dispose = function() {
    this.reset();
    this.remove();
}

export {
    FdTextBox2D
};