import {
    FdBezierCurveAlgorithm
} from '../../FdMicroApp/Analysis/FdBezierCurveAlgorithm';

import {
    FdMisc
} from '../../FdMisc/FdMisc';

import {
    FdPolylineEditor
} from '../../FdMicroApp/Analysis/FdPolylineEditor';

/**
 * Cylinder类
 * @alias FdModel.FdCylinder
 * @class
 *
 * @example
 * var viewer = Freedo.FdApp.createDefaultViewer('freedoContainer');
 * //如果option参数中不包含pts属性，则会激活手动选取节点的功能
 * var ent = mm.add('Cylinder', {
 *     radius: 1,
 *     pts: 点组,
 *     showBaseLine: true,
 *     innerRadius: 0.8
 * });
 */
class FdCylinder {
    constructor(viewer, id, option) {
        this._viewer = viewer;

        this._lsner = undefined;

        this._ple = new FdPolylineEditor(viewer);
        this._ple.setAutoLoop(false);
        this._ple.setAutoDelLines(true);

        this._handler = undefined;

        this._event = new Freedo.Event();
        this._eventHelper = new Freedo.EventHelper();

        this._id = id;
        this._data = option;

        this._tempEntities = [];
        this._tempLines = [];
        this._entities = [];
        this._coreLine = undefined;       

        
        this._offsetCylinder = new Freedo.Cartesian2(0, 0);
        this.create();
    }

    /**
     * 获取实体的属性数据
     */
    get option() {
        return Freedo.clone(this.getData(), true);
    }
}

/**
 * 创建
 * @private
 */
FdCylinder.prototype.create = function() {
    if(!Freedo.defined(this._data)) {
        console.error("FdCylinder.prototype.create：参数无效");
        return;
    }
    if(!Freedo.defined(this._id)) {
        this._id = Freedo.createGuid();
    }
    if(!this._data.pts) {
        this._ple.run();
        this.addLsnr();
    } else {
        this.build();
    }
}

FdCylinder.prototype.updateM = function() {
    if (this._offsetCylinder.x < 0) {
        this._offsetCylinder.x = 1;
    }
    this._offsetCylinder.x -= 0.1;
    viewer.scene.requestRender();
}

/**
 * 构建实体
 * @private
 */
FdCylinder.prototype.build = function() {
    var option = this._data;
    var smoothPts;
    var newPts = [];
    for(var k in option.pts) {
        var newObj = {
            position: option.pts[k]
        }
        newPts.push(newObj);
    }
    if(option.smoothRate == 0)
        smoothPts = points;
    else
        smoothPts = this.smoothPolyline(newPts);

    if(option.showBaseLine) {
        this._coreLine = this.addCoreLine(option.pts);
    }

    if(option.innerRadius) {
        this._entities = this.buildTube();
    } else {
        this._entities = this.buildCylinder();
    }

    this._event.raiseEvent('EntityAdded', {});
    this._viewer.scene.requestRender();
    this._inter = setInterval(this.updateM.bind(this), 150);
}

FdCylinder.prototype.buildCylinder = function() {
    var option = this._data;
    var pts = option.pts;

    var data = this.buildSkeleton(pts, option.radius);

    var polygons = [];
    for(var k = 0; k < data.length; k++) {
        var startPts = Freedo.clone(data[k].startPts, true);
        var endPts = Freedo.clone(data[k].endPts, true);
        var c = new Freedo.Color(1.0, 0.0, 0.0, 0.8);
        var polygon = this._viewer.scene.primitives.add(this.createAreaPrimitive(startPts, c));
        polygons.push(polygon);
        polygon = this._viewer.scene.primitives.add(this.createAreaPrimitive(endPts, c));
        polygons.push(polygon);

        //闭合一下
        startPts.push(startPts[0]);
        endPts.push(endPts[0]);
        
        var cl = 0;
        for(var j = 0; j < startPts.length - 1; j++) {
            var delta = FreeDo.Cartesian3.distance(startPts[j], startPts[j + 1]);
            cl += delta;
        }
        
        for(var j = 0; j < startPts.length - 1; j++) {
            var square = [];
            square.push(startPts[j]);
            square.push(startPts[j + 1]);
            square.push(endPts[j + 1]);
            square.push(endPts[j]);
            
            var ddd = FreeDo.Cartesian3.distance(startPts[j], startPts[j + 1]);
            
            var cirTimes = 1;
            var mStart = j/startPts.length*cirTimes;
            var mEnd = (j+1)/startPts.length*cirTimes-0.0001;
            
            if(option.enableMaterial)
                polygon = this._viewer.scene.primitives.add(this.createRectPrimitive(square, undefined, undefined, mStart, mEnd, cl/cirTimes));
            else
                polygon = this._viewer.scene.primitives.add(this.createAreaPrimitive(square));
            polygons.push(polygon);
        }
    }

    return polygons;
}

FdCylinder.prototype.buildTube = function() {
    var option = this._data;

    var data = this.buildSkeleton(option.pts, option.radius);
    var dataInner = this.buildSkeleton(option.pts, option.innerRadius);

    var polygons = [];
    for(var k = 0; k < data.length; k++) {
        var startPts = Freedo.clone(data[k].startPts, true);
        var endPts = Freedo.clone(data[k].endPts, true);
        var startPtsInner = Freedo.clone(dataInner[k].startPts, true);
        var endPtsInner = Freedo.clone(dataInner[k].endPts, true);

        var c = new Freedo.Color(1.0, 0.0, 0.0, 0.8);

        //两头
        var polygon = this._viewer.scene.primitives.add(this.createAreaPrimitive(startPts, c, startPtsInner));
        polygons.push(polygon);
        polygon = this._viewer.scene.primitives.add(this.createAreaPrimitive(endPts, c, endPtsInner));
        polygons.push(polygon);

        c = new Freedo.Color(1.0, 1.0, 0.0, 0.8);
        //闭合一下
        startPts.push(startPts[0]);
        endPts.push(endPts[0]);
        for(var j = 0; j < startPts.length - 1; j++) {
            var square = [];
            square.push(startPts[j]);
            square.push(startPts[j + 1]);
            square.push(endPts[j + 1]);
            square.push(endPts[j]);
            polygon = this._viewer.scene.primitives.add(this.createAreaPrimitive(square));
            polygons.push(polygon);
        }

        c = new Freedo.Color(1.0, 0.0, 0.0, 0.8);

        //闭合一下
        startPtsInner.push(startPtsInner[0]);
        endPtsInner.push(endPtsInner[0]);
        for(var j = 0; j < startPtsInner.length - 1; j++) {
            var square = [];
            square.push(startPtsInner[j]);
            square.push(startPtsInner[j + 1]);
            square.push(endPtsInner[j + 1]);
            square.push(endPtsInner[j]);
            polygon = this._viewer.scene.primitives.add(this.createAreaPrimitive(square));
            polygons.push(polygon);
        }
    }

    return polygons;
}

FdCylinder.prototype.buildSkeleton = function(pts, radius) {
    var option = {
        radius: radius
    }
    var tempEnt = undefined;
    var dist = 5;
    var startPtSize = 10;
    var endPtSize = 20;
    var showAssitEntities = false;
    var data = [];
    for(var i = 0; i < pts.length - 1; i++) {
        var startPts = [];
        var endPts = [];
        if(i == 0) {
            var pt1, pt2, pt3;
            pt1 = pts[i];
            if(i + 1 == pts.length - 1) {
                pt2 = pts[i + 1];
                var v1 = Freedo.Cartesian3.subtract(pt2, pt1, new Freedo.Cartesian3());
                var randomPt = new Freedo.Cartesian3(pt2.x, pt2.y, pt2.z + 1);
                var v2 = Freedo.Cartesian3.subtract(randomPt, pt1, new Freedo.Cartesian3());

                var vT = Freedo.Cartesian3.cross(v1, v2, new Freedo.Cartesian3());
                var vTn = Freedo.Cartesian3.normalize(vT, new Freedo.Cartesian3());

                //圆面上的一个向量
                var vTRadius = Freedo.Cartesian3.multiplyByScalar(vTn, option.radius, new Freedo.Cartesian3());
                var vTRadius2 = Freedo.Cartesian3.multiplyByScalar(vTn, option.radius * 1.0, new Freedo.Cartesian3());

                for(var k = 0; k < 360; k = k + dist) {
                    var quat = Freedo.Quaternion.fromAxisAngle(v1, Freedo.Math.toRadians(k), new Freedo.Quaternion());
                    var rMatrix = Freedo.Matrix4.fromTranslationQuaternionRotationScale(new Freedo.Cartesian3(), quat, new Freedo.Cartesian3(1, 1, 1));

                    //对向量进行旋转
                    var newVTRadius = Freedo.Matrix4.multiplyByPointAsVector(rMatrix, vTRadius, new Freedo.Cartesian3());
                    var newVTRadius2 = Freedo.Matrix4.multiplyByPointAsVector(rMatrix, vTRadius2, new Freedo.Cartesian3());

                    //计算圆环上的新点
                    var color = new Freedo.Color(Freedo.Math.nextRandomNumber(),
                        Freedo.Math.nextRandomNumber(),
                        Freedo.Math.nextRandomNumber(), 1);
                    var pti = new Freedo.Cartesian3(pt1.x + newVTRadius.x, pt1.y + newVTRadius.y, pt1.z + newVTRadius.z);
                    tempEnt = this._viewer.entities.add({
                        position: pti,
                        point: {
                            color: color,
                            show: showAssitEntities,
                            pixelSize: startPtSize
                        }
                    });
                    this._tempEntities.push(tempEnt);

                    var pti2 = new Freedo.Cartesian3(pt2.x + newVTRadius2.x, pt2.y + newVTRadius2.y, pt2.z + newVTRadius2.z);
                    tempEnt = this._viewer.entities.add({
                        position: pti2,
                        point: {
                            color: color,
                            show: showAssitEntities,
                            pixelSize: endPtSize
                        }
                    });
                    this._tempEntities.push(tempEnt);
                    //var line = this._viewer.scene.primitives.add(this.createLinePrimitive([pti, pti2]));
                    //this._tempLines.push(line);

                    startPts.push(pti);
                    endPts.push(pti2);
                }
            }
            if(i + 1 < pts.length - 1) {
                pt2 = pts[i + 1];
                pt3 = pts[i + 2];
                var v1 = FreeDo.Cartesian3.subtract(pt1, pt2, new FreeDo.Cartesian3());
                //沿着前进方向的向量
                var v1Forward = FreeDo.Cartesian3.subtract(pt2, pt1, new FreeDo.Cartesian3());
                var v2 = FreeDo.Cartesian3.subtract(pt3, pt2, new FreeDo.Cartesian3());

                var angle = Freedo.Cartesian3.angleBetween(v1, v2);
                var v1n = FreeDo.Cartesian3.normalize(v1, new FreeDo.Cartesian3());
                var v2n = FreeDo.Cartesian3.normalize(v2, new FreeDo.Cartesian3());
                //角平分线向量
                var mv = FreeDo.Cartesian3.add(v1n, v2n, new FreeDo.Cartesian3());

                //头顶方向上的向量
                var vT = Freedo.Cartesian3.cross(v1, v2, new Freedo.Cartesian3());
                var vTn = Freedo.Cartesian3.normalize(vT, new Freedo.Cartesian3());

                //圆面上的一个向量
                var vTRadius = Freedo.Cartesian3.multiplyByScalar(vTn, option.radius, new Freedo.Cartesian3());
                var vTRadius2 = Freedo.Cartesian3.multiplyByScalar(vTn, option.radius * 1.0, new Freedo.Cartesian3());
                for(var k = 0; k < 360; k = k + dist) {
                    var quat = Freedo.Quaternion.fromAxisAngle(v1Forward, Freedo.Math.toRadians(k), new Freedo.Quaternion());
                    var rMatrix = Freedo.Matrix4.fromTranslationQuaternionRotationScale(new Freedo.Cartesian3(), quat, new Freedo.Cartesian3(1, 1, 1));

                    //对向量进行旋转
                    var newVTRadius = Freedo.Matrix4.multiplyByPointAsVector(rMatrix, vTRadius, new Freedo.Cartesian3());

                    //计算圆环上的新点
                    var color = new Freedo.Color(Freedo.Math.nextRandomNumber(),
                        Freedo.Math.nextRandomNumber(),
                        Freedo.Math.nextRandomNumber(), 1);
                    var pti = new Freedo.Cartesian3(pt1.x + newVTRadius.x, pt1.y + newVTRadius.y, pt1.z + newVTRadius.z);
                    tempEnt = this._viewer.entities.add({
                        position: pti,
                        point: {
                            color: color,
                            show: showAssitEntities,
                            pixelSize: startPtSize
                        }
                    });
                    this._tempEntities.push(tempEnt);
                    //计算拐角处的点
                    var v2Forward = Freedo.Cartesian3.cross(vT, mv, new Freedo.Cartesian3());
                    var quat2 = Freedo.Quaternion.fromAxisAngle(v2Forward, Freedo.Math.toRadians(k), new Freedo.Quaternion());
                    var rMatrix2 = Freedo.Matrix4.fromTranslationQuaternionRotationScale(new Freedo.Cartesian3(), quat2, new Freedo.Cartesian3(1, 1, 1));

                    //对向量进行旋转
                    var newVTRadius2 = Freedo.Matrix4.multiplyByPointAsVector(rMatrix2, vTRadius2, new Freedo.Cartesian3());

                    var pti2 = new Freedo.Cartesian3(pt2.x + newVTRadius2.x, pt2.y + newVTRadius2.y, pt2.z + newVTRadius2.z);
                    tempEnt = this._viewer.entities.add({
                        position: pti2,
                        point: {
                            color: color,
                            show: showAssitEntities,
                            pixelSize: endPtSize
                        }
                    });
                    this._tempEntities.push(tempEnt);
                    //var line = this._viewer.scene.primitives.add(this.createLinePrimitive([pti, pti2]));
                    //this._tempLines.push(line);
                    startPts.push(pti);
                    endPts.push(pti2);
                }
            }
        }
        if(i != 0) {
            if(i + 1 == pts.length - 1) {
                var pt1, pt2, pt3;
                pt1 = pts[i - 1];
                pt2 = pts[i];
                pt3 = pts[i + 1];
                var v1 = Freedo.Cartesian3.subtract(pt1, pt2, new Freedo.Cartesian3());
                var v2 = Freedo.Cartesian3.subtract(pt3, pt2, new Freedo.Cartesian3());

                var angle = Freedo.Cartesian3.angleBetween(v1, v2);
                var v1n = FreeDo.Cartesian3.normalize(v1, new FreeDo.Cartesian3());
                var v2n = FreeDo.Cartesian3.normalize(v2, new FreeDo.Cartesian3());
                //角平分线向量
                var mv = FreeDo.Cartesian3.add(v1n, v2n, new FreeDo.Cartesian3());

                var vT = Freedo.Cartesian3.cross(v1, v2, new Freedo.Cartesian3());
                var vTn = Freedo.Cartesian3.normalize(vT, new Freedo.Cartesian3());

                //圆面上的一个向量
                var vTRadius = Freedo.Cartesian3.multiplyByScalar(vTn, option.radius, new Freedo.Cartesian3());
                var vTRadius2 = Freedo.Cartesian3.multiplyByScalar(vTn, option.radius, new Freedo.Cartesian3());

                for(var k = 0; k < 360; k = k + dist) {
                    var v1Forward = Freedo.Cartesian3.cross(vT, mv, new Freedo.Cartesian3());
                    var quat1 = Freedo.Quaternion.fromAxisAngle(v1Forward, Freedo.Math.toRadians(k), new Freedo.Quaternion());
                    var rMatrix1 = Freedo.Matrix4.fromTranslationQuaternionRotationScale(new Freedo.Cartesian3(), quat1, new Freedo.Cartesian3(1, 1, 1));

                    //对向量进行旋转
                    var newVTRadius1 = Freedo.Matrix4.multiplyByPointAsVector(rMatrix1, vTRadius, new Freedo.Cartesian3());

                    var color = new Freedo.Color(Freedo.Math.nextRandomNumber(),
                        Freedo.Math.nextRandomNumber(),
                        Freedo.Math.nextRandomNumber(), 1);

                    var pti = new Freedo.Cartesian3(pt2.x + newVTRadius1.x, pt2.y + newVTRadius1.y, pt2.z + newVTRadius1.z);
                    tempEnt = this._viewer.entities.add({
                        position: pti,
                        point: {
                            color: color,
                            show: showAssitEntities,
                            pixelSize: startPtSize
                        }
                    });
                    this._tempEntities.push(tempEnt);

                    var quat2 = Freedo.Quaternion.fromAxisAngle(v2, Freedo.Math.toRadians(k), new Freedo.Quaternion());
                    var rMatrix2 = Freedo.Matrix4.fromTranslationQuaternionRotationScale(new Freedo.Cartesian3(), quat2, new Freedo.Cartesian3(1, 1, 1));

                    //对向量进行旋转
                    var newVTRadius2 = Freedo.Matrix4.multiplyByPointAsVector(rMatrix2, vTRadius2, new Freedo.Cartesian3());

                    var pti2 = new Freedo.Cartesian3(pt3.x + newVTRadius2.x, pt3.y + newVTRadius2.y, pt3.z + newVTRadius2.z);
                    tempEnt = this._viewer.entities.add({
                        position: pti2,
                        point: {
                            color: color,
                            show: showAssitEntities,
                            pixelSize: endPtSize
                        }
                    });
                    this._tempEntities.push(tempEnt);
                    //var line = this._viewer.scene.primitives.add(this.createLinePrimitive([pti, pti2]));
                    //this._tempLines.push(line);
                    startPts.push(pti);
                    endPts.push(pti2);
                }
            }
            if(i + 1 < pts.length - 1) {
                var pt1, pt2, pt3, pt4;
                pt1 = pts[i - 1];
                pt2 = pts[i];
                pt3 = pts[i + 1];
                pt4 = pts[i + 2];
                var v1 = Freedo.Cartesian3.subtract(pt1, pt2, new Freedo.Cartesian3());
                var v2 = Freedo.Cartesian3.subtract(pt3, pt2, new Freedo.Cartesian3());
                var v3 = Freedo.Cartesian3.subtract(pt2, pt3, new Freedo.Cartesian3());
                var v4 = Freedo.Cartesian3.subtract(pt4, pt3, new Freedo.Cartesian3());

                var angle = Freedo.Cartesian3.angleBetween(v1, v2);
                var angle2 = Freedo.Cartesian3.angleBetween(v3, v4);

                var v1n = FreeDo.Cartesian3.normalize(v1, new FreeDo.Cartesian3());
                var v2n = FreeDo.Cartesian3.normalize(v2, new FreeDo.Cartesian3());
                var v3n = FreeDo.Cartesian3.normalize(v3, new FreeDo.Cartesian3());
                var v4n = FreeDo.Cartesian3.normalize(v4, new FreeDo.Cartesian3());
                //角平分线向量
                var mv = FreeDo.Cartesian3.add(v1n, v2n, new FreeDo.Cartesian3());
                var mv2 = FreeDo.Cartesian3.add(v3n, v4n, new FreeDo.Cartesian3());

                //走向是否换向了
                var angle = Freedo.Math.toDegrees(Freedo.Cartesian3.angleBetween(mv, mv2));

                var vT = Freedo.Cartesian3.cross(v1, v2, new Freedo.Cartesian3());
                var vTn = Freedo.Cartesian3.normalize(vT, new Freedo.Cartesian3());

                var vT2 = Freedo.Cartesian3.cross(v3, v4, new Freedo.Cartesian3());
                var vTn2 = Freedo.Cartesian3.normalize(vT2, new Freedo.Cartesian3());

                //圆面上的一个向量
                var vTRadius = Freedo.Cartesian3.multiplyByScalar(vTn, option.radius, new Freedo.Cartesian3());
                var vTRadius2 = Freedo.Cartesian3.multiplyByScalar(vTn2, option.radius * 1.0, new Freedo.Cartesian3());
                for(var k = 0; k < 360; k = k + dist) {
                    var v1Forward = Freedo.Cartesian3.cross(vT, mv, new Freedo.Cartesian3());
                    var quat1 = Freedo.Quaternion.fromAxisAngle(v1Forward, Freedo.Math.toRadians(k), new Freedo.Quaternion());
                    var rMatrix1 = Freedo.Matrix4.fromTranslationQuaternionRotationScale(new Freedo.Cartesian3(), quat1, new Freedo.Cartesian3(1, 1, 1));

                    //对向量进行旋转
                    var newVTRadius1 = Freedo.Matrix4.multiplyByPointAsVector(rMatrix1, vTRadius, new Freedo.Cartesian3());

                    var color = new Freedo.Color(Freedo.Math.nextRandomNumber(),
                        Freedo.Math.nextRandomNumber(),
                        Freedo.Math.nextRandomNumber(), 1);

                    var pti = new Freedo.Cartesian3(pt2.x + newVTRadius1.x, pt2.y + newVTRadius1.y, pt2.z + newVTRadius1.z);
                    tempEnt = this._viewer.entities.add({
                        position: pti,
                        point: {
                            color: color,
                            show: showAssitEntities,
                            pixelSize: startPtSize
                        }
                    });
                    this._tempEntities.push(tempEnt);

                    var v2Forward = Freedo.Cartesian3.cross(vT2, mv2, new Freedo.Cartesian3());

                    var angleDelta = k;
                    if(angle > 90)
                        angleDelta = k + 180;

                    var quat2 = Freedo.Quaternion.fromAxisAngle(v2Forward, Freedo.Math.toRadians(angleDelta), new Freedo.Quaternion());
                    var rMatrix2 = Freedo.Matrix4.fromTranslationQuaternionRotationScale(new Freedo.Cartesian3(), quat2, new Freedo.Cartesian3(1, 1, 1));

                    //对向量进行旋转
                    var newVTRadius2 = Freedo.Matrix4.multiplyByPointAsVector(rMatrix2, vTRadius2, new Freedo.Cartesian3());

                    var pti2 = new Freedo.Cartesian3(pt3.x + newVTRadius2.x, pt3.y + newVTRadius2.y, pt3.z + newVTRadius2.z);
                    tempEnt = this._viewer.entities.add({
                        position: pti2,
                        point: {
                            color: color,
                            show: showAssitEntities,
                            pixelSize: endPtSize
                        }
                    });
                    this._tempEntities.push(tempEnt);
                    //var line = this._viewer.scene.primitives.add(this.createLinePrimitive([pti, pti2]));
                    //this._tempLines.push(line);
                    startPts.push(pti);
                    endPts.push(pti2);
                }
            }
        }

        data.push({
            startPts: startPts,
            endPts: endPts
        });
    }

    return data;
}

FdCylinder.prototype.calcAngle = function(p0, p1, p2) {
    var v1 = FreeDo.Cartesian3.subtract(p0, p1, new FreeDo.Cartesian3());
    var v2 = FreeDo.Cartesian3.subtract(p2, p1, new FreeDo.Cartesian3());

    var angle = Freedo.Cartesian3.angleBetween(v1, v2);
    return angle;
}

FdCylinder.prototype.createAreaPrimitive = function(pts, color, hole) {
    var N;
    var r = new Freedo.Cartesian3();
    var v1 = Freedo.Cartesian3.subtract(pts[1], pts[0], new Freedo.Cartesian3());
    var v2 = Freedo.Cartesian3.subtract(pts[2], pts[1], new Freedo.Cartesian3());
    N = Freedo.Cartesian3.cross(v1, v2, new Freedo.Cartesian3());
    N = Freedo.Cartesian3.normalize(N, new Freedo.Cartesian3());

    var angle = Freedo.Cartesian3.angleBetween(N, Freedo.Cartesian3.UNIT_Z);
    var axis = Freedo.Cartesian3.cross(N, Freedo.Cartesian3.UNIT_Z, new Freedo.Cartesian3());
    var quat = Freedo.Quaternion.fromAxisAngle(axis, angle, new Freedo.Quaternion());
    var rMatrix = Freedo.Matrix4.fromTranslationQuaternionRotationScale(new Freedo.Cartesian3(), quat, new Freedo.Cartesian3(1, 1, 1));
    var newN = Freedo.Matrix4.multiplyByPointAsVector(rMatrix, N, new Freedo.Cartesian3());

    var newPts = [];
    for(var k in pts) {
        var newPt = Freedo.Matrix4.multiplyByPointAsVector(rMatrix, pts[k], new Freedo.Cartesian3());
        newPts.push(newPt);
    }
    //var holerev = hole.slice().reverse();
    //newPts.push(holerev);
    for(var k in hole) {
        var newPt = Freedo.Matrix4.multiplyByPointAsVector(rMatrix, hole[k], new Freedo.Cartesian3());
        newPts.push(newPt);
    }

    var geo;
    if( /*viewer.scene.globe*/ false) {

    } else {
        var positions = [];
        for(var i = 0; i < pts.length; i++) {
            positions.push(pts[i].x * 1);
            positions.push(pts[i].y * 1);
            positions.push(pts[i].z * 1);
        }

        var holeIndex = [];
        if(hole) {
            for(var i = 0; i < hole.length; i++) {
                positions.push(hole[i].x * 1);
                positions.push(hole[i].y * 1);
                positions.push(hole[i].z * 1);

            }
            holeIndex.push(pts.length);
        }

        var index = Freedo.PolygonPipeline.triangulate(newPts, holeIndex);

        var positionsFloat64 = new Float64Array(positions);
        geo = new Freedo.Geometry({
            attributes: {
                position: new Freedo.GeometryAttribute({
                    componentDatatype: Freedo.ComponentDatatype.DOUBLE,
                    componentsPerAttribute: 3,
                    values: positionsFloat64
                })
            },
            indices: new Uint16Array(index),
            primitiveType: Freedo.PrimitiveType.TRIANGLES,
            boundingSphere: Freedo.BoundingSphere.fromVertices(positionsFloat64)
        });
    }

    if(!color)
        color = new Freedo.Color(1.0, 1.0, 0.0, 0.8);

    return new Freedo.Primitive({
        geometryInstances: new Freedo.GeometryInstance({
            geometry: geo,
            attributes: {
                color: Freedo.ColorGeometryInstanceAttribute.fromColor(color)
            }
        }),
        appearance: new Freedo.PerInstanceColorAppearance({
            flat: true,
            translucent: true
        }),
        /*appearance: new Freedo.MaterialAppearance({
            materialSupport: Freedo.MaterialAppearance.MaterialSupport.TEXTURED,
            material: new Freedo.Material({
                fabric: {
                    type: 'DiffuseMap',
                    uniforms: {
                        image: 'http://localhost:9900/Examples/static/favicon.ico',
                        offset: offsetCylinder //添加一个纹理坐标偏移量的uniform变量
                    },
                    components: {
                        //修改着色器给纹理坐标添加偏移量
                        diffuse: 'texture2D(image, fract(repeat * materialInput.st+offset)).channels'
                        //diffuse: 'texture2D(image, materialInput.st+offset).channels'
                    }
                }
            })
        }),*/
        asynchronous: false
    });
}

FdCylinder.prototype.createRectPrimitive = function(pts, color, hole, mStart, mEnd, circleLong) {
    var N;
    var r = new Freedo.Cartesian3();
    var v1 = Freedo.Cartesian3.subtract(pts[1], pts[0], new Freedo.Cartesian3());
    var v2 = Freedo.Cartesian3.subtract(pts[2], pts[1], new Freedo.Cartesian3());
    N = Freedo.Cartesian3.cross(v1, v2, new Freedo.Cartesian3());
    N = Freedo.Cartesian3.normalize(N, new Freedo.Cartesian3());

    var angle = Freedo.Cartesian3.angleBetween(N, Freedo.Cartesian3.UNIT_Z);
    var axis = Freedo.Cartesian3.cross(N, Freedo.Cartesian3.UNIT_Z, new Freedo.Cartesian3());
    var quat = Freedo.Quaternion.fromAxisAngle(axis, angle, new Freedo.Quaternion());
    var rMatrix = Freedo.Matrix4.fromTranslationQuaternionRotationScale(new Freedo.Cartesian3(), quat, new Freedo.Cartesian3(1, 1, 1));
    var newN = Freedo.Matrix4.multiplyByPointAsVector(rMatrix, N, new Freedo.Cartesian3());

    var newPts = [];
    for(var k in pts) {
        var newPt = Freedo.Matrix4.multiplyByPointAsVector(rMatrix, pts[k], new Freedo.Cartesian3());
        newPts.push(newPt);
    }
    //var holerev = hole.slice().reverse();
    //newPts.push(holerev);
    for(var k in hole) {
        var newPt = Freedo.Matrix4.multiplyByPointAsVector(rMatrix, hole[k], new Freedo.Cartesian3());
        newPts.push(newPt);
    }

    var geo;
    if( /*viewer.scene.globe*/ false) {

    } else {
        var positions = [];
        for(var i = 0; i < pts.length; i++) {
            positions.push(pts[i].x * 1);
            positions.push(pts[i].y * 1);
            positions.push(pts[i].z * 1);
        }
        
        var textCoor = [];
        var d1 = FreeDo.Cartesian3.distance(pts[0], pts[1]);
        var d2 = FreeDo.Cartesian3.distance(pts[1], pts[2]);
        var d = d2 / circleLong;
        //d = 1;
        /*textCoor.push(0.0,0.0);
        textCoor.push(0.0,1.0);
        textCoor.push(d,1.0);
        textCoor.push(d,0.0);*/
        textCoor.push(0.0,mStart);
        textCoor.push(0.0,mEnd);
        textCoor.push(d,mEnd);
        textCoor.push(d,mStart);

        var holeIndex = [];
        
        if(hole) {
            for(var i = 0; i < hole.length; i++) {
                positions.push(hole[i].x * 1);
                positions.push(hole[i].y * 1);
                positions.push(hole[i].z * 1);

            }
            holeIndex.push(pts.length);
        }

        var index = Freedo.PolygonPipeline.triangulate(newPts, holeIndex);

        var positionsFloat64 = new Float64Array(positions);
        geo = new Freedo.Geometry({
            attributes: {
                position: new Freedo.GeometryAttribute({
                    componentDatatype: Freedo.ComponentDatatype.DOUBLE,
                    componentsPerAttribute: 3,
                    values: positionsFloat64
                }),
                st: new FreeDo.GeometryAttribute({
                    componentDatatype: FreeDo.ComponentDatatype.FLOAT,
                    componentsPerAttribute: 2,
                    values: textCoor
                })
            },
            indices: new Uint16Array(index),
            primitiveType: Freedo.PrimitiveType.TRIANGLES,
            boundingSphere: Freedo.BoundingSphere.fromVertices(positionsFloat64)
        });
    }

    if(!color)
        color = new Freedo.Color(1.0, 1.0, 0.0, 0.8);

    var arrowImg = Freedo.FdApp.getFreedoXPath() + 'Assets/Textures/ori/redtube.jpg';
    return new Freedo.Primitive({
        geometryInstances: new Freedo.GeometryInstance({
            geometry: geo,
            attributes: {
                color: Freedo.ColorGeometryInstanceAttribute.fromColor(color)
            }
        }),
        /*appearance: new Freedo.PerInstanceColorAppearance({
            flat: true,
            translucent: true
        }),*/
        appearance: new Freedo.MaterialAppearance({
            materialSupport: Freedo.MaterialAppearance.MaterialSupport.TEXTURED,
            material: new Freedo.Material({
                fabric: {
                    type: 'DiffuseMap',
                    uniforms: {
                        image: arrowImg,
                        repeat: 4,
                        offset: this._offsetCylinder //添加一个纹理坐标偏移量的uniform变量
                    },
                    components: {
                        //修改着色器给纹理坐标添加偏移量
                        diffuse: 'texture2D(image, fract(repeat * materialInput.st+offset)).channels'
                        //diffuse: 'texture2D(image, materialInput.st+offset).channels'
                    }
                }
            })
        }),
        asynchronous: false
    });
}

FdCylinder.prototype.addLsnr = function() {
    if(this._lsner) {
        this._lsner();
        this._lsner = undefined;
    }

    var that = this;

    this._lsner = this._ple.on(function(eventType, param) {
        if(eventType == 'PLAdd') {
            that.onPolylineAdded(param);
        }

        if(eventType == 'EditEnd') {
            that._data.pts = Freedo.clone(param.pts);
            that.reDraw();
        }
    });
}

FdCylinder.prototype.onPolylineAdded = function(param) {
    this._data.pts = Freedo.clone(param.polyline.pts);
    this.build();
    this._ple.cancel();
}

/**
 * 计算控制点
 * @private
 * @param {Cartesian3} p0p 第一个点
 * @param {Cartesian3} p1p 第二个点
 * @param {Cartesian3} p2p 第三个点
 * @returns {Array} 返回[Cartesian3,Cartesian3]格式的控制点
 */
FdCylinder.prototype.calcCP = function(p0p, p1p, p2p) {
    var p0,
        p1,
        p2;
    p0 = p0p.position;
    p1 = p1p.position;
    p2 = p2p.position;

    var cp = [];
    var v1 = FreeDo.Cartesian3.subtract(p0, p1, new FreeDo.Cartesian3());
    var v2 = FreeDo.Cartesian3.subtract(p2, p1, new FreeDo.Cartesian3());

    var axis = FreeDo.Cartesian3.cross(v1, v2, new FreeDo.Cartesian3());

    var v1n = FreeDo.Cartesian3.normalize(v1, new FreeDo.Cartesian3());
    var v2n = FreeDo.Cartesian3.normalize(v2, new FreeDo.Cartesian3());
    var mv = FreeDo.Cartesian3.add(v1n, v2n, new FreeDo.Cartesian3()); //角平分线向量

    var quat1 = Freedo.Quaternion.fromAxisAngle(axis, Freedo.Math.PI_OVER_TWO, new Freedo.Quaternion());
    var quat2 = Freedo.Quaternion.fromAxisAngle(axis, -1 * Freedo.Math.PI_OVER_TWO, new Freedo.Quaternion());

    var m31 = Freedo.Matrix3.fromQuaternion(quat1, new Freedo.Matrix3());
    var m32 = Freedo.Matrix3.fromQuaternion(quat2, new Freedo.Matrix3());

    var mv1 = Freedo.Matrix3.multiplyByVector(m31, mv, new Freedo.Cartesian3());
    var mv2 = Freedo.Matrix3.multiplyByVector(m32, mv, new Freedo.Cartesian3());

    var mv1n = FreeDo.Cartesian3.normalize(mv1, new FreeDo.Cartesian3());
    var mv2n = FreeDo.Cartesian3.normalize(mv2, new FreeDo.Cartesian3());

    var len1 = FreeDo.Cartesian3.distance(p0, p1);
    var len2 = FreeDo.Cartesian3.distance(p2, p1);
    var len = 1;
    if(len1 < len2) {
        len = len1 / this._nearRate; //this._smoothRate;
    } else
        len = len2 / this._nearRate; // this._smoothRate;

    mv1n = FreeDo.Cartesian3.multiplyByScalar(mv1n, len, new FreeDo.Cartesian3());
    mv2n = FreeDo.Cartesian3.multiplyByScalar(mv2n, len, new FreeDo.Cartesian3());

    var cp0 = FreeDo.Cartesian3.add(p1, mv1n, new FreeDo.Cartesian3());
    var cp1 = FreeDo.Cartesian3.add(p1, mv2n, new FreeDo.Cartesian3());

    var dis1 = FreeDo.Cartesian3.distance(p0, cp0);
    var dis2 = FreeDo.Cartesian3.distance(p0, cp1);

    if(dis1 < dis2)
        return [cp0, cp1];
    else
        return [cp1, cp0];
}

/**
 * 设置路径贴近率
 * @private
 * @param {Number} rate 贴近率，建议1-100之间
 */
FdCylinder.prototype.setNearRate = function(rate) {
    this._nearRate = rate;
    this.reDraw();
}

/**
 * 平滑化
 * @private
 * @param {Object[]} pts 需要进行平滑化的点组
 * @returns 平滑后的点组，[Cartesian3]
 */
FdCylinder.prototype.smoothPolyline = function(pts) {
    var aPt = [];
    if(pts.length < 3) {
        for(var i = 0; i < pts.length; i++) {
            aPt.push(pts[i].position);
        }
    } else {
        for(var i = 0; i <= pts.length - 2; i++) {
            var ca = {},
                cb = {},
                pa = {},
                pb = {};
            if(i == 0) {
                ca = pts[0];
                cb.position = this.calcCP(pts[0], pts[1], pts[2])[0];
                pa = pts[0];
                pb = pts[1];
            } else if(i == pts.length - 2) {
                ca.position = this.calcCP(pts[i - 1], pts[i], pts[i + 1])[1];
                cb = pts[i + 1];
                pa = pts[i];
                pb = pts[i + 1];
            } else {
                ca.position = this.calcCP(pts[i - 1], pts[i], pts[i + 1])[1];
                cb.position = this.calcCP(pts[i], pts[i + 1], pts[i + 2])[0];
                pa = pts[i];
                pb = pts[i + 1];
            }
            var bz = new FdBezierCurveAlgorithm(pa, pb, ca, cb);

            /*
            this._viewer.entities.add({
            position: ca.position,
            ellipsoid: {
            material: Freedo.Color.YELLOW.withAlpha(1),
            radii: new Freedo.Cartesian3(5, 5, 5)
            }
            });
            this._viewer.entities.add({
            position: cb.position,
            ellipsoid: {
            material: Freedo.Color.RED.withAlpha(1),
            radii: new Freedo.Cartesian3(5, 5, 5)
            }
            });
             */

            var segPts = bz.getVertexForCurve(this._smoothRate);
            aPt = aPt.concat(segPts);
        }
    }

    //把重复的相邻点删除掉
    var newPts = [];
    for(var i = 0; i < aPt.length; i++) {
        var o = {};
        o = aPt[i];
        if(newPts.length > 0) {
            var len = FreeDo.Cartesian3.distance(newPts[newPts.length - 1], o);
            if(len > 0.0001)
                newPts.push(o);
        } else
            newPts.push(o);
    }
    return newPts;
}

/**
 * 设置是否显示核线
 * @param {Boolean} show true表示显示核线
 */
FdCylinder.prototype.showBaseLine = function(show) {
    this._data.showBaseLine = show;
}

FdCylinder.prototype.addCoreLine = function(pts) {
    var pl = this._viewer.scene.primitives.add(this.createLinePrimitive(pts));
    return pl;
}

FdCylinder.prototype.createLinePrimitive = function(pts) {
    return new Freedo.Primitive({
        geometryInstances: new Freedo.GeometryInstance({
            geometry: new Freedo.PolylineGeometry({
                positions: pts,
                width: 5,
                vertexFormat: Freedo.PolylineMaterialAppearance.VERTEX_FORMAT
            })
        }),
        appearance: new Freedo.PolylineMaterialAppearance({
            material: new Freedo.Material({
                fabric: {
                    type: 'PolylineArrow',
                    //width: 5,
                    uniforms: {
                        color: new Freedo.Color(1.0, 1.0, 0, 1.0)
                    }
                }
            }),
            renderState: new Freedo.RenderState({
                polygonOffset: {
                    enabled: true,
                    factor: -100.0,
                    units: -100.0,
                },
                depthTest: {
                    enabled: true,
                    func: Freedo.WebGLConstants.LEQUAL,
                }
            }),
        }),
        depthFailAppearance: new Freedo.PolylineMaterialAppearance({
            material: new Freedo.Material({
                fabric: {
                    type: 'PolylineArrow',
                    //width: 5,
                    uniforms: {
                        color: new Freedo.Color(1, 1, 0, 1.0)
                    }
                }
            }),
        }),
    });
}

/**
 * 注册事件捕捉
 * @param {FdModel~FdCylinderCallback} listener 当有事件触发时被执行的函数。
 * @param {Object} [scope] listener函数执行时的绑定的对象。
 * @returns {Freedo.Event~RemoveCallback} 返回一个函数，调用该函数可以取消监听。
 */
FdCylinder.prototype.on = function(listener, scope) {
    return this._eventHelper.add(this._event, listener, scope);
}

/**
 * @callback FdModel.FdCylinder~FdCylinderCallback
 * @param {String} eventType 事件类型有以下几种：Start（绘制开始）/Cancel（绘制取消）
 * @param {Object} eventArg Start/Cancel事件返回空值
 */

/**
 * 设置路径平滑度
 * @private
 * @param {Number} r 平滑度，建议1-11之间，数字越大越平滑
 */
FdCylinder.prototype.setSmoothRate = function(r) {
    this._smoothRate = r;
    this.reDraw();
}

/**
 * 清空
 * @private
 */
FdCylinder.prototype.clear = function(r) {
    //删除polygon
    for(var key in this._entities) {
        var entity = this._entities[key];
        this._viewer.scene.primitives.remove(entity);
    }
    //删除核线
    this._viewer.scene.primitives.remove(this._coreLine);

    //删除插值点实体
    for(var key in this._tempEntities) {
        this._viewer.entities.remove(this._tempEntities[key]);
    }
    //删除插值连接线
    for(var key in this._tempLines) {
        this._viewer.scene.primitives.remove(this._tempLines[key]);
    }

    this._entities = [];
    this._tempLines = [];
    this._viewer.scene.requestRender();
}

/**
 * 获取数据
 * @returns {Object[]} 返回值格式：
 * [{
 *     pts: [Cartesian3]
 * }...]
 */
FdCylinder.prototype.getData = function() {
    if(!this._data)
        return this._data;
    var d = Freedo.clone(this._data, true);
    return {
        id: this._id,
        type: 'Cylinder',
        option: d
    }
}

/**
 * 开始进行节点编辑
 * @param {String} 组ID
 */
FdCylinder.prototype.startEdit = function() {
    this.addLsnr();
    this._ple.startEdit(this._data.pts);
}

/**
 * 编辑上一个节点
 */
FdCylinder.prototype.editPre = function() {
    this._ple.editPre();
}

/**
 * 编辑下一个节点
 */
FdCylinder.prototype.editNext = function() {
    this._ple.editNext();
}

/**
 * 编辑下一个节点
 */
FdCylinder.prototype.endEdit = function() {
    this._ple.endEdit();
    this._lsner();
}

/**
 * 删除实体
 */
FdCylinder.prototype.remove = function() {
    this.clear();
    this._data = undefined;
}

/**
 * 重绘
 * @private
 */
FdCylinder.prototype.reDraw = function(r) {
    this.clear();
    this.build();
}

/**
 * 设置高度偏移量
 * @param {Number} offset 高度偏移量
 */
FdCylinder.prototype.setHeightOffset = function(offset) {
    this._ple.setHeightOffset(offset);
}

/**
 * 重置
 */
FdCylinder.prototype.reset = function() {
    if(this._lsner)
        this._lsner();
    this._lsner = undefined;
    this._ple.cancel();
    clearInterval(this._inter);
}

/**
 * 销毁所有资源
 */
FdCylinder.prototype.dispose = function() {
    this.reset();
    this.remove();
}

export {
    FdCylinder
};