import {
    FdMisc
} from '../../FdMisc/FdMisc';

/*
 * 线段量测工具。
 * @private
 * @alias FdAnalysisLineDistance
 * @class
 */
class FdAnalysisLineDistance {
    constructor(viewer) {
        this.PositionType = {
            NONE: 0,
            ON_MODEL: 1,
            ON_TERRAIN: 2,
            ON_ELLIPSOID: 3
        };

        this.ResultType = {
            StraightLine: 1,
            HorizontalAndVertical: 2
        };

        this._viewer = viewer;

        this._event = new FreeDo.Event();
        this._eventHelper = new FreeDo.EventHelper();

        this._data = {
            line: [],
            polyline: []
        }
        this._results = [];
        this._startPoint = undefined;
        this._tempPoint = undefined;

        this._polylineCollection = new Freedo.PolylineCollection();
        this._viewer.scene.primitives.add(this._polylineCollection);

        this._collection = new Freedo.PrimitiveCollection();
        this._viewer.scene.primitives.add(this._collection);

        this._tempLine;
        this._tempPointEntities = [null, null];
        this._tempSumLabelEntity = undefined;
        this._sumLabel = [];
        this._lastLabel = undefined;

        this._points = [];
        this._pointsCalced = [];

        this._hanlder = {};

        this._ballSize = 0.5;
        this._lineWidth = 3;
        this._fontSize = '22px 黑体';

        this._bInSurvey = false;
        this._surveyType = '';

        this._useDepthPicking = true;
        this._pickTranslucentDepth = true;

        this._bRmStarting = false;
    }
}

/**
 * 根据屏幕坐标得到世界坐标
 * @private
 * @param {Object} windowPosition 鼠标屏幕坐标
 * @returns {Object} 场景内求交得到的三维坐标
 */
FdAnalysisLineDistance.prototype.pickPosition = function(windowPosition) {
    var position = Freedo.FdMisc.FdGeometric.pickPosition(this._viewer, windowPosition);
    if (position) {
        return {
            type: this.PositionType.ON_TERRAIN,
            position: position
        };
    } else {
        return {
            type: this.PositionType.NONE,
            position: position
        };
    }
}

FdAnalysisLineDistance.prototype.removeTmpLine = function() {
    if (this._tempLine) {
        this._viewer.entities.remove(this._tempLine);
        this._tempLine = undefined;
    }

    if (this._tempSumLabelEntity) {
        this._viewer.entities.remove(this._tempSumLabelEntity);
        this._tempSumLabelEntity = undefined;
    }

    for (var i = 0; i < this._tempPointEntities.length; i++) {
        if (!!this._tempPointEntities[i]) {
            this._viewer.entities.remove(this._tempPointEntities[i]);
            this._tempPointEntities[i] = null;
        }
    }

    if (this._surveyType == 'LINE_DISTANCE')
        this._startPoint = undefined;
    this._bRmStarting = false;
    this._viewer.scene.requestRender();
}

FdAnalysisLineDistance.prototype.calcLineType = function(start, end) {
    if (!this._viewer.scene.globe)
        return this.ResultType.StraightLine;
    var startPointCartographic = Freedo.Cartographic.fromCartesian(start.position);
    var endPointCartographic = Freedo.Cartographic.fromCartesian(end.position);

    var len = Freedo.FdMisc.FdGeometric.calculateLength([start.position, end.position]);
    var hDelta = Math.abs(startPointCartographic.height - endPointCartographic.height);
    if (hDelta > len / 10)
        return this.ResultType.HorizontalAndVertical;
    else
        return this.ResultType.StraightLine;
}

/**
 * 创建线段
 * @private
 * @param {Object} p1 起点
 * @param {Object} p2 终点
 * @returns 返回线段对象
 */
FdAnalysisLineDistance.prototype.createLinePrimitive = function(p1, p2) {
    var geo, followSurface;
    if (this._viewer.scene.globe) {
        followSurface = true;
    } else {
        followSurface = false;
        /*var positions = new Float64Array([p1.x, p1.y, p1.z, p2.x, p2.y, p2.z]);
        geo = new Freedo.Geometry({
            attributes: {
                position: new Freedo.GeometryAttribute({
                    componentDatatype: Freedo.ComponentDatatype.DOUBLE,
                    componentsPerAttribute: 3,
                    values: positions
                })
            },
            indices: new Uint16Array([0, 1]),
            primitiveType: Freedo.PrimitiveType.LINES,
            boundingSphere: Freedo.BoundingSphere.fromVertices(positions)
        });*/
    }
    geo = new Freedo.PolylineGeometry({
        positions: [p1, p2],
        width: 2.0,
        followSurface: followSurface,
        vertexFormat: Freedo.PolylineMaterialAppearance.VERTEX_FORMAT
    });
    return new Freedo.Primitive({
        geometryInstances: new Freedo.GeometryInstance({
            geometry: geo
        }),
        appearance: new Freedo.PolylineMaterialAppearance({
            material: new Freedo.Material({
                fabric: {
                    type: 'Color',
                    uniforms: {
                        color: new Freedo.Color(1.0, 0.3, 0.3, 1.0)
                    }
                }
            }),
            renderState: new Freedo.RenderState({
                polygonOffset: {
                    enabled: true,
                    factor: -1.0,
                    units: -1.0,
                },
                depthTest: {
                    enabled: true,
                    func: Freedo.WebGLConstants.LEQUAL,
                }
            }),
        }),
        depthFailAppearance: new Freedo.PolylineMaterialAppearance({
            material: new Freedo.Material({
                fabric: {
                    type: 'PolylineDash',
                    uniforms: {
                        color: new Freedo.Color(1.0, 0.3, 0.3, 0.5)
                    }
                }
            }),
        }),
    });
}

/**
 * 创建Label
 * @private
 * @param {Object} pos 位置
 * @param {String} text 文字
 * @returns 返回对象
 */
FdAnalysisLineDistance.prototype.createLabel = function(pos, text) {
    return this._viewer.entities.add({
        position: pos,
        label: {
            style: Freedo.LabelStyle.FILL_AND_OUTLINE,
            text: text,
            show: true,
            showBackground: true,
            backgroundColor: new Freedo.Color(0.165, 0.165, 0.165, 1),
            font: this._fontSize,
            horizontalOrigin: Freedo.HorizontalOrigin.CENTER,
            verticalOrigin: Freedo.VerticalOrigin.BOTTOM,
            pixelOffset: new Freedo.Cartesian2(1, 1),
            disableDepthTestDistance: Number.POSITIVE_INFINITY
        }
    });
}

FdAnalysisLineDistance.prototype.addResult = function(endPoint) {
    var g = Freedo.FdMisc.FdGeometric;
    var resultType = this.calcLineType(this._startPoint, endPoint);
    this._points.pop();
    this._points.push(endPoint.position);
    this._points.push(endPoint.position);

    //动态计算球的大小
    this._ballSize = g.calculateLength(this._points) / 1000;

    if (resultType === this.ResultType.StraightLine) {
        var positions = [this._startPoint.position, endPoint.position];
        var sumLength = g.calculateLength(positions);
        if (sumLength < 0.01) {
            this._points.pop();
            return;
        }

        var line = this._viewer.scene.primitives.add(this.createLinePrimitive(this._startPoint.position, endPoint.position));

        let pos = g.getCenter(this._startPoint.position, endPoint.position);
        let len = g.calculateLength(positions);
        let text = this.getLengthText(len);
        var labelEntity = this.createLabel(pos, text);

        var startPointEntity = this._viewer.entities.add({
            position: this._startPoint.position,
            show: true,
            point: {
                color: Freedo.Color.YELLOW,
                pixelSize: 5,
                show: false,
                point: false
            }
        });
        var endPointEntity = this._viewer.entities.add({
            position: endPoint.position,
            show: true,
            point: {
                color: Freedo.Color.YELLOW,
                pixelSize: 5,
                show: false,
                point: false
            }
        });

        this._results.push({
            type: resultType,
            labelEntity: labelEntity,
            line: line,
            startPointEntity: startPointEntity,
            endPointEntity: endPointEntity
        });

        var data = {
            pointsG: [g.tg(this._startPoint.position), g.tg(endPoint.position)],
            pointsC: [this._startPoint.position, endPoint.position],
            length: len,
            vLength: 0,
            hLength: 0
        };
        if (this._surveyType == "LINE_DISTANCE") {
            this._data.line.push(data);
            this._event.raiseEvent('DataChanged', data);
        }
        if (this._surveyType == "SEGMENTS_DISTANCE") {
            this._pointsCalced.push(data);
        }
    } else if (resultType === this.ResultType.HorizontalAndVertical) {
        var startPointCartographic = Freedo.Cartographic.fromCartesian(this._startPoint.position);
        var endPointCartographic = Freedo.Cartographic.fromCartesian(endPoint.position);

        var positions = [this._startPoint.position, endPoint.position];
        var sumLength = g.calculateLength(positions);
        if (sumLength < 0.01) {
            this._points.pop();
            return;
        }

        var lowPoint,
            highPoint,
            verticalOffset,
            horizontalOffset;
        if (startPointCartographic.height > endPointCartographic.height) {
            highPoint = this._startPoint.position;
            lowPoint = endPoint.position;
            verticalOffset = startPointCartographic.height - endPointCartographic.height;
        } else {
            highPoint = endPoint.position;
            lowPoint = this._startPoint.position;
            verticalOffset = endPointCartographic.height - startPointCartographic.height;
        }

        var up = new Freedo.Cartesian3();
        Freedo.Cartesian3.normalize(lowPoint, up);
        Freedo.Cartesian3.multiplyByScalar(up, verticalOffset, up);
        var crossPoint = new Freedo.Cartesian3();
        Freedo.Cartesian3.add(lowPoint, up, crossPoint);

        horizontalOffset = Freedo.Cartesian3.distance(crossPoint, highPoint);

        var line = this._viewer.scene.primitives.add(this.createLinePrimitive(highPoint, lowPoint));
        var verticalLine = this._viewer.scene.primitives.add(this.createLinePrimitive(lowPoint, crossPoint));
        var horizontalLine = this._viewer.scene.primitives.add(this.createLinePrimitive(highPoint, crossPoint));

        let pos = g.getCenter(highPoint, lowPoint);
        let len = g.calculateLength([highPoint, lowPoint]);
        let text = this.getLengthText(len);
        var lineLabelEntity = this.createLabel(pos, text);

        pos = g.getCenter(crossPoint, lowPoint);
        text = this.getLengthText(verticalOffset);
        var verticalLineLabelEntity = this.createLabel(pos, text);

        pos = g.getCenter(crossPoint, highPoint);
        text = this.getLengthText(horizontalOffset);
        var horizontalLineLabelEntity = this.createLabel(pos, text);

        var startPointEntity = this._viewer.entities.add({
            position: this._startPoint.position,
            point: {
                color: Freedo.Color.YELLOW,
                pixelSize: 5,
                show: false,
                point: false
            }
        });
        var endPointEntity = this._viewer.entities.add({
            position: endPoint.position,
            point: {
                color: Freedo.Color.YELLOW,
                show: false,
                pixelSize: 5,
                point: false
            }
        });

        this._results.push({
            type: resultType,
            line: line,
            verticalLine: verticalLine,
            horizontalLine: horizontalLine,
            lineLabelEntity: lineLabelEntity,
            verticalLineLabelEntity: verticalLineLabelEntity,
            horizontalLineLabelEntity: horizontalLineLabelEntity,
            startPointEntity: startPointEntity,
            endPointEntity: endPointEntity
        });

        var data = {
            pointsG: [g.tg(this._startPoint.position), g.tg(endPoint.position)],
            pointsC: [this._startPoint.position, endPoint.position],
            length: len,
            vLength: verticalOffset,
            hLength: horizontalOffset
        };
        if (this._surveyType == "LINE_DISTANCE") {
            this._data.line.push(data);
            this._event.raiseEvent('DataChanged', data);
        }
        if (this._surveyType == "SEGMENTS_DISTANCE") {
            this._pointsCalced.push(data);
        }
    }

    if (this._points.length > 3 && this._surveyType == 'SEGMENTS_DISTANCE') {
        if (this._lastLabel == undefined) {
            let text = this.getLengthText(g.calculateLength(this._points));
            this._lastLabel = this.createLabel(endPoint.position, text);
        } else {
            this._lastLabel.position = endPoint.position;
            this._lastLabel.label.text = this.getLengthText(g.calculateLength(this._points));
        }
    }

    if (this._surveyType == 'SEGMENTS_DISTANCE')
        this._startPoint = Freedo.clone(endPoint, true);

    endPoint = undefined;
    this._viewer.scene.requestRender();
}

/*
 * 清理量测线等实体
 * @param {Function}
 */
FdAnalysisLineDistance.prototype.clearResults = function() {
    this.removeTmpLine();

    if (this._lastLabel) {
        this._viewer.entities.remove(this._lastLabel);
        this._lastLabel = undefined;
    }

    for (var i = 0; i < this._results.length; ++i) {
        if (this._results[i].type === this.ResultType.StraightLine) {
            this._viewer.scene.primitives.remove(this._results[i].line);
            this._viewer.entities.remove(this._results[i].labelEntity);
            this._viewer.entities.remove(this._results[i].startPointEntity);
            this._viewer.entities.remove(this._results[i].endPointEntity);
        } else if (this._results[i].type === this.ResultType.HorizontalAndVertical) {
            this._viewer.scene.primitives.remove(this._results[i].line);
            this._viewer.scene.primitives.remove(this._results[i].verticalLine);
            this._viewer.scene.primitives.remove(this._results[i].horizontalLine);
            this._viewer.entities.remove(this._results[i].lineLabelEntity);
            this._viewer.entities.remove(this._results[i].verticalLineLabelEntity);
            this._viewer.entities.remove(this._results[i].horizontalLineLabelEntity);
            this._viewer.entities.remove(this._results[i].startPointEntity);
            this._viewer.entities.remove(this._results[i].endPointEntity);
        }
    }

    for (var key in this._sumLabel) {
        this._viewer.entities.remove(this._sumLabel[key]);
    }

    this._viewer.scene.primitives.remove(this._polylineCollection);

    this._results = [];
    this._sumLabel = [];
    this._data = {
        line: [],
        polyline: []
    }
    this._points = [];
    this._pointsCalced = [];
    this._viewer.scene.requestRender();
}

FdAnalysisLineDistance.prototype.pickStartPoint = function(windowPosition) {
    this._points = [];
    this._pointsCalced = [];
    var pickResult = this.pickPosition(windowPosition);
    if (pickResult.type !== this.PositionType.NONE) {
        this._startPoint = pickResult;
        this._points.push(pickResult.position);
        this._points.push(pickResult.position);
        this._tempPointEntities[0] = this._viewer.entities.add({
            position: this._startPoint.position,
            show: false,
            point: {
                color: Freedo.Color.YELLOW,
                pixelSize: 5,
                show: false,
                point: false
            }
        });
    }
}

FdAnalysisLineDistance.prototype.getLengthText = function(length) {
    return length > 1000 ? (length / 1000).toFixed(2) + '千米' : length.toFixed(2) + '米'
}

FdAnalysisLineDistance.prototype.pickEndPoint = function(windowPosition) {
    var pickResult = this.pickPosition(windowPosition);
    if (pickResult.type !== this.PositionType.NONE) {
        this.addResult(pickResult);
        this._bRmStarting = true;
        setTimeout(this.removeTmpLine.bind(this), 100);
    }
}

FdAnalysisLineDistance.prototype.handleLeftClick = function(movement) {
    if (this._bInSurvey == false)
        return;
    if (this._startPoint) {
        this.pickEndPoint(movement.position);
    } else {
        this.pickStartPoint(movement.position);
    }
};

