FdAnalysisLineDistance.prototype.addSumInfo = function() {
    if (this._surveyType == 'SEGMENTS_DISTANCE') {
        var sumLen = Freedo.FdMisc.FdGeometric.calculateLength(this._points);
        if (this._points.length >= 2) {
            this._data.polyline.push({
                lines: Freedo.clone(this._pointsCalced, true),
                sumLenght: sumLen
            });

            this._event.raiseEvent('DataChanged', {
                lines: Freedo.clone(this._pointsCalced, true),
                sumLenght: sumLen
            });
        }
    }
    this._viewer.scene.requestRender();
}

FdAnalysisLineDistance.prototype.handleRightClick = function(movement) {
    if (this._bInSurvey == false)
        return;
    if (!!this._startPoint) {
        this._startPoint = undefined;
        this.addSumInfo();
    }
    this.removeTmpLine();
}

FdAnalysisLineDistance.prototype.callbackPosition = function() {
    this._viewer.scene.requestRender();
    return [this._startPoint.position, this._tempPoint.position];
}

FdAnalysisLineDistance.prototype.handleMouseMove = function(movement) {
    if (this._bInSurvey == false || this._bRmStarting)
        return;
    if (!!this._startPoint && !Freedo.Cartesian3.equals(movement.endPosition, movement.startPosition)) {
        var tmpPt = this.pickPosition(movement.endPosition);
        if (Freedo.defined(tmpPt.position))
            this._tempPoint = tmpPt;
        else
            return;

        if (this._tempLine === undefined) {
            var posCP = new Freedo.CallbackProperty(this.callbackPosition.bind(this), false);
            this._tempLine = this._viewer.entities.add({
                name: 'MeasureLine',
                polyline: {
                    positions: posCP,
                    width: this._lineWidth,
                    material: Freedo.Color.RED,
                    depthFailMaterial: Freedo.Color.GREEN
                }
            });
        }
    }
}

FdAnalysisLineDistance.prototype.onKeyDown = function() {
    if (this._bInSurvey == false)
        return;
    if (window.event.keyCode == 27) {
        this.handleRightClick();
    }
}

FdAnalysisLineDistance.prototype.getData = function(type) {
    return Freedo.clone(this._data, true);
}

FdAnalysisLineDistance.prototype.setData = function(v) {
    this._surveyType = "LINE_DISTANCE";
    for (var k in v.line) {
        this._points = [];
        this._startPoint = {
            position: v.line[k].pointsC[0]
        };
        this.addResult({
            position: v.line[k].pointsC[1]
        });
    }

    this._surveyType = "SEGMENTS_DISTANCE";
    for (var k in v.polyline) {
        this._points = [];
        var polyline = v.polyline[k];
        for (var j in polyline.lines) {
            if (j == "0") {
                this._points.push(polyline.lines[j].pointsC[0]);
                this._points.push(polyline.lines[j].pointsC[0]);
            }
            this._startPoint = {
                position: polyline.lines[j].pointsC[0]
            };
            this.addResult({
                position: polyline.lines[j].pointsC[1]
            });
        }
        this.addSumInfo();
    }

    this._startPoint = undefined;
}

/**
 * 注册事件捕捉
 * @param {FdAnalysisLineDistance~FdAnalysisLineDistanceCallback} listener 当有事件触发时被执行的函数。
 * @param {Object} [scope] listener函数执行时的绑定的对象。
 * @returns {Freedo.Event~RemoveCallback} 返回一个函数，调用该函数可以取消监听。
 */
FdAnalysisLineDistance.prototype.on = function(listener, scope) {
    return this._eventHelper.add(this._event, listener, scope);
}

/**
 * @callback FdAnalysisLineDistance~FdAnalysisLineDistanceCallback
 * @param {String} eventType 事件类型有：Start（量测开始），Cancel（量测结束），DataChanged（数据变化）
 * @param {Object} eventArg Start/Cancel无返回值，DataChanged类型事件参数包含相关信息
 */

FdAnalysisLineDistance.prototype.run = function(type) {
    this._surveyType = type;
    this._hanlder = new Freedo.ScreenSpaceEventHandler(this._viewer.scene.canvas);
    this._hanlder.setInputAction(this.handleLeftClick.bind(this), Freedo.ScreenSpaceEventType.LEFT_CLICK);
    this._hanlder.setInputAction(this.handleRightClick.bind(this), Freedo.ScreenSpaceEventType.LEFT_DOUBLE_CLICK);
    this._hanlder.setInputAction(this.handleRightClick.bind(this), Freedo.ScreenSpaceEventType.RIGHT_CLICK);
    this._hanlder.setInputAction(this.handleMouseMove.bind(this), Freedo.ScreenSpaceEventType.MOUSE_MOVE);

    window.addEventListener('keydown', this.onKeyDown.bind(this));
    this._bInSurvey = true;
}

/**
 * 重置
 */
FdAnalysisLineDistance.prototype.reset = function() {
    this._bInSurvey = false;
    this._hanlder.removeInputAction(Freedo.ScreenSpaceEventType.LEFT_CLICK);
    this._hanlder.removeInputAction(Freedo.ScreenSpaceEventType.LEFT_DOUBLE_CLICK);
    this._hanlder.removeInputAction(Freedo.ScreenSpaceEventType.RIGHT_CLICK);
    this._hanlder.removeInputAction(Freedo.ScreenSpaceEventType.MOUSE_MOVE);
    window.removeEventListener('keydown', this.onKeyDown.bind(this));
};

/**
 * 销毁资源
 */
FdAnalysisLineDistance.prototype.removeAll = function() {
    this.clearResults();
};

/**
 * 销毁资源
 */
FdAnalysisLineDistance.prototype.dispose = function() {
    this.reset();
    this.clearResults();
};

export {
    FdAnalysisLineDistance
};