var _FreedoXURL;

/**
 * 获取FreedoX.js文件的路径
 * @alias FdApp.getFreedoXPath
 * @returns {String} FreedoX.js文件的路径
 * @example
 * var freedoXPath = Freedo.FdApp.getFreedoXPath();
 * 示例代码中调用该函数的输出结果是这个样子： '../FreedoX/'
 */
function getFreedoXPath() {
    if (typeof _FreedoXURL === 'undefined') {
        var scripts = document.getElementsByTagName('script');
        for(var ii =0 ; ii <scripts.length ; ii++){
            var path = scripts[ii].getAttribute('src');
            if(Freedo.defined(path) && path.indexOf('FreedoX.js') !== -1){
                _FreedoXURL = path.substr(0 ,path.indexOf('FreedoX.js'));
            }
        }
    }
   return _FreedoXURL;
}

export {
    getFreedoXPath
};