class FdViewSwitcher {
    /**
     * 三维模型浏览方式，目前支持三种模型浏览方式：无球模式（'MODEL_VIEW'），平面模式（'COLUMBUS_VIEW'），正常模式（'NORMAL_VIEW'）。默认值为正常模式，即三维球浏览方式。
     * @alias FdViewSwitcher
     * @private
     * @class
     * 
     * @example
     * //开启无球浏览模式
     * var viewer = Freedo.FdApp.createDefaultViewer('freedoContainer');
     * var tileset = viewer.scene.primitives.add(new Freedo.FreedoPModelset({
     *  url : '../../sampleData/文化室2018z'
     * }));
     * tileset.readyPromise.then(function(tileset) {
     *  viewer.camera.viewBoundingSphere(tileset.boundingSphere, new Freedo.HeadingPitchRange(0, -0.5, 0));
     *  var vs = new Freedo.FdTools.FdViewSwitcher(viewer);
     *  vs.start("MODEL_VIEW");
     * }); 
     */
    constructor(viewer) {
        this._viewer = viewer;
        this._tempBall = null;
        this._spSize = 0;

        this._oldType = "";

        this._event = new FreeDo.Event();
        this._eventHelper = new FreeDo.EventHelper();
    }
}

/**
 * 事件类型有：Start，Cancel
 * @param {Function} listener 当有事件触发时被执行的函数。
 * @param {Object} [scope] listener函数执行时的绑定的对象。
 * @returns {Freedo.Event~RemoveCallback} 返回一个函数，调用该函数可以取消监听。
 */
FdViewSwitcher.prototype.on = function(listener, scope) {
    return this._eventHelper.add(this._event, listener, scope);
}

FdViewSwitcher.prototype.checkModelExist = function() {
    return false;
}

/**
 * 获取当前模式
 * @returns {String} 类型：'MODEL_VIEW'：无球模式，'COLUMBUS_VIEW'：平面模式，'NORMAL_VIEW'：正常模式
 */
FdViewSwitcher.prototype.getMode = function() {
    return this._oldType;
}

/**
 * 激活浏览模式
 * @param {String} type 类型：'MODEL_VIEW'：无球模式，'COLUMBUS_VIEW'：平面模式，'NORMAL_VIEW'：正常模式
 */
FdViewSwitcher.prototype.start = function(type) {
    var that = this;

    this._viewer.scene.noSphereMode = true;

    if(type === "MODEL_VIEW") {
        this._viewer.scene.skyAtmosphere.show = false;
        this._viewer.scene.fog.enabled = false;
        this._viewer.scene.globe.show = false;

        for(var i = 0; i < this._viewer.dataSources.length; i++) {
            var ds = this._viewer.dataSources.get(i);
            ds.show = false;
        }
    }

    if(type === "COLUMBUS_VIEW") {
        //this._viewer.scene.morphToColumbusView(1);
        this._viewer.scene.mode = Freedo.SceneMode.COLUMBUS_VIEW;

        if(this._oldType == "MODEL_VIEW") {
            this._viewer.terrainProvider = new Freedo.EllipsoidTerrainProvider();
        }

        for(var i = 0; i < this._viewer.dataSources.length; i++) {
            var ds = this._viewer.dataSources.get(i);
            ds.show = true;
        }

        this._viewer.scene.globe.show = true;
    }

    if(type === "2D") {
        //暂时取消了此模式
        if(this._tempBall == null) {
            this._viewer.entities.remove(this._tempBall);
            this._tempBall = null;
        }

        this._viewer.scene.morphTo2D(0.02);

        this._viewer.terrainProvider = new Freedo.EllipsoidTerrainProvider();

        for(var i = 0; i < this._viewer.dataSources.length; i++) {
            var ds = this._viewer.dataSources.get(i);
            ds.show = true;
        }

        this._viewer.trackedEntity = undefined;
    }

    if(type === 'NORMAL_VIEW') {
        this.cancel();
    }

    this._oldType = type;

    this._event.raiseEvent("Start", {
        mode: this._oldType
    });
    
    this._viewer.scene.requestRender();
}


FdViewSwitcher.prototype.morphTo3D = function() {
    this._viewer.scene.mode = Freedo.SceneMode.SCENE3D;
}

/**
 * 退出特殊浏览模式，还原到默认浏览模式。
 */
FdViewSwitcher.prototype.cancel = function() {
    if(this._oldType == "MODEL_VIEW") {

    }

    this._viewer.scene.globe.show = true;
    this._viewer.scene.skyAtmosphere.show = true;
    this._viewer.scene.fog.enabled = true;

    this._viewer.scene.noSphereMode = false;

    setTimeout(this.morphTo3D.bind(this), 500);

    for(var i = 0; i < this._viewer.dataSources.length; i++) {
        var ds = this._viewer.dataSources.get(i);
        ds.show = true;
    }

    if(this._tempBall != null) {
        this._viewer.entities.remove(this._tempBall);
        this._tempBall = null;
    }

    this._event.raiseEvent("Cancel", {
        mode: this._oldType
    });

    this._oldType = "NORMAL_VIEW";
    
    this._viewer.scene.requestRender();
}

FdViewSwitcher.prototype.reset = function() {
    this.cancel();
}

FdViewSwitcher.prototype.dispose = function() {
    this.reset();
}

export {
    FdViewSwitcher
};