/*
 * 贝塞尔曲线类
 * @alias FdBezierCurveAlgorithm
 * @class 用于折线平滑
 * @private
 * @param {Object} pointA 线段的起点
 * @param {Object} pointB 线段的终点
 * @param {Object} controlPointA 起点的控制点
 * @param {Object} controlPointB 终点的控制点
 */
class FdBezierCurveAlgorithm {
	constructor(pointA, pointB, controlPointA, controlPointB) {
		this.mPointA = pointA.position;
		this.mPointB = pointB.position;
		this.mControlPointA = controlPointA.position;
		this.mControlPointB = controlPointB.position;
	}
}

FdBezierCurveAlgorithm.prototype.coefficients = function(t, a, b, c, d) {
	var m_t = 1. - t;
	b = m_t * m_t;
	c = t * t;
	d = c * t;
	a = b * m_t;
	b *= 3. * t;
	c *= 3. * m_t;
	return {
		a: a,
		b: b,
		c: c,
		d: d
	}
}

FdBezierCurveAlgorithm.prototype.getPointAt = function(t, x, y, z) {
	if(t < 0)
		t = 0.0;
	if(t > 1)
		t = 1.0;

	var a, b, c, d;
	var tabcd = this.coefficients(t, a, b, c, d);

	x = tabcd.a * this.mPointA.x + tabcd.b * this.mControlPointA.x + tabcd.c * this.mControlPointB.x + tabcd.d * this.mPointB.x;
	y = tabcd.a * this.mPointA.y + tabcd.b * this.mControlPointA.y + tabcd.c * this.mControlPointB.y + tabcd.d * this.mPointB.y;
	z = tabcd.a * this.mPointA.z + tabcd.b * this.mControlPointA.z + tabcd.c * this.mControlPointB.z + tabcd.d * this.mPointB.z;

	return {
		x: x,
		y: y,
		z: z
	}
}

/*
 * 得到平滑之后的点组
 * @param {Number} [smooth] 平滑度，取值为1-8
 * @returns {Array} 返回新点数组
 */
FdBezierCurveAlgorithm.prototype.getVertexForCurve = function(smooth) {
	var df_t_interval = 0;
	var nCount = 0;

	switch(smooth) {
		case 1: // t = 1  2个点
			df_t_interval = 1;
			nCount = 2;
			break;
		case 2: // t = 0.5  3个点
			df_t_interval = 0.5;
			nCount = 3;
			break;
		case 3: // t = 0.2 6个点
			df_t_interval = 0.2;
			nCount = 6;
			break;
		case 4: // t = 0.1 11个点
			df_t_interval = 0.1;
			nCount = 11;
			break;
		case 5: // t = 0.05 21个点
			df_t_interval = 0.05;
			nCount = 21;
			break;
		case 6: // t = 0.02; 51个点
			df_t_interval = 0.02;
			nCount = 51;
			break;
		case 7: // t = 0.01; 101个点
			df_t_interval = 0.01;
			nCount = 101;
			break;
		case 8: // t = 0.005; 201个点
			df_t_interval = 0.005;
			nCount = 201;
			break;
		case 9: // t = 0.005; 401个点
			df_t_interval = 0.0025;
			nCount = 401;
			break;
		case 10: // t = 0.005; 801个点
			df_t_interval = 0.00125;
			nCount = 801;
			break;
		case 11: // t = 0.005; 1601个点
			df_t_interval = 0.000625;
			nCount = 1601;
			break;
		default:
			df_t_interval = 0.005;
			nCount = 201;
			break;
	}

	var points = [];

	var t = 0;
	for(var i = 0; i < nCount; ++i) {
		var point = {};
		var x = 0,
			y = 0,
			z = 0;

		var xyz = this.getPointAt(t, x, y, z);
		point.x = xyz.x;
		point.y = xyz.y;
		point.z = xyz.z;

		points.push(point);

		t += df_t_interval;
	}

	return points;
}

export {
    FdBezierCurveAlgorithm
};