import { LayerData } from './LayerData.js';
/**
 * DrillData
 * @private
 */
class DrillData {
    constructor(json) {
        this.name = json.name;
        this.waterDepth = parseFloat(json.water_depth);
        var a = json.pos.split(',');
        this.cartographic = Freedo.Cartographic.fromDegrees(parseFloat(a[0]), parseFloat(a[1]), parseFloat(a[2]));
        this.cartesian = Freedo.Cartesian3.fromRadians(this.cartographic.longitude, this.cartographic.latitude, this.cartographic.height);
        this.cartesianOnZeroHeight = Freedo.Cartesian3.fromRadians(this.cartographic.longitude, this.cartographic.latitude, 0);
        this.pos = json.pos;

        this.layers = [];
        for (var i = json.layer.length - 1; i >= 0; i--) {
            this.layers.push(new LayerData(json.layer[i]));
        }
        this.layers.sort(function(l1, l2) {
            return l1.depth - l2.depth;
        });
        for (var i = 0; i < this.layers.length; i++) {
            this.layers[i].thickness = i > 0 ? this.layers[i].depth - this.layers[i - 1].depth : this.layers[i].depth;
        }
    }
}
export { DrillData };