import {
    FdMisc
} from '../FdMisc/FdMisc';

/*
 * 裁剪操作器类
 * 具体调用方法为：
 * 1、startClip设置类型
 * 2、show显示坐标轴操作框架
 * 3、updateMatrix4更新姿态信息
 * @alias FdClipPlaneEditor
 * @class
 * @private
 */
class FdClipPlaneEditor {
    constructor(viewer, isPlaneMode) {
        this._viewer = viewer;

        this._bInTransform = false;
        this._transType = 'MOVE';
        this._bInRun = false;

        this._startPt = null;
        this._endPt = null;

        this._points2 = null;

        this._xAxisPL = null;
        this._yAxisPL = null;
        this._zAxisPL = null;

        this._XYPlane = null;
        this._YZPlane = null;
        this._XZPlane = null;

        this._XYCircle = null;
        this._XZCircle = null;

        this._selectedAxis = '';

        this._mouseDownPos = null;
        this._mouseDownXY = null;

        this._mouseDownStartXY = null;
        this._mouseDownEndXY = null;

        this._initSphere = null;

        this._useDepthPicking = true;
        this._pickTranslucentDepth = true;

        this._fixedAxis = '';
        this._DISPERRAD = 111000;

        this._event = new Freedo.Event();
        this._eventHelper = new Freedo.EventHelper();

        this._oldPt = null;
        this._started = false;

        this._mouseDown = false;

        this._lastDis = -1;

        this._alpha = 0.5;
        this._axisZoom = 1;

        this.PositionType = {
            NONE: 0,
            ON_MODEL: 1,
            ON_TERRAIN: 2,
            ON_ELLIPSOID: 3
        };

        this._t12 = undefined;

        this._handler = undefined;

        this._xzOri = 1;
        this._yzOri = 1;

        this._oriObj = {};

        this._m4 = undefined;

        this._isPlaneMode = isPlaneMode;
    }
}

/*
 * 注册事件捕捉
 * @param {FdClipPlaneEditor~FdClipPlaneEditorCallback} listener 当有事件触发时被执行的函数。
 * @param {Object} [scope] listener函数执行时的绑定的对象。
 * @returns {Freedo.Event~RemoveCallback} 返回一个函数，调用该函数可以取消监听。
 */
FdClipPlaneEditor.prototype.on = function(listener, scope) {
    return this._eventHelper.add(this._event, listener, scope);
}

/**
 * @callback FdClipPlaneEditor~FdClipPlaneEditorCallback
 * @param {String} eventType 事件类型有：Transform（即将开始），Transforming（正在进行变换），Transformed（结束）
 * @param {Object} eventArg Transforming事件参数中，主要记录了鼠标相关的信息
 */

/**
 * 设置裁剪操作器坐标框架的类型
 * @param {String} type 类型，CLIPYZ、CLIPXY、CLIPXYNEG、CLIPXZ分别对应不同的初始操作面。
 */
FdClipPlaneEditor.prototype.startClip = function(type) {
    this._transType = type;
    if(this._bInTransform)
        return;
    this._bInTransform = true;
    this.addListeners();
    this._bInRun = true;
}

/**
 * 显示裁剪操作器坐标框架
 * @param {Object} sceneSphere 包围球
 */
FdClipPlaneEditor.prototype.show = function(sceneSphere) {
    this._initSphere = FreeDo.clone(sceneSphere, true);
    this.calcAxisZoom();
    this.clearAxis();
    this.calcCallbackArgs();
    this.showAxis();
}

/**
 * 实时更新裁剪操作器坐标框架的姿态
 * @param {Freedo.Matrix4} m4 矩阵
 */
FdClipPlaneEditor.prototype.updateMatrix4 = function(m4) {
    this._m4 = Freedo.clone(m4, true);
}

/**
 * 实时更新裁剪操作器坐标框架的位置和大小，不需要调用这个，框架的变换只受矩阵的影响
 * @param {Object} sceneSphere 包围球
 * @private
 */
FdClipPlaneEditor.prototype.updateSceneSphere = function(sceneSphere) {
    this._initSphere = FreeDo.clone(sceneSphere, true);
}

FdClipPlaneEditor.prototype.addListeners = function() {
    this._handler = new Freedo.ScreenSpaceEventHandler(this._viewer.scene.canvas);
    this._handler.setInputAction(this.onLeftDown.bind(this), FreeDo.ScreenSpaceEventType.LEFT_DOWN);
    this._handler.setInputAction(this.onMouseMove.bind(this), FreeDo.ScreenSpaceEventType.MOUSE_MOVE);
    this._handler.setInputAction(this.onLeftUp.bind(this), FreeDo.ScreenSpaceEventType.LEFT_UP);
    this._handler.setInputAction(this.onWheel.bind(this), FreeDo.ScreenSpaceEventType.WHEEL);

    var that = this;
    /*
    this._viewer.camera.on(function(eventType, param) {
    	if(eventType == 'Changed') {
    		that.onWheel();
    	}
    });*/

    window.addEventListener('keydown', this.onKeyDown.bind(this));
}

FdClipPlaneEditor.prototype.calcAxisZoom = function() {
    if(this._bInTransform === false) {
        this._axisZoom = 1;
        return;
    }

    var startPt = this._initSphere.center;
    var endPt = this._viewer.camera.positionWC;
    var dist1 = 1,
        dist2 = 1;

    dist1 = Freedo.Cartesian3.distance(startPt, endPt) / 6;
    dist2 = this._viewer.camera.getPixelSize(this._initSphere, 100, 100);

    this._axisZoom = dist1;

    if(this._axisZoom > 400000)
        this._axisZoom = 400000;
    if(this._axisZoom < 20)
        this._axisZoom = 20;
}

FdClipPlaneEditor.prototype.onWheel = function() {
    if(this._bInTransform === false)
        return;

    this.calcAxisZoom();
}

FdClipPlaneEditor.prototype.onKeyDown = function() {
    if(this._bInTransform === false)
        return;
}

FdClipPlaneEditor.prototype.onMouseMove = function(movement) {
    if(!this._bInTransform)
        return;

    if(this._selectedAxis != '') {
        this._event.raiseEvent('Transforming', {
            MouseDownStartXY: this._mouseDownStartXY,
            MouseDownEndXY: this._mouseDownEndXY,
            MouseDownXY: this._mouseDownXY,
            MouseMoveXY: movement.endPosition,
            SelectedAxis: this._selectedAxis,
            Ori: this._oriObj
        });

        this.regenPts();
        this.calcAxisZoom();
    }
    this._viewer.scene.requestRender();
}

FdClipPlaneEditor.prototype.regenPts = function() {
    if(this._points2 === null)
        this._points2 = this._viewer.scene.primitives.add(new Freedo.PointPrimitiveCollection());
    if(this._points2.length > 0) {
        this._points2.removeAll();
    }

    var result = Freedo.Cartesian3.fromDegreesArrayHeights([longitude, latitude, height,
        longitude, latitude, height + length
    ]);
    if(this._isPlaneMode){
        result = [new Freedo.Cartesian3(longitude, latitude, height),
        new Freedo.Cartesian3(longitude, latitude, height + length)];
    }

    if(this._m4 !== undefined) {
        result[0] = Freedo.Matrix4.multiplyByPoint(this._m4, result[0], new Freedo.Cartesian3());
        result[1] = Freedo.Matrix4.multiplyByPoint(this._m4, result[1], new Freedo.Cartesian3());
    }

    this._points2.add({
        position: result[0],
        pixelSize: 10,
        show: false,
        color: Freedo.Color.YELLOW
    });
    this._points2.add({
        position: result[1],
        pixelSize: 10,
        show: false,
        color: Freedo.Color.YELLOW
    });
}

FdClipPlaneEditor.prototype.onLeftDown = function(movement) {
    if(!this._bInTransform)
        return;

    this.regenPts();

    this._mouseDown = true;
    this._oldPt = null;
    this._started = false;
    this._lastDis = -1;

    var pickedFeature = this._viewer.scene.pick(movement.position);
    if(!FreeDo.defined(pickedFeature) || !FreeDo.defined(pickedFeature.id) || !FreeDo.defined(pickedFeature.id._name)) {
        var pickedObjects = this._viewer.scene.drillPick(movement.position);
        for(var i = 0; i < pickedObjects.length; i++) {
            if(FreeDo.defined(pickedObjects[i].id) && FreeDo.defined(pickedObjects[i].id._name)) {
                pickedFeature = pickedObjects[i];
                break;
            }
        }
        if(!FreeDo.defined(pickedFeature))
            return;
    }

    if(FreeDo.defined(pickedFeature.id) && FreeDo.defined(pickedFeature.id._name)) {
        var pickName = pickedFeature.id._name;
        if((pickName === 'X_AXIS' || pickName === 'Y_AXIS' || pickName === 'Z_AXIS') && this._transType.indexOf('CLIP') >= 0) {
            pickedFeature.id.polyline.material.color = new Freedo.Color(1, 1, 0, 1);
        }

        if(pickName === 'XY_CIRCLE' || pickName === 'YZ_CIRCLE' || pickName === 'XZ_CIRCLE') {
            pickedFeature.id.polyline.material.color = new Freedo.Color(1, 1, 0, 1);
        }

        if(pickName === 'XY_CIRCLE' || pickName === 'XZ_CIRCLE' || pickName === 'YZ_CIRCLE' ||
            pickName === 'XY' || pickName === 'XZ' || pickName === 'YZ' ||
            pickName === 'X_AXIS' || pickName === 'Y_AXIS' || pickName === 'Z_AXIS') {
            this._selectedAxis = FreeDo.clone(pickName, true);
            this._viewer.scene.screenSpaceCameraController.enableRotate = false;
            this._viewer.scene.screenSpaceCameraController.enableLook = false;
            this._mouseDownPos = null;

            var ray = this._viewer.camera.getPickRay(movement.position);
            this._oriObj.rayDirection = ray.direction;

            this._event.raiseEvent('Transform', {});
        }
    }

    this._mouseDownXY = movement.position;
    var pt0 = this._points2.get(0);
    var pt1 = this._points2.get(1);
    this._mouseDownStartXY = pt0.computeScreenSpacePosition(this._viewer.scene, new Freedo.Cartesian2());
    this._mouseDownEndXY = pt1.computeScreenSpacePosition(this._viewer.scene, new Freedo.Cartesian2());
    this._viewer.scene.requestRender();
}

FdClipPlaneEditor.prototype.backToEnableRotate = function(movement) {
    if(this._selectedAxis === '') {
        this._viewer.scene.screenSpaceCameraController.enableRotate = true;
        this._viewer.scene.screenSpaceCameraController.enableLook = true;
    }
}

FdClipPlaneEditor.prototype.onLeftUp = function(movement) {
    //要给个延迟，不能直接调用
    setTimeout(this.backToEnableRotate.bind(this), 500);

    if(!this._bInTransform)
        return;

    this._mouseDown = false;

    if(this._selectedAxis === 'XY_CIRCLE') {
        this._XYCircle.polyline.material.color = new Freedo.Color(0, 0, 1, this._alpha)
    }

    if(this._selectedAxis === 'XZ_CIRCLE') {
        this._XZCircle.polyline.material.color = new Freedo.Color(0, 1, 0, this._alpha)
    }

    if(this._selectedAxis === 'YZ_CIRCLE') {
        this._YZCircle.polyline.material.color = new Freedo.Color(1, 0, 0, this._alpha)
    }

    if(this._selectedAxis === 'X_AXIS') {
        this._xAxisPL.polyline.material.color = FreeDo.Color.RED;
    }
    if(this._selectedAxis === 'Y_AXIS') {
        this._yAxisPL.polyline.material.color = FreeDo.Color.GREEN;
    }
    if(this._selectedAxis === 'Z_AXIS') {
        this._zAxisPL.polyline.material.color = FreeDo.Color.BLUE;
    }

    if(this._selectedAxis !== '') {
        this._event.raiseEvent('Transformed', {});
    }

    this._selectedAxis = '';
    this._viewer.scene.requestRender();
}

FdClipPlaneEditor.prototype.clearAxis = function() {
    if(this._xAxisPL != null) {
        this._viewer.entities.remove(this._xAxisPL);
        this._viewer.entities.remove(this._yAxisPL);
        this._viewer.entities.remove(this._zAxisPL);

        this._viewer.scene.primitives.remove(this._points2);
        this._points2 = null;

        this._viewer.entities.remove(this._XZPlane);
        this._viewer.entities.remove(this._YZPlane);
        this._viewer.entities.remove(this._XYPlane);
        this._viewer.entities.remove(this._XYCircle);
        this._viewer.entities.remove(this._XZCircle);
        this._viewer.entities.remove(this._YZCircle);
    }
    this._selectedAxis = '';
}

var position = null;
var length = null;
var posGraphic = null;
var longitude = null;
var latitude = null;
var height = null;
var rio = [];

FdClipPlaneEditor.prototype.calcCallbackArgs = function() {
    position = this._initSphere.center;
    length = this._initSphere.radius * 4.0;
    if(length > 160)
        length = 160;
    posGraphic = Freedo.Cartographic.fromCartesian(position);

    if(this._isPlaneMode) {
        longitude = position.x;
        latitude = position.y;
        height = position.z;
        rio = [1, 1];
    } else {
        longitude = Freedo.Math.toDegrees(posGraphic.longitude);
        latitude = Freedo.Math.toDegrees(posGraphic.latitude);
        height = posGraphic.height;
        rio = Freedo.FdMisc.FdGeometric.getLongAndLatRatio(posGraphic);
    }

    length = this._axisZoom;
}

FdClipPlaneEditor.prototype.callbackXYHalfCircle = function(time, result) {
    this.calcCallbackArgs();
    var width = length * 0.9 / this._DISPERRAD;
    var aPos = [];

    var widthLong = FreeDo.Math.toDegrees(length * 0.9 * rio[0]);
    var widthLat = FreeDo.Math.toDegrees(length * 0.9 * rio[1]);

    for(var i = 0; i <= Math.PI / 2; i = i + 0.1) {
        var longi = longitude + widthLong * Math.cos(i);
        var lat = latitude + widthLat * Math.sin(i);
        var pt = Freedo.Cartesian3.fromDegrees(longi, lat, height);
        aPos.push(pt);
    }
    result = aPos;

    return result;
}

FdClipPlaneEditor.prototype.callbackXZHalfCircle = function(time, result) {
    this.calcCallbackArgs();

    var widthLong = FreeDo.Math.toDegrees(length * 0.9 * rio[0]);
    var widthLat = FreeDo.Math.toDegrees(length * 0.9 * rio[1]);
    if(this._isPlaneMode) {
        var widthLong = length * 0.9;
        var widthLat = length * 0.9;
    }

    var aPos = [];
    var dStart = Math.PI / 2 + 0.3;
    var dEnd = Math.PI / 2 - 0.3;
    if(this._selectedAxis == 'XZ_CIRCLE') {
        dStart = Math.PI * 2;
        dEnd = 0;
    }

    //for (var i = Math.PI / 2; i >= 0; i = i - 0.001) {
    for(var i = dStart; i >= dEnd; i = i - 0.001) {
        var longi = longitude + widthLong * Math.cos(i);
        var lat = latitude;
        var h = height + length * 0.9 * Math.sin(i);
        var pt;
        if(this._isPlaneMode) {
            pt = new Freedo.Cartesian3(longi, lat, h);
        } else
            pt = Freedo.Cartesian3.fromDegrees(longi, lat, h);
        aPos.push(pt);
    }

    result = aPos;

    this.reCalcPos(result);

    return result;
}

FdClipPlaneEditor.prototype.callbackYZHalfCircle = function(time, result) {
    this.calcCallbackArgs();

    var widthLong = FreeDo.Math.toDegrees(length * 0.9 * rio[0]);
    var widthLat = FreeDo.Math.toDegrees(length * 0.9 * rio[1]);

    if(this._isPlaneMode) {
        var widthLong = length * 0.9;
        var widthLat = length * 0.9;
    }

    var aPos = [];

    var dStart = Math.PI / 2 + 0.3;
    var dEnd = Math.PI / 2 - 0.3;
    if(this._selectedAxis == 'YZ_CIRCLE') {
        dStart = Math.PI * 2;
        dEnd = 0;
    }
    for(var i = dStart; i >= dEnd; i = i - 0.001) {
        //for (var i = Math.PI / 2; i >= 0; i = i - 0.001){
        var longi = longitude;
        var lat = latitude + widthLat * Math.cos(i);
        var h = height + length * 0.9 * Math.sin(i);
        var pt;
        if(this._isPlaneMode) {
            pt = new Freedo.Cartesian3(longi, lat, h);
        } else
            pt = Freedo.Cartesian3.fromDegrees(longi, lat, h);
        aPos.push(pt);
    }
    result = aPos;

    this.reCalcPos(result);

    return result;
}

FdClipPlaneEditor.prototype.callbackXYCCBPMin = function(time, result) {
    this.calcCallbackArgs();

    if(this._mouseDown && this._selectedAxis == 'XY_CIRCLE')
        result = length * 0.75;
    else
        result = length * 0.5 * 2;

    return result;
}

FdClipPlaneEditor.prototype.callbackXYRect = function(time, result) {
    this.calcCallbackArgs();

    var widthLong = FreeDo.Math.toDegrees(length * 1.2 * rio[0]);
    var widthLat = FreeDo.Math.toDegrees(length * 1.2 * rio[1]);
    if(this._isPlaneMode) {
        widthLong = length * 1.2;
        widthLat = length * 1.2;
        result = [new Freedo.Cartesian3(longitude + widthLong, latitude + widthLat, height),
            new Freedo.Cartesian3(longitude - widthLong, latitude + widthLat, height),
            new Freedo.Cartesian3(longitude - widthLong, latitude - widthLat, height),
            new Freedo.Cartesian3(longitude + widthLong, latitude - widthLat, height),
            new Freedo.Cartesian3(longitude + widthLong, latitude + widthLat, height)
        ];
    } else {
        result = Freedo.Cartesian3.fromDegreesArrayHeights([
            longitude + widthLong, latitude + widthLat, height,
            longitude - widthLong, latitude + widthLat, height,
            longitude - widthLong, latitude - widthLat, height,
            longitude + widthLong, latitude - widthLat, height,
            longitude + widthLong, latitude + widthLat, height
        ]);
    }

    this.reCalcPos(result);

    return result;
}

FdClipPlaneEditor.prototype.callbackXYShow = function(time, result) {
    this.calcCallbackArgs();
    result = true;
    if(this._selectedAxis == 'XY_CIRCLE')
        result = false;

    return result;
}

//XY厚度
FdClipPlaneEditor.prototype.callbackXYH = function(time, result) {
    return height + 0.1;
}

FdClipPlaneEditor.prototype.callbackXZ = function(time, result) {
    this.calcCallbackArgs();

    var widthLong = FreeDo.Math.toDegrees(length * 1 * rio[0]);
    var widthLat = FreeDo.Math.toDegrees(length * 1 * rio[1]);

    result = Freedo.Cartesian3.fromDegreesArrayHeights([
        longitude + widthLong, latitude, height - length,
        longitude + widthLong, latitude, height + length,
        longitude - widthLong, latitude, height + length,
        longitude - widthLong, latitude, height - length,
        longitude + widthLong, latitude, height - length
    ]);
    return result;
}

FdClipPlaneEditor.prototype.callbackXZMinimumHeights = function(time, result) {
    this.calcCallbackArgs();
    result = [height, height];
    return result;
}

FdClipPlaneEditor.prototype.callbackXZMaximumHeights = function(time, result) {
    this.calcCallbackArgs();
    result = [height + length * 0.5, height + length * 0.5];
    return result;
}

FdClipPlaneEditor.prototype.callbackYZ = function(time, result) {
    this.calcCallbackArgs();

    var widthLong = FreeDo.Math.toDegrees(length * 1 * rio[0]);
    var widthLat = FreeDo.Math.toDegrees(length * 1 * rio[1]);
    result = Freedo.Cartesian3.fromDegreesArrayHeights([
        longitude, latitude + widthLat, height - length,
        longitude, latitude + widthLat, height + length,
        longitude, latitude - widthLat, height + length,
        longitude, latitude - widthLat, height - length,
        longitude, latitude + widthLat, height - length
    ]);
    return result;
}

FdClipPlaneEditor.prototype.callbackXYCCBP = function(time, result) {
    this.calcCallbackArgs();
    result = Freedo.Cartesian3.fromDegrees(longitude, latitude);
    return result;
}

FdClipPlaneEditor.prototype.callbackX = function(time, result) {
    this.calcCallbackArgs();

    var widthLong = 0;
    if(this._isPlaneMode) {
        widthLong = length;
        result = [new Freedo.Cartesian3(longitude, latitude, height),
            new Freedo.Cartesian3(longitude + widthLong, latitude, height)
        ];
    } else {
        widthLong = FreeDo.Math.toDegrees(length * rio[0]);
        result = Freedo.Cartesian3.fromDegreesArrayHeights([longitude, latitude, height,
            longitude + widthLong, latitude, height
        ]);
    }

    if(this._m4 !== undefined) {
        result[0] = Freedo.Matrix4.multiplyByPoint(this._m4, result[0], new Freedo.Cartesian3());
        result[1] = Freedo.Matrix4.multiplyByPoint(this._m4, result[1], new Freedo.Cartesian3());
    }

    this._oriObj.xAxis = result;

    return result;
}

FdClipPlaneEditor.prototype.callbackY = function(time, result) {
    this.calcCallbackArgs();

    var widthLat = 0;
    if(this._isPlaneMode) {
        widthLat = length;
        result = [new Freedo.Cartesian3(longitude, latitude, height),
            new Freedo.Cartesian3(longitude, latitude + widthLat, height)
        ];
    } else {
        widthLat = length / this._DISPERRAD;
        result = Freedo.Cartesian3.fromDegreesArrayHeights([longitude, latitude, height,
            longitude, latitude + widthLat, height
        ]);
    }

    if(this._m4 !== undefined) {
        result[0] = Freedo.Matrix4.multiplyByPoint(this._m4, result[0], new Freedo.Cartesian3());
        result[1] = Freedo.Matrix4.multiplyByPoint(this._m4, result[1], new Freedo.Cartesian3());
    }

    this._oriObj.yAxis = result;

    return result;
}

FdClipPlaneEditor.prototype.callbackZ = function(time, result) {
    this.calcCallbackArgs();

    if(this._isPlaneMode) {
        result = [new Freedo.Cartesian3(longitude, latitude, height),
            new Freedo.Cartesian3(longitude, latitude, height + length)
        ];
    } else {
        result = Freedo.Cartesian3.fromDegreesArrayHeights([longitude, latitude, height,
            longitude, latitude, height + length
        ]);
    }

    if(this._m4 !== undefined) {
        result[0] = Freedo.Matrix4.multiplyByPoint(this._m4, result[0], new Freedo.Cartesian3());
        result[1] = Freedo.Matrix4.multiplyByPoint(this._m4, result[1], new Freedo.Cartesian3());
    }

    return result;
}

FdClipPlaneEditor.prototype.callbackStart = function(time, result) {
    this.calcCallbackArgs();
    result = Freedo.Cartesian3.fromDegreesArrayHeights([longitude, latitude, height]);

    if(this._m4 !== undefined) {
        result[0] = Freedo.Matrix4.multiplyByPoint(this._m4, result[0], new Freedo.Cartesian3());
    }

    return result;
}

FdClipPlaneEditor.prototype.callbackH = function(time, result) {
    this.calcCallbackArgs();
    result = height;
    return result;
}

FdClipPlaneEditor.prototype.reCalcPos = function(result) {
    if(this._m4 !== undefined) {
        for(var key in result) {
            result[key] = Freedo.Matrix4.multiplyByPoint(this._m4, result[key], new Freedo.Cartesian3());
        }
    }
}

FdClipPlaneEditor.prototype.showAxis = function() {
    var xyCBPRect = new Freedo.CallbackProperty(this.callbackXYRect.bind(this), false);
    var xyHalfCircleCBP = new Freedo.CallbackProperty(this.callbackXYHalfCircle.bind(this), false);
    var xzHalfCircleCBP = new Freedo.CallbackProperty(this.callbackXZHalfCircle.bind(this), false);
    var yzHalfCircleCBP = new Freedo.CallbackProperty(this.callbackYZHalfCircle.bind(this), false);
    var xyCCBPMin = new Freedo.CallbackProperty(this.callbackXYCCBPMin.bind(this), false);

    //var xyCBPShow = new Freedo.CallbackProperty(this.callbackXYShow.bind(this), false);
    //var xyCBPH = new Freedo.CallbackProperty(this.callbackXYH.bind(this), false);
    var xzCBP = new Freedo.CallbackProperty(this.callbackXZ.bind(this), false);
    //var xzCBPMax = new Freedo.CallbackProperty(this.callbackXZMaximumHeights.bind(this), false);
    //var xzCBPMin = new Freedo.CallbackProperty(this.callbackXZMinimumHeights.bind(this), false);
    var yzCBP = new Freedo.CallbackProperty(this.callbackYZ.bind(this), false);
    var xyCCBP = new Freedo.CallbackProperty(this.callbackXYCCBP.bind(this), false);

    var xCP = new Freedo.CallbackProperty(this.callbackX.bind(this), false);
    var yCP = new Freedo.CallbackProperty(this.callbackY.bind(this), false);
    var zCP = new Freedo.CallbackProperty(this.callbackZ.bind(this), false);

    var hCP = new Freedo.CallbackProperty(this.callbackH.bind(this), false);
    //var hStartCP = new Freedo.CallbackProperty(this.callbackStart.bind(this), false);

    this._xAxisPL = this._viewer.entities.add({
        name: 'X_AXIS',
        polyline: {
            positions: xCP,
            width: 0,
            show: true, //辅助线
            followSurface: false,
            material: new Freedo.PolylineArrowMaterialProperty(Freedo.Color.RED)
        }
    });

    this._yAxisPL = this._viewer.entities.add({
        name: 'Y_AXIS',
        polyline: {
            positions: yCP,
            width: 0,
            show: true, //辅助线
            followSurface: false,
            material: new Freedo.PolylineArrowMaterialProperty(Freedo.Color.GREEN)
        }
    });

    this._zAxisPL = this._viewer.entities.add({
        name: 'Z_AXIS',
        polyline: {
            positions: zCP,
            width: 8,
            show: this._transType == 'CLIPXY', //Z轴主线
            followSurface: false,
            material: new Freedo.PolylineArrowMaterialProperty(Freedo.Color.BLUE)
        }
    });

    if(this._transType.indexOf('CLIP') >= 0) {
        this._YZPlane = this._viewer.entities.add({
            name: 'YZ',
            polyline: {
                positions: yzCBP,
                width: 2,
                show: this._transType == 'CLIPYZ', //隐藏
                followSurface: false,
                material: new Freedo.Color(1, 0, 0, this._alpha)
            }
        });
    }

    if(this._transType.indexOf('CLIP') >= 0) {
        this._XYPlane = this._viewer.entities.add({
            name: 'XY_RECT',
            polyline: {
                positions: xyCBPRect,
                width: 0.8,
                show: true, //方框
                followSurface: false,
                material: new Freedo.Color(0, 0, 1, this._alpha)
            }
        });
    }

    if(this._transType.indexOf('CLIP') >= 0) {
        this._XZPlane = this._viewer.entities.add({
            name: 'XZ',
            polyline: {
                positions: xzCBP,
                width: 2,
                show: false, //隐藏
                followSurface: false,
                material: new Freedo.Color(0, 1, 0, this._alpha)
            }
        });
    }

    this._XYCircle = this._viewer.entities.add({
        name: 'XY_NONE_CIRCLE',
        position: xyCCBP,
        ellipse: {
            semiMinorAxis: xyCCBPMin,
            semiMajorAxis: xyCCBPMin,
            height: hCP,
            show: false, //隐藏
            material: new Freedo.Color(1, 1, 0, 0.2)
        }
    });
    if(this._transType.indexOf('ROTATE') >= 0 || this._transType.indexOf('SCALE') >= 0) {
        this._XYCircle = this._viewer.entities.add({
            name: 'XY_CIRCLE',
            polyline: {
                positions: xyHalfCircleCBP,
                width: 4,
                show: true, //隐藏
                material: new Freedo.Color(0, 0, 1, this._alpha)
            }
        });
    }

    if(this._transType.indexOf('CLIPXY') >= 0) {
        this._XZCircle = this._viewer.entities.add({
            name: 'XZ_CIRCLE',
            polyline: {
                positions: xzHalfCircleCBP,
                width: 4,
                show: true, //小弧形
                material: new Freedo.Color(0, 1, 0, this._alpha)
            }
        });
        this._YZCircle = this._viewer.entities.add({
            name: 'YZ_CIRCLE',
            polyline: {
                positions: yzHalfCircleCBP,
                width: 4,
                show: true, //小弧形
                material: new Freedo.Color(1, 0, 0, this._alpha)
            }
        });
    }

    this.regenPts();
    this._viewer.scene.requestRender();
}

/**
 * 重置
 */
FdClipPlaneEditor.prototype.reset = function() {
    this._handler = this._handler && this._handler.destroy();
    this._bInTransform = false;
    this.clearAxis();
    this._mouseDownPos = null;
    this._initSphere = null;
    this._fixedAxis = '';
}

/**
 * 销毁所有资源
 */
FdClipPlaneEditor.prototype.dispose = function() {
    this.reset();
}

export {
    FdClipPlaneEditor
};