let mobx = require('mobx');

/**
 * 相机路径的历史记录
 *
 * @class
 * @alias FdCamera.FdCameraHistory
 * 
 * @example
 * var viewer = Freedo.FdApp.createDefaultViewer('freedoContainer');
 * var ch = new Freedo.FdCameraHistory(viewer.camera);
 * ch.prev(); // 相机回撤到上一个姿态
 * ch.next(); // 相机恢复到下一个姿态
 */
class FdCameraHistory {
    constructor(camera) {
        this._camera = camera;

        if (typeof camera._cameraHistory !== 'undefined') {
            throw new Error('Current camera has already create history!');
        }

        camera._cameraHistory = this;

        //this._currentCameraInfo = [lon, lat, height, heading, pitch, roll];
        this._cameraPath = [];

        // this._cameraPathCurrentIndex = -1;
        mobx.extendObservable(this, {
            _cameraPathCurrentIndex: -1
        });

        mobx.autorun(() => {
            this._cameraPathCurrentIndex;
            if (typeof this._event !== 'undefined') {
                this._event.raiseEvent('CameraPathChanged');
            }
        });

        this._lastHistoryOpFrameCount = 0;
        this._delayRecordViewValid = true;

        function _getRawCameraInfo(camera, result) {
            if (typeof result === 'undefined') {
                result = {};
            }
            result.position = camera.positionWC.clone();
            result.heading = camera.heading;
            result.pitch = camera.pitch;
            result.roll = camera.roll;

            return result;
        }

        var that = this;
        this._delayRecordView = _.debounce(function() {
            var camera = that._camera;
            if (that._delayRecordViewValid) {
                var cp = _getRawCameraInfo(camera);
                if (that._cameraPath.length >= 10) {
                    that._cameraPath.splice(0, 1);
                    that._cameraPathCurrentIndex = that._cameraPathCurrentIndex - 1;
                };
                that._cameraPath.splice(that._cameraPathCurrentIndex + 1, that._cameraPath.length - 1 - that._cameraPathCurrentIndex, cp);
                that._cameraPathCurrentIndex = that._cameraPath.length - 1;
                //console.log('cameraPath added ' + Date.now());

                that._updateCameraHistoryStatus();
            }
        }, 1000);

        this._delayRecordView();

        this._camera.percentageChanged = 0.0001;
        this._cameraChangedEventListener = this._camera.changed.addEventListener(function() {
            var camera = that._camera;

            if (camera._scene._frameState.frameNumber <= that._lastHistoryOpFrameCount) {
                that._delayRecordViewValid = false;
            } else {
                that._delayRecordViewValid = true;
                //console.log('_delayRecordView ' + Date.now());
                that._delayRecordView();
            }

            // _getCurrentCameraInfo(camera, that._currentCameraInfo);
            //that._event.raiseEvent('Changed', that._currentCameraInfo);
        });

        this._event = new Freedo.Event();
        this._eventHelper = new Freedo.EventHelper();
    }

    _updateCameraHistoryStatus() {
        this._event.raiseEvent('CameraPathChanged');
    }

    /**
     * 获取当前的路径数量
     */
    get pathLength() {
        return this._cameraPath.length;
    }

    /**
     * 获取当前相机所在路径的索引
     */
    get currentIndex() {
        return this._cameraPathCurrentIndex;
    }

    // set currentIndex(value) {
    //     this._cameraPathCurrentIndex = value;
    //     this._updateCameraHistoryStatus();
    // }

    /**
     * 设置相机到上一个视点位置
     */
    prev() {
        if (this._cameraPath.length == 0)
            return;
        // 在_lastHistoryOpFrameCount赋值之前调用cancelFlight，防止this._lastHistoryOpFrameCount失效
        this._camera.cancelFlight();

        var viewerOpts = {};
        this._lastHistoryOpFrameCount = Number.MAX_SAFE_INTEGER;

        if (this._cameraPathCurrentIndex > 0) {
            //this._cameraPathCurrentIndex = this._cameraPathCurrentIndex - 1;
            this._cameraPathCurrentIndex -= 1;
        } else {
            return;
        }

        var cp = this._cameraPath[this._cameraPathCurrentIndex];

        viewerOpts.destination = cp.position;
        viewerOpts.orientation = {
            heading: cp.heading,
            pitch: cp.pitch,
            roll: cp.roll
        }
        viewerOpts.duration = 1;

        var that = this;
        viewerOpts.complete = function() {
            that._lastHistoryOpFrameCount = that._camera._scene._frameState.frameNumber;
        }
        viewerOpts.cancel = function() {
            that._lastHistoryOpFrameCount = that._camera._scene._frameState.frameNumber;
        }

        var camera = this._camera;
        if (!Freedo.Matrix4.equals(camera.transform, Freedo.Matrix4.IDENTITY)) {
            viewerOpts.endTransform = Freedo.Matrix4.clone(camera.transform);
            camera.lookAtTransform(Freedo.Matrix4.IDENTITY);
        }

        this._camera.flyTo(viewerOpts);

        //_getCurrentCameraInfo(this._camera, this._currentCameraInfo);
        //this._event.raiseEvent('Changed', this._currentCameraInfo);
    }

    /**
     * 设置相机到下一个视点位置
     */
    next() {
        if (this._cameraPath.length == 0)
            return;
        // 在_lastHistoryOpFrameCount赋值之前调用，防止this._lastHistoryOpFrameCount失效
        this._camera.cancelFlight();

        var viewerOpts = {};
        this._lastHistoryOpFrameCount = Number.MAX_SAFE_INTEGER;

        if (this._cameraPathCurrentIndex + 1 < this._cameraPath.length) {
            this._cameraPathCurrentIndex = this._cameraPathCurrentIndex + 1;
        } else {
            return;
        }

        var cp = this._cameraPath[this._cameraPathCurrentIndex];

        viewerOpts.destination = cp.position;
        viewerOpts.orientation = {
            heading: cp.heading,
            pitch: cp.pitch,
            roll: cp.roll
        }
        viewerOpts.duration = 1;

        var that = this;
        viewerOpts.complete = function() {
            that._lastHistoryOpFrameCount = that._camera._scene._frameState.frameNumber;
        }
        viewerOpts.cancel = function() {
            that._lastHistoryOpFrameCount = that._camera._scene._frameState.frameNumber;
        }

        var camera = this._camera;
        if (!Freedo.Matrix4.equals(camera.transform, Freedo.Matrix4.IDENTITY)) {
            viewerOpts.endTransform = Freedo.Matrix4.clone(camera.transform);
            camera.lookAtTransform(Freedo.Matrix4.IDENTITY);
        }
        this._camera.flyTo(viewerOpts);

        //_getCurrentCameraInfo(this._camera, this._currentCameraInfo);
        //this._event.raiseEvent('Changed', this._currentCameraInfo);
    }

    /**
     * 注册捕捉FdCameraHistory类的回调函数
     * @param {FdCamera.FdCameraHistory~FdCameraHistoryChangedCallback} listener 当有事件触发时被执行的函数。事件类型有CameraPathChanged
     * @param {Object} [scope] listener函数执行时的绑定的对象。
     * @returns {Freedo.Event~RemoveCallback} 返回一个函数，调用该函数可以取消监听。
     *
     * @example
     *  cameraHistory.on(function (eventType) {
     *      if (eventType === 'CameraPathChanged') {
     *          // console.log('pathLength: ' + cameraHistory.pathLength + ' currentIndex:' + cameraHistory.currentIndex);
     *          cameraHistoryUI.pathLength = cameraHistory.pathLength;
     *          cameraHistoryUI.currentIndex = cameraHistory.currentIndex;
     *      }
     *  });
     */
    on(listener, scope) {
        return this._eventHelper.add(this._event, listener, scope);
    }

    /**
     * @callback FdCamera.FdCameraHistory~FdCameraHistoryChangedCallback
     * @param {String} eventType 事件类型有以下几种：事件类型有CameraPathChanged
     */

    /**
     * 销毁
     */
    dispose() {
        if (typeof this._cameraChangedEventListener !== 'undefined') {
            this._cameraChangedEventListener();
            this._cameraChangedEventListener = undefined;
        }

        if (this._camera._cameraHistory === this) {
            this._camera._cameraHistory = undefined;
        }

        if (typeof this._delayRecordView !== 'undefined') {
            this._delayRecordView = undefined;
        }

        if (typeof this._camera != 'undefined') {
            this._camera = undefined;
        }

        this._eventHelper.removeAll();
        this._event = undefined;
        this._eventHelper = undefined;
    }
}

export {
    FdCameraHistory
};