import {
    FdMisc
} from '../../FdMisc/FdMisc';

/**
 * 点选高亮
 * @alias FdMicroApp.FdPickPModel
 * @class
 */
class FdPickPModel {
    constructor(viewer) {
        this._viewer = viewer;
        this._bInSurvey = false;
        this._tss = new Set();
        
        this._clickMode = Freedo.ScreenSpaceEventType.LEFT_CLICK;
        this._highlightColor = Freedo.Color.RED.withAlpha(0.8);

        this._event = new Freedo.Event();
        this._eventHelper = new Freedo.EventHelper();
    }
}

/*
 * 设置激活模式
 * @param {Number} mode 参见ScreenSpaceEventType的取值
 */
FdPickPModel.prototype.setClickMode = function(mode) {
    this._clickMode = mode;
}

/**
 * 设置高亮颜色，默认黄色
 *
 * @param {Number} r 颜色R分量，范围0~255
 * @param {Number} g 颜色G分量，范围0~255
 * @param {Number} b 颜色B分量，范围0~255
 * @param {Number} a 透明度，范围0~1
 */
FdPickPModel.prototype.setHighlightColor = function(r, g, b, a) {
    this._highlightColor = new Freedo.Color(r/255, g/255, b/255, a);
}

/*
 * 开始
 */
FdPickPModel.prototype.start = function() {
    this._hanlder = new Freedo.ScreenSpaceEventHandler(this._viewer.scene.canvas);
    this._hanlder.setInputAction(this.handleLeftClick.bind(this), this._clickMode);//默认Freedo.ScreenSpaceEventType.LEFT_CLICK
    this._bInSurvey = true;
}

/*
 * 取消
 */
FdPickPModel.prototype.cancel = function() {
    this.reset();
}

/**
 * 注册事件捕捉
 * @param {FdMicroApp.FdPickPModel~FdPickPModelCallback} listener 当有事件触发时被执行的函数。
 * @param {Object} [scope] listener函数执行时的绑定的对象。
 * @returns {Freedo.Event~RemoveCallback} 返回一个函数，调用该函数可以取消监听。
 */
FdPickPModel.prototype.on = function(listener, scope) {
    return this._eventHelper.add(this._event, listener, scope);
}

/**
 * @callback FdMicroApp.FdPickPModel~FdPickPModelCallback
 * @param {String} eventType 事件类型有：DataChanged
 * @param {Object} eventArg DataChanged事件返回值为模型的URL
 */

FdPickPModel.prototype.handleLeftClick = function(movement) {
    if (this._bInSurvey == false)
        return;

    this.pickModel(movement.position);
};

FdPickPModel.prototype.pickModel = function(windowPosition) {
    var cartesian = undefined;
    var bHasPt = false;
    var scene = this._viewer.scene;
    var position;
    var pObjs = scene.drillPick(windowPosition);
    if (Freedo.defined(pObjs) && pObjs.length > 0) {
        for (var kk = 0; kk < pObjs.length; kk++) {
            if (pObjs[kk].tileset.style != undefined)
                continue;

            this._tss.add(pObjs[kk]);

            if (Freedo.defined(pObjs[kk].id) && pObjs[kk].id.name == 'PINICON') {
                //输出图标对应的模型的ID
                console.info(pObjs[kk].id.sceneObjectID);
            }
            
            var c = this._highlightColor;
            var style = new Freedo.Cesium3DTileStyle();
            style.color = {
                evaluateColor : function(frameState, feature, result) {
                    return Freedo.Color.clone(c, result);
                }
            };

            pObjs[kk].tileset.style = style;

            this._event.raiseEvent('DataChanged', pObjs[kk].tileset.basePath);
        }
    } else {
        for (let item of this._tss.keys()) {
            item.tileset.style = undefined;
        }
        this._tss.clear();
    }
    this._viewer.scene.requestRender();
};

/**
 * 获取数据
 * @returns {String[]} 返回值格式：
 * [模型地址]
 */
FdPickPModel.prototype.getData = function() {
    if (Freedo.defined(this._tss)) {
        var data = [];
        for (let item of this._tss.keys()) {
            data.push(item.tileset.basePath);
        }

        return data;
    }
};

FdPickPModel.prototype.reset = function() {
    this._bInSurvey = false;
    this._hanlder = this._hanlder && this._hanlder.destroy();
    for (let item of this._tss.keys()) {
        item.tileset.style = undefined;
    }
    this._tss.clear();
};

export {
    FdPickPModel
};