/**
 * 设置天空盒或者背景色
 *
 * 
 * @alias FdApp.setSkyBox
 * @param {Freedo.Scene} Scene 场景对象
 * @param {Object} option 设置参数
 * @param {Number[]} option.color 设置背景色,数组长度必须是4，数组每个值必须在0-1之间。例如红色[1.0 ,0.0 ,0.0 ,1.0] （与option.imagePaths互斥，不能同时设置）
 * @param {String[]} option.imagePaths 设置天空盒图片路径，数组长度必须为6，且每个路径必须为有效的图片路径（与option.color，不能同时设置）
 * 
 * @example
 * Freedo.FdApp.setSkyBox(viewer.scene, {
 *     color: [1.0, 0.5, 0.0, 1.0]
 * });
 * 
 * //or
 * var imagePaths = [
 *     '../static/SkyBox/tycho2t3_80_px.jpg',
 *     '../static/SkyBox/tycho2t3_80_mx.jpg',
 *     '../static/SkyBox/tycho2t3_80_py.jpg',
 *     '../static/SkyBox/tycho2t3_80_my.jpg',
 *     '../static/SkyBox/tycho2t3_80_pz.jpg',
 *     '../static/SkyBox/tycho2t3_80_mz.jpg'
 * ];
 * Freedo.FdApp.setSkyBox(viewer.scene, imagePaths);
 * 
 */
function setSkyBox(scene, option) {
    if (!(scene instanceof Freedo.Scene)) {
        throw new Freedo.DeveloperError('The parameter is not an instance of Freedo.Scene.');
    }
    if (!Freedo.defined(option)) {
        throw new Freedo.DeveloperError('option is required.');
    }

    var color = option.color;
    var imgPaths = option.imagePaths;

    if (!Freedo.defined(color) && !Freedo.defined(imgPaths)) {
        throw new Freedo.DeveloperError('color or imagePaths is required in option.');
    }

    if (Freedo.defined(color) && Freedo.defined(imgPaths)) {
        throw new Freedo.DeveloperError('color & imagePaths can\'t be set at the same time.');
    }
    scene.skyAtmosphere = undefined;

    if (Freedo.defined(color)) {
        scene.backgroundColor = new Freedo.Color(color[0], color[1], color[2], color[3]);
        scene.skyBox = undefined;
    } else {
        if (imgPaths.length !== 6) {
            throw new Freedo.DeveloperError('imagePaths must have six valid image path.');
        }
        scene.backgroundColor = new Freedo.Color(0.0, 0.0, 0.0, 1.0);

        scene.skyBox = new Freedo.SkyBox({
            sources: {
                positiveX: imgPaths[0],
                negativeX: imgPaths[1],
                positiveY: imgPaths[2],
                negativeY: imgPaths[3],
                positiveZ: imgPaths[4],
                negativeZ: imgPaths[5]
            }
        });
    }

    scene.requestRender();
}

/**
 * 设置贴地后的天空盒
 * @alias FdApp.setSkyBoxOnGround
 * @param {Freedo.Scene} Scene 场景对象
 * @param {String[]} imagePaths 设置天空盒图片路径，数组长度必须为6，且每个路径必须为有效的图片路径。
 * 
 * @example
 * var imagePaths = [
 *     '../FreedoX/Assets/Textures/SkyBoxOnGround/rightav9.jpg',
 *     '../FreedoX/Assets/Textures/SkyBoxOnGround/leftav9.jpg',
 *     '../FreedoX/Assets/Textures/SkyBoxOnGround/frontav9.jpg',
 *     '../FreedoX/Assets/Textures/SkyBoxOnGround/backav9.jpg',
 *     '../FreedoX/Assets/Textures/SkyBoxOnGround/topav9.jpg',
 *     '../FreedoX/Assets/Textures/SkyBoxOnGround/bottomav9.jpg'
 * ];
 * Freedo.FdApp.setSkyBoxOnGround(viewer.scene, imagePaths);
 */
function setSkyBoxOnGround(scene, imagePaths) {
    if (!(scene instanceof Freedo.Scene)) {
        throw new Freedo.DeveloperError('The parameter is not an instance of Freedo.Scene.');
    }

    if (!Freedo.defined(imagePaths)) {
        throw new Freedo.DeveloperError('imagePaths is required.');
    }

    if (imagePaths.length !== 6) {
        throw new Freedo.DeveloperError('imagePaths must have six valid image path.');
    }
    scene.skyAtmosphere = undefined;
    scene.skyBox = undefined;
    scene.backgroundColor = new Freedo.Color(0.0, 0.0, 0.0, 1.0);

    scene.skyBoxOnGround = new Freedo.SkyBox({
        sources: {
            positiveX: imagePaths[0],
            negativeX: imagePaths[1],
            positiveY: imagePaths[2],
            negativeY: imagePaths[3],
            positiveZ: imagePaths[4],
            negativeZ: imagePaths[5]
        }
    });

    scene.requestRender();
}

export { setSkyBox, setSkyBoxOnGround };