import { FdCommon } from './FdCommon';

/**
 * 用户服务
 * @namespace
 * @alias FdServer.FdUserServer
 */
class FdUserServer {

}

/**
 * 获取验证码
 * @param {String} telephone 手机号
 * @param {String} baseURL 域名
 */
FdUserServer.getVerificationCode = async function(telephone, baseURL) {
    let data = {
        'sendMode': '1',
        'sendNum': telephone.toString(),
        'signName': '飞渡科技',
        'useCategory': 'regist'
    }
    try {
        let url = `${baseURL}/cloudlink/cloudlink-core-framework/verfy/getVerifyCode`;
        let response = await FdCommon.ajaxJson({
            url: url,
            data: data,
            type: 'POST'
        })
    } catch (error) {
        console.error(error);
    }
}

/**
 * 用户注册
 * @param {Object} data 传参对象
 * @param {String} data.telnumber 用户的手机号
 * @param {String} data.password 注册密码，明文传值
 * @param {Number} data.verifycode 验证码
 * @param {Number} [data.username] 用户名
 * @param {FdServer.FdUserServer~ResponseCallback} response 事件完成回调函数
 */
FdUserServer.registerUser = async function(data, baseURL, callback) {
    let response = [];
    try {
        let url = baseURL + '/cloudlink/freeserver-admin/auth/regist';
        response = await FdCommon.ajaxJson({
            url: url,
            data: data,
            type: 'POST'
        })
    } catch (error) {
        console.error(error);
    }
    callback(response);
}

/**
 * 更换密码
 * @param {Object} data 传值参数对象
 * @param {String} data.mobileNum 用户的手机号码
 * @param {String} data.password 新的密码，明文传递
 * @param {Number} data.verifyCode 验证码
 * @param {FdServer.FdUserServer~ResponseCallback} response 事件完成回调函数
 */
FdUserServer.changePassword = async function(data, baseURL, callback) {
    let response = [];
    try {
        let url = baseURL + '/cloudlink/cloudlink-core-framework/login/resetPassword';
        response = await FdCommon.ajaxJson({
            url: url,
            data: data,
            type: 'POST'
        })
    } catch (error) {
        console.error(error);
    }
    callback(response);
}

/**
 * 用户登录
 * @param {Object} data 传值参数对象
 * @param {String} data.loginNum 用户的手机号
 * @param {String} data.password 用户的密码，明文不加密
 * @param {FdServer.FdUserServer~ResponseCallback} callback 事件完成回调函数
 */
FdUserServer.userLogin = async function(data, baseURL, callback) {
    let response = [];
    try {
        data.appId = '0154981b-11c5-49ad-839d-a3420af507d8';
        let url = `${baseURL}/cloudlink/cloudlink-core-framework/login/login`;
        response = await FdCommon.ajaxJson({
            url: url,
            data: data,
            type: 'POST'
        })
    } catch (error) {
        console.error(error);
    }
    callback(response);
}

/**
 * FdUserServer类方法调用回调函数
 * @callback FdServer.FdUserServer~ResponseCallback
 * @param {Object} response 调用请求获取的返回值
 */

export {
    FdUserServer
}