//FdPlaneViewCameraHelper.js
/*
无球场下相机操作辅助类
Camera的一些设置函数如heading、pitch、lookAt、setView、flyTo等，由于其计算逻辑与椭球体相关，在无球的笛卡尔坐标系下不可用
为在无球的笛卡尔坐标系下提供类似的操作封装本类

方向角定义：
1.坐标系X轴正向朝东，Y轴正向朝北，Z轴正向朝上
2.偏航角（heading）：相机方向与正北向的夹角，偏东为正
3.俯仰角（pitch）：相机方向与水平面的夹角，向上为正
*/

function FdPlaneViewCameraHelper() {
}

//偏航角（弧度）
FdPlaneViewCameraHelper.getHeading = function(camera) {
    if (!Freedo.defined(camera)) {
        throw new Freedo.DeveloperError('camera is required');
    }
    return getHeading(camera.direction, camera.up);
};

//俯仰角（弧度）
FdPlaneViewCameraHelper.getPitch = function(camera) {
    if (!Freedo.defined(camera)) {
        throw new Freedo.DeveloperError('camera is required');
    }
    return getPitch(camera.direction);
};

/**
 * 设置相机视角
 * @param {Camera} camera 相机对象
 * @param {Cartesian3} target 看向的位置.
 * @param {Cartesian3|HeadingPitchRange} offset 相对target的偏移. 
**/
FdPlaneViewCameraHelper.lookAt = function(camera, target, offset) {
    if (!Freedo.defined(camera)) {
        throw new Freedo.DeveloperError('camera is required');
    }
    if (!Freedo.defined(target)) {
        throw new Freedo.DeveloperError('target is required');
    }
    if (!Freedo.defined(offset)) {
        throw new Freedo.DeveloperError('offset is required');
    }

    var cartesianOffset;
    if (Freedo.defined(offset.heading)) {
        cartesianOffset = offsetFromHeadingPitchRange(offset.heading, offset.pitch, offset.range);
    } else {
        cartesianOffset = offset;
    }

    Freedo.Cartesian3.add(target, cartesianOffset, camera.position);
    Freedo.Cartesian3.subtract(target, camera.position, scratchCameraAt);
    Freedo.Cartesian3.normalize(scratchCameraAt, camera.direction);
    
    if (Freedo.Cartesian3.equalsEpsilon(camera.direction, Freedo.Cartesian3.UNIT_Z, Freedo.Math.EPSILON4)) {
        Freedo.Cartesian3.fromElements(0.0, -1.0, 0.0, scratchCameraUp);
    } else if (Freedo.Cartesian3.equalsEpsilon(camera.direction, negateUnitZ, Freedo.Math.EPSILON4)) {
        Freedo.Cartesian3.clone(Freedo.Cartesian3.UNIT_Y, scratchCameraUp);
    } else {
        Freedo.Cartesian3.clone(Freedo.Cartesian3.UNIT_Z, scratchCameraUp);
    }

    Freedo.Cartesian3.cross(scratchCameraAt, scratchCameraUp, scratchCameraRight);
    Freedo.Cartesian3.cross(scratchCameraRight, scratchCameraAt, scratchCameraUp);
    
    Freedo.Cartesian3.normalize(scratchCameraUp, camera.up);
    Freedo.Cartesian3.normalize(scratchCameraRight, camera.right);
};

/**
 * 设置相机视角
 * @param {Camera} camera 相机对象
 * @param {Object} 选项对象:
 * @param {Cartesian3} options.destination 目标位置.
 * @param {Object} [options.orientation] 目标朝向: {direction:,up:} 或 {heading:,pitch:}.
**/
FdPlaneViewCameraHelper.setView = function(camera, options) {
    if (!Freedo.defined(camera)) {
        throw new Freedo.DeveloperError('camera is required');
    }

    options = Freedo.defaultValue(options, Freedo.defaultValue.EMPTY_OBJECT);
    var orientation = Freedo.defaultValue(options.orientation, Freedo.defaultValue.EMPTY_OBJECT);
    var destination = Freedo.defaultValue(options.destination, Freedo.Cartesian3.clone(camera.position, scratchCameraPosition));

    var direction = camera.direction;
    var up = camera.up;
    if (Freedo.defined(orientation.direction) && Freedo.defined(orientation.up)) {
        direction = orientation.direction;
        up = orientation.up;
    } else if (Freedo.defined(orientation.heading) || Freedo.defined(orientation.pitch)) {
        var heading = Freedo.defaultValue(orientation.heading, 0);
        var pitch = Freedo.defaultValue(orientation.pitch, 0);
        var negateDirection = offsetFromHeadingPitchRange(heading, pitch, 1);
        Freedo.Cartesian3.negate(negateDirection, scratchCameraAt);
        direction = Freedo.Cartesian3.normalize(scratchCameraAt, scratchCameraAt);

        if (Freedo.Cartesian3.equalsEpsilon(direction, Freedo.Cartesian3.UNIT_Z, Freedo.Math.EPSILON4)) {
            up = Freedo.Cartesian3.fromElements(0.0, -1.0, 0.0, scratchCameraUp);
        } else if (Freedo.Cartesian3.equalsEpsilon(direction, negateUnitZ, Freedo.Math.EPSILON4)) {
            up = Freedo.Cartesian3.clone(Freedo.Cartesian3.UNIT_Y, scratchCameraUp);
        } else {
            up = Freedo.Cartesian3.clone(Freedo.Cartesian3.UNIT_Z, scratchCameraUp);
        }
    }

    var right = Freedo.Cartesian3.cross(direction, up, scratchCameraRight);
    up = Freedo.Cartesian3.cross(right, direction, scratchCameraUp);

    Freedo.Cartesian3.clone(destination, camera.position);
    Freedo.Cartesian3.normalize(direction, camera.direction);
    Freedo.Cartesian3.normalize(up, camera.up);
    Freedo.Cartesian3.normalize(right, camera.right);
};

/**
 * 相机飞行
 * @param {Scene} scene 场景对象
 * @param {Object} options 选项对象:
 * @param {Cartesian3} options.destination 目标位置.
 * @param {Object} [options.orientation] 目标朝向: {direction:,up:} 或 {heading:,pitch:}.
 * @param {Number} [options.duration] 飞行时间.
 * @param {Camera~FlightCompleteCallback} [options.complete] 完成时执行的回调函数.
 * @param {Camera~FlightCancelledCallback} [options.cancel] 取消时执行的回调函数.
 * @param {EasingFunction|EasingFunction~Callback} [options.easingFunction] 插值方法.
 */
FdPlaneViewCameraHelper.flyTo = function(scene, options) {
    options = Freedo.defaultValue(options, Freedo.defaultValue.EMPTY_OBJECT);
    var destination = options.destination;

    if (!Freedo.defined(scene)) {
        throw new Freedo.DeveloperError('scene is required.');
    }
    if (!Freedo.defined(destination)) {
        throw new Freedo.DeveloperError('destination is required.');
    }

    FdPlaneViewCameraHelper.cancelFlight();

    var orientation = Freedo.defaultValue(options.orientation, Freedo.defaultValue.EMPTY_OBJECT);
    if (Freedo.defined(orientation.direction)) {
        orientation = directionUpToHeadingPitchRoll(scene.camera, destination, orientation, scratchSetViewOptions.orientation);
    }

    var flightTween;

    newOptions.destination = destination;
    newOptions.heading = orientation.heading;
    newOptions.pitch = orientation.pitch;
    newOptions.roll = orientation.roll;
    newOptions.duration = options.duration;
    newOptions.complete = function () {
        if(flightTween === FdPlaneViewCameraFlightPath._currentFlight){
            FdPlaneViewCameraFlightPath._currentFlight = undefined;
        }
        if (Freedo.defined(options.complete)) {
            options.complete();
        }
    };
    newOptions.cancel = options.cancel;
    newOptions.easingFunction = options.easingFunction;

    flightTween = scene.tweens.add(FdPlaneViewCameraFlightPath.createTween(scene, newOptions));
    FdPlaneViewCameraHelper._currentFlight = flightTween;
};


function distanceToBoundingSphere3D(camera, radius) {
    var frustum = camera.frustum;
    var tanPhi = Math.tan(frustum.fovy * 0.5);
    var tanTheta = frustum.aspectRatio * tanPhi;
    return Math.max(radius / tanTheta, radius / tanPhi);
}

var scratchLookAtHeadingPitchRangeOffset = new Freedo.Cartesian3();
var scratchLookAtHeadingPitchRangeQuaternion1 = new Freedo.Quaternion();
var scratchLookAtHeadingPitchRangeQuaternion2 = new Freedo.Quaternion();
var scratchHeadingPitchRangeMatrix3 = new Freedo.Matrix3();

function offsetFromHeadingPitchRange(heading, pitch, range) {
    pitch = Freedo.Math.clamp(pitch, -Freedo.Math.PI_OVER_TWO, Freedo.Math.PI_OVER_TWO);
    heading = Freedo.Math.zeroToTwoPi(heading) - Freedo.Math.PI_OVER_TWO;

    var pitchQuat = Freedo.Quaternion.fromAxisAngle(Freedo.Cartesian3.UNIT_Y, -pitch, scratchLookAtHeadingPitchRangeQuaternion1);
    var headingQuat = Freedo.Quaternion.fromAxisAngle(Freedo.Cartesian3.UNIT_Z, -heading, scratchLookAtHeadingPitchRangeQuaternion2);
    var rotQuat = Freedo.Quaternion.multiply(headingQuat, pitchQuat, headingQuat);
    var rotMatrix = Freedo.Matrix3.fromQuaternion(rotQuat, scratchHeadingPitchRangeMatrix3);

    var offset = Freedo.Cartesian3.clone(Freedo.Cartesian3.UNIT_X, scratchLookAtHeadingPitchRangeOffset);
    Freedo.Matrix3.multiplyByVector(rotMatrix, offset, offset);
    Freedo.Cartesian3.negate(offset, offset);
    Freedo.Cartesian3.multiplyByScalar(offset, range, offset);
    return offset;
}

var scratchflyToBoundingSphereTransform = new Freedo.Matrix4();
var scratchflyToBoundingSphereDestination = new Freedo.Cartesian3();
var scratchflyToBoundingSphereDirection = new Freedo.Cartesian3();
var scratchflyToBoundingSphereUp = new Freedo.Cartesian3();
var scratchflyToBoundingSphereRight = new Freedo.Cartesian3();
var scratchFlyToBoundingSphereCart4 = new Freedo.Cartesian4();
var scratchFlyToBoundingSphereQuaternion = new Freedo.Quaternion();
var scratchFlyToBoundingSphereMatrix3 = new Freedo.Matrix3();

/**
 * 相机飞行
 * @param {Scene} scene 场景对象
 * @param {Object} options 选项对象:
 * @param {Cartesian3} options.destination 目标位置.
 * @param {Number} [options.duration] 飞行时间.
 * @param {HeadingPitchRange} [options.offset] The offset from the target in the local east-north-up reference frame centered at the target.
 * @param {Camera~FlightCompleteCallback} [options.complete] 完成时执行的回调函数.
 * @param {Camera~FlightCancelledCallback} [options.cancel] 取消时执行的回调函数.
 * @param {EasingFunction|EasingFunction~Callback} [options.easingFunction] 插值方法.
 */
FdPlaneViewCameraHelper.flyToBoundingSphere = function (scene, boundingSphere, options) {
    //>>includeStart('debug', pragmas.debug);
    if (!Freedo.defined(boundingSphere)) {
        throw new DeveloperError('boundingSphere is required.');
    }
    //>>includeEnd('debug');

    var camera = scene.camera;

    options = Freedo.defaultValue(options, Freedo.defaultValue.EMPTY_OBJECT);

    camera._setTransform(Freedo.Matrix4.IDENTITY);

    var offset = options.offset;
    if (!Freedo.defined(offset)) {
        offset = Freedo.HeadingPitchRange.clone(Freedo.Camera.DEFAULT_OFFSET);
    }

    if (!Freedo.defined(offset.range) || offset.range === 0.0) {
        offset.range = distanceToBoundingSphere3D(camera, boundingSphere.radius);
    }

    var position = offsetFromHeadingPitchRange(offset.heading, offset.pitch, offset.range);
    position = Freedo.Cartesian3.add(position, boundingSphere.center, scratchflyToBoundingSphereDestination);

    //var transform = Transforms.eastNorthUpToFixedFrame(boundingSphere.center, Ellipsoid.WGS84, scratchflyToBoundingSphereTransform);
    //Matrix4.multiplyByPoint(transform, position, position);

    var direction;
    var up;

    direction = Freedo.Cartesian3.subtract(boundingSphere.center, position, scratchflyToBoundingSphereDirection);
    Freedo.Cartesian3.normalize(direction, direction);

    // up = Matrix4.multiplyByPointAsVector(transform, Cartesian3.UNIT_Z, scratchflyToBoundingSphereUp);
    up = Freedo.Cartesian3.clone(Freedo.Cartesian3.UNIT_Z, scratchflyToBoundingSphereUp);
    if (1.0 - Math.abs(Freedo.Cartesian3.dot(direction, up)) < Freedo.Math.EPSILON6) {
        var rotateQuat = Freedo.Quaternion.fromAxisAngle(direction, offset.heading, scratchFlyToBoundingSphereQuaternion);
        var rotation = Freedo.Matrix3.fromQuaternion(rotateQuat, scratchFlyToBoundingSphereMatrix3);

        // Freedo.Cartesian3.fromCartesian4(Freedo.Matrix4.getColumn(transform, 1, scratchFlyToBoundingSphereCart4), up);
        up = Freedo.Cartesian3.clone(Freedo.Cartesian3.UNIT_Y, scratchflyToBoundingSphereUp);
        
        Freedo.Matrix3.multiplyByVector(rotation, up, up);
    }

    var right = Freedo.Cartesian3.cross(direction, up, scratchflyToBoundingSphereRight);
    Freedo.Cartesian3.cross(right, direction, up);
    Freedo.Cartesian3.normalize(up, up);

    FdPlaneViewCameraHelper.flyTo(scene, {
        destination: position,
        orientation: {
            direction: direction,
            up: up
        },
        duration: options.duration,
        complete: options.complete,
        cancel: options.cancel,
        endTransform: options.endTransform,
        maximumHeight: options.maximumHeight,
        easingFunction: options.easingFunction,
        flyOverLongitude: options.flyOverLongitude,
        flyOverLongitudeWeight: options.flyOverLongitudeWeight,
        pitchAdjustHeight: options.pitchAdjustHeight
    });
};

/**
 * 取消相机飞行
**/
FdPlaneViewCameraHelper.cancelFlight = function() {
    if (Freedo.defined(FdPlaneViewCameraHelper._currentFlight)) {
        FdPlaneViewCameraHelper._currentFlight.cancelTween();
        FdPlaneViewCameraHelper._currentFlight = undefined;
    }
};


function getHeading(at, up) {
    var heading = 0;
    if (at.z < Freedo.Math.EPSILON5-1) {
        heading = Math.atan2(up.x, up.y);
    } else if (at.z > 1-Freedo.Math.EPSILON5) {
        heading = Math.atan2(-up.x, -up.y);
    } else {
        heading = Math.atan2(at.x, at.y);
    }
    if (isNaN(heading)) {
        throw new Freedo.DeveloperError('');
    }
    return heading;
}

function getPitch(at) {
    var pitch = 0;
    if (at.z < Freedo.Math.EPSILON5-1) {
        pitch = -Freedo.Math.PI_OVER_TWO;
    } else if (at.z > 1-Freedo.Math.EPSILON5) {
        pitch = Freedo.Math.PI_OVER_TWO;
    } else {
        var d = Math.sqrt(at.x*at.x+at.y*at.y);
        pitch = Math.atan(at.z/d);
    }
    if (isNaN(pitch)) {
        throw new Freedo.DeveloperError('');
    }
    return pitch;
}

var scratchLookAtHeadingPitchRangeOffset = new Freedo.Cartesian3();
var scratchLookAtHeadingPitchRangeQuaternion1 = new Freedo.Quaternion();
var scratchLookAtHeadingPitchRangeQuaternion2 = new Freedo.Quaternion();
var scratchHeadingPitchRangeMatrix3 = new Freedo.Matrix3();

function offsetFromHeadingPitchRange(heading, pitch, range) {
    pitch = Freedo.Math.clamp(pitch, -Freedo.Math.PI_OVER_TWO, Freedo.Math.PI_OVER_TWO);
    heading = Freedo.Math.zeroToTwoPi(heading);

    var pitchQuat = Freedo.Quaternion.fromAxisAngle(Freedo.Cartesian3.UNIT_X, pitch, scratchLookAtHeadingPitchRangeQuaternion1);
    var headingQuat = Freedo.Quaternion.fromAxisAngle(Freedo.Cartesian3.UNIT_Z, -heading, scratchLookAtHeadingPitchRangeQuaternion2);
    var rotQuat = Freedo.Quaternion.multiply(headingQuat, pitchQuat, headingQuat);
    var rotMatrix = Freedo.Matrix3.fromQuaternion(rotQuat, scratchHeadingPitchRangeMatrix3);

    var offset = Freedo.Cartesian3.clone(Freedo.Cartesian3.UNIT_Y, scratchLookAtHeadingPitchRangeOffset);
    Freedo.Matrix3.multiplyByVector(rotMatrix, offset, offset);
    Freedo.Cartesian3.negate(offset, offset);
    Freedo.Cartesian3.multiplyByScalar(offset, range, offset);
    return offset;
}

var scratchCameraAt = new Freedo.Cartesian3();
var scratchCameraRight = new Freedo.Cartesian3();
var scratchCameraUp = new Freedo.Cartesian3();
var scratchCameraPosition = new Freedo.Cartesian3();
var negateUnitZ = new Freedo.Cartesian3(0.0,0.0,-1.0);


FdPlaneViewCameraHelper._currentFlight = undefined;

var newOptions = {
    destination : undefined,
    heading : undefined,
    pitch : undefined,
    roll : undefined,
    duration : undefined,
    complete : undefined,
    cancel : undefined,
    easingFunction : undefined
};

var scratchSetViewOptions = {
    destination : undefined,
    orientation : {
        direction : undefined,
        up : undefined,
        heading : undefined,
        pitch : undefined,
        roll : undefined
    }
};

var scratchToHPRDirection = new Freedo.Cartesian3();
var scratchToHPRUp = new Freedo.Cartesian3();
var scratchToHPRRight = new Freedo.Cartesian3();

function directionUpToHeadingPitchRoll(camera, position, orientation, result) {
    var direction = Freedo.Cartesian3.clone(orientation.direction, scratchToHPRDirection);
    var up = Freedo.Cartesian3.clone(orientation.up, scratchToHPRUp);
    var right = Freedo.Cartesian3.cross(direction, up, scratchToHPRRight);

    result.heading = getHeading(direction, up);
    result.pitch = getPitch(direction);
    result.roll = 0;

    return result;
}

var FdPlaneViewCameraFlightPath = {
};

function emptyFlight(complete, cancel) {
    return {
        startObject : {},
        stopObject : {},
        duration : 0.0,
        complete : complete,
        cancel : cancel
    };
}

function wrapCallback(controller, cb) {
    function wrapped() {
        if (typeof cb === 'function') {
            cb();
        }

        controller.enableInputs = true;
    }
    return wrapped;
}

function adjustAngleForLERP(startAngle, endAngle) {
    if (Freedo.Math.equalsEpsilon(startAngle, Freedo.Math.TWO_PI, Freedo.Math.EPSILON11)) {
        startAngle = 0.0;
    }

    if (endAngle > startAngle + Math.PI) {
        startAngle += Freedo.Math.TWO_PI;
    } else if (endAngle < startAngle - Math.PI) {
        startAngle -= Freedo.Math.TWO_PI;
    }

    return startAngle;
}

var scratchStartPosition = new Freedo.Cartesian3();
var scratchLerpPosition = new Freedo.Cartesian3();

function createUpdate(camera, duration, destination, heading, pitch, roll) {
    var startPosition = Freedo.Cartesian3.clone(camera.position, scratchStartPosition);
    var startHeading = adjustAngleForLERP(FdPlaneViewCameraHelper.getHeading(camera), heading);
    var startPitch = FdPlaneViewCameraHelper.getPitch(camera);

    function isolateUpdateFunction() {
        return function update(value) {
            var time = value.time / duration;
            var position = Freedo.Cartesian3.lerp(startPosition, destination, time, scratchLerpPosition);
            FdPlaneViewCameraHelper.setView(camera, {
                destination : position,
                orientation: {
                    heading : Freedo.Math.lerp(startHeading, heading, time),
                    pitch : Freedo.Math.lerp(startPitch, pitch, time),
                    roll : Freedo.Math.lerp(0, roll, time)
                }
            });
        };
    }
    return isolateUpdateFunction();
}

FdPlaneViewCameraFlightPath.createTween = function(scene, options) {
    var camera = scene.camera;
    var destination = options.destination;
    var easingFunction = Freedo.defaultValue(options.easingFunction, Freedo.EasingFunction.LINEAR_NONE);

    var duration = options.duration;
    if (!Freedo.defined(duration)) {
        duration = Math.ceil(Freedo.Cartesian3.distance(camera.position, destination) / 100.0) + 2.0;
        duration = Math.min(duration, 3.0);
    }

    var heading = Freedo.defaultValue(options.heading, 0.0);
    var pitch = Freedo.defaultValue(options.pitch, 0.0);
    var roll = Freedo.defaultValue(options.roll, 0.0);

    var controller = scene.screenSpaceCameraController;
    controller.enableInputs = false;

    var complete = wrapCallback(controller, options.complete);
    var cancel = wrapCallback(controller, options.cancel);

    if (duration <= 0.0) {
        var newOnComplete = function() {
            var update = createUpdate(camera, 1.0, destination, heading, pitch, roll);
            update({ time: 1.0 });

            if (typeof complete === 'function') {
                complete();
            }
        };
        return emptyFlight(newOnComplete, cancel);
    }

    var update = createUpdate(camera, duration, destination, heading, pitch, roll);

    return {
        duration : duration,
        easingFunction : easingFunction,
        startObject : {
            time : 0.0
        },
        stopObject : {
            time : duration
        },
        update : update,
        complete : complete,
        cancel: cancel
    };
};


export {
    FdPlaneViewCameraHelper
};