import {
    FdMisc
} from '../../FdMisc/FdMisc';

import {
    FdTransformHelper
} from '../Edit/FdTransformHelper';

/**
 * 多段线绘制类
 * 调用run方法以后，监控PLAdd事件，即可从参数中得到多段线信息
 * @alias FdMicroApp.FdPolylineEditor
 * @example
 * var viewer = Freedo.FdApp.createDefaultViewer('freedoContainer');
 * var ple = new Freedo.FdMicroApp.FdPolylineEditor(viewer);
 * ple.setHeightOffset(100);
 * ple.start();
 * @class
 */
class FdPolylineEditor {
    constructor(viewer) {
        this._viewer = viewer;

        // 辅助变量
        this._polylines = {};

        //引用
        this._event = new FreeDo.Event();
        this._eventHelper = new FreeDo.EventHelper();

        this._curGuid = undefined;
        this._startPoint = undefined;
        this._tempLine = undefined;
        this._tempPoint = {};

        this._startCapture = false;
        this._bKeydown = false;
        this._lineWidth = 2;

        this._autoClose = true;
        this._autoDelLines = false;
        this._singleLineMode = false;

        this._heightOffset = 0;

        this._hanlder = undefined;

        //编辑相关的成员变量
        this._editPTs = [];
        this._editIndex = 0;
        this._editPl = undefined;
        this._editPt = undefined;
        this._editTran = new FdTransformHelper(viewer);
        this._editTran.setAxisSize(5);
        this._editLsnr = undefined;
    }

    get pleOptions() {
        var data = [];
        for (var k in this._polylines) {
            data.push({
                pts: this._polylines[k].pts
            })
        }

        return data;
    }

    set pleOptions(v) {
        this.removeAll();
        for (var k in v) {
            var pts = v[k].pts;
            this.setStartPt(pts[0]);
            for (var i = 0; i < pts.length - 1; i++) {
                this.addResult(pts[i + 1]);
            }
        }
        this._startPoint = undefined;
        this._curGuid = undefined;
    }
}

/**
 * 激活多段线编辑功能
 * @private
 */
FdPolylineEditor.prototype.run = function() {
    this.registerHandlers();
    this._startCapture = true;
    this._event.raiseEvent('Start', {});
}

/**
 * 激活多段线编辑功能
 */
FdPolylineEditor.prototype.start = function() {
    this.run();
}

FdPolylineEditor.prototype.callbackPt = function(time, result) {
    result = this._editPts[this._editIndex];
    return result;
}

/**
 * 开始进行节点编辑
 */
FdPolylineEditor.prototype.startEditPt = function() {
    if (!this._editPt) {
        var ptCP = new Freedo.CallbackProperty(this.callbackPt.bind(this), false);
        this._editPt = this._viewer.entities.add({
            position: ptCP,
            point: {
                pixelSize: 10,
                color: Freedo.Color.YELLOW.withAlpha(0.9)
            }
        });
    }

    this._editPt.boundingSphere = new Freedo.BoundingSphere(this._editPts[this._editIndex], 10);
    this._editPt.modelMatrix = Freedo.clone(Freedo.Matrix4.IDENTITY, true);

    this._viewer.camera.viewBoundingSphere(this._editPt.boundingSphere, new Freedo.HeadingPitchRange(10, -0.5, 0));
    this._viewer.camera.lookAtTransform(Freedo.Matrix4.IDENTITY);

    this._editTran.setData([this._editPt]);
    this._editTran.start("MOVE");
}

/**
 * 开始进行节点编辑
 */
FdPolylineEditor.prototype.startEdit = function(pts) {
    this._editPts = Freedo.clone(pts, true);
    this._editPl = this._viewer.scene.primitives.add(this.createLinePrimitive(pts));
    this._editIndex = 0;
    this.startEditPt();

    if (this._editLsnr)
        this._editLsnr();

    var that = this;
    this._editLsnr = this._editTran.on(function(eventType, eventArg) {
        if (eventType == 'Transforming') {
            var cPos = that._editPts[that._editIndex];
            var gra = Freedo.Cartographic.fromDegrees(eventArg.TMoveX, eventArg.TMoveY, eventArg.TMoveZ); //Freedo.Cartographic.fromCartesian(cPos);
            that._editPts[that._editIndex] = Freedo.Cartographic.toCartesian(gra);
        }
        if (eventType == 'Transformed') {
            that._editPt.boundingSphere = new Freedo.BoundingSphere(that._editPts[that._editIndex], 10);
            if (that._editPl)
                that._viewer.scene.primitives.remove(that._editPl);

            that._editPl = that._viewer.scene.primitives.add(that.createLinePrimitive(that._editPts));
            var gra = Freedo.Cartographic.fromDegrees(eventArg.TMoveX, eventArg.TMoveY, eventArg.TMoveZ);
            that._event.raiseEvent('Transformed', {
                positionG: gra
            });
        }
    });
}

/**
 * 编辑上一个节点
 */
FdPolylineEditor.prototype.editPre = function() {
    this._editIndex -= 1;
    if (this._editIndex < 0)
        this._editIndex = 0;
    this.startEditPt();
}

/**
 * 编辑下一个节点
 */
FdPolylineEditor.prototype.editNext = function() {
    this._editIndex += 1;
    if (this._editIndex > this._editPts.length - 1)
        this._editIndex = this._editPts.length - 1;
    this.startEditPt();
}

/**
 * 编辑结束
 */
FdPolylineEditor.prototype.endEdit = function() {
    this._editTran.cancel();
    this._viewer.entities.remove(this._editPt);
    this._viewer.scene.primitives.remove(this._editPl);
    this._event.raiseEvent('EditEnd', {
        pts: this._editPts
    });
}

/**
 * 设置是否自动闭合
 * @param {Boolean} close true为自动闭合
 */
FdPolylineEditor.prototype.setAutoLoop = function(close) {
    this._autoClose = close;
}

/**
 * 设置是否自动删除辅助线
 * @param {Boolean} autoDel true为自动删除
 */
FdPolylineEditor.prototype.setAutoDelLines = function(autoDel) {
    this._autoDelLines = autoDel;
}

/**
 * 设置高度偏移量
 * @param {Number} offset 偏移量
 */
FdPolylineEditor.prototype.setHeightOffset = function(offset) {
    this._heightOffset = offset;
}

FdPolylineEditor.prototype.onKeyDown = function() {
    if (!this._startCapture)
        return;

    this.bKeydown = true;
    if (window.event.keyCode == 27) {
        this.onMouseRightClick(undefined);
    }
}

FdPolylineEditor.prototype.onKeyUp = function() {
    if (!this._startCapture)
        return;

    this.bKeydown = false;
}

/**
 * 如果想接着上一次绘制的线条继续绘制
 * 则开始绘制前先设置起点
 * @param {Object} pt 起点
 */
FdPolylineEditor.prototype.setStartPt = function(pt) {
    this._startPoint = pt;

    this._curGuid = Freedo.createGuid();
    this._polylines[this._curGuid] = {};
    this._polylines[this._curGuid].lines = [];
    this._polylines[this._curGuid].pts = [];
    this._polylines[this._curGuid].ptsG = [];
    this._polylines[this._curGuid].pts[0] = pt;
}

FdPolylineEditor.prototype.callbackX = function() {
    this._viewer.scene.requestRender();
    return [this._startPoint, this._tempPoint];
}

FdPolylineEditor.prototype.updateTmpLine = function(windowPosition) {
    var posG = FdMisc.FdGeometric.pickPositionG(this._viewer, windowPosition); //度度米
    if (Freedo.defined(posG)) {
        var posC;
        if (this._viewer.scene.globe)
            posC = FdMisc.FdGeometric.tc(posG);
        else
            posC = new Freedo.Cartesian3(posG[0], posG[1], posG[2]);
        this._tempPoint = posC;

        if (this._tempLine === undefined) {
            var xCP = new Freedo.CallbackProperty(this.callbackX.bind(this), false);
            this._tempLine = this._viewer.entities.add({
                name: 'MeasureLine',
                polyline: {
                    positions: xCP,
                    followSurface: this._viewer.scene.globe ? true : false,
                    width: this._lineWidth,
                    show: true,
                    material: Freedo.Color.RED,
                    depthFailMaterial: Freedo.Color.RED
                }
            });
        }
    }
}

FdPolylineEditor.prototype.removeTmpLine = function() {
    if (this._tempLine) {
        this._viewer.entities.remove(this._tempLine);
        this._tempLine = undefined;
    }
}

/**
 * 创建线段
 * @private
 * @param {Object} p1 起点
 * @param {Object} p2 终点
 * @returns 返回线段对象
 */
FdPolylineEditor.prototype.createLinePrimitive = function(pts) {
    var geo, followSurface;
    if (this._viewer.scene.globe) {
        followSurface = true;
    } else {
        followSurface = false;
        /*var positions = new Float64Array([p1.x, p1.y, p1.z, p2.x, p2.y, p2.z]);
        geo = new Freedo.Geometry({
            attributes: {
                position: new Freedo.GeometryAttribute({
                    componentDatatype: Freedo.ComponentDatatype.DOUBLE,
                    componentsPerAttribute: 3,
                    values: positions
                })
            },
            indices: new Uint16Array([0, 1]),
            primitiveType: Freedo.PrimitiveType.LINES,
            boundingSphere: Freedo.BoundingSphere.fromVertices(positions)
        });*/
    }
    geo = new Freedo.PolylineGeometry({
        positions: pts,
        width: 2.0,
        followSurface: followSurface,
        vertexFormat: Freedo.PolylineMaterialAppearance.VERTEX_FORMAT
    });
    return new Freedo.Primitive({
        geometryInstances: new Freedo.GeometryInstance({
            geometry: geo
        }),
        appearance: new Freedo.PolylineMaterialAppearance({
            material: new Freedo.Material({
                fabric: {
                    type: 'Color',
                    uniforms: {
                        color: new Freedo.Color(1.0, 0.3, 0.3, 1.0)
                    }
                }
            }),
            renderState: new Freedo.RenderState({
                polygonOffset: {
                    enabled: true,
                    factor: -1.0,
                    units: -1.0,
                },
                depthTest: {
                    enabled: true,
                    func: Freedo.WebGLConstants.LEQUAL,
                }
            })
        }),
        depthFailAppearance: new Freedo.PolylineMaterialAppearance({
            material: new Freedo.Material({
                fabric: {
                    type: 'Color',
                    uniforms: {
                        color: new Freedo.Color(1.0, 0, 0, 1)
                    }
                }
            })
        })
    });
}

FdPolylineEditor.prototype.addResult = function(endPoint) {
    var positions = [this._startPoint, endPoint];
    /*var line = this._viewer.entities.add({
        name: 'MeasureLine',
        polyline: {
            positions: positions,
            width: this._lineWidth,
            material: Freedo.Color.RED,
            followSurface: false,
            show: true,
            //加这个之后将使得本线条无法求交
            depthFailMaterial: Freedo.Color.RED
        }
    });*/
    var line = this._viewer.scene.primitives.add(this.createLinePrimitive([positions[0], positions[1]]));

    if (Freedo.defined(this._polylines[this._curGuid])) {
        this._polylines[this._curGuid].lines.push(line);
        this._polylines[this._curGuid].pts.push(endPoint);
        if (this._viewer.scene.globe) {
            var ptG = FdMisc.FdGeometric.tg(endPoint);
            this._polylines[this._curGuid].ptsG.push(ptG);
        } else {
            this._polylines[this._curGuid].ptsG.push(endPoint);
        }
    }

    this._startPoint = FreeDo.clone(endPoint, true);
    this._endPoint = undefined;
    this._viewer.scene.requestRender();
}

FdPolylineEditor.prototype.pickStartPoint = function(windowPosition) {
    var posG = FdMisc.FdGeometric.pickPositionG(this._viewer, windowPosition);
    if (Freedo.defined(posG)) {
        posG[2] += this._heightOffset;
        if (this._viewer.scene.globe) {
            this._startPoint = FdMisc.FdGeometric.tc(posG);
            this._polylines[this._curGuid].pts.push(FdMisc.FdGeometric.tc(posG));
        } else {
            this._startPoint = new Freedo.Cartesian3(posG[0], posG[1], posG[2]);
            this._polylines[this._curGuid].pts.push(this._startPoint);
        }

        this._polylines[this._curGuid].ptsG.push(posG);

        this._event.raiseEvent('PointAdded', {
            position: posG
        });
    }
}

FdPolylineEditor.prototype.pickEndPoint = function(windowPosition) {
    var posG = FdMisc.FdGeometric.pickPositionG(this._viewer, windowPosition);
    if (Freedo.defined(posG)) {
        posG[2] += this._heightOffset;
        if (this._viewer.scene.globe)
            this.addResult(FdMisc.FdGeometric.tc(posG));
        else
            this.addResult(new Freedo.Cartesian3(posG[0], posG[1], posG[2]));

        this._event.raiseEvent('PointAdded', {
            position: posG
        });
    }
}

FdPolylineEditor.prototype.onMouseLeftClick = function(movement) {
    if (!this._startCapture)
        return;

    if (!Freedo.defined(this._curGuid)) {
        this._curGuid = Freedo.createGuid();
        this._polylines[this._curGuid] = {};
        this._polylines[this._curGuid].lines = [];
        this._polylines[this._curGuid].pts = [];
        this._polylines[this._curGuid].ptsG = [];
    }

    if (!Freedo.defined(this._polylines[this._curGuid]))
        return;

    if (this._startPoint) {
        this.pickEndPoint(movement.position);
    } else {
        this.pickStartPoint(movement.position);
    }
}

FdPolylineEditor.prototype.delResult = function(guid) {
    if (!guid)
        guid = this._curGuid;
    var lines = this._polylines[guid].lines;
    if (lines) {
        for (var key in lines) {
            var l = lines[key];
            //this._viewer.entities.remove(l);
            this._viewer.scene.primitives.remove(l);
        }
    }
    delete this._polylines[guid];
    this._viewer.scene.requestRender();
}

FdPolylineEditor.prototype.polylineAdded = function() {
    var data = {
        id: this._curGuid,
        polyline: this._polylines[this._curGuid]
    };
    this._event.raiseEvent('PLAdd', data);

    this._event.raiseEvent('DataChanged', data);

    if (this._autoDelLines) {
        var curLines = this._polylines[this._curGuid].lines;
        for (var key2 in curLines) {
            var l = curLines[key2];
            //this._viewer.entities.remove(l);
            this._viewer.scene.primitives.remove(l);
        }
        delete this._polylines[this._curGuid].lines;
    }
    this._viewer.scene.requestRender();
}

FdPolylineEditor.prototype.onMouseRightClick = function(movement) {
    if (!this._startCapture)
        return;

    if (!!this._startPoint) {
        //首尾相连
        var p0 = this._polylines[this._curGuid].pts[0];
        var pN = this._polylines[this._curGuid].pts[this._polylines[this._curGuid].pts.length - 1];

        if (this._autoClose) {
            /*var line = this._viewer.entities.add({
                name: 'MeasureLine',
                polyline: {
                    positions: [pN, p0],
                    width: this._lineWidth,
                    material: Freedo.Color.RED,
                    depthFailMaterial: Freedo.Color.RED
                }
            });*/
            var line = this._viewer.scene.primitives.add(this.createLinePrimitive([pN, p0]));
            this._polylines[this._curGuid].lines.push(line);
            this._polylines[this._curGuid].pts.push(p0);
            var ptG = FdMisc.FdGeometric.tg(p0);
            this._polylines[this._curGuid].ptsG.push(ptG);

            //闭合以后依然只有3个点则删除
            if (this._polylines[this._curGuid].pts.length <= 3) {
                this.delResult(this._curGuid);
            }
        } else {
            if (this._polylines[this._curGuid].pts.length <= 1) {
                this.delResult(this._curGuid);
            }
        }

        if (this._polylines[this._curGuid])
            this.polylineAdded();

        this.removeTmpLine();
        this._startPoint = undefined;
        this._curGuid = undefined;

        //this._viewer.scene.globe.depthTestAgainstTerrain = this._depthTestAgainstTerrain;
    }
    this._viewer.scene.requestRender();
}

/**
 * 结束绘制
 */
FdPolylineEditor.prototype.forceFinish = function() {
    this.onMouseRightClick();
}

FdPolylineEditor.prototype.onMouseMove = function(movement) {
    if (!this._startCapture)
        return;

    if (!!this._startPoint && !FreeDo.Cartesian3.equals(movement.endPosition, movement.startPosition)) {
        this.updateTmpLine(movement.endPosition);
    }
}

FdPolylineEditor.prototype.registerHandlers = function() {
    this._hanlder = this._hanlder && this._hanlder.destroy();

    this._hanlder = new FreeDo.ScreenSpaceEventHandler(this._viewer.scene.canvas);
    this._hanlder.setInputAction(this.onMouseLeftClick.bind(this), FreeDo.ScreenSpaceEventType.LEFT_CLICK);
    this._hanlder.setInputAction(this.onMouseRightClick.bind(this), FreeDo.ScreenSpaceEventType.RIGHT_CLICK);
    this._hanlder.setInputAction(this.onMouseMove.bind(this), FreeDo.ScreenSpaceEventType.MOUSE_MOVE);
}

/**
 * 获取数据
 * @returns {Object} 返回值格式：
 * {
 *     编号1:{
 *         lines: [线段实体,线段实体,...]
 *         pts: [Cartesian3],
 *         ptsG: [[经度,纬度,高度],...]
 *     }
 *  
 *     编号2:{
 *         lines: [线段实体,线段实体,...]
 *         pts: [Cartesian3],
 *         ptsG: [[经度,纬度,高度],...]
 *     }
 * }
 */
FdPolylineEditor.prototype.getData = function() {
    return this._polylines;
}

/**
 * 取消多段线编辑功能
 */
FdPolylineEditor.prototype.cancel = function() {
    this._startCapture = false;

    this._hanlder = this._hanlder && this._hanlder.destroy();

    this._event.raiseEvent('Cancel', {});
}

/**
 * 全部删除
 */
FdPolylineEditor.prototype.removeAll = function() {
    for (var key in this._polylines) {
        for (var key2 in this._polylines[key].lines) {
            var l = this._polylines[key].lines[key2];
            this._viewer.scene.primitives.remove(l);
        }
        delete this._polylines[key];
    }
    this.removeTmpLine();
    this._startPoint = undefined;
    this._curGuid = undefined;
    this._viewer.scene.requestRender();
}

/**
 * 隐藏全部
 */
FdPolylineEditor.prototype.hideAll = function() {
    for (var key in this._polylines) {
        for (var key2 in this._polylines[key].lines) {
            var l = this._polylines[key].lines[key2];
            l.show = false;
        }
    }
    this._viewer.scene.requestRender();
}

/**
 * 注册事件捕捉
 * @param {FdMicroApp.FdPolylineEditor~FdPolylineEditorCallback} listener 当有事件触发时被执行的函数。
 * @param {Object} [scope] listener函数执行时的绑定的对象。
 * @returns {Freedo.Event~RemoveCallback} 返回一个函数，调用该函数可以取消监听。
 */
FdPolylineEditor.prototype.on = function(listener, scope) {
    return this._eventHelper.add(this._event, listener, scope);
}

/**
 * @callback FdMicroApp.FdPolylineEditor~FdPolylineEditorCallback
 * @param {String} eventType 事件类型有以下几种：Start（开始）/Cancel（取消）/PLAdd（新增Polyline）
 * @param {Object} eventArg Start/Cancel无返回值，PLAdd事件返回值是包含Polyline相关信息
 */

/**
 * 重置
 */
FdPolylineEditor.prototype.reset = function() {
    this.cancel();
}

/**
 * 销毁所有资源
 */
FdPolylineEditor.prototype.dispose = function() {
    this.reset();
}

export {
    FdPolylineEditor
};