/**
 * PMTS数据管理器
 *
 * @alias FdTools.FdPMTSManager
 * @param {Freedo.Viewer} viewer 视景器
 * @class
 *
 * @example
 * var container = document.getElementById('freedoContainer');
 * var pmtsManager = new Freedo.FdTools.FdPMTSManager(viewer);
 */
class FdPMTSManager {
    constructor(viewer) {
        this._pmtsOpts = new Freedo.AssociativeArray();
        this._pmtsCollection = new Freedo.AssociativeArray();

        this._viewer = viewer;

        this._event = new Freedo.Event();
        this._eventHelper = new Freedo.EventHelper();
    }

    /**
     * 
     * @param {Object} options pmts数据的配置选项
     * @param {String} options.url pmts的url
     * @param {String} [options.id] pmts的id，如果不指定，则会自动创建一个id。
     * @param {String} [options.modelMatrix] pmts中模型的姿态矩阵数组，包含16个数值
     * @param {String} [token] 用户token
     * @returns {String} 返回所创建pmts数据的id
     */
    add(options, token) {
        if (typeof options.id === 'undefined') {
            options.id = Freedo.createGuid();
        }

        let queryURL = options.url;

        this._pmtsOpts.set(options.id, Freedo.clone(options, true));
        let pmts = {};
        this._pmtsCollection.set(options.id, pmts);

        let that = this;

        // Freedo.FdServer.FdShareServer.getLayerAccesskeyResource(queryURL, 'json', function(jsonPMTSCapabilities, error) {
        Freedo.FdServer.FdPMTSServer.getResource(queryURL, token, function(jsonPMTSCapabilities, error) {
            if (error) {
                console.error('FdServer.FdShareServer.getLayerAccesskeyResource got error: ' + error);
                return;
            }

            pmts.pModelInfos = [];
            jsonPMTSCapabilities.contents.layers.forEach(function(layer) {
                let matrixSets = Freedo.FdServer.FdPMTSParser.getMatrixSets(jsonPMTSCapabilities, layer);
                matrixSets.forEach(function(matrixSet) {
                    matrixSet.matrix.forEach(function(matrix) {
                        let pModelURL = Freedo.FdServer.FdPMTSParser.getPModelURL(layer,
                            matrixSet, matrix, token);

                        let pModel = that._viewer.scene.primitives.add(new Freedo.FreedoPModelset({
                            url: pModelURL
                        }));

                        if (typeof options.modelMatrix !== 'undefined') {
                            pModel.modelMatrix = Freedo.Matrix4.fromArray(options.modelMatrix);
                        }

                        pmts.pModelInfos.push({
                            pModel: pModel,
                            matrix: matrix,
                            matrixSet: matrixSet,
                            layer: layer
                        });
                    })
                })
            });

            that._event.raiseEvent('PmtsLoaded', options.id);
        });

        this._event.raiseEvent('Changed');

        return options.id;
    }

    /**
     * 删除指定的pmts数据
     * @param {String} id pmts的id
     */
    remove(id) {
        this._pmtsOpts.remove(id);
        let pmts = this._pmtsCollection.get(id);

        if (typeof pmts !== 'undefined') {
            pmts.pModelInfos.forEach((element) => {
                this._viewer.scene.primitives.remove(element.pModel);
            });
        }

        this._pmtsCollection.remove(id);
        this._event.raiseEvent('Changed');
    }

    /**
     * 删除所有pmts数据
     */
    removeAll() {
        this._pmtsOpts.removeAll();

        this._pmtsCollection.values.forEach(function(pmts) {
            if (typeof pmts.pModelInfos !== 'undefined') {
                pmts.pModelInfos.forEach((element) => {
                    this._viewer.scene.primitives.remove(element.pModel);
                });
            }
        })
        this._pmtsCollection.removeAll();

        this._event.raiseEvent('Changed');
    }

    /**
     * 获取pmts的配置信息
     * @param {String} id pmts的id
     * @returns {Object} pmts配置信息，形式如下：
     * ```
     * {
     *      pModelInfos: [{
     *          pModel: pModel,
     *          matrix: matrix,
     *          matrixSet: matrixSet,
     *          layer: layer
     *      }, {
     *          pModel: pModel,
     *          matrix: matrix,
     *          matrixSet: matrixSet,
     *          layer: layer
     *      }]
     * } 
     * ```
     * 其中pModel属于Freedo.FreedoPModelset类型。
     */
    getPmts(id) {
        return this._pmtsCollection.get(id);
    }

    /**
     * 设置pmts的模型姿态矩阵
     * @param {String} id pmts的id
     * @param {Number[]} modelMatrixArray 模型姿态矩阵数组，包含16个数值 
     */
    setModelMatrix(id, modelMatrixArray) {
        let modelMatrix = Freedo.Matrix4.fromArray(modelMatrixArray);
        let pmts = this._pmtsCollection.get(id);
        pmts.pModelInfos.forEach(function(pModelInfo) {
            pModelInfo.pModel.modelMatrix = modelMatrix;
        });

        let options = this._pmtsOpts.get(id);
        options.modelMatrix = modelMatrixArray;

        this._event.raiseEvent('Changed');
    }

    /**
     * 获取pmts的模型姿态矩阵
     * @param {String} id pmts的id
     * @returns {Number[]} 模型姿态矩阵数组，包含16个数值 
     */
    getModelMatrix(id) {
        let options = this._pmtsOpts.get(id);
        let modelMatrixArray = options.modelMatrix;
        return modelMatrixArray;
    }

    /**
     * 获取PMTS配置数据，注意不可更改，形式如下：
     * ```
     * {  
     *    'xxx1': {  
     *        url: 'xxx',  
     *        token: undefined  
     *    },  
     *    'xxx2': {  
     *    }  
     * }  
     * ```
     */
    get data() {
        return Freedo.clone({
            collection: this._pmtsOpts.values
        }, true);
    };

    /**
     * 注册捕捉FdPMTSManager类的回调函数。
     * @param {FdTools.FdPMTSManager~FdPMTSManagerCallback} listener 当有事件触发时被执行的函数，FdPMTSManager的事件类型只有“Changed”、“PmtsLoaded”
     * @param {Object} [scope] listener函数执行时的绑定的对象。
     * @returns {Freedo.Event~RemoveCallback} 返回一个函数，调用该函数可以取消监听。
     * 
     * @example
     * 
     * pmtsManager.on(function(eventType, param){
     *      if(eventType === 'Changed'){
     * 
     *      }
     * });
     * 
     */
    on(listener, scope) {
        return this._eventHelper.add(this._event, listener, scope);
    }

    /**
     * 重置影像图层管理器中的资源，但不包含事件监听者。
     * @private
     */
    reset() {
        this.removeAll();
    }

    /**
     * 销毁所有资源
     */
    dispose() {
        this.reset();
        this._eventHelper.removeAll();
    }
}

// function getPublicSharedPmtsURL(id, accessKey) {
//     return '//cfgateway.gbim360.cn/freeserver-pmts/services/share/' + id +
//         '/pmts/1.0.0/PMTSCapabilities.json?accesskey=' + accessKey;
// }

function getPublicSharedPmtsURL(id, accessKey) {
    if (typeof g_pmtsServer === 'undefined') {
        g_pmtsServer = 'cfgateway.gbim360.cn';
    }

    return '//' + g_pmtsServer + '/freeserver-pmts/services/share/' + id +
        '/pmts/1.0.0/PMTSCapabilities.json?accesskey=' + accessKey;
}

FdPMTSManager.getPublicSharedPmtsURL = getPublicSharedPmtsURL;

export { FdPMTSManager };