//var smallPicSize = 128;
var captureScreenCanvas;

function captureScreen(scene, width, height) {
    if (!Freedo.defined(captureScreenCanvas)) {
        captureScreenCanvas = document.createElement('canvas');
        // captureScreenCanvas.width = smallPicSize;
        // captureScreenCanvas.height = smallPicSize;
    }

    captureScreenCanvas.setAttribute('width', width);
    captureScreenCanvas.setAttribute('height', height);

    var sceneCanvas = scene.canvas;
    var sceneWidth = sceneCanvas.width;
    var sceneHeight = sceneCanvas.height;

    var sx, sy, sw, sh;
    if (sceneWidth >= sceneHeight) {
        sx = (sceneWidth - sceneHeight) * 0.5;
        sy = 0;
        sw = sh = sceneHeight;
    } else {
        sx = 0;
        sy = (sceneHeight - sceneWidth) * 0.5;
        sw = sh = sceneWidth;
    }

    var smallCanvasContext2D = captureScreenCanvas.getContext('2d');
    //smallCanvasContext2D.drawImage(sceneCanvas, sx, sy, sw, sh, 0, 0, smallPicSize, smallPicSize);
    smallCanvasContext2D.drawImage(sceneCanvas, sx, sy, sw, sh, 0, 0, width, height);
    var smallImageDataBase64 = captureScreenCanvas.toDataURL('image/jpeg');
    return smallImageDataBase64;
}

/**
 * 截屏
 *
 * @alias FdCamera.capture
 * @param {Freedo.Scene} scene 场景对象
 * @param {Callback} captureResultFunc 回调函数，捕获以后，通过回调函数获取相关图片，该回调函数有三个参数，参数为base64位字符串image图像，以及图像的长宽。
 * @param {Number} [width] 图像宽度,默认为128
 * @param {Number} [height] 图像高度,默认为128
 * 
 * @example
 *
 * function saveCapturedScreenImage(imageData,width ,height){}
 *
 * var scene = viewer.scene;
 *
 * Freedo.FdCamera.capture(scene ,saveCapturedScreenImage ,width ,height);
 */
function capture(scene, captureResultFunc, width, height) {
    width = Freedo.defaultValue(width, 128);
    height = Freedo.defaultValue(height, 128);

    scene.requestRender();
    var removeEventListenFunc = scene._postRender.addEventListener(function() {
        var smallImageDataBase64 = captureScreen(scene, width, height);
        captureResultFunc(smallImageDataBase64, width, height);
        removeEventListenFunc();
    }, null);
}

export { capture };