const compassHtml =
    `
<svg id='compass' viewBox='-299 379 12 36' preserveAspectRatio='xMidYMid meet' focusable='false' style='display: block; width: 100%; height: 100%;'>
    <g viewBox='-299 379 12 36'>
        <style>
            .st0 {
                fill: #eb2a1c
            }

            .st1 {
                fill-opacity: .15
            }

            .st2 {
                fill: #fff
            }

            .st3 {
                fill-opacity: 7.000000e-02
            }

            .st4 {
                fill: #424242
            }
        </style>
        <path class='st0' d='M-293 379l6 18h-12z'></path>
        <path class='st1' d='M-293 379l6 18h-6z'></path>
        <path class='st2' d='M-293 415l6-18h-12z'></path>
        <path class='st3' d='M-293 415v-18h-6z'></path>
        <circle id='Oval-3' class='st4' cx='-293' cy='397' r='2'>
        </circle>
    </g>
</svg>
`;


/**
 * 指北针
 * @alias FdWidgets.FdCompass
 * @param {Freedo.Viewer} viewer Freedo视景器
 * @param {Element} compassContainer 指北针所属容器
 */
class FdCompass {
    constructor(viewer, compassContainer) {
        this._viewer = viewer;
        this._compassContainer = compassContainer;
        compassContainer.innerHTML = compassHtml;
        let compass = compassContainer.firstElementChild;
        this._compass = compass;

        function setCompassDirection(cameraHeading) {
            var camHeading = Freedo.Math.toDegrees(viewer.camera.heading);
            //var heading = -camHeading - 45.0;
            var heading = -camHeading;
            let transformStr = 'rotate(' + heading + 'deg)';
            compass.style.webkitTransform = transformStr;
            compass.style.MozTransform = transformStr;
            compass.style.msTransform = transformStr;
            compass.style.OTransform = transformStr;
            compass.style.transform = transformStr;
        }

        setCompassDirection(viewer.camera.heading);

        this._removeListener = viewer.camera.changed.addEventListener(function () {
            //console.log(viewer.camera.heading);
            setCompassDirection(viewer.camera.heading);
        });

        this._compass.onclick = function () { console.log('svg clicked!'); };
    }

    /**
     * 销毁指北针
     */
    dispose() {
        this._removeListener();
        this._removeListener = undefined;
        this._compassContainer.innerHTML = '';
        this._viewer = undefined;
        this._compass = undefined;
    }
}

export {
    FdCompass
};
