import { FdCommon } from './FdCommon';

/**
 * PMTS相关服务
 * @namespace
 * @alias FdServer.FdPMTSServer
 * @private
 */
class FdModelServer {
    constructor() {
        throw new Error('FdModelServer should not be instantiated!');
    }
}

/**
 * 获取成功处理完成的模型资源个数
 * @param {Object} data 传参
 * @param {String} data.keyword 关键字
 * @param {String} baseURL 域名
 * @param {String} token 用户权限token
 * @param {FdServer.FdModelServer~ResponseCallback} response 异步回调
 * @private
 */
FdModelServer.getResourceFinishCount = async function(data, baseURL, token, callback) {
    let url = `${baseURL}/cloudlink/freeserver-one/services/one/resource/finish/count?token=${token}`;
    let response = [];
    try {
        response = await FdCommon.ajaxJson({
            url: url,
            type: 'POST'
        })
    } catch (error) {
        console.error('获取失败', error);
    }
    callback(response);
}

