/**
 * 表单上传模型
 * @param {Object} data 
 * @param {File} data.modelfile File
 * @param {Number} data.srctype 源文件格式
 * @param {Number} data.destype 目标文件格式
 * @param {String} data.opts 高级设置中相关参数转成的json文件
 * @param {String} baseURL 
 * @param {String} token
 * @param {FdServer.FdModelServer~ResponseCallback} response 异步回调
 * @private
 */
FdModelServer.oneStepFreedo = async function(data, baseURL, token, uploadCB, callback) {
    let url = `${baseURL}/cloudlink/zuul/freeserver-one/services/one/import?token=${token}`;
    let response = [];
    try {
        response = await uploadAjax({
            url: url,
            data: data,
            uploadCB: uploadCB
        })
    } catch (error) {
        console.error('获取失败', error);
    }
    callback(JSON.parse(response));
}

/**
 * 上传模型的功能，老版本的上传模型，估计需要更改可以做临时使用，不建议继续深化。
 * @private 
 */
var uploadAjax = async function(opt) {
    return new Promise((resolve, reject) => {
        var xhr = new XMLHttpRequest();
        xhr.withCredentials = true;

        function onprogress(evt) {
            opt.uploadCB(evt, xhr)
        };

        xhr.upload.addEventListener('progress', onprogress, false);
        xhr.addEventListener("readystatechange", function() {
            if (this.readyState === 4) {
                if (xhr.status >= 200 && xhr.status < 300) {
                    resolve(xhr.responseText);
                } else {
                    reject(xhr.responseText);
                }
            }
        });

        xhr.open("POST", opt.url);
        xhr.send(opt.data);
    })
}

