import {
    FdBezierCurveAlgorithm
} from './Analysis/FdBezierCurveAlgorithm';

import {
    FdMisc
} from '../FdMisc/FdMisc';

import {
    FdPolylineEditor
} from './Analysis/FdPolylineEditor';

/**
 * 道路绘制功能
 * @alias FdMicroApp.FdDrawRoad
 * @class
 *
 * @example
 * var viewer = Freedo.FdApp.createDefaultViewer('freedoContainer');
 * var dr = new FdDrawRoad(viewer);
 * dr.start('一级');
 */
class FdDrawRoad {
    constructor(viewer) {
        this._viewer = viewer;
        this._scene = this._viewer.scene;

        // 辅助变量
        this._roads = [];
        this._centerLines = [];

        this._roadsOptions = [];

        this._lsner = undefined;

        this._ple = new FdPolylineEditor(viewer);
        this._ple.setAutoLoop(false);
        this._ple.setAutoDelLines(true);

        this._handler = undefined;

        this._event = new Freedo.Event();
        this._eventHelper = new Freedo.EventHelper();

        this._level = undefined;
        this._smoothRate = 5;
        this._nearRate = 4;
        this._hideCenterLine = true;

        this._currentId = undefined;
    }

    get roadsOptions() {
        return this._roadsOptions;
    }

    set roadsOptions(v) {
        if(!Freedo.defined(v)) {
            return;
        }

        this.clearRoads();

        for(var k in v) {
            this.addRoad(v[k]['id'], v[k]['pts'], v[k]['level'], v[k]['width']);
        }
    }
}

/**
 * 激活道路绘制功能，激活后，直接在地面选点即可
 * @param {String} level 可选值为：一级,二级,三级
 */
FdDrawRoad.prototype.start = function(level) {
    if(!Freedo.defined(level)) {
        console.error("start：参数level无效");
        return;
    }
    this._ple.run();

    this._level = level;
    
    this.addLsnr();

    this._event.raiseEvent('Start', {});
}

FdDrawRoad.prototype.addLsnr = function() {
    if(this._lsner)
        this._lsner();
    var that = this;
    this._lsner = this._ple.on(function(eventType, param) {
        if(eventType == 'PLAdd') {
            //that._ple.cancel();
            that.onPLAdd(param);
        }

        if(eventType == 'EditEnd') {
            for(var k in that._roadsOptions) {
                if(that._currentId == that._roadsOptions[k].id) {
                    that._roadsOptions[k].pts = Freedo.clone(param.pts);
                }
            }
            that.reDraw();
        }
    });
}

FdDrawRoad.prototype.onPLAdd = function(param) {
    this.addRoad(undefined, param.polyline.pts, this._level, 5);
}

/**
 * 增加道路
 * @param {String} id 道路ID
 * @param {Cartesian3[]} pts 道路点组
 * @param {String} level 可选值为：一级,二级,三级
 * @param {Number} width 道路宽度
 */
FdDrawRoad.prototype.addRoad = function(id, pts, level, width) {
    if(!Freedo.defined(id))
        id = Freedo.createGuid();
    var roadUrl = this.getRoadUrlByLevel(level);
    var newPts = [];
    var td = Freedo.Math.toDegrees;
    for(var i = 0; i < pts.length; i++) {
        var pt = Freedo.Cartographic.fromCartesian(pts[i]);
        newPts.push({
            lon: td(pt.longitude),
            lat: td(pt.latitude),
            height: pt.height + 3
        });
    }

    var road = this.drawRoad(newPts, width, roadUrl);
    if(!this._hideCenterLine) {
        var centerLine = this._viewer.scene.primitives.add(this.createLinePrimitive(pts));
        this._centerLines.push(centerLine);
    }

    this._viewer.scene.requestRender();
    this._roads.push(road);

    var data = {
        id: id,
        pts: pts,
        level: level,
        width: width
    };
    this._roadsOptions.push(data);

    this._event.raiseEvent('RoadAdd', {});
    this._event.raiseEvent('DataChanged', data);
}

/**
 * 地理坐标转世界坐标
 * @private
 * @param {Array} HPRArray，包含对象（对象属性：long,lat,height）的数组，其中经纬度的单位为度
 * @returns {Array} Cartesian3数组
 */
FdDrawRoad.prototype.TransfPointToCartesian3 = function(HPRArray) {
    var cartesianarray = [];
    for(let i = 0; i < HPRArray.length; i++) {
        cartesianarray[i] = FreeDo.Cartesian3.fromDegrees(HPRArray[i].lon, HPRArray[i].lat, HPRArray[i].height);
    }
    return cartesianarray;
}

/**
 *世界坐标转换为相对坐标
 * @private
 * @param {Array} cartesianarray Cartesian3数组
 * @returns {Array} Cartesian3数组
 */
var originPt = new FreeDo.Cartesian3();
FdDrawRoad.prototype.TransfCartesian3ToSelf = function(cartesianarray) {
    var array = [];
    for(var i = 0; i < cartesianarray.length; i++) {
        array[i] = new Freedo.Cartesian3(cartesianarray[i].x - originPt.x, cartesianarray[i].y - originPt.y, cartesianarray[i].z - originPt.z);
    }
    return array;
}

/**
 * n个点拓宽后得到的2n个点
 * @private
 * @param {Array} cartesians Cartesian3数组
 * @param {Array} relarray Cartesian3数组
 * @param {Number} width 道路中心到路边的距离
 * @returns {Array} Cartesian3数组
 */
FdDrawRoad.prototype.getPointArray = function(cartesians, relarray, width) {

    // 第一点第二点构成的向量
    var xl1 = new FreeDo.Cartesian3();
    // 第二点第三点构成的向量
    var xl2 = new FreeDo.Cartesian3();
    // 两个法向量
    var f1 = new FreeDo.Cartesian3();
    var f2 = new FreeDo.Cartesian3();
    // 两个单位法向量
    var normalxl1 = new FreeDo.Cartesian3();
    var normalxl2 = new FreeDo.Cartesian3();
    // 合向量
    var addxl1 = new FreeDo.Cartesian3();
    var addxl2 = new FreeDo.Cartesian3();
    // 两个点
    var p1 = new FreeDo.Cartesian3();
    var p2 = new FreeDo.Cartesian3();
    //
    var midwidth = null;
    // 夹角
    var angle = null;
    // 存放计算出来的点
    var calculatearray = [];
    // 判断点的个数
    switch(relarray.length) {
        case 0:

            break
        case 1:

            break

        case 2:
            // 以该点为起始点的向量
            FreeDo.Cartesian3.subtract(relarray[1], relarray[0], xl1);
            // 该向量的两个法向量
            FreeDo.Cartesian3.cross(cartesians[0], xl1, f1);
            FreeDo.Cartesian3.cross(xl1, cartesians[0], f2);
            // 单位法向量
            FreeDo.Cartesian3.normalize(f1, normalxl1);
            FreeDo.Cartesian3.normalize(f2, normalxl2);
            // 以width为模长的法向量
            normalxl1.x = normalxl1.x * width;
            normalxl1.y = normalxl1.y * width;
            normalxl1.z = normalxl1.z * width;
            normalxl2.x = normalxl2.x * width;
            normalxl2.y = normalxl2.y * width;
            normalxl2.z = normalxl2.z * width;
            // 求两个点的坐标(起点)
            FreeDo.Cartesian3.add(relarray[0], normalxl1, p1);
            FreeDo.Cartesian3.add(relarray[0], normalxl2, p2);
            // 将两个点存放到数组中
            calculatearray.push(new FreeDo.Cartesian3(p1.x, p1.y, p1.z));
            calculatearray.push(new FreeDo.Cartesian3(p2.x, p2.y, p2.z));

            // 求两个点的坐标（终点）
            FreeDo.Cartesian3.add(relarray[1], normalxl1, p1);
            FreeDo.Cartesian3.add(relarray[1], normalxl2, p2);
            // 将两个点存放到数组中
            calculatearray.push(new FreeDo.Cartesian3(p1.x, p1.y, p1.z));
            calculatearray.push(new FreeDo.Cartesian3(p2.x, p2.y, p2.z));
            break

        default:

            // 计算是否有三点一线的情况，有则删去中间点
            for(let i = 1; i < relarray.length - 1; i++) {
                // 计算第二个点和第一个点的差值，得到向量1
                FreeDo.Cartesian3.subtract(relarray[i], relarray[i - 1], xl1);
                // 计算第三个点和第二个点的差值，得到向量2
                FreeDo.Cartesian3.subtract(relarray[i + 1], relarray[i], xl2);
                // 判断两个向量的夹角是否为0
                if(FreeDo.Cartesian3.angleBetween(xl1, xl2) == 0) {
                    // 如果条件成立则删除中间点
                    relarray.splice(i--, 1);
                }
            }
            for(let i = 0; i < relarray.length; i++) {
                // 第一个点
                if(i == 0) {
                    // 以该点为起始点的向量
                    FreeDo.Cartesian3.subtract(relarray[i + 1], relarray[i], xl1);
                    // 该向量的两个法向量
                    FreeDo.Cartesian3.cross(cartesians[i], xl1, f1);
                    FreeDo.Cartesian3.cross(xl1, cartesians[i], f2);
                    // 单位法向量
                    FreeDo.Cartesian3.normalize(f1, normalxl1);
                    FreeDo.Cartesian3.normalize(f2, normalxl2);
                    // 以width为模长的法向量
                    normalxl1.x = normalxl1.x * width;
                    normalxl1.y = normalxl1.y * width;
                    normalxl1.z = normalxl1.z * width;
                    normalxl2.x = normalxl2.x * width;
                    normalxl2.y = normalxl2.y * width;
                    normalxl2.z = normalxl2.z * width;
                    // 求两个点的坐标
                    FreeDo.Cartesian3.add(relarray[i], normalxl1, p1);
                    FreeDo.Cartesian3.add(relarray[i], normalxl2, p2);
                    // 将两个点存放到数组中
                    calculatearray.push(new FreeDo.Cartesian3(p1.x, p1.y, p1.z));
                    calculatearray.push(new FreeDo.Cartesian3(p2.x, p2.y, p2.z));

                    // 最后一个点
                } else if(i == relarray.length - 1) {
                    // 以该点为起始点的向量
                    FreeDo.Cartesian3.subtract(relarray[i], relarray[i - 1], xl1);
                    // 该向量的两个法向量
                    FreeDo.Cartesian3.cross(cartesians[i], xl1, f1);
                    FreeDo.Cartesian3.cross(xl1, cartesians[i], f2);
                    // 单位法向量
                    FreeDo.Cartesian3.normalize(f1, normalxl1);
                    FreeDo.Cartesian3.normalize(f2, normalxl2);
                    // 以width为模长的法向量
                    normalxl1.x = normalxl1.x * width;
                    normalxl1.y = normalxl1.y * width;
                    normalxl1.z = normalxl1.z * width;
                    normalxl2.x = normalxl2.x * width;
                    normalxl2.y = normalxl2.y * width;
                    normalxl2.z = normalxl2.z * width;
                    // 求两个点的坐标
                    FreeDo.Cartesian3.add(relarray[i], normalxl1, p1);
                    FreeDo.Cartesian3.add(relarray[i], normalxl2, p2);
                    // 将两个点存放到数组中
                    calculatearray.push(new FreeDo.Cartesian3(p1.x, p1.y, p1.z));
                    calculatearray.push(new FreeDo.Cartesian3(p2.x, p2.y, p2.z));
                    // 中间的点
                } else {
                    // 求中间点法向量的模长
                    FreeDo.Cartesian3.subtract(relarray[i - 1], relarray[i], xl1);
                    FreeDo.Cartesian3.subtract(relarray[i + 1], relarray[i], xl2);
                    angle = FreeDo.Cartesian3.angleBetween(xl1, xl2) / 2;
                    midwidth = width / Math.sin(angle);

                    FreeDo.Cartesian3.subtract(relarray[i + 1], relarray[i], xl1);
                    FreeDo.Cartesian3.subtract(relarray[i], relarray[i - 1], xl2);
                    FreeDo.Cartesian3.normalize(xl1, xl1);
                    FreeDo.Cartesian3.normalize(xl2, xl2);
                    FreeDo.Cartesian3.add(xl1, xl2, addxl1);

                    FreeDo.Cartesian3.cross(cartesians[i], addxl1, f1);
                    FreeDo.Cartesian3.normalize(f1, normalxl1);
                    normalxl1.x = normalxl1.x * midwidth;
                    normalxl1.y = normalxl1.y * midwidth;
                    normalxl1.z = normalxl1.z * midwidth;
                    FreeDo.Cartesian3.add(relarray[i], normalxl1, p1);

                    FreeDo.Cartesian3.cross(addxl1, cartesians[i], f2);
                    FreeDo.Cartesian3.normalize(f2, normalxl2);
                    normalxl2.x = normalxl2.x * midwidth;
                    normalxl2.y = normalxl2.y * midwidth;
                    normalxl2.z = normalxl2.z * midwidth;
                    FreeDo.Cartesian3.add(relarray[i], normalxl2, p2);

                    calculatearray.push(new FreeDo.Cartesian3(p1.x, p1.y, p1.z));
                    calculatearray.push(new FreeDo.Cartesian3(p2.x, p2.y, p2.z));
                }
            };
    }
    return calculatearray;
}

/**
 * 根据道路级别得到对应的图片资源路径
 * @private
 * @param {String} level 道路级别
 * @returns {String} 对应的图片资源路径
 */
FdDrawRoad.prototype.getRoadUrlByLevel = function(level) {
    var roadUrl = null;
    if(level == '一级') {
        roadUrl = Freedo.FdApp.getFreedoXPath() + 'Assets/Textures/road/road1.jpg';
    } else if(level == '二级') {
        roadUrl = Freedo.FdApp.getFreedoXPath() + 'Assets/Textures/road/road2.jpg';
    } else if(level == '三级') {
        roadUrl = Freedo.FdApp.getFreedoXPath() + 'Assets/Textures/road/road3.jpg';
    } else {
        roadUrl = null;
    }
    return roadUrl;
}

/**
 * 计算控制点
 * @private
 * @param {Cartesian3} p0p 第一个点
 * @param {Cartesian3} p1p 第二个点
 * @param {Cartesian3} p2p 第三个点
 * @returns {Array} 返回[Cartesian3,Cartesian3]格式的控制点
 */
FdDrawRoad.prototype.calcCP = function(p0p, p1p, p2p) {
    var p0,
        p1,
        p2;
    p0 = p0p.position;
    p1 = p1p.position;
    p2 = p2p.position;

    var cp = [];
    var v1 = FreeDo.Cartesian3.subtract(p0, p1, new FreeDo.Cartesian3());
    var v2 = FreeDo.Cartesian3.subtract(p2, p1, new FreeDo.Cartesian3());

    var axis = FreeDo.Cartesian3.cross(v1, v2, new FreeDo.Cartesian3());

    var v1n = FreeDo.Cartesian3.normalize(v1, new FreeDo.Cartesian3());
    var v2n = FreeDo.Cartesian3.normalize(v2, new FreeDo.Cartesian3());
    var mv = FreeDo.Cartesian3.add(v1n, v2n, new FreeDo.Cartesian3());

    var quat1 = Freedo.Quaternion.fromAxisAngle(axis, Freedo.Math.PI_OVER_TWO, new Freedo.Quaternion());
    var quat2 = Freedo.Quaternion.fromAxisAngle(axis, -1 * Freedo.Math.PI_OVER_TWO, new Freedo.Quaternion());

    var m31 = Freedo.Matrix3.fromQuaternion(quat1, new Freedo.Matrix3());
    var m32 = Freedo.Matrix3.fromQuaternion(quat2, new Freedo.Matrix3());

    var mv1 = Freedo.Matrix3.multiplyByVector(m31, mv, new Freedo.Cartesian3());
    var mv2 = Freedo.Matrix3.multiplyByVector(m32, mv, new Freedo.Cartesian3());

    var mv1n = FreeDo.Cartesian3.normalize(mv1, new FreeDo.Cartesian3());
    var mv2n = FreeDo.Cartesian3.normalize(mv2, new FreeDo.Cartesian3());

    var len1 = FreeDo.Cartesian3.distance(p0, p1);
    var len2 = FreeDo.Cartesian3.distance(p2, p1);
    var len = 1;
    if(len1 < len2) {
        len = len1 / this._nearRate; //this._smoothRate;
    } else
        len = len2 / this._nearRate; // this._smoothRate;

    mv1n = FreeDo.Cartesian3.multiplyByScalar(mv1n, len, new FreeDo.Cartesian3());
    mv2n = FreeDo.Cartesian3.multiplyByScalar(mv2n, len, new FreeDo.Cartesian3());

    var cp0 = FreeDo.Cartesian3.add(p1, mv1n, new FreeDo.Cartesian3());
    var cp1 = FreeDo.Cartesian3.add(p1, mv2n, new FreeDo.Cartesian3());

    var dis1 = FreeDo.Cartesian3.distance(p0, cp0);
    var dis2 = FreeDo.Cartesian3.distance(p0, cp1);

    if(dis1 < dis2)
        return [cp0, cp1];
    else
        return [cp1, cp0];
}

/**
 * 平滑化
 * @private
 * @param {Boolean} pts 需要进行平滑化的点组
 * @returns 平滑后的点组，[Cartesian3]
 */
FdDrawRoad.prototype.smoothPolyline = function(pts) {
    var aPt = [];
    if(pts.length < 3) {
        for(var i = 0; i < pts.length; i++) {
            aPt.push(pts[i].position);
        }
    } else {
        for(var i = 0; i <= pts.length - 2; i++) {
            var ca = {},
                cb = {},
                pa = {},
                pb = {};
            if(i == 0) {
                ca = pts[0];
                cb.position = this.calcCP(pts[0], pts[1], pts[2])[0];
                pa = pts[0];
                pb = pts[1];
            } else if(i == pts.length - 2) {
                ca.position = this.calcCP(pts[i - 1], pts[i], pts[i + 1])[1];
                cb = pts[i + 1];
                pa = pts[i];
                pb = pts[i + 1];
            } else {
                ca.position = this.calcCP(pts[i - 1], pts[i], pts[i + 1])[1];
                cb.position = this.calcCP(pts[i], pts[i + 1], pts[i + 2])[0];
                pa = pts[i];
                pb = pts[i + 1];
            }
            var bz = new FdBezierCurveAlgorithm(pa, pb, ca, cb);

            /*
            this._viewer.entities.add({
            position: ca.position,
            ellipsoid: {
            material: Freedo.Color.YELLOW.withAlpha(1),
            radii: new Freedo.Cartesian3(5, 5, 5)
            }
            });
            this._viewer.entities.add({
            position: cb.position,
            ellipsoid: {
            material: Freedo.Color.RED.withAlpha(1),
            radii: new Freedo.Cartesian3(5, 5, 5)
            }
            });
             */

            var segPts = bz.getVertexForCurve(this._smoothRate);
            aPt = aPt.concat(segPts);
        }
    }

    //把重复的相邻点删除掉
    var newPts = [];
    for(var i = 0; i < aPt.length; i++) {
        var obj = {};
        obj = aPt[i];
        if(newPts.length > 0) {
            var len = FreeDo.Cartesian3.distance(newPts[newPts.length - 1], obj);
            if(len > 0.0001)
                newPts.push(obj);
        } else
            newPts.push(obj);
    }
    return newPts;
}

/**
 * 绘制道路
 * @private
 * @param {Array} HPRArray 包含对象（对象属性：long,lat,height）的数组
 * @param {Number} roadwidth 路宽
 */
FdDrawRoad.prototype.drawRoad = function(HPRArray, roadwidth, roadimgurl) {
    // 输入系列点后转换成世界坐标
    var cartesians1 = this.TransfPointToCartesian3(HPRArray);
    var aNewPts1 = [];
    for(let i = 0; i < cartesians1.length; i++) {
        aNewPts1[i] = {
            position: cartesians1[i]
        }
    }
    cartesians1 = this.smoothPolyline(aNewPts1);

    originPt.x = cartesians1[0].x;
    originPt.y = cartesians1[0].y;
    originPt.z = cartesians1[0].z;
    var relarray1 = this.TransfCartesian3ToSelf(cartesians1);
    // 点拓宽后返回的结果1
    var result1 = this.getPointArray(cartesians1, relarray1, roadwidth);
    // 拓宽后的点
    var calculatearray1 = result1;

    // 存放路基点的数组
    var HPRArray2 = [];
    for(let i = 0; i < HPRArray.length; i++) {
        HPRArray2[i] = {
            lon: HPRArray[i].lon,
            lat: HPRArray[i].lat,
            height: HPRArray[i].height - 3
        }
    }
    // 路基宽
    var roadbasewidth = roadwidth + 4;
    // 输入系列点后转换成世界坐标
    var cartesians2 = this.TransfPointToCartesian3(HPRArray2);
    aNewPts1 = [];
    for(let i = 0; i < cartesians2.length; i++) {
        aNewPts1[i] = {
            position: cartesians2[i]
        }
    }
    cartesians2 = this.smoothPolyline(aNewPts1);

    var relarray2 = this.TransfCartesian3ToSelf(cartesians2);
    // 点拓宽后返回的结果2
    var result2 = this.getPointArray(cartesians2, relarray2, roadbasewidth);
    // 拓宽后的点
    var calculatearray2 = result2;

    //	顶点位置坐标数组
    var p = [];

    for(let i = 0; i < calculatearray2.length; i++) {
        if(i % 2 == 0) {
            p.push(calculatearray2[i].x);
            p.push(calculatearray2[i].y);
            p.push(calculatearray2[i].z);

            p.push(calculatearray1[i].x);
            p.push(calculatearray1[i].y);
            p.push(calculatearray1[i].z);
        } else {
            p.push(calculatearray1[i].x);
            p.push(calculatearray1[i].y);
            p.push(calculatearray1[i].z);

            p.push(calculatearray2[i].x);
            p.push(calculatearray2[i].y);
            p.push(calculatearray2[i].z);
        }
    }

    var positions = new Float64Array(p);
    // console.log(positions)

    // 顶点纹理坐标数组
    var t = [];
    var d1 = null;
    var d2 = null;
    var d = null;
    var tu = 0.0;

    t.push(tu, 0.0);
    t.push(tu, 0.1);
    t.push(tu, 0.9);
    t.push(tu, 0.99);
    var range1 = result1.length / 2 - 1;
    var num = roadwidth * 2 + Math.sqrt(4 * 4 + 3 * 3) * 2;
    for(let i = 0; i < range1; i++) {
        d1 = FreeDo.Cartesian3.distance(calculatearray1[2 * i + 2], calculatearray1[2 * i]) / num;
        d2 = FreeDo.Cartesian3.distance(calculatearray1[2 * i + 3], calculatearray1[2 * i + 1]) / num;
        d = (d1 + d2) / 2;
        tu = tu + d;

        t.push(tu, 0.0);
        t.push(tu, 0.1);
        t.push(tu, 0.9);
        t.push(tu, 0.99);
    }
    var texCoords = new Float32Array(t);
    // console.log(t)

    //	三角形索引数组
    var ind = [];
    var range2 = (result1.length / 2 - 2) * 4 + 2;
    for(let i = 0; i <= range2; i++) {
        if((i - 3) % 4 != 0) {
            ind.push(i + 0);
            ind.push(i + 4);
            ind.push(i + 5);
            ind.push(i + 5);
            ind.push(i + 1);
            ind.push(i + 0);
        }
    }
    var indices = new Uint16Array(ind);
    // console.log(indices)
    var roadmidposition = [];
    for(var i = 0; i < cartesians1.length; i++) {
        roadmidposition.push(cartesians1[i].x);
        roadmidposition.push(cartesians1[i].y);
        roadmidposition.push(cartesians1[i].z);
    }
    var RoadBoundingSphere = FreeDo.BoundingSphere.fromVertices(roadmidposition);
    var geometry = new FreeDo.Geometry({
        attributes: {
            position: new FreeDo.GeometryAttribute({
                componentDatatype: FreeDo.ComponentDatatype.DOUBLE,
                componentsPerAttribute: 3,
                values: positions
            }),
            st: new FreeDo.GeometryAttribute({
                componentDatatype: FreeDo.ComponentDatatype.FLOAT,
                componentsPerAttribute: 2,
                values: texCoords
            })
        },
        indices: indices,
        primitiveType: FreeDo.PrimitiveType.TRIANGLES,
        boundingSphere: FreeDo.BoundingSphere.fromVertices(positions)
    })
    var instance = new FreeDo.GeometryInstance({
        geometry: geometry,
        modelMatrix: new FreeDo.Matrix4(
            1, 0, 0, originPt.x,
            0, 1, 0, originPt.y,
            0, 0, 1, originPt.z,
            0, 0, 0, 1),
        attributes: {
            color: FreeDo.ColorGeometryInstanceAttribute.fromColor(FreeDo.Color.RED)
        },
        id: 'trangle'
    })

    // viewer.scene.primitives.add(new FreeDo.Primitive({
    //   geometryInstances: instance,
    //   appearance: new FreeDo.PerInstanceColorAppearance({
    //     flat: true,
    //     translucent: false
    //   })
    // }))

    var primitive = this._viewer.scene.primitives.add(new FreeDo.Primitive({
        geometryInstances: instance,
        appearance: new FreeDo.MaterialAppearance({
            material: new FreeDo.Material({
                fabric: {
                    type: 'Image',
                    uniforms: {
                        image: roadimgurl
                    }
                }
            })

        })
    }));
    return {
        primitive: primitive,
        boundingsphere: RoadBoundingSphere
    };
}

/**
 * 创建多段线
 * @private
 * @param {Object} pts 点组
 * @returns 返回线段对象
 */
FdDrawRoad.prototype.createLinePrimitive = function(pts) {
    return new Freedo.Primitive({
        geometryInstances: new Freedo.GeometryInstance({
            geometry: new Freedo.PolylineGeometry({
                positions: pts,
                width: 2.0,
                vertexFormat: Freedo.PolylineMaterialAppearance.VERTEX_FORMAT
            })
        }),
        appearance: new Freedo.PolylineMaterialAppearance({
            material: new Freedo.Material({
                fabric: {
                    type: 'Color',
                    uniforms: {
                        color: new Freedo.Color(1.0, 0.3, 0.3, 1.0)
                    }
                }
            }),
            renderState: new Freedo.RenderState({
                polygonOffset: {
                    enabled: true,
                    factor: -100.0,
                    units: -100.0,
                },
                depthTest: {
                    enabled: true,
                    func: Freedo.WebGLConstants.LEQUAL,
                }
            }),
        }),
        depthFailAppearance: new Freedo.PolylineMaterialAppearance({
            material: new Freedo.Material({
                fabric: {
                    type: 'PolylineDash',
                    uniforms: {
                        color: new Freedo.Color(1.0, 0.3, 0.3, 0.5)
                    }
                }
            }),
        }),
    });
}

/**
 * 退出道路绘制功能
 */
FdDrawRoad.prototype.cancel = function() {
    this.reset();
    this._event.raiseEvent('Cancel', {});
}

/**
 * 设置路径平滑度
 * @param {Number} rate 平滑度，建议1-11之间，数字越小越平滑
 */
FdDrawRoad.prototype.setSmoothRate = function(rate) {
    this._smoothRate = rate;
    this.reDraw();
}

/**
 * 设置路径贴近率
 * @param {Number} rate 贴近率，建议1-100之间
 */
FdDrawRoad.prototype.setNearRate = function(rate) {
    this._nearRate = rate;
    this.reDraw();
}

/**
 * 设置隐藏中轴线
 * @param {Number} hidden
 */
FdDrawRoad.prototype.setCenterLineHide = function(hidden) {
    this._hideCenterLine = hidden;
    this.reDraw();
}

FdDrawRoad.prototype.reDraw = function() {
    var data = Freedo.clone(this.roadsOptions, true);
    this.clearRoads();
    this._data = [];
    for(var k in data) {
        this.addRoad(data[k]['id'], data[k]['pts'], data[k]['level'], data[k]['width']);
    }
}

/**
 * 注册事件捕捉
 * @param {FdMicroApp.FdDrawRoad~FdDrawRoadCallback} listener 当有事件触发时被执行的函数。
 * @param {Object} [scope] listener函数执行时的绑定的对象。
 * @returns {Freedo.Event~RemoveCallback} 返回一个函数，调用该函数可以取消监听。
 */
FdDrawRoad.prototype.on = function(listener, scope) {
    return this._eventHelper.add(this._event, listener, scope);
}

/**
 * @callback FdMicroApp.FdDrawRoad~FdDrawRoadCallback
 * @param {String} eventType 事件类型有：Start,Cancel
 * @param {Object} eventArg Start/Cancel无返回值
 */

/**
 * 开始进行节点编辑
 * @param {String} 组ID
 */
FdDrawRoad.prototype.startEdit = function(id) {
    this.addLsnr();
    for(var k in this._roadsOptions) {
        if(this._roadsOptions[k].id == id) {
            this._currentId = id;
            this._ple.startEdit(this._roadsOptions[k].pts);
        }
    }
}

/**
 * 编辑上一个节点
 */
FdDrawRoad.prototype.editPre = function() {
    this._ple.editPre();
}

/**
 * 编辑下一个节点
 */
FdDrawRoad.prototype.editNext = function() {
    this._ple.editNext();
}

/**
 * 编辑下一个节点
 */
FdDrawRoad.prototype.endEdit = function() {
    this._ple.endEdit();
}

/**
 * 清空
 */
FdDrawRoad.prototype.clearRoads = function() {
    for(var key in this._roads) {
        this._viewer.scene.primitives.remove(this._roads[key].primitive);
    }

    for(var key in this._centerLines) {
        this._viewer.scene.primitives.remove(this._centerLines[key]);
    }
    this._viewer.scene.requestRender();
    this._roads = [];
    this._roadsOptions = [];
    this._centerLines = [];
}

/**
 * 获取数据
 * @returns {Object[]} 返回值格式（宽度单位为米）：
 * [{
        id: GUID,
        pts: [Cartesian3],
        level: 级别,
        width: 宽度
   }...]
 */
FdDrawRoad.prototype.getData = function() {
    return Freedo.clone(this._roadsOptions, true);
}

/**
 * 删除
 */
FdDrawRoad.prototype.removeAll = function() {
    this.clearRoads();
}

/**
 * 重置
 */
FdDrawRoad.prototype.reset = function() {
    if(this._lsner)
        this._lsner();
    this._lsner = undefined;
    this._ple.cancel();
}

/**
 * 销毁所有资源
 */
FdDrawRoad.prototype.dispose = function() {
    this.reset();
    this.clearRoads();
}

export {
    FdDrawRoad
};