import { FdCommon } from './FdCommon';

/**
 * 分享服务
 * @namespace
 * @alias FdServer.FdShareServer
 */
class FdShareServer {}

/**
 * 获取场景的ObjectId，用于判断是否需要进行重新添加数据资源，如果有就直接调用添加分享的功能，
 * 没有返回值的话需要调用添加数据资源的方法FdShareServer.addShareObjectID
 * @param {Object} params 传入对象
 * @param {String} params.id 分享场景的id
 * @param {String} params.name 分享场景的场景name
 * @param {String} baseURL 域名
 * @param {String} token 用户权限token
 * @param {FdServer.FdShareServer~ResponseCallback} response 异步操作回调函数
 * @private
 */
FdShareServer.getShareObjectID = async function(params, baseURL, token, callback) {
    let url = `${baseURL}/cloudlink/cloudlink-core-framework/app/dataresource/queryList?token=${token}&appId=0154981b-11c5-49ad-839d-a3420af507d8&dataId=${params.id}&dataName=${params.name}`;
    let response = [];
    try {
        response = await FdCommon.ajaxJson({
            url: url,
            type: 'GET'
        })
    } catch (error) {
        console.log(error);
    }
    callback(response);
}

/**
 * 添加数据资源，生成一个objectId用于添加分享
 * @param {Object} params 传入对象
 * @param {String} params.id 分享场景的id
 * @param {String} params.name 分享场景的场景name
 * @param {String} baseURL 域名
 * @param {String} token 用户权限token
 * @param {FdServer.FdShareServer~ResponseCallback} response 异步操作回调函数
 * @private
 */
FdShareServer.addShareObjectID = async function(params, baseURL, token, callback) {
    let url = `${baseURL}/cloudlink/cloudlink-core-framework/app/dataresource/add?token=${token}`;
    let timeline = new Date().getTime();
    let response = [];
    let data = {
        'appId': '0154981b-11c5-49ad-839d-a3420af507d8',
        'resourceCode': `freedo_data_lid_${timeline}`,
        'resourceName': `freedo_data_lname_${timeline}`,
        'parentId': '610be183-1dda-43ed-8737-49fa2a55cc80',
        'dataId': params.id,
        'dataName': params.name
    }
    try {
        response = await FdCommon.ajaxJson({
            url: url,
            data: data,
            type: 'POST'
        })
    } catch (error) {
        console.log(error);
    }
    callback(response);
}

/**
 * 获取用户所有的信息列表，用来选择被分享的用户
 * @param {String} baseURL 域名
 * @param {String} token 用户权限token
 * @param {FdServer.FdShareServer~ResponseCallback} response 异步操作回调函数
 */
FdShareServer.getUserList = async function(baseURL, token, callback) {
    let url = `${baseURL}/cloudlink/cloudlink-core-framework/user/getListByApp?token=${token}&appId=0154981b-11c5-49ad-839d-a3420af507d8`;
    let response = [];
    try {
        response = await FdCommon.ajaxJson({
            url: url,
            type: 'GET'
        })
    } catch (error) {
        console.log(error);
    }
    callback(response);
}

/**
 * 准备就绪，开始分享
 * @param {Object} params 传入参数
 * @param {String} params.type 分享的类型  目前支持两种方式 1代表不需要token的分享 3代表个人名义分享
 * @param {String} params.objectId 获取数据资源的id
 * @param {String[]} params.accessKeyList 被分享用户的列表,如果type是1则需要传入被分享用户的id，如果是3则需要传入一个随机id即可(可以调用Freedo.createGuid()来随机生成)
 * @param {String} params.effectiveTime 分享的有效开始时间，时间戳的形式精确到毫秒
 * @param {String} params.expireTime 分享有效的截止时间，时间戳的形式精确到毫秒
 * @param {String} baseURL 域名
 * @param {String} token 用户权限token
 * @param {FdServer.FdShareServer~ResponseCallback} response 异步操作回调函数
 * @private
 */
FdShareServer.addShareScene = async function(params, baseURL, token, callback) {
    let url = `${baseURL}/cloudlink/cloudlink-core-framework/app/dataresource/share/add?token=${token}`;
    let response = [];
    let data = {
        'shareType': params.shareType,
        'dataresourceId': params.objectId,
        'accessKeyList': params.accessKeyList,
        'status': 1,
        'dataType': params.dataType,
        'effectiveTime': params.effectiveTime,
        'expireTime': params.expireTime,
        'remark': params.remark
    };
    try {
        response = await FdCommon.ajaxJson({
            url: url,
            data: data,
            type: 'POST'
        })
    } catch (error) {
        console.log(error);
    }
    callback(response);
}

/**
 * 一键分享的功能
 * @param {Object} params 传参
 * @param {Object} params.lid id，场景对应pid，图层对应lid
 * @param {Object} params.name 分享内容的name
 * @param {Object} params.shareType 分享类型 目前支持两种方式 1代表不需要token的分享 3代表个人名义分享
 * @param {String[]} params.accessKeyList 被分享用户的集合，如果是accesskey方式需要使用Freedo.createGuid()创建一个accesskey 即[Freedo.createGuid()]
 * @param {Object} params.effectiveTime 有效起始时间
 * @param {Object} params.expireTime 有效截止时间
 * @param {Object} params.dataType 资源类型
 * @param {String} baseURL 域名地址
 * @param {String} token 用户权限凭证
 * @param {FdServer.FdShareServer~ResponseCallback} response 异步操作回调函数
 */
FdShareServer.share = async function(params, baseURL, token, callback) {
    FdShareServer.getShareObjectID({
        id: params.lid,
        name: params.name
    }, baseURL, token, res => {
        let objectId = undefined;
        if (res.rows.length) {
            objectId = res.rows[0].objectId;
            FdShareServer.addShareScene({
                shareType: params.shareType,
                objectId: objectId,
                accessKeyList: params.accessKeyList,
                effectiveTime: params.effectiveTime,
                expireTime: params.expireTime,
                dataType: params.dataType,
                remark: params.remark
            }, baseURL, token, res => {
                callback(res);
            })
        } else {
            // 添加数据资源，需要创建一下objectId
            Freedo.FdServer.FdShareServer.addShareObjectID({
                id: params.lid,
                name: params.name
            }, baseURL, token, res => {
                objectId = res.rows[0].objectId;
                FdShareServer.addShareScene({
                    shareType: params.shareType,
                    objectId: objectId,
                    accessKeyList: params.accessKeyList,
                    effectiveTime: params.effectiveTime,
                    expireTime: params.expireTime,
                    dataType: params.dataType,
                    remark: params.remark
                }, baseURL, token, res => {
                    callback(res);
                })
            })
        }
    })
}

