/**
 * 以accesskey的方式去获取当前图层的结果
 * @param {String} accesskeyURL accesskey访问的url
 * @param {String} dataType 获取结果类型，PMTS应该传json类型，影像和高程应该传xml
 * @param {FdServer.FdShareServer~ResponseCallback} response 异步操作回调函数
 * 
 * @example
 * let accesskeyURL = '附带accesskey的url';
 * let dataType = 'xml' || 'json';
 * Freedo.FdServer.FdShareServer.getLayerAccesskeyResource(accesskeyURL, dataType, res=> {});
 */
FdShareServer.getLayerAccesskeyResource = async function(accesskeyURL, dataType, callback) {
    try {
        let response = await FdCommon.ajaxJson({
            url: accesskeyURL,
            type: 'GET',
            dataType: dataType
        })
        callback(response);
    } catch (error) {
        callback(undefined, error);
    }
}

