import { DrillData } from './DrillData.js';
import { interpolate, getOffsetPoint, getSimulateDrill } from './geologySimulationTools.js';

var repeat = new Freedo.Cartesian2(100, 50);
var scratchGetNormalV1 = new Freedo.Cartesian3();
var scratchGetNormalV2 = new Freedo.Cartesian3();
var scratchGetNormalN = new Freedo.Cartesian3();
var center = new Freedo.Cartographic();
var sharedIndices = new Uint16Array([0, 1, 2, 2, 1, 3]);

/**
 * 地质模拟管理类
 *
 * @alias FdTools.FdGeologySimulationManager
 * @param {Freedo.Scene} scene 场景
 * @param {String} json 记录钻孔点信息的JSON文件
 * @param {String} [textureDir] 纹理路径，默认为：'../../sampleData/GeologySimulation/GeoTexture/'
 * @class
 *
 * @example
 * var viewer = Freedo.FdApp.createDefaultViewer('freedoContainer');
 *
 *  var geologySimulationManager;
 *  Freedo.loadJson('./static/GeologySimulation/drills.json').then(function(jsonData) {
 *      geologySimulationManager = new Freedo.FdTools.FdGeologySimulationManager(scene ,jsonData);
 *  }).otherwise(function(error) {
 *      console.log('Load drills file failed.');
 *  });
 * 
 *
 */
class FdGeologySimulationManager {

    constructor(scene, json, textureDir) {
        this._drillDataArray = [];
        this._layerTextureMap = new Map();

        this._bottomName = 'bottom';

        this._debugPointEntities = undefined;

        this._groundErasers = [];

        this._geologyPrimitives = [];

        this._scene = scene;

        this._lastHeightPrimitive = undefined;
        this._drillOptions = {

        };

        if (!Freedo.defined(textureDir)) {
            textureDir = '../../sampleData/GeologySimulation/GeoTexture/';
        }

        var drillsJson = json.Geology.Drill;
        for (var i = drillsJson.length - 1; i >= 0; i--) {

            this._drillDataArray.push(new DrillData(drillsJson[i]));

            var layersJson = drillsJson[i].layer;
            for (var j = layersJson.length - 1; j >= 0; j--) {
                var layerJson = layersJson[j];
                if (!this._layerTextureMap.has(layerJson.name)) {
                    this._layerTextureMap.set(layerJson.name, textureDir + layerJson.texture);
                }
            }
        }
        this._layerTextureMap.set(this._bottomName, textureDir + 'bottom.jpg');

        var cartesians = [];
        for (var i = this._drillDataArray.length - 1; i >= 0; i--) {
            cartesians.push(this._drillDataArray[i].cartesianOnZeroHeight);
        }
        this._extent = Freedo.BoundingSphere.fromPoints(cartesians);

        this._event = new Freedo.Event();
        this._eventHelper = new Freedo.EventHelper();
    }

    /**
     * 地质模拟选项 ,参考{@link FdTools.FdGeologySimulationManager#addGeologySimulation}
     * @type {Object}
     * 
     * @example
     * var drillsOptions = geologySimulationManager.drillOptions ;
     * for(var key in drillsOptions){
     *      var drillOption = drillsOptions[key] ;
     *      var cartographics = drillOption.cartographics ; //输入的需要进行插值的原始三维坐标（经纬度坐标）数组。
     *      var maxStep = drillOption.maxStep ; //插值的最大步长
     *      var tileS = drillOption.tileS; //水平方向分多少块
     *      var tileT = drillOption.tileT; //竖直方向分多少块
     *      var flipUp = drillOption.flipUp; //偏移
     *      var buildBottom = drillOption.buildBottom ; //是否构建底部
     * }
     * 
     */
    get drillOptions() {
        return this._drillOptions;
    }

    /**
     * 事件类型有：dataAdded ,dataRemoved
     * @param {FdTools.FdGeologySimulationManager~FdGeologySimulationManagerCallback} listener 当有事件触发时被执行的函数。
     * @param {Object} [scope] listener函数执行时的绑定的对象。
     * @returns {Freedo.Event~RemoveCallback} 返回一个函数，调用该函数可以取消监听。
     * 
     * @example
     * 
     * geologySimulationManager.on(function(eventType ,param) {
     *  if(eventType === 'dataAdded'){
     *     var geologyObject = param ;
     *     geologyObject.points ; // 输入的地质模拟多边形的顶点数组
     *     geologyObject.GroundErasePrimitive ;//地形挖洞的对象
     *     geologyObject.geologyPrimitives ;//添加的地质面对象数组
     *  } else if( eventType === 'dataRemoved'){
     * 
     *  }
     * });
     */
    on(listener, scope) {
        return this._eventHelper.add(this._event, listener, scope);
    }

    /**
     * @callback FdTools.FdGeologySimulationManager~FdGeologySimulationManagerCallback
     * @param {String} eventType 事件类型有：dataAdded(添加了一个地质模拟项)，dataRemoved(清空了所有的地质模拟项)
     * @param {Object} eventArg dataAdded事件的参数为刚添加的对象的封装，参考{@link FdMicroApp.FdGeologySimulationHandler#getData} ，dataRemoved不带参数
     */

    /**
     * 经纬坐标点插值，并贴合地面高程（异步返回）
     * @private
     * @param {Freedo.Cartographics[]} cartographics  输入的需要进行插值的原始三维坐标（经纬度坐标）数组。
     * @param {Number} maxStep       插值的最大步长。
     * @returns {Freedo.Cartesian3[]} 经过插值后的三维点坐标。
     */
    interpolateCartographics(cartographics, maxStep) {
        var terrainProvider = this._scene.terrainProvider;
        var positions = [];
        for (var i = 1; i < cartographics.length; i++) {
            var r = interpolate(cartographics[i - 1], cartographics[i], maxStep);
            if (i === 1) {
                positions.push(cartographics[i - 1]);
            }

            for (var j = 0; j < r.length; j++) {
                positions.push(r[j]);
            }

            positions.push(cartographics[i]);
        }

        if (!Freedo.defined(terrainProvider)) {
            return Freedo.when.resolve(positions);
        }

        return Freedo.sampleTerrainMostDetailed(terrainProvider, positions);
    }

    /**
     * 调试显示点
     * @private
     * @param {Freedo.Viewer} viewer  场景Viewer。
     *
     *
     */
    addDebugPoints(viewer) {

        if (!this._debugPointEntities) {
            this._debugPointEntities = [];
            for (var i = this._drillDataArray.length - 1; i >= 0; i--) {

                var pointEntity = viewer.entities.add({
                    position: this._drillDataArray[i].cartesian,
                    point: {
                        pixelSize: 5,
                        color: Freedo.Color.WHITE,
                        outlineColor: Freedo.Color.BLACK,
                        outlineWidth: 2,
                        //disableDepthTestDistance: Number.POSITIVE_INFINITY,
                        heightReference: Freedo.HeightReference.CLAMP_TO_GROUND
                    }
                });
                this._debugPointEntities.push(pointEntity);
            }
        }
    };

    /**
     * 构建地质模拟的侧面及底面并加入场景
     * @private
     * @param {Array} drills
     * @param {Number} tileS
     * @param {Number} tileT
     * @param {Number} flipUp
     * @param {Number} buildBottom
     *
     */
    addGeologyPrimitives(drills, tileS, tileT, flipUp, buildBottom, drillsInfo) {
        var primitiveCollection = this._scene.primitives;

        var primitives = [];
        var geometriesArray = [];

        var maxDepth = 0.0;
        var length = 0;
        for (var i = 0; i < drills.length - 1; ++i) {
            var drill1 = drills[i];
            var drill2 = drills[i + 1];
            var drill1Pos = Freedo.Cartesian3.fromRadians(drill1.pos.longitude, drill1.pos.latitude, drill1.pos.height);
            var drill2Pos = Freedo.Cartesian3.fromRadians(drill2.pos.longitude, drill2.pos.latitude, drill2.pos.height);
            var distance = Freedo.Cartesian3.distance(drill1Pos, drill2Pos);
            buildGeologyGeometriesBetweenTwoDrills(drill1, drill2, length, distance, tileS, tileT, flipUp);
            length += distance;

            center.longitude += drill1.pos.longitude;
            center.latitude += drill1.pos.latitude;
        }
        center.longitude += drills[i].pos.longitude;
        center.latitude += drills[i].pos.latitude;
        center.longitude /= drills.length;
        center.latitude /= drills.length;
        var normal = Freedo.Ellipsoid.WGS84.geodeticSurfaceNormalCartographic(center);

        var offsetMatrix;
        if (flipUp) {
            var offsetCartesian = new Freedo.Cartesian3();
            Freedo.Cartesian3.multiplyByScalar(normal, maxDepth, offsetCartesian);
            offsetMatrix = Freedo.Matrix4.fromTranslation(offsetCartesian);
        } else {
            offsetMatrix = Freedo.Matrix4.IDENTITY;
        }

        if (buildBottom) {
            buildBottomGeometry();
        }

        for (var i = 0; i < geometriesArray.length; i++) {
            var geometries = geometriesArray[i];
            var texture = this._layerTextureMap.get(geometries.name);
            addPrimitive(texture, geometries, drillsInfo);
        }

        //水线
        buildWaterLine(drills);

        return primitives;

        function buildWaterLine(drills) {
            var positions = [];
            for (var i = 0; i < drills.length; i++) {
                var depth = drills[i].waterDepth;
                var pos = drills[i].pos;
                positions.push(Freedo.Cartesian3.fromRadians(pos.longitude, pos.latitude, pos.height - depth));
            }
            var geometry = new Freedo.PolylineGeometry({
                positions: positions,
                width: 3.0,
                vertexFormat: Freedo.VertexFormat.POSITION_ONLY,
                followSurface: false
            });
            var primitive = primitiveCollection.add(new Freedo.Primitive({
                asynchronous: false,
                geometryInstances: [new Freedo.GeometryInstance({
                    geometry: geometry,
                    modelMatrix: offsetMatrix,
                    attributes: {
                        color: Freedo.ColorGeometryInstanceAttribute.fromColor(new Freedo.Color(0.3, 0.3, 1.0, 1.0))
                    }
                })],
                appearance: new Freedo.PolylineColorAppearance({
                    translucent: false
                })
            }));
            primitives.push(primitive);
        }

        function buildGeologyGeometriesBetweenTwoDrills(drill1, drill2, startLength, distance, tileS, tileT) {
            var index = 1;
            var i, j;
            var drill1Depth = 0.0;
            var drill2Depth = 0.0;
            var drill1Layer, drill1LayerName;
            var drill2Layer, drill2LayerName;
            for (i = 0; i < drill1.layers.length; ++i) {
                drill1Layer = drill1.layers[i];
                drill1LayerName = drill1Layer.name;

                for (j = 0; j < drill2.layers.length; ++j) {

                    drill2Layer = drill2.layers[j];

                    if (!drill2Layer.allocated) {
                        drill2Layer.allocated = true;

                        drill2LayerName = drill2Layer.name;

                        if (drill2LayerName === drill1LayerName) {
                            addGeometry(index++, drill1LayerName, drill1.pos, drill1Depth, drill1Depth + drill1Layer.thickness,
                                drill2.pos, drill2Depth, drill2Depth + drill2Layer.thickness, startLength, distance, tileS, tileT);
                            drill1Depth += drill1Layer.thickness;
                            drill2Depth += drill2Layer.thickness;
                            break;
                        } else {
                            addGeometry(index++, drill2LayerName, drill1.pos, drill1Depth, drill1Depth,
                                drill2.pos, drill2Depth, drill2Depth + drill2Layer.thickness, startLength, distance, tileS, tileT);
                            drill2Depth += drill2Layer.thickness;
                        }
                    }
                }
                if (j === drill2.layers.length) {
                    addGeometry(index++, drill1LayerName, drill1.pos, drill1Depth, drill1Depth + drill1Layer.thickness,
                        drill2.pos, drill2Depth, drill2Depth, startLength, distance, tileS, tileT);
                    drill1Depth += drill1Layer.thickness;
                }
            }
            for (; j < drill2.layers.length; ++j) {
                drill2Layer = drill2.layers[j];
                if (!drill2Layer.allocated) {
                    drill2Layer.allocated = true;
                    drill2LayerName = drill2Layer.name;
                    addGeometry(index++, drill2LayerName, drill1.pos, drill1Depth, drill1Depth,
                        drill2.pos, drill2Depth, drill2Depth + drill2Layer.thickness, startLength, distance, tileS, tileT);
                    drill2Depth += drill2Layer.thickness;
                }
            }
        }

        function getNormal(c1, c2, c3) {
            Freedo.Cartesian3.subtract(c2, c1, scratchGetNormalV1);
            Freedo.Cartesian3.subtract(c3, c2, scratchGetNormalV2);
            Freedo.Cartesian3.cross(scratchGetNormalV1, scratchGetNormalV2, scratchGetNormalN);
            return Freedo.Cartesian3.normalize(scratchGetNormalN, scratchGetNormalN);
        }

        function addGeometry(index, name,
            cartographic1, depth10, depth11,
            cartographic2, depth20, depth21,
            startLength, distance,
            tileS, tileT) {

            var geometries = undefined;
            var depthMaxTemp = Math.max(depth11, depth21);
            if (maxDepth < depthMaxTemp) {
                maxDepth = depthMaxTemp;
            }

            for (var i = 0; i < geometriesArray.length; i++) {
                if (!geometriesArray[i].closed && geometriesArray[i].name === name &&
                    Freedo.Math.equalsEpsilon(geometriesArray[i].depth0, depth10, Freedo.Math.EPSILON5) &&
                    Freedo.Math.equalsEpsilon(geometriesArray[i].depth1, depth11, Freedo.Math.EPSILON5)) {
                    geometries = geometriesArray[i];
                    break;
                }
            }
            if (!Freedo.defined(geometries)) {
                geometries = [];
                geometries.closed = false;
                geometries.name = name;
                geometriesArray.push(geometries);
            }
            geometries.depth0 = depth20;
            geometries.depth1 = depth21;
            if (depth20 === depth21) {
                geometries.closed = true;
            }

            var cartesian1 = Freedo.Cartesian3.fromRadians(cartographic1.longitude, cartographic1.latitude, cartographic1.height);
            var normal1 = Freedo.Ellipsoid.WGS84.geodeticSurfaceNormalCartographic(cartographic1);
            var cartesian2 = Freedo.Cartesian3.fromRadians(cartographic2.longitude, cartographic2.latitude, cartographic2.height);
            var normal2 = Freedo.Ellipsoid.WGS84.geodeticSurfaceNormalCartographic(cartographic2);
            var pos10 = getOffsetPoint(cartesian1, normal1, -depth10);
            var pos20 = getOffsetPoint(cartesian2, normal2, -depth20);
            var pos11 = getOffsetPoint(cartesian1, normal1, -depth11);
            var pos21 = getOffsetPoint(cartesian2, normal2, -depth21);
            var positions = new Float64Array(4 * 3);
            positions[0] = pos10.x;
            positions[1] = pos10.y;
            positions[2] = pos10.z;
            positions[3] = pos11.x;
            positions[4] = pos11.y;
            positions[5] = pos11.z;
            positions[6] = pos20.x;
            positions[7] = pos20.y;
            positions[8] = pos20.z;
            positions[9] = pos21.x;
            positions[10] = pos21.y;
            positions[11] = pos21.z;

            var texCoords = new Float32Array(4 * 2);
            texCoords[0] = startLength / tileS / repeat.x;
            texCoords[1] = depth10 / tileT / repeat.y;
            texCoords[2] = startLength / tileS / repeat.x;
            texCoords[3] = depth11 / tileT / repeat.y;
            texCoords[4] = (startLength + distance) / tileS / repeat.x;
            texCoords[5] = depth20 / tileT / repeat.y;
            texCoords[6] = (startLength + distance) / tileS / repeat.x;
            texCoords[7] = depth21 / tileT / repeat.y;

            var geometry = new Freedo.Geometry({
                attributes: {
                    position: new Freedo.GeometryAttribute({
                        componentDatatype: Freedo.ComponentDatatype.DOUBLE,
                        componentsPerAttribute: 3,
                        values: positions
                    }),
                    st: new Freedo.GeometryAttribute({
                        componentDatatype: Freedo.ComponentDatatype.FLOAT,
                        componentsPerAttribute: 2,
                        values: texCoords
                    })
                },
                indices: sharedIndices,
                primitiveType: Freedo.PrimitiveType.TRIANGLES,
                boundingSphere: Freedo.BoundingSphere.fromVertices(positions)
            });
            geometry.layerName = name;
            geometry.layerIndex = index;
            geometry.depth = (Math.abs(depth11 - depth10) + Math.abs(depth21 - depth20)) / 2.0;
            geometries.push(geometry);
        }

        function addPrimitive(texture, geometries, drillsInfo) {
            var geometryInstances = [];
            var layerName;
            var depth = 0.0;

            for (var i = 0; i < geometries.length; ++i) {
                geometryInstances.push(new Freedo.GeometryInstance({
                    geometry: geometries[i],
                    modelMatrix: offsetMatrix
                }));
                layerName = geometries[i].layerName;
                depth += geometries[i].depth;
            }
            depth /= geometries.length;

            var primitive = primitiveCollection.add(new Freedo.Primitive({
                asynchronous: false,
                geometryInstances: geometryInstances,
                appearance: new Freedo.MaterialAppearance({
                    materialSupport: Freedo.MaterialAppearance.MaterialSupport.TEXTURED,
                    material: new Freedo.Material({
                        fabric: {
                            materials: {
                                first: {
                                    type: 'DiffuseMap',
                                    uniforms: {
                                        image: texture,
                                        repeat: repeat
                                    }
                                }
                            },
                            uniforms: {
                                value: [0.0, 0.0, 0.0, 1.0]
                            },
                            components: {
                                diffuse: 'vec3(value[0][0], value[0][1], value[1][0]) + first.diffuse',
                                alpha: 'value[1][1]'
                            }
                        }
                    })
                })
            }));
            primitive.layerName = layerName;
            primitive.drillsInfo = drillsInfo;
            primitive.depth = depth;
            primitive.offsetMatrix = offsetMatrix;
            primitives.push(primitive);
        }

        function buildBottomGeometry() {

            var geometries = [];
            geometries.closed = false;
            geometries.name = 'bottom';
            geometriesArray.push(geometries);

            var cartesians = [];
            var center = new Freedo.Cartesian3();
            for (var i = 0; i < drills.length; ++i) {
                var drill = drills[i];
                var depth = drill.layers[drill.layers.length - 1].depth;
                var cartesian = Freedo.Cartesian3.fromRadians(drill.pos.longitude, drill.pos.latitude, drill.pos.height);
                var normal = Freedo.Ellipsoid.WGS84.geodeticSurfaceNormalCartographic(drill.pos);
                var cartesian1 = getOffsetPoint(cartesian, normal, -depth);
                cartesians.push(cartesian1);
                Freedo.Cartesian3.add(center, cartesian1, center);
            }
            Freedo.Cartesian3.multiplyByScalar(center, 1.0 / cartesians.length, center);
            var num = cartesians.length + 1;
            var positions = new Float64Array(num * 3);
            var texCoords = new Float32Array(num * 2);
            var mintex_s = Number.POSITIVE_INFINITY;
            var mintex_t = Number.POSITIVE_INFINITY;
            positions[0] = center.x;
            positions[1] = center.y;
            positions[2] = center.z;
            texCoords[0] = 0;
            texCoords[1] = 0;
            for (var i = 0; i < num - 1; i++) {
                positions[3 + i * 3 + 0] = cartesians[i].x;
                positions[3 + i * 3 + 1] = cartesians[i].y;
                positions[3 + i * 3 + 2] = cartesians[i].z;
                texCoords[2 + i * 2 + 0] = (cartesians[i].x - center.x) / repeat.x;
                if (texCoords[2 + i * 2 + 0] < mintex_s) {
                    mintex_s = texCoords[2 + i * 2 + 0];
                }
                texCoords[2 + i * 2 + 1] = (cartesians[i].y - center.y) / repeat.y;
                if (texCoords[2 + i * 2 + 1] < mintex_t) {
                    mintex_t = texCoords[2 + i * 2 + 1];
                }
            }

            for (var i = 0; i < num; i++) {
                texCoords[i * 2 + 0] -= mintex_s;
                texCoords[i * 2 + 1] -= mintex_t;
            }

            var geometry = new Freedo.Geometry({
                attributes: {
                    position: new Freedo.GeometryAttribute({
                        componentDatatype: Freedo.ComponentDatatype.DOUBLE,
                        componentsPerAttribute: 3,
                        values: positions
                    }),
                    st: new Freedo.GeometryAttribute({
                        componentDatatype: Freedo.ComponentDatatype.FLOAT,
                        componentsPerAttribute: 2,
                        values: texCoords
                    })
                },
                primitiveType: Freedo.PrimitiveType.TRIANGLE_FAN,
                boundingSphere: Freedo.BoundingSphere.fromVertices(positions)
            });
            geometry.layerName = 'bottom';
            geometries.push(geometry);
        }
    }

    /**
     *
     * @private
     * @param {Freedo.Cartesian3[]} cartographics
     * @param {refRadius} tileS
     *
     */
    getSimulateDrills(cartographics, refRadius) {
        var drills = [];
        if (this._drillDataArray.length > 0) {
            var scope = this;
            for (var i = 0, n = cartographics.length; i < n; i++) {
                var drill = getSimulateDrill(scope, cartographics[i], refRadius);
                if (Freedo.defined(drill)) {
                    drills.push({
                        pos: cartographics[i],
                        waterDepth: drill.waterDepth,
                        drillInfo: drill.drillsInfo,
                        layers: drill.layers
                    });
                }
            }
        }

        return drills;
    };

    getRelativeDrillsInfo(drills, cartographics) {
        var drillsInfo = [];
        for (var i = 0; i < cartographics.length - 1; i++) {
            var cartographic = cartographics[i];
            var index = -1,
                minDistance = Number.MAX_VALUE;
            for (var j = 0; j < drills.length; j++) {
                var x = cartographic.longitude - drills[j].pos.longitude;
                var y = cartographic.latitude - drills[j].pos.latitude;

                var dis2 = x * x + y * y;

                if (minDistance > dis2) {
                    index = j;
                    minDistance = dis2;
                }
            }

            var drillInfo = {
                drillsInfo: drills[index].drillInfo,
                pos: drills[index].pos
            }
            drillsInfo.push(drillInfo);
        }

        return drillsInfo;
    }

    /**
     * 构建地质模拟的侧面及底面并加入场景
     * @param {Freedo.Cartographic[]} cartographics  输入的需要进行插值的原始三维坐标（经纬度坐标）数组。
     * @param {Number} [maxStep]       插值的最大步长 ，默认为1。
     * @param {Number} [tileS]       水平方向分多少块，默认为5。
     * @param {Number} [tileT]       竖直方向分多少块，默认为5。
     * @param {Boolean} [flipUp]      地质层显示是否到地上。
     * @param {Number} [buildBottom]  是否构建底部，默认为true。
     *
     * @example
     * var points = [
     *     new Freedo.Cartographic(2.0303325298072714, 0.4873716058277563, 52.69139335005104),
     *     new Freedo.Cartographic(2.030319963427302, 0.48738285295499395, 55.34687523086545),
     *     new Freedo.Cartographic(2.03028628513872, 0.4873924498939492, 44.66087555229469),
     *     new Freedo.Cartographic(2.03026872726754, 0.4873898657279729, 44.236593776935734),
     *     new Freedo.Cartographic(2.030262094002657, 0.4873739129711501, 45.97863005368587),
     *     new Freedo.Cartographic(2.030274276720867, 0.4873593189830798, 47.8334224833019),
     *     new Freedo.Cartographic(2.0303002959620713, 0.4873574933398821, 50.549889179969426),
     *     new Freedo.Cartographic(2.0303325298072714, 0.4873716058277563, 52.69139335005104)
     * ];
     * geologySimulationManager.addGeologySimulation(points, 1, 5, 5, undefined, true);
     * 
     */
    addGeologySimulation(cartographics, maxStep, tileS, tileT, flipUp, buildBottom) {
        if (cartographics.length < 2) {
            throw new Freedo.RuntimeError('The input points array is invalid !');
        }

        maxStep = Freedo.defaultValue(maxStep, 1);
        tileS = Freedo.defaultValue(tileS, 5);
        tileT = Freedo.defaultValue(tileT, 5);
        flipUp = Freedo.defaultValue(flipUp, false);
        buildBottom = Freedo.defaultValue(buildBottom, true);

        if (cartographics.length > 2) {
            var start = cartographics[0];
            cartographics.push(start); //连接收尾
        }

        this._drillOptions['Drill' + (new Date().getTime())] = {
            points: cartographics,
            maxStep: maxStep,
            tileS: tileS,
            tileT: tileT,
            flipUp: flipUp,
            buildBottom: buildBottom
        };

        var scene = this._scene;
        scene.globe.depthTestAgainstTerrain = true;
        scene.screenSpaceCameraController.enableCollisionDetection = false;

        var terrainProvider = scene.terrainProvider;
        var scope = this;
        this.interpolateCartographics(cartographics, maxStep, terrainProvider).then(function(positions) {
            //清除地面
            var cartesians = [];
            for (var i = 0; i < positions.length - 1; i++) {
                cartesians.push(Freedo.Cartesian3.fromRadians(positions[i].longitude, positions[i].latitude, positions[i].height));
            }

            var addedObject = {
                pints: cartographics
            };
            if (!flipUp) {
                var erasePrimitive = new Freedo.GroundErasePrimitive({
                    geometryInstances: new Freedo.GeometryInstance({
                        geometry: new Freedo.PolygonGeometry({
                            polygonHierarchy: new Freedo.PolygonHierarchy(cartesians)
                        }),
                        attributes: {
                            color: Freedo.ColorGeometryInstanceAttribute.fromColor(new Freedo.Color(0.0, 0.0, 0.0, 1.0))
                        }
                    }),
                    classificationType: Freedo.ClassificationType.CESIUM_3D_TILE,
                });
                addedObject.erasePritive = erasePrimitive;
                scope._groundErasers.push(scene.primitives.add(erasePrimitive));
            }

            //构造地质层
            var drills = scope.getSimulateDrills(positions, 100);
            if (drills.length > 0) {
                var drillsInfo = scope.getRelativeDrillsInfo(drills, cartographics);
                var geologyPrimitives = scope.addGeologyPrimitives(drills, tileS, tileT, flipUp, buildBottom, drillsInfo);
                scope._geologyPrimitives = scope._geologyPrimitives.concat(geologyPrimitives);
                addedObject.geologyPrimitives = geologyPrimitives;
            }

            scope._event.raiseEvent('dataAdded', addedObject);

            scope._scene.requestRender();
        }, function(error) {
            throw new Freedo.RuntimeError('TerrainProvider maybe invalidate. Check if there is a terrain in scene !');
        });
    }

    /**
     * 选择地质模拟的底面或者侧面并高亮（TODO，该函数不完善，需要后续改进）
     * @param {Freedo.Cartesian2} position 屏幕坐标点
     * 
     */
    pickGeologySimulations(position) {
        if (Freedo.defined(this._lastHeightPrimitive)) {
            this._lastHeightPrimitive.appearance.material.uniforms.value = [0.0, 0.0, 0.0, 1.0];
            this._lastHeightPrimitive = undefined;
        }
        var oldValue = this._scene.globe.depthTestAgainstTerrain;
        this._scene.globe.depthTestAgainstTerrain = true;
        var feature = scene.pick(position);

        var pickedOjbect = undefined;
        if (Freedo.defined(feature) && Freedo.defined(feature.primitive)) {

            feature.primitive.appearance.material.uniforms.value = [0.8, 0.6, 0.6, 0.9];
            this._lastHeightPrimitive = feature.primitive;

            pickedOjbect = {
                layerName: feature.primitive.layerName,
                depth: feature.primitive.depth,
                primitive: feature.primitive,
                drillsInfo: feature.primitive.drillsInfo,
                offsetMatrix: feature.primitive.offsetMatrix
            }
        }
        this._scene.globe.depthTestAgainstTerrain = oldValue;

        this._scene.requestRender();

        return pickedOjbect;
    }

    /**
     * 清除所有已经构建的地质模拟对象
     *
     * @example
     * geologySimulationManager.removeAllGeologySimulations() ;
     */
    removeAllGeologySimulations() {
        this._lastHeightPrimitive = undefined;

        var scene = this._scene;
        for (var i = this._groundErasers.length - 1; i >= 0; i--) {
            scene.primitives.remove(this._groundErasers[i]);
        }
        this._groundErasers.splice(0);
        for (var i = this._geologyPrimitives.length - 1; i >= 0; i--) {
            scene.primitives.remove(this._geologyPrimitives[i]);
        }
        this._geologyPrimitives.splice(0);

        this._event.raiseEvent('dataRemoved', {});

        scene.requestRender();
    }

    /**
     * 清除所有
     *
     */
    dispose() {
        removeAllGeologySimulations();
        this._layerTextureMap.clear();
        this._drillDataArray = [];

        this._eventHelper.removeAll();
    }
}

export {
    FdGeologySimulationManager
};