class FdCommon {

}

/**
 * 公用的调用ajax的请求方法
 * @param {Object} options 传递参数对象
 * @param {String} options.type 请求的类型 仅支持post和get两个方式
 * @param {String} options.dataType 返回值类型 仅支持json和XML两种返回结果
 * @param {Boolean} options.async 是否是异步请求 true同意异步 false拒绝异步
 * @param {Object} options.data 向后台传递返回的信息对象
 */
FdCommon.fdAjax = function(options) {
    options = options || {};
    options.type = (options.type || 'GET').toUpperCase();
    options.dataType = options.dataType || 'json';
    options.async = options.async || true;
    var params = '';
    if (options.data) {
        params = JSON.stringify(options.data);
    }
    if (window.XMLHttpRequest) {
        var xhr = new XMLHttpRequest();
    } else {
        //IE6及其以下版本浏览器
        var xhr = new ActiveXObject('Microsoft.XMLHTTP');
    }

    xhr.onreadystatechange = function() {
        if (xhr.readyState === 4) {
            var status = xhr.status;
            if (status >= 200 && status < 300) {
                options.success && options.success(xhr.responseText, xhr.responseXML);
            } else {
                options.fail && options.fail(xhr.responseText, xhr.responseXML);
            }
        }
    }
    if (options.type === 'GET') {
        xhr.open('GET', options.url + params, options.async);
        xhr.send(null);
    } else if (options.type === 'POST') {
        xhr.open('POST', options.url, options.async);
        //设置表单提交时的内容类型
        // xhr.setRequestHeader('Content-Type', 'application/json');
        xhr.setRequestHeader('Content-type', 'application/json; charset=utf-8');
        if (options.token !== undefined) {
            xhr.setRequestHeader('Authorization', options.token);
        }
        xhr.send(params);
    }
}

/**
 * 封装的异步请求
 * @private 
 */
FdCommon.ajaxJson = async function(opt) {
    let responseType = opt.dataType || 'json'
    return new Promise((resolve, reject) => {
        FdCommon.fdAjax({
            url: opt.url, //请求地址
            type: opt.type || 'POST', //请求方式
            data: opt.data || null, //请求参数
            dataType: opt.dataType || 'json',
            success: (response, xml) => {
                if (responseType === 'json') {
                    if (response) {
                        let res = JSON.parse(response);
                        resolve(res);
                    } else {
                        reject(response);
                    }
                } else {
                    resolve(xml);
                }
            },
            fail: (response, xml) => {
                let res = JSON.parse(response);
                reject(res);
            }
        });
    });
}

export {
    FdCommon
}