import { FdCommon } from './FdCommon';

/**
 * PMTS服务数据解析
 * 
 * PMTS服务提供的服务包括PModel数据服务、结构树信息服务、部件属性信息服务等在内的一整套数据服务的集成。
 * PMTS的结构是这样的，PMTS的描述文件中会包含多个图层信息(Layer)，每个图层包含多个MatrixSet，
 * 每一个MatrixSet包含多个Matrix。Matrix则对应一个PModel数据服务。
 * 
 * 示例代码如下：
 * ```
 * // jsonPMTSCapabilities是一个PMTS配置文件，json对象。可以通过该对象拿到所有的图层信息(Layer)
 * jsonPMTSCapabilities.contents.layers.forEach(layer => {
 *     // 通过图层获取所有的MatrixSet对象
 *     let matrixSets = Freedo.FdServer.FdPMTSParser.getMatrixSets(jsonPMTSCapabilities, layer);
 *     matrixSets.forEach(matrixSet => {
 *         // 通过MatrixSet对象获取其所包含的Matrix对象
 *         matrixSet.matrix.forEach(matrix => {
 *             // 通过Matrix对象获取相对应的PModel数据服务的URL
 *             let pModelURL = Freedo.FdServer.FdPMTSParser.getPModelURL(layer,
 *                 matrixSet, matrix, token);
 * 
 *             // 通过pModelURL来加载PModel几何数据到三维场景中去
 *             var pModel = viewer.scene.primitives.add(new Freedo.FreedoPModelset({
 *                 url: pModelURL
 *             }));
 * 
 *             // 此处是将三维场景中创建的pModel数据和所对应的Matrix等关联起来
 *             pModelInfos.push({
 *                 pModel,
 *                 matrix,
 *                 matrixSet,
 *                 layer
 *             });
 * 
 *             // PModel数据加载完成以后，将相机定位到PModel几何数据所在位置
 *             pModel.readyPromise.then(function (pModel) {
 *                 viewer.camera.viewBoundingSphere(pModel.boundingSphere,
 *                     new Freedo.HeadingPitchRange(
 *                         0, -0.5, 0));
 *             });
 *         })
 * 
 *         // matrixSet.uid是MatrixSet的跟节点，相当于最上层的部件ID
 *         // 通过该部件ID，可以获取该部件的节点信息
 *         // 节点信息包含包含该节点的ID、名称、子节点ID等信息
 *         Freedo.FdServer.FdPMTSParser.getUidJson(layer, matrixSet, matrix, matrixSet.uid,
 *             token,
 *             (json, error) => {
 *                 console.log('--------------uid-------------');
 *                 if (typeof error === 'undefined') {
 *                     console.log(json);
 *                 } else {
 *                     console.log('error: ' + error);
 *                 }
 *                 console.log(' ');
 *             });
 * 
 *         // 通过该部件ID，可以获取该部件的子节点信息
 *         // 部件的子节点信息，是一个包含多个子节点信息的数组对象
 *         Freedo.FdServer.FdPMTSParser.getCidJson(layer, matrixSet, matrix, matrixSet.uid,
 *             token,
 *             (json, error) => {
 *                 console.log('--------------cid-------------');
 *                 if (typeof error === 'undefined') {
 *                     console.log(json);
 *                 } else {
 *                     console.log('error: ' + error);
 *                 }
 *                 console.log(' ');
 *             });
 * 
 *         // 通过部件ID，获取部件的属性信息
 *         // 部件的属性信息包含BoundsMin、BoundsMax，以及部件的非几何属性信息
 *         Freedo.FdServer.FdPMTSParser.getPropJson(layer, matrixSet, matrix, matrixSet.uid,
 *             token,
 *             (json, error) => {
 *                 console.log('--------------prop-------------');
 *                 if (typeof error === 'undefined') {
 *                     console.log(json);
 *                 } else {
 *                     console.log('error: ' + error);
 *                 }
 *                 console.log(' ');
 *             });
 *     })
 * })
 * ```
 * 
 * @alias FdServer.FdPMTSParser
 * @namespace
 */
class FdPMTSParser {
    constructor() {
        throw new Error('FdPMTSParser should not be instantiated!');
    }
}

// file uid cid prop
let _getResourceTemplateURL = function(layer, matrixSet, resourceType) {
    if (layer.resourceurl) {
        return layer.resourceurl.find((element) => {
            return element.type === resourceType;
        }).template;
    }
    if (matrixSet.resourceurl) {
        return matrixSet.resourceurl.find((element) => {
            return element.type === resourceType;
        }).template;
    }
}

function _getUidQueryStr(layer, matrixSet, matrix, UID, token) {
    let matrixSetIdentifier = matrixSet.identifier;
    let matrixIdentifier = matrix.identifier;
    let template = _getResourceTemplateURL(layer, matrixSet, 'uid');
    template = template.replace(/{matrixset}/g, matrixSetIdentifier);
    template = template.replace(/{matrix}/g, matrixIdentifier);
    template = template.replace(/{uid}/g, UID);
    if (typeof token !== 'undefined') {
        template = template + `?token=${token}`;
    }

    return template;
}

function _getCidQueryStr(layer, matrixSet, matrix, tileno, CID, token) {
    let matrixSetIdentifier = matrixSet.identifier;
    let matrixIdentifier = matrix.identifier;
    let template = _getResourceTemplateURL(layer, matrixSet, 'cid');
    if (!matrixSet.propstoragetype || matrixSet.propstoragetype === 1) {
        template = template.replace(/{matrixset}/g, matrixSetIdentifier);
        template = template.replace(/{matrix}/g, matrixIdentifier);
        template = template.replace(/{cid}/g, CID);
    }
    if (matrixSet.propstoragetype === 2) {
        template = template.replace(/{tileno}/g, tileno);
        template = template.replace(/{pguid}/g, CID);
    }
    if (typeof token !== 'undefined') {
        template = template + `?token=${token}`;
    }

    return template;
}

function _getPropQueryStr(layer, matrixSet, matrix, prop, token) {
    let matrixSetIdentifier = matrixSet.identifier;
    let matrixIdentifier = matrix.identifier;
    let template = _getResourceTemplateURL(layer, matrixSet, 'prop');
    if (!matrixSet.propstoragetype || matrixSet.propstoragetype === 1) {
        template = template.replace(/{matrixset}/g, matrixSetIdentifier);
        template = template.replace(/{matrix}/g, matrixIdentifier);
        template = template.replace(/{comid}/g, prop);
    }
    if (matrixSet.propstoragetype === 2) {
        template = template.replace(/{guid}/g, prop);
    }
    if (typeof token !== 'undefined') {
        template = template + `?token=${token}`;
    }

    return template;
}

function _getTagQueryStr(layer, matrixSet, matrix, tag, token) {
    let matrixSetIdentifier = matrixSet.identifier;
    let matrixIdentifier = matrix.identifier;
    let template = _getResourceTemplateURL(layer, matrixSet, 'tag');
    template = template.replace(/{matrixset}/g, matrixSetIdentifier);
    template = template.replace(/{matrix}/g, matrixIdentifier);
    template = template.replace(/{tag}/g, tag);
    if (typeof token !== 'undefined') {
        template = template + `?token=${token}`;
    }

    return template;
}


/**
 * 根据ID获取Layer对象
 * @param {Object} jsonPMTSCapabilities PMTS配置信息，JSON对象
 * @param {String} layerIdentifier Layer的ID
 * @returns {Object} Layer对象
 */
FdPMTSParser.getLayerFromID = function(jsonPMTSCapabilities, layerIdentifier) {
    return jsonPMTSCapabilities.contents.layers.find((element) => {
        return element.identifier === layerIdentifier;
    })
}

/**
 * 根据ID获取MatrixSet对象
 * @param {Object} jsonPMTSCapabilities PMTS配置信息，JSON对象
 * @param {String} matrixSetIdentifier MatrixSet的ID
 * @returns {Object} MatrixSet对象
 */
FdPMTSParser.getMatrixSetFromID = function(jsonPMTSCapabilities, matrixSetIdentifier) {
    return jsonPMTSCapabilities.contents.matrixsets.find((element) => {
        return element.identifier === matrixSetIdentifier;
    })
}

/**
 * 根据ID获取Matrix对象
 * @param {Object} matrixSet MatrixSet对象，JSON对象
 * @param {String} matrixIdentifier Matrix的ID
 * @returns {Object} Matrix对象
 */
FdPMTSParser.getMatrixFromID = function(matrixSet, matrixIdentifier) {
    return matrixsets.find((element) => {
        return element.identifier === matrixIdentifier;
    })
}

/**
 * 获取Layer对象所属的所有MatrixSet
 * @param {Object} jsonPMTSCapabilities PMTS配置信息，JSON对象
 * @param {Object} layer Layer对象
 * @returns {Object[]} MatrixSet对象数组 
 */
FdPMTSParser.getMatrixSets = function(jsonPMTSCapabilities, layer) {
    let matrixSets = [];
    layer.matrixsetlink.forEach((matrixsetlink) => {
        let matrixSet = FdPMTSParser.getMatrixSetFromID(jsonPMTSCapabilities, matrixsetlink);
        if (typeof matrixSet === 'undefined') {
            throw new Error('');
        }

        matrixSets.push(matrixSet);
    });

    return matrixSets;
}

/**
 * 获得加载PModel数据的URL
 * @param {Object} layer Layer对象
 * @param {Object} matrixSet MatrixSet对象
 * @param {Object} matrix Matrix对象
 * @param {String} token 用户认证token，如果没有token，可以设置为undefined
 * @returns {String} 加载PModel数据的URL
 */
FdPMTSParser.getPModelURL = function(layer, matrixSet, matrix, token) {
    let matrixSetIdentifier = matrixSet.identifier;
    let matrixIdentifier = matrix.identifier;
    let template = _getResourceTemplateURL(layer, matrixSet, 'file');
    template = template.replace(/{matrixset}/g, matrixSetIdentifier);
    template = template.replace(/{matrix}/g, matrixIdentifier);
    template = template.replace(/{filepath}/g, matrixIdentifier);
    if (typeof token !== 'undefined') {
        template = template + `?token=${token}`;
    }

    return template;
}

/**
 * 处理整理获取到的
 * @param {Object} jsonPMTSCapabilities
 * @private 
 */
function printJsonPMTSCapabilitiesInfo(jsonPMTSCapabilities, token) {
    let pModelInfos = [];
    jsonPMTSCapabilities.contents.layers.forEach(layer => {
        let matrixSets = Freedo.FdServer.FdPMTSParser.getMatrixSets(jsonPMTSCapabilities, layer);
        matrixSets.forEach(matrixSet => {
            matrixSet.matrix.forEach(matrix => {
                let pModelURL = Freedo.FdServer.FdPMTSParser.getPModelURL(layer,
                    matrixSet, matrix, token);
                pModelInfos.push({
                    pModelURL: pModelURL,
                    matrix: matrix,
                    matrixSet: matrixSet,
                    layer: layer
                });
            })
        })
    })
    return pModelInfos;
}

/**
 * 获取PMTS的模型添加地址列表
 * @param {String} url PMTS地址，带有token或者accesskey
 * @param {FdServer.FdPMTSParser~Callback} callback 获取部件的子节点信息的回调函数
 */
FdPMTSParser.getPModelURLArray = async function(url, callback) {
    let queryString = url.split('?')[1];
    var endUserOptions = Freedo.queryToObject(queryString);
    if (endUserOptions.accesskey) {
        Freedo.FdServer.FdShareServer.getLayerAccesskeyResource(url, 'json', (jsonPMTSCapabilities, error) => {
            if (error) {
                console.error('FdServer.FdShareServer.getLayerAccesskeyResource got error: ' + error);
                return;
            }
            let result = printJsonPMTSCapabilitiesInfo(jsonPMTSCapabilities);
            callback(result);
        });
    } else if (endUserOptions.token) {
        Freedo.FdServer.FdPMTSServer.getResource(url.split('?')[0], endUserOptions.token, (jsonPMTSCapabilities, error) => {
            if (error) {
                console.error('FdPMTSServer.getJson got error: ' + error);
                return;
            }
            let result = printJsonPMTSCapabilitiesInfo(jsonPMTSCapabilities, endUserOptions.token);
            callback(result);
        });
    } else {
        callback([])
    }
}


async function _getJson(url, callback) {
    try {
        let response = await FdCommon.ajaxJson({
            url: url,
            type: 'GET'
        })
        callback(response);
    } catch (error) {
        callback(undefined, error);
    }
}

/**
 * 获取部件的节点信息
 * @param {Object} layer Layer对象
 * @param {Object} matrixSet MatrixSet对象
 * @param {Object} matrix Matrix对象
 * @param {String} uid 部件ID
 * @param {String} token 用户认证token，如果没有token，可以设置为undefined
 * @param {FdServer.FdPMTSParser~Callback} callback 获取部件的节点信息的回调函数
 * 
 * @example
 *         Freedo.FdServer.FdPMTSParser.getUidJson(layer, matrixSet, matrixSet.uid,
 *             token,
 *             (json, error) => {
 *                 console.log('--------------uid-------------');
 *                 if (typeof error === 'undefined') {
 *                     console.log(json);
 *                 } else {
 *                     console.log('error: ' + error);
 *                 }
 *                 console.log(' ');
 *             });
 */
FdPMTSParser.getUidJson = function(layer, matrixSet, matrix, uid, token, callback) {
    let uidQueryStr = _getUidQueryStr(layer, matrixSet, matrix, uid, token);
    // Freedo.FdServer.FdPMTSServer.getJson(uidQueryStr, (json, error) => {
    //     callback(json, error);
    // });
    _getJson(uidQueryStr, (json, error) => {
        callback(json, error);
    });
}

/**
 * 获取部件的子节点信息
 * @param {Object} layer Layer对象
 * @param {Object} matrixSet MatrixSet对象
 * @param {Object} matrix Matrix对象
 * @param {String} uid 部件ID
 * @param {String} token 用户认证token，如果没有token，可以设置为undefined
 * @param {FdServer.FdPMTSParser~Callback} callback 获取部件的子节点信息的回调函数
 * @example
 *         Freedo.FdServer.FdPMTSParser.getCidJson(layer, matrixSet, matrixSet.uid,
 *             token,
 *             (json, error) => {
 *                 console.log('--------------cid-------------');
 *                 if (typeof error === 'undefined') {
 *                     console.log(json);
 *                 } else {
 *                     console.log('error: ' + error);
 *                 }
 *                 console.log(' ');
 *             });
 */
FdPMTSParser.getCidJson = function(layer, matrixSet, matrix, tileno, uid, token, callback) {
    let cidQueryStr = _getCidQueryStr(layer, matrixSet, matrix, tileno, uid, token);
    // Freedo.FdServer.FdPMTSServer.getJson(cidQueryStr, (json, error) => {
    //     callback(json, error);
    // });
    _getJson(cidQueryStr, (json, error) => {
        callback(json, error);
    });
}

/**
 * 获取部件的属性信息
 * @param {Object} layer Layer对象
 * @param {Object} matrixSet MatrixSet对象
 * @param {Object} matrix Matrix对象
 * @param {String} uid 部件ID
 * @param {String} token 用户认证token，如果没有token，可以设置为undefined
 * @param {FdServer.FdPMTSParser~Callback} callback 获取部件的属性信息的回调函数
 * @example
 *         Freedo.FdServer.FdPMTSParser.getPropJson(layer, matrixSet, matrixSet.uid,
 *             token,
 *             (json, error) => {
 *                 console.log('--------------prop-------------');
 *                 if (typeof error === 'undefined') {
 *                     console.log(json);
 *                 } else {
 *                     console.log('error: ' + error);
 *                 }
 *                 console.log(' ');
 *             });
 */
FdPMTSParser.getPropJson = function(layer, matrixSet, matrix, uid, token, callback) {
    let propQueryStr = _getPropQueryStr(layer, matrixSet, matrix, uid, token);
    _getJson(propQueryStr, (json, error) => {
        callback(json, error);
    });
}

/**
 * 获取部件的属性信息
 * @param {Object} layer Layer对象
 * @param {Object} matrixSet MatrixSet对象
 * @param {Object} matrix Matrix对象
 * @param {String} uid 部件ID
 * @param {String} token 用户认证token，如果没有token，可以设置为undefined
 * @param {FdServer.FdPMTSParser~Callback} callback 获取部件的属性信息的回调函数
 * @example
 *         Freedo.FdServer.FdPMTSParser.getTagJson(layer, matrixSet, matrixSet.uid,
 *             token,
 *             (json, error) => {
 *                 console.log('--------------prop-------------');
 *                 if (typeof error === 'undefined') {
 *                     console.log(json);
 *                 } else {
 *                     console.log('error: ' + error);
 *                 }
 *                 console.log(' ');
 *             });
 */
FdPMTSParser.getTagJson = function(layer, matrixSet, matrix, uid, token, callback) {
    let tagQueryStr = _getTagQueryStr(layer, matrixSet, matrix, uid, token);
    _getJson(tagQueryStr, (json, error) => {
        callback(json, error);
    });
}


function getPositionFromString(arrayStr) {
    var strArr = arrayStr.split(',');
    var result = [];
    result = strArr.map(function(data) {
        return +data;
    })

    return result;
}

function getBoundingSphereFromBoundsMinMax(boundsMin, boundsMax, position, modelMatrix) {
    var maxcorner = new Freedo.Cartesian3(boundsMin[0], boundsMin[1], boundsMin[2]);
    var mincorner = new Freedo.Cartesian3(boundsMax[0], boundsMax[1], boundsMax[2]);
    var pos = new Freedo.Cartesian3.fromDegrees(position[0], position[1], position[2]);

    if (Freedo.Cartesian3.magnitudeSquared(pos) > 6378137 * 6378137 * 0.00001) { // 球面模式下
        //局部坐标转全局坐标
        var local2world = FreeDo.Transforms.eastNorthUpToFixedFrame(pos);

        var t = mincorner.y;
        mincorner.y = -mincorner.z;
        mincorner.z = t;
        t = maxcorner.y;
        maxcorner.y = -maxcorner.z;
        maxcorner.z = t;
        Freedo.Matrix4.multiplyByPoint(local2world, mincorner, mincorner);
        Freedo.Matrix4.multiplyByPoint(local2world, maxcorner, maxcorner);
    } else { // 平面模式下
        Freedo.Cartesian3.add(pos, mincorner, mincorner);
        Freedo.Cartesian3.add(pos, maxcorner, maxcorner);
    }

    if (modelMatrix) {
        Freedo.Matrix4.multiplyByPoint(modelMatrix, mincorner, mincorner);
        Freedo.Matrix4.multiplyByPoint(modelMatrix, maxcorner, maxcorner);
    }

    var bs = FreeDo.BoundingSphere.fromCornerPoints(mincorner, maxcorner);
    return bs;
}

/**
 * 获取Matrix的包围球信息
 * @param {Object} matrix Matrix对象
 * @param {Freedo.Matrix4} offsetModelMatrix 模型偏移矩阵（如果三维窗口中展示的模型做了偏移，则需要在此处传入偏移矩阵，才能让包围球的信息准确）
 * @returns {FreeDo.BoundingSphere} Matrix的包围球信息
 */
FdPMTSParser.getMatrixBoundingSphere = function(matrix, offsetModelMatrix) {
    let bmin = getPositionFromString(matrix.metadata.localbounds.min);
    let bmax = getPositionFromString(matrix.metadata.localbounds.max);
    let position = getPositionFromString(matrix.metadata.position);
    let temp = position[0];
    position[0] = position[1];
    position[1] = temp;
    let bs = getBoundingSphereFromBoundsMinMax(bmin, bmax, position, offsetModelMatrix);

    return bs;
}

/**
 * 获取Layer的包围球信息
 * @param {Object} jsonPMTSCapabilities PMTS配置信息，JSON对象
 * @param {Object} layer Layer对象
 * @param {Freedo.Matrix4} offsetModelMatrix 模型偏移矩阵（如果三维窗口中展示的模型做了偏移，则需要在此处传入偏移矩阵，才能让包围球的信息准确）
 * @returns {FreeDo.BoundingSphere} Matrix的包围球信息
 */
FdPMTSParser.getLayerBoundingSphere = function(jsonPMTSCapabilities, layer, offsetModelMatrix) {
    let matrixSets = Freedo.FdServer.FdPMTSParser.getMatrixSets(jsonPMTSCapabilities, layer);
    matrixSets.forEach(matrixSet => {
        return getMatrixBoundingSphere(matrix, offsetModelMatrix);
    });
}

/**
 * 获取部件的包围球信息
 * @param {Object} layer Layer对象
 * @param {Object} matrixSet MatrixSet对象
 * @param {Object} matrix Matrix对象
 * @param {String} uid 部件ID
 * @param {String} token 用户认证token
 * @param {Freedo.Matrix4} offsetModelMatrix 模型偏移矩阵
 * @param {FdServer.FdPMTSParser~Callback} callback 获取部件包围球信息的回调函数
 */
FdPMTSParser.getUidBoundingSphere = function(layer, matrixSet, matrix, uid, token, offsetModelMatrix, callback) {
    if (matrix.uid === uid) { // 根节点的uid获取不到boundingbox，可以从matrix中获取
        let bs = FdPMTSParser.getMatrixBoundingSphere(matrix, offsetModelMatrix);
        callback(bs);
    } else {
        FdPMTSParser.getUidJson(layer, matrixSet, matrix, uid, token, (uidJson, error) => {
            if (typeof error !== 'undefined') {
                callback(undefined, error);
                return;
            }

            if (uidJson && uidJson.BoundsMin && uidJson.BoundsMax) {
                let bmin = getPositionFromString(uidJson.BoundsMin);
                let bmax = getPositionFromString(uidJson.BoundsMax);
                let position = getPositionFromString(matrix.metadata.position);
                let temp = position[0];
                position[0] = position[1];
                position[1] = temp;
                let bs = getBoundingSphereFromBoundsMinMax(bmin, bmax, position, offsetModelMatrix);
                callback(bs);
            } else {
                // do nothing.
                callback(undefined, 'getUidBoundingSphere got error!');
            }
        });
    }
}

/**
 * FdPMTSParser的回调函数类型
 * @callback FdServer.FdPMTSParser~Callback
 *
 * @param {Object} [result] 返回相应的对象，可能是MatrixSet、Matrix等，如果出现错误，则此参数为undefined
 * @param {String} [error] 返回错误字符串，如果是undefined，则表示没有错误
 */

export { FdPMTSParser };