import { FdKeyboard } from '../FdTools/FdKeyboard';

var cameraCurrent = undefined;
var kbCamera = undefined;
var kbCameraCB = undefined;
var kbV = 10;

function enableKeyboard(viewer, enable) {
    cameraCurrent = viewer.camera;
    if(kbCamera){
        kbCameraCB();
        kbCamera = undefined;
    }
        
    kbCamera = new FdKeyboard(viewer);
    kbCameraCB = kbCamera.on(function(eventType, attach) {
        if(eventType === "timer") {
            //左方向或者
            if(attach.metaData[37]) {
                Freedo.FdCamera.lookLeft(cameraCurrent, 5);
            }
            //右方向
            if(attach.metaData[39]) {
                Freedo.FdCamera.lookRight(cameraCurrent, 5);
            }
            //左方向或者
            if(attach.metaData[38]) {
                cameraCurrent.lookUp(5 / 180 * Freedo.Math.PI);
            }
            //右方向
            if(attach.metaData[40]) {
                cameraCurrent.lookDown(5 / 180 * Freedo.Math.PI);
            }
            //左方向或者A键
            if(attach.metaData[65]) {
                cameraCurrent.moveLeft(kbV / 33);
            }
            //右方向或者D键
            if(attach.metaData[68]) {
                cameraCurrent.moveRight(kbV / 33);
            }
            //右方向或者W键
            if(attach.metaData[87]) {
                cameraCurrent.moveForward(kbV / 33);
            }
            //右方向或者S键
            if(attach.metaData[83]) {
                cameraCurrent.moveBackward(kbV / 33);
            }
        }
    });
    if(enable) {
        kbCamera.enable();
    } else {
        kbCamera.disable();
    }
}

function setKeyboardV(v) {
    kbV = v;
}

export {
    enableKeyboard,
    setKeyboardV
};