import {
    FdMath
} from './FdMath';

/**
 * 几何类
 * @class
 * @alias FdMisc.FdGeometric
 */
class FdGeometric {
    constructor() {

    }
}
/**
 * 获取多段线的长度
 * @param {Cartesian3[]} positions 点组
 * @returns {Number} 长度，单位米
 */
FdGeometric.calculateLength = function(positions) {
    var length = 0;
    for(var i = 1; i < positions.length; ++i) {
        length += Freedo.Cartesian3.distance(positions[i - 1], positions[i]);
    }
    return length;
}

FdGeometric.getLengthText = function(length) {
    return length > 1000 ? (length / 1000).toFixed(2) + '千米' : length.toFixed(2) + '米'
}

/**
 * 获取两个点的中心点
 * @param {Cartesian3} p1 起点
 * @param {Cartesian3} p1 终点
 * @returns {Cartesian3} 返回中点
 */
FdGeometric.getCenter = function(p1, p2) {
    var p = new Freedo.Cartesian3();
    Freedo.Cartesian3.add(p1, p2, p);
    Freedo.Cartesian3.multiplyByScalar(p, 0.5, p);
    return p;
}

/**
 * 获取指定位置的经纬度换算比例
 * @param {Cartographic} cartPos 指定点
 * @returns {Number[]} 返回数组，第一个值表示经度比例，第二个值表示纬度比例，乘以米数即可得到对应的经纬度值
 */
FdGeometric.getLongAndLatRatio = function(cartPos) {
    var newCartPos = cartPos.clone();
    newCartPos.latitude += 0.0000001;

    var egLong = new FreeDo.EllipsoidGeodesic();
    var endLongPos = newCartPos.clone();
    endLongPos.longitude += 0.001;
    egLong.setEndPoints(newCartPos, endLongPos);
    var longRatio = 0.001 / egLong.surfaceDistance;

    var egLat = new FreeDo.EllipsoidGeodesic();
    var endLatPos = newCartPos.clone();
    endLatPos.latitude += 0.001;
    egLat.setEndPoints(newCartPos, endLatPos);
    var latRatio = 0.001 / egLat.surfaceDistance;

    return [longRatio, latRatio];
}

/**
 * 获取鼠标位置的世界坐标
 * @param {Viewer} viewer Viewer
 * @param {Object} windowPosition 鼠标屏幕坐标信息
 * @returns {Cartesian3} 返回世界坐标
 */
FdGeometric.pickPosition = function(viewer, windowPosition) {
    var cartesian = undefined;

    var viewer = viewer;
    var scene = viewer.scene;
    if(scene.mode !== Freedo.SceneMode.MORPHING) {
        var pickedObject = scene.pick(windowPosition);
        if(scene.pickPositionSupported && Freedo.defined(pickedObject)) {
            cartesian = viewer.scene.pickPosition(windowPosition);
            if(cartesian)
                cartesian.type = 0;
        } else {
            if(viewer.terrainProvider instanceof Freedo.EllipsoidTerrainProvider)
                cartesian = viewer.camera.pickEllipsoid(windowPosition, scene.globe.ellipsoid);
            else {
                var ray = scene.camera.getPickRay(windowPosition);
                if(scene.globe)
                    cartesian = scene.globe.pick(ray, scene);
            }
            if(cartesian)
                cartesian.type = 1;
        }
    }

    return cartesian;
}

/**
 * 获取鼠标位置的世界坐标
 * @param {Viewer} viewer Viewer
 * @param {Object} windowPosition 鼠标位置的屏幕坐标信息
 * @returns {Number[]} 返回世界坐标[经度,纬度,高度]，经纬度是以度为单位，高度以米为单位
 */
FdGeometric.pickPositionG = function(viewer, windowPosition) {
    var worldPosition = undefined;

    var cartesian = this.pickPosition(viewer, windowPosition);
    if(cartesian) {
        worldPosition = [];
        var carto;
        if(viewer.scene.globe){
            carto = Freedo.Cartographic.fromCartesian(cartesian, viewer.scene.globe.ellipsoid);
            var td = Freedo.Math.toDegrees;
            worldPosition[0] = td(carto.longitude);
            worldPosition[1] = td(carto.latitude);
            worldPosition[2] = carto.height;
        }
        else{
            worldPosition[0] = cartesian.x;
            worldPosition[1] = cartesian.y;
            worldPosition[2] = cartesian.z;
        }
        worldPosition[3] = cartesian.type;
    }
    return worldPosition;
}

/**
 * 转换为世界坐标
 * @param {Array} position [经度,纬度,高度]，单位分别是度、度、米
 * @returns {Cartesian3} 返回Cartesian3格式的坐标
 */
FdGeometric.tc = function(position) {
    var tr = Freedo.Math.toRadians;
    return Freedo.Cartesian3.fromRadians(tr(position[0]), tr(position[1]), position[2]);
}

/**
 * Cartesian3类型的点转换为[经度,纬度,高度]格式
 * @param {Array} position Cartesian3格式的点
 * @returns {Number[]} [经度,纬度,高度]，单位分别是度、度、米
 */
FdGeometric.tg = function(position) {
    var worldPosition = [];
    var carto = Freedo.Cartographic.fromCartesian(position);
    var td = Freedo.Math.toDegrees;
    worldPosition[0] = td(carto.longitude);
    worldPosition[1] = td(carto.latitude);
    worldPosition[2] = carto.height;
    return worldPosition;
}

/*
 * 计算平面内点在矢量的哪边
 * @param {Object} StartXY 矢量起点
 * @param {Object} EndXY 矢量终点
 * @param {Object} MoveXY 需要计算的点
 * @returns 1表示在左边，-1表示在右边
 */
FdGeometric.leftOfLine = function(StartXY, EndXY, MoveXY) {
    var x1 = StartXY.x;
    var y1 = StartXY.y;
    var x2 = EndXY.x;
    var y2 = EndXY.y;
    var x3 = MoveXY.x;
    var y3 = MoveXY.y;
    var area = ((x1 - x3) * (y2 - y3) - (y1 - y3) * (x2 - x3)) / 2;
    if(area > 0)
        return 1;
    else
        return -1;
}

/*
 * 计算平面内两个矢量的夹角
 * @param {Object} xy1 第一个矢量的起点
 * @param {Object} xy2 第一个矢量的终点
 * @param {Object} xy3 第二个矢量的起点
 * @param {Object} xy4 第二个矢量的终点 
 * @returns 返回夹角值，单位是度
 */
FdGeometric.calcAngle = function(xy1, xy2, xy3, xy4) {
    var vv0 = [xy2.x - xy1.x, xy2.y - xy1.y];
    var vv1 = [xy4.x - xy3.x, xy4.y - xy3.y];
    var c = (vv0[0] * vv1[0] + vv0[1] * vv1[1]) / (Math.sqrt(Math.pow(vv0[0], 2) + Math.pow(vv0[1], 2)) * Math.sqrt(Math.pow(vv1[0], 2) + Math.pow(vv1[1], 2)))
    var angle = Freedo.Math.toDegrees(Math.acos(c));
    return angle * FdGeometric.leftOfLine(xy1, xy2, xy4);
}

/*
 * 计算多边形的面积
 * @param {Array} PointX 横坐标数组
 * @param {Array} PointY 纵坐标数组
 * @param {String} MapUnits 坐标的单位
 * @returns 面积，单位是平方米
 */
FdGeometric.calcArea = function(PointX, PointY, MapUnits) {
    var Count = PointX.length;
    if(Count > 2) {
        var mtotalArea = 0;

        if(MapUnits == 'DEGREES') //经纬度坐标下的球面多边形
        {
            var LowX = 0.0;
            var LowY = 0.0;
            var MiddleX = 0.0;
            var MiddleY = 0.0;
            var HighX = 0.0;
            var HighY = 0.0;

            var AM = 0.0;
            var BM = 0.0;
            var CM = 0.0;

            var AL = 0.0;
            var BL = 0.0;
            var CL = 0.0;

            var AH = 0.0;
            var BH = 0.0;
            var CH = 0.0;

            var CoefficientL = 0.0;
            var CoefficientH = 0.0;

            var ALtangent = 0.0;
            var BLtangent = 0.0;
            var CLtangent = 0.0;

            var AHtangent = 0.0;
            var BHtangent = 0.0;
            var CHtangent = 0.0;

            var ANormalLine = 0.0;
            var BNormalLine = 0.0;
            var CNormalLine = 0.0;

            var OrientationValue = 0.0;

            var AngleCos = 0.0;

            var Sum1 = 0.0;
            var Sum2 = 0.0;
            var Count2 = 0;
            var Count1 = 0;

            var Sum = 0.0;
            var Radius = 6378000;

            for(var i = 0; i < Count; i++) {
                if(i == 0) {
                    LowX = PointX[Count - 1] * Math.PI / 180;
                    LowY = PointY[Count - 1] * Math.PI / 180;
                    MiddleX = PointX[0] * Math.PI / 180;
                    MiddleY = PointY[0] * Math.PI / 180;
                    HighX = PointX[1] * Math.PI / 180;
                    HighY = PointY[1] * Math.PI / 180;
                } else if(i == Count - 1) {
                    LowX = PointX[Count - 2] * Math.PI / 180;
                    LowY = PointY[Count - 2] * Math.PI / 180;
                    MiddleX = PointX[Count - 1] * Math.PI / 180;
                    MiddleY = PointY[Count - 1] * Math.PI / 180;
                    HighX = PointX[0] * Math.PI / 180;
                    HighY = PointY[0] * Math.PI / 180;
                } else {
                    LowX = PointX[i - 1] * Math.PI / 180;
                    LowY = PointY[i - 1] * Math.PI / 180;
                    MiddleX = PointX[i] * Math.PI / 180;
                    MiddleY = PointY[i] * Math.PI / 180;
                    HighX = PointX[i + 1] * Math.PI / 180;
                    HighY = PointY[i + 1] * Math.PI / 180;
                }

                AM = Math.cos(MiddleY) * Math.cos(MiddleX);
                BM = Math.cos(MiddleY) * Math.sin(MiddleX);
                CM = Math.sin(MiddleY);
                AL = Math.cos(LowY) * Math.cos(LowX);
                BL = Math.cos(LowY) * Math.sin(LowX);
                CL = Math.sin(LowY);
                AH = Math.cos(HighY) * Math.cos(HighX);
                BH = Math.cos(HighY) * Math.sin(HighX);
                CH = Math.sin(HighY);

                CoefficientL = (AM * AM + BM * BM + CM * CM) / (AM * AL + BM * BL + CM * CL);
                CoefficientH = (AM * AM + BM * BM + CM * CM) / (AM * AH + BM * BH + CM * CH);

                ALtangent = CoefficientL * AL - AM;
                BLtangent = CoefficientL * BL - BM;
                CLtangent = CoefficientL * CL - CM;
                AHtangent = CoefficientH * AH - AM;
                BHtangent = CoefficientH * BH - BM;
                CHtangent = CoefficientH * CH - CM;

                AngleCos = (AHtangent * ALtangent + BHtangent * BLtangent + CHtangent * CLtangent) / (Math.sqrt(AHtangent * AHtangent + BHtangent * BHtangent + CHtangent * CHtangent) * Math.sqrt(ALtangent * ALtangent + BLtangent * BLtangent + CLtangent * CLtangent));

                AngleCos = Math.acos(AngleCos);

                ANormalLine = BHtangent * CLtangent - CHtangent * BLtangent;
                BNormalLine = 0 - (AHtangent * CLtangent - CHtangent * ALtangent);
                CNormalLine = AHtangent * BLtangent - BHtangent * ALtangent;

                if(AM != 0)
                    OrientationValue = ANormalLine / AM;
                else if(BM != 0)
                    OrientationValue = BNormalLine / BM;
                else
                    OrientationValue = CNormalLine / CM;

                if(OrientationValue > 0) {
                    Sum1 += AngleCos;
                    Count1++;

                } else {
                    Sum2 += AngleCos;
                    Count2++;
                }
            }

            if(Sum1 > Sum2) {
                Sum = Sum1 + (2 * Math.PI * Count2 - Sum2);
            } else {
                Sum = (2 * Math.PI * Count1 - Sum1) + Sum2;
            }

            //平方米
            mtotalArea = (Sum - (Count - 2) * Math.PI) * Radius * Radius;
        } else {
            //非经纬度坐标下的平面多边形
            var i, j;
            var p1x, p1y;
            var p2x, p2y;
            for(i = Count - 1, j = 0; j < Count; i = j, j++) {

                p1x = PointX[i];
                p1y = PointY[i];

                p2x = PointX[j];
                p2y = PointY[j];

                mtotalArea += p1x * p2y - p2x * p1y;
            }
            mtotalArea /= 2.0;
        }
        return mtotalArea;
    }
    return 0;
}

export {
    FdGeometric
};