function _td(radians) {
    return (typeof radians === 'undefined') ? undefined : Freedo.Math.toDegrees(radians);
}
/**
 * 获取相机位置和姿态信息
 *
 * @alias FdCamera.getCurrentCameraInfo
 *
 * @param {Freedo.Camera} camera 场景相机对象
 * @returns {Number[]} 返回一个数组，分别记录了相机的经纬度坐标位置和姿态（角度值），即[longitude,latitude,height,heading,pitch,roll]
 * 
 * @example
 * var cameraInfo = getCurrentCameraInfo(camera);
 * 
 * 
 */
function getCurrentCameraInfo(camera) {
    var pos = camera.positionCartographic;
    return [
        _td(pos.longitude),
        _td(pos.latitude),
        pos.height,
        _td(camera.heading),
        _td(camera.pitch),
        _td(camera.roll)
    ];
}

/**
 * 飞入指定的相机方位，会有一个飞入的过程。
 *
 * @alias FdCamera.flyToByCameraInfo
 * @param {Freedo.Camera} camera 场景相机对象
 * @param {Number[]} cameraInfo 存放相机信息的数组，该数组的组成与{@link FdCamera.getCurrentCameraInfo}相同。
 * @param {Object} options 包含一些选项，暂不开放
 * 
 * @example
 * var cameraInfo = [119.0,39.0,5000.0,0.0,-90.0,0.0];
 * Freedo.FdCamera.flyToByCameraInfo(viewer.scene.camera ,cameraInfo) ;
 */
function flyToByCameraInfo(camera, cameraInfo, options) {
    var currentCameraInfo = getCurrentCameraInfo(camera);
    if (!_.isEqual(currentCameraInfo, cameraInfo)) {
        var cam = cameraInfo;

        var oldTransform;
        if (!Freedo.Matrix4.equals(camera.transform, Freedo.Matrix4.IDENTITY)) {
            oldTransform = Freedo.Matrix4.clone(camera.transform, oldTransform);
            camera.lookAtTransform(Freedo.Matrix4.IDENTITY);
        }

        var viewerOpts;
        if (typeof options === 'undefined') {
            viewerOpts = {};
        } else {
            viewerOpts = Freedo.clone(options, true);
        }

        viewerOpts.destination = Freedo.Cartesian3.fromDegrees(cam[0], cam[1], cam[2]);
        viewerOpts.orientation = {
            heading: Freedo.Math.toRadians(cam[3]),
            pitch: Freedo.Math.toRadians(cam[4]),
            roll: Freedo.Math.toRadians(cam[5])
        }
        if (typeof oldTransform !== 'undefined' && typeof viewerOpts.endTransform === 'undefined') {
            viewerOpts.endTransform = oldTransform;
        }

        camera.flyTo(viewerOpts);

        // _getCurrentCameraInfo(camera, this._currentCameraInfo);
        // this._event.raiseEvent('Changed', this._currentCameraInfo);

        return true;
    } else {
        return false;
    }
}

/**
 * 直接跳入指定的相机方位，不会有飞行的过程。
 *
 * @alias FdCamera.goToByCameraInfo
 * @param {Freedo.Camera} camera 场景相机对象
 * @param {Number[]} cameraInfo 存放相机信息的数组，该数组的组成与{@link FdCamera.getCurrentCameraInfo}相同。
 * 
 * @example
 * 
 * var cameraInfo = [119.0,39.0,5000.0,0.0,-90.0,0.0];
 * Freedo.FdCamera.goToByCameraInfo(viewer.scene.camera ,cameraInfo) ;
 * 
 */
function goToByCameraInfo(camera, cameraInfo) {
    var currentCameraInfo = getCurrentCameraInfo(camera);
    if (!_.isEqual(currentCameraInfo, cameraInfo)) {
        var cam = cameraInfo;

        var oldTransform;
        if (!Freedo.Matrix4.equals(camera.transform, Freedo.Matrix4.IDENTITY)) {
            oldTransform = Freedo.Matrix4.clone(camera.transform, oldTransform);
            camera.lookAtTransform(Freedo.Matrix4.IDENTITY);
        }

        camera.setView({
            destination: Freedo.Cartesian3.fromDegrees(cam[0], cam[1], cam[2]),
            orientation: {
                heading: Freedo.Math.toRadians(cam[3]),
                pitch: Freedo.Math.toRadians(cam[4]),
                roll: Freedo.Math.toRadians(cam[5])
            }
        });

        if (typeof oldTransform !== 'undefined') {
            camera.lookAtTransform(oldTransform);
        }

        // _getCurrentCameraInfo(camera, this._currentCameraInfo);
        // this._event.raiseEvent('Changed', this._currentCameraInfo);

        return true;
    } else {
        return false;
    }
}

export { getCurrentCameraInfo, flyToByCameraInfo, goToByCameraInfo };