import { FdCommon } from './FdCommon';

/**
 * 场景服务
 * @namespace
 * @alias FdServer.FdSceneServer
 */
class FdSceneServer {}

/**
 * 获取用户场景列表个数
 * @param {Object} data 传值对象
 * @param {String} data.keyword 关键字
 * @param {String} baseURL 域名
 * @param {String} token 用户权限token
 * @param {FdServer.FdSceneServer~ResponseCallback} response 异步操作回调函数
 */
FdSceneServer.getUserSceneCount = async function(data, baseURL, token, callback) {
    let url = `${baseURL}/cloudlink//freeserver-ocelot/scene/count?token=${token}`;
    let response = [];
    try {
        response = await FdCommon.ajaxJson({
            data: data,
            url: url,
            type: 'POST'
        })
    } catch (error) {
        console.error('获取用户场景失败', error);
    }
    callback(response);
}

/**
 * 获取用户的场景列表
 * @param {Object} data 传值对象
 * @param {String} data.keyword 关键字
 * @param {Number} data.offset 分页的偏移量
 * @param {Number} data.pagecount 每页数量
 * @param {String} baseURL 域名
 * @param {String} token 用户权限token
 * @param {FdServer.FdSceneServer~ResponseCallback} response 异步操作回调函数 
 */
FdSceneServer.getUserScenes = async function(data, baseURL, token, callback) {
    let url = `${baseURL}/cloudlink/freeserver-ocelot/scene/getall?token=${token}`;
    let response = [];
    try {
        response = await FdCommon.ajaxJson({
            data: data,
            url: url,
            type: 'POST'
        })
    } catch (error) {
        console.error('获取用户场景失败', error);
    }
    callback(response);
}

/**
 * 获取用户主动分享的场景
 * @param {String} baseURL 域名
 * @param {String} token 用户权限token
 * @param {FdServer.FdSceneServer~ResponseCallback} response 异步操作回调函数 
 */
FdSceneServer.getUserShareScenes = async function(baseURL, token, callback) {
    let url = `${baseURL}/cloudlink/cloudlink-core-framework/app/dataresource/share/queryMyShareList?token=${token}&status=1&dataType=scene`;
    let response = [];
    try {
        response = await FdCommon.ajaxJson({
            url: url,
            type: 'GET'
        })
    } catch (error) {
        console.log('获取用户分享场景失败', error);
    }
    callback(response);
}

