/**
 * 保存用户场景
 * @param {Object} data 需要保存的内容
 * @param {String} baseURL 域名
 * @param {String} token 用户的权限token
 * @param {FdServer.FdSceneServer~ResponseCallback} response 异步操作回调函数 
 */
FdSceneServer.saveUserScene = async function(data, baseURL, token, callback) {
    let url = `${baseURL}/cloudlink/freeserver-ocelot/scene/upsert?token=${token}`;
    let response = [];
    try {
        response = await FdCommon.ajaxJson({
            url: url,
            data: data,
            type: 'POST'
        })
    } catch (error) {

    }
    callback(response);
}

/**
 * 删除用户的场景
 * @param {String} pid 场景的id
 * @param {String} baseURL 域名
 * @param {String} token 用户的权限token
 * @param {FdServer.FdSceneServer~ResponseCallback} response 异步操作回调函数 
 */
FdSceneServer.deleteUserScene = async function(pid, baseURL, token, callback) {
    let url = `${baseURL}/cloudlink/freeserver-ocelot/scene/remove/${pid}?token=${token}`;
    let response = [];
    try {
        response = await FdCommon.ajaxJson({
            url: url,
            type: 'GET'
        })
    } catch (error) {
        console.log('删除场景失败', error);
    }
    callback(response);
}

/**
 * 删除分享的场景
 * @param {String} id 被删除场景的id
 * @param {String} baseURL 域名
 * @param {String} token 用户的权限token
 * @param {FdServer.FdSceneServer~ResponseCallback} response 异步操作回调函数 
 */
FdSceneServer.deleteShareScene = async function(id, baseURL, token, callback) {
    let url = `${baseURL}/cloudlink/cloudlink-core-framework/app/dataresource/share/delete?token=${token}`;
    let data = {
        'markId': id
    }
    let response = [];
    try {
        response = await FdCommon.ajaxJson({
            url: url,
            data: data,
            type: 'POST'
        })
    } catch (error) {
        console.log('删除分享场景失败', error);
    }
    callback(response);
}

/**
 * FdSceneServer相关的方法事件回调函数
 * @callback FdServer.FdSceneServer~ResponseCallback
 * @param {Object} response 调用请求获取的返回值
 */

export {
    FdSceneServer
}