var defaultPlaneViewerOptions = {
    globe: false,
    skyBox: false,
    sun: false,
    scene3DOnly: true,
    skyAtmosphere: false
};

var defaultViewerOptions = {
    imageryProvider: Freedo.createTileMapServiceImageryProvider({
        url: Freedo.buildModuleUrl('Assets/Textures/NaturalEarthII')
    }),
    baseLayerPicker: false,
    //geocoder: new OpenStreetMapNominatimGeocoder(),
    geocoder: false, // 这个开发以后，gbim360不能用，需要在外部手动控制
    timeline: false,
    animation: false,
    vrButton: false,
    navigationHelpButton: false,
    sceneModePicker: false,
    fullscreenButton: false,
    homeButton: false,
    selectionIndicator: false,
    infoBox: false,
    contextOptions: {
        webgl: {
            antialias: !mobilecheck()
        }
    }

    //imageryProviderViewModels: getProviderViewModels()
};

/**
 * 创建Freedo默认的Viewer类
 * @alias FdApp.createDefaultViewer
 * @param {Element | String} viewerContainer 承载三维窗口的DOM元素或者ID
 * @param {Object} viewerOptions 请参见Freedo API文档中的Viewer构造函数中的options参数说明
 * @param {Object} loadOptions 加载配置参数
 * @param {Boolean} loadOptions.hasSkyBoxOnGround 是否显示近地天空盒，默认值是true
 * @param {Boolean} loadOptions.hasProvinceBoundaries 是否显示中国省界，默认值是false
 * @param {Boolean} loadOptions.planeMode 平面模式
 * @param {Boolean} loadOptions.batchDraw 分批渲染
 * @param {Boolean} loadOptions.batchDrawTargetFPS 分批渲染目标帧率
 * @returns {Object} 返回创建的Freedo.Viewer类对象
 * @example
 * var viewer = Freedo.FdApp.createDefaultViewer('freedoContainer', { homeButton: true }, { hasProvinceBoundaries: true });
 */
function createDefaultViewer(viewerContainer, viewerOptions, loadOptions) {
    Freedo.Camera.DEFAULT_VIEW_RECTANGLE = Freedo.Rectangle.fromDegrees(80, 5, 135, 55); // 指定HOME到中国的位置
    Freedo.BingMapsApi.defaultKey = 'AsosOjY68GIcKsRMisb8KcmGD266hYjtDHD5-EC-YTJUHi1Cxj1K3zWhLcp-SPNZ';

    var hasDefaultImageryLayer;
    var hasSkyBoxOnGround;
    var hasProvinceBoundaries;

    // viewerOptions = FdApp.defaultViewerOptions;
    // Object.assign(viewerOptions, FdApp.defaultViewerOptions);
    loadOptions = Freedo.defaultValue(loadOptions, Freedo.defaultValue.EMPTY_OBJECT);

    var planeMode = Freedo.defaultValue(loadOptions.planeMode, false);

    if (planeMode) {
        var vp = _.assign(defaultViewerOptions, defaultPlaneViewerOptions);
        viewerOptions = _.assign(vp, viewerOptions);
    } else {
        viewerOptions = _.assign(defaultViewerOptions, viewerOptions);
    }
    var viewer = new Freedo.Viewer(viewerContainer, viewerOptions);

    viewer._cesiumWidget._creditContainer.style.display = 'none';

    viewer.scene._useBD = Freedo.defaultValue(loadOptions.batchDraw, false);
    if (viewer.scene._useBD && Freedo.defined(loadOptions.batchDrawTargetFPS)) {
        viewer.scene._maxTimeToBeUsedPerRender = 1000 / loadOptions.batchDrawTargetFPS;
    }
    
    //hasDefaultImageryLayer = Freedo.defaultValue(loadOptions.hasDefaultImageryLayer, true);
    hasSkyBoxOnGround = Freedo.defaultValue(loadOptions.hasSkyBoxOnGround, true);
    hasProvinceBoundaries = Freedo.defaultValue(loadOptions.hasProvinceBoundaries, false);

    var pathFreedoX = getFreedoXPath();

    // china topojson
    if (hasProvinceBoundaries) {
        const topojsonDatasource = new Freedo.GeoJsonDataSource();
        topojsonDatasource.load(pathFreedoX + 'Assets/Misc/china.topojson', {
            stroke: Freedo.Color.YELLOW,
            fill: new Freedo.Color(0, 0, 0, 0),
            strokeWidth: 5,
            markerSymbol: '?'
        }).then(function(dataSource) {
            //Iterate over all entities and set their show property
            //to true only if they are part of the current series.
            var collection = dataSource._entityCollection;
            var entities = collection.values;
            collection.suspendEvents();
            for (var i = 0; i < entities.length; i++) {
                var entity = entities[i];
                //entity.show = value === entity.seriesName;
                entity.polygon.distanceDisplayCondition = new Freedo.DistanceDisplayCondition(5, 20e7);
            }
            collection.resumeEvents();
        });
        viewer.dataSources.add(topojsonDatasource);
    }

    viewer.camera.percentageChanged = 0.0001;
    if (viewer.scene.globe) {
        viewer.scene.globe.depthTestAgainstTerrain = true;
    }
    viewer.scene.pickTranslucentDepth = true;
    if (viewer.scene.moon) {
        viewer.scene.moon.show = false;
    }
    if (viewer.scene.sun) {
        viewer.scene.sun.show = false; //added by meisongjun
    }

    //added by meisongjun
    viewer.extend(Freedo.viewerFreedoPModelInspectorMixin);
    viewer.cesium3DTilesInspector.setVisibility(false);

    // added by txf
    viewer._cesiumWidget._supportsImageRenderingPixelated = Freedo.FeatureDetection.supportsImageRenderingPixelated();
    viewer._cesiumWidget._forceResize = true;
    if (Freedo.FeatureDetection.supportsImageRenderingPixelated()) {
        let dpr = window.devicePixelRatio;
        // 适度降低分辨率
        while (dpr >= 2.0) {
            dpr /= 2.0;
        }
        //alert(dpr);
        viewer.resolutionScale = dpr;
    }

    //added by meisongjun
    viewer.scene.requestRenderMode = true;
    viewer.scene.maximumRenderTimeChange = Infinity;

    viewer.scene.screenSpaceCameraController.enableCollisionDetection = false;

    // if (hasDefaultImageryLayer) {
    //     var imageryLayersManager = project.getImageryLayersManager();
    //     imageryLayersManager.addDefaultImageryLayer();
    // }

    if (hasSkyBoxOnGround && !planeMode) {
        viewer.scene.skyboxOnGround = new Freedo.SkyBox({
            sources: {
                positiveX: pathFreedoX + 'Assets/Textures/SkyBoxOnGround/rightav9.jpg',
                negativeX: pathFreedoX + 'Assets/Textures/SkyBoxOnGround/leftav9.jpg',
                positiveY: pathFreedoX + 'Assets/Textures/SkyBoxOnGround/frontav9.jpg',
                negativeY: pathFreedoX + 'Assets/Textures/SkyBoxOnGround/backav9.jpg',
                positiveZ: pathFreedoX + 'Assets/Textures/SkyBoxOnGround/topav9.jpg',
                negativeZ: pathFreedoX + 'Assets/Textures/SkyBoxOnGround/bottomav9.jpg'
            }
        });
    }

    if (planeMode) {
        viewer.scene._screenSpaceCameraController = new FdScreenSpaceCameraController(viewer.scene);
    }

    let canvas = viewer.canvas;
    canvas.addEventListener('keydown', function(e) {
        var keyID = e.keyCode ? e.keyCode : e.which;
        if (keyID === 187) { // 键盘 '+' 键
            var visibility = viewer.cesium3DTilesInspector._container.style.visibility;
            if (visibility === 'hidden') {
                viewer.cesium3DTilesInspector.setVisibility(true);
                //viewer.cesium3DTilesInspector._container.style.visibility='visible';//可见
            } else {
                viewer.cesium3DTilesInspector.setVisibility(false);
                //viewer.cesium3DTilesInspector._container.style.visibility='hidden';//隐藏
            }
            //e.preventDefault();
        }
    });

    return viewer;
}

export {
    createDefaultViewer
};