import { FdCommon } from './FdCommon';

/**
 * PMTS相关服务
 * @namespace
 * @alias FdServer.FdPMTSServer
 */
class FdPMTSServer {}


/**
 * 获取资源信息列表个数
 * @param {Object} data  传值的对象
 * @param {String} keyword 搜索的关键字
 * @param {String} servicetype 查询类型，默认是PMTS
 * @param {String} baseURL 域名
 * @param {String} token 用户权限token
 * @param {FdServer.FdPMTSServer~FdGetResourceListCallback} response 事件完成回调函数
 */
FdPMTSServer.getResourceCount = async function(data, baseURL, token, callback) {
    let url = `${baseURL}/cloudlink/freeserver-admin/admin/layer/count?token=${token}`;
    let response = [];
    let err;
    if (!data) {
        data = {};
        data.servicetype = 'PMTS';
    }
    try {
        response = await FdCommon.ajaxJson({
            url: url,
            data: data,
            type: 'POST'
        })
    } catch (error) {
        err = error;
        console.log('获取列表失败');
    }
    callback(response, err);
}

/**
 * 获取资源信息列表
 * @param {Object} data  传值的对象
 * @param {String} keyword 搜索的关键字
 * @param {Number} offset 偏移数据
 * @param {Number} pagecount 每页个数
 * @param {String} servicetype 查询类型，默认是PMTS
 * @param {String} baseURL 域名
 * @param {String} token 用户权限token
 * @param {FdServer.FdPMTSServer~FdGetResourceListCallback} response 事件完成回调函数
 */
FdPMTSServer.getResourceList = async function(data, baseURL, token, callback) {
    let url = `${baseURL}/cloudlink/freeserver-admin/admin/layer/get?token=${token}`;
    let response = [];
    let err;
    if (!data) {
        data = {};
        data.servicetype = 'PMTS';
    }
    try {
        response = await FdCommon.ajaxJson({
            url: url,
            data: data,
            type: 'POST'
        })
    } catch (error) {
        err = error;
        console.log('获取列表失败');
    }
    callback(response, err);
}

/**
 * FdPMTSServer类相关方法事件完成回调函数
 * @callback FdServer.FdPMTSServer~FdGetResourceListCallback
 * @param {Object} response 调用请求获取的返回值
 * @param {String} error 如果成功，error为undefined，否则有值
 */

/**
 * 获取单个资源的数据
 * @param {String} capaurl 从ResourceList中获取到的，单个PMTS数据服务的capaurl字段
 * @param {String} token 用户token
 * @param {FdServer.FdPMTSServer~FdGetResourceCallback} callback 事件完成回调函数
 */
FdPMTSServer.getResource = async function(capaurl, token, callback) {
    let pmtsUrl = capaurl;
    let response = undefined;
    if (token) {
        pmtsUrl = capaurl + '?token=' + token
    }
    try {
        response = await FdCommon.ajaxJson({
            url: pmtsUrl,
            type: 'GET'
        })
    } catch (error) {
        console.error('错误');
    }
    callback(response);
}

/**
 * FdPMTSServer类相关方法事件完成回调函数
 * @callback FdServer.FdPMTSServer~FdGetResourceCallback
 * @param {Object} response 调用请求获取的返回值
 * @param {String} error 如果成功，error为undefined，否则有值
 */

export {
    FdPMTSServer
}