import {
    FdMisc
} from '../../FdMisc/FdMisc';

import {
    FdPolylineEditor
} from './FdPolylineEditor';

var EARTH_RADIUSMETERS = 6371000.0;
var METERS_PER_DEGREE = 2.0 * Math.PI * EARTH_RADIUSMETERS / 360.0;
var RADIANS_PER_DEGREE = Math.PI / 180.0;
var DEGREES_PER_RADIAN = 180.0 / Math.PI;

/*
 * 面量测
 * @private
 * @alias FdAnalysisArea
 * @class
 */
class FdAnalysisArea {
    constructor(viewer) {
        this._viewer = viewer;
        this._scene = this._viewer.scene;

        // 辅助变量
        this._polygons = [];
        this._polygons3D = [];
        this._labels = [];
        this._data = [];

        this._lsner = undefined;
        this._currentType = undefined;

        this._ple = new FdPolylineEditor(viewer);
        this._ple.setAutoDelLines(false);
        this._ple.setHeightOffset(0);

        this._event = new Freedo.Event();
        this._eventHelper = new Freedo.EventHelper();

        this._labelOpt = {
            style: Freedo.LabelStyle.FILL_AND_OUTLINE,
            show: false,
            showBackground: true,
            backgroundColor: FreeDo.Color['BLACK'].withAlpha(.8),
            fillColor: FreeDo.Color['WHITE'],
            font: '20px 雅黑',
            pixelOffset: new FreeDo.Cartesian2(0, 0),
            disableDepthTestDistance: Number.POSITIVE_INFINITY
        };
    }
}

/**
 * 事件类型有：Start,Cancel
 * @param {FdAnalysisArea~FdAnalysisAreaCallback} listener 当有事件触发时被执行的函数。
 * @param {Object} [scope] listener函数执行时的绑定的对象。
 * @returns {Freedo.Event~RemoveCallback} 返回一个函数，调用该函数可以取消监听。
 */
FdAnalysisArea.prototype.on = function(listener, scope) {
    return this._eventHelper.add(this._event, listener, scope);
}

/**
 * @callback FdAnalysisArea~FdAnalysisAreaCallback
 * @param {String} eventType 事件类型有：Start（量测开始），Cancel（量测结束），DataChanged（数据变化）
 * @param {Object} eventArg Start/Cancel无返回值，DataChanged类型事件参数包含相关信息
 */

/*
 * 开始进行量测的交互操作
 * @private
 */
FdAnalysisArea.prototype.run = function(type) {
    this._ple.run();

    if (this._lsner) {
        this._lsner();
        this._lsner = undefined;
    }

    var that = this;

    this._lsner = this._ple.on(function(eventType, eventArg) {
        if (eventType == 'PLAdd') {
            that.onPLAdd(eventArg);
        }
        if (eventType == 'PointAdded') {
            if (Freedo.defined(eventArg.position[3]) && eventArg.position[3] == 0) {
                if (that._currentType == undefined) {
                    that._currentType = 'OnModel';
                }
            } else
                that._currentType = 'OnEarth';
        }
    });

    this._event.raiseEvent('Start', {

    });
}

FdAnalysisArea.prototype.createAreaPrimitive = function(pts) {
    var N;
    var r = new Freedo.Cartesian3();
    var v1 = Freedo.Cartesian3.subtract(pts[1], pts[0], new Freedo.Cartesian3());
    var v2 = Freedo.Cartesian3.subtract(pts[2], pts[1], new Freedo.Cartesian3());
    N = Freedo.Cartesian3.cross(v1, v2, new Freedo.Cartesian3());
    N = Freedo.Cartesian3.normalize(N, new Freedo.Cartesian3());

    var angle = Freedo.Cartesian3.angleBetween(N, Freedo.Cartesian3.UNIT_Z);
    var axis = Freedo.Cartesian3.cross(N, Freedo.Cartesian3.UNIT_Z, new Freedo.Cartesian3());
    var quat = Freedo.Quaternion.fromAxisAngle(axis, angle, new Freedo.Quaternion());
    var rMatrix = Freedo.Matrix4.fromTranslationQuaternionRotationScale(new Freedo.Cartesian3(), quat, new Freedo.Cartesian3(1, 1, 1));
    var newN = Freedo.Matrix4.multiplyByPointAsVector(rMatrix, N, new Freedo.Cartesian3());

    var newPts = [];
    for (var k in pts) {
        var newPt = Freedo.Matrix4.multiplyByPointAsVector(rMatrix, pts[k], new Freedo.Cartesian3());
        newPts.push(newPt);
    }

    var geo;
    if ( /*viewer.scene.globe*/ false) {

    } else {
        var positions = [];
        for (var i = 0; i < pts.length; i++) {
            positions.push(pts[i].x * 1);
            positions.push(pts[i].y * 1);
            positions.push(pts[i].z * 1);
        }

        var index = Freedo.PolygonPipeline.triangulate(newPts);

        var positionsFloat64 = new Float64Array(positions);
        geo = new Freedo.Geometry({
            attributes: {
                position: new Freedo.GeometryAttribute({
                    componentDatatype: Freedo.ComponentDatatype.DOUBLE,
                    componentsPerAttribute: 3,
                    values: positionsFloat64
                })
            },
            indices: new Uint16Array(index),
            primitiveType: Freedo.PrimitiveType.TRIANGLES,
            boundingSphere: Freedo.BoundingSphere.fromVertices(positionsFloat64)
        });
    }
    /*geo = new Freedo.PolylineGeometry({
        positions: [p1, p2],
        width: 2.0,
        followSurface: followSurface,
        vertexFormat: Freedo.PolylineMaterialAppearance.VERTEX_FORMAT
    });*/
    return new Freedo.Primitive({
        geometryInstances: new Freedo.GeometryInstance({
            geometry: geo,
            attributes: {
                color: Freedo.ColorGeometryInstanceAttribute.fromColor(new Freedo.Color(1.0, 1.0, 0.0, 1.0))
            }
        }),
        appearance: new Freedo.PerInstanceColorAppearance({
            flat: true,
            translucent: true
        }),
        asynchronous: false
    });
}

/*
 * 添加面
 * @private
 */
FdAnalysisArea.prototype.addArea = function(pts) {
    var polygon;
    if (this._viewer.scene.globe) {
        if (this._currentType == 'OnEarth' || this._currentType == '' || this._currentType == undefined) {
            polygon = this._viewer.entities.add({
                name: 'AREAGEO',
                polygon: {
                    hierarchy: new Freedo.PolygonHierarchy(pts),
                    material: Freedo.Color.ORANGE.withAlpha(0.5),
                    outline: true,
                    outlineColor: Freedo.Color.BLACK
                }
            });
        } else {
            polygon = this._viewer.scene.primitives.add(this.createAreaPrimitive(pts));
        }

    } else {
        polygon = this._viewer.scene.primitives.add(this.createAreaPrimitive(pts));
    }

    var labelCenter = this.xyzCenter(pts);
    var aLonLat = [];
    for (var i = 0; i < pts.length - 1; i++) {
        var lonLat = this.toLonLat(pts[i]);
        aLonLat.push(lonLat);
    }

    var aLon = [];
    var aLat = [];
    var ptsG = [];
    for (var i = 0; i < pts.length - 1; i++) {
        var lonLat = this.toLonLat(pts[i]);
        aLon.push(lonLat.lon);
        aLat.push(lonLat.lat);
        ptsG.push([lonLat.lon, lonLat.lat]);
    }

    //计算面积
    //var proportion = this.SphericalPolygonAreaMeters(aLonLat);
    var proportion = 0;

    if (this._viewer.scene.globe && this._currentType != 'OnModel')
    //proportion = Freedo.FdMisc.FdGeometric.calcArea(aLon, aLat, 'DEGREES');
        proportion = this.calcArea3D(pts);
    else
        proportion = this.calcArea3D(pts);

    if (proportion < 0)
        proportion *= -1;

    proportion = Freedo.FdMisc.FdMath.round(proportion, 2);

    var labelEntity = this._viewer.entities.add({
        label: this._labelOpt
    });
    if (proportion > 1000000) {
        proportion = Freedo.FdMisc.FdMath.round(proportion / 1000000, 2);;
        labelEntity.label.text = proportion + '平方公里';
    } else {
        labelEntity.label.text = proportion + '平方米';
    }

    if (this._viewer.scene.globe && this._currentType != 'OnModel')
        labelEntity.position = FreeDo.Cartesian3.fromDegrees(labelCenter.lon, labelCenter.lat, 0.1);
    else
        labelEntity.position = new FreeDo.Cartesian3(labelCenter.lon, labelCenter.lat, labelCenter.height);

    labelEntity.label.show = true;

    this._labels.push(labelEntity);

    if (this._viewer.scene.globe)
        this._polygons.push(polygon);
    else
        this._polygons3D.push(polygon);

    var data = {
        pointsG: ptsG,
        pointsC: pts,
        area: proportion,
        type: this._currentType
    };
    this._data.push(data);

    this._event.raiseEvent('DataChanged', data);
    this._viewer.scene.requestRender();
    this._currentType = undefined;
}

/*
 * 新添加多段线之后的响应函数
 * @private
 */
FdAnalysisArea.prototype.onPLAdd = function(param) {
    if (!param.polyline)
        return;

    if (param.polyline.pts.length == 2) {
        this._ple.delResult();
        return;
    }

    this.addArea(param.polyline.pts);
}

/*
 * 将笛卡尔坐标转换为地理坐标
 * @private
 */
FdAnalysisArea.prototype.toLonLat = function(cartesian3) {
    var scartographic = FreeDo.Cartographic.fromCartesian(cartesian3);
    var lon = FreeDo.Math.toDegrees(scartographic.longitude);
    var lat = FreeDo.Math.toDegrees(scartographic.latitude);
    var height = scartographic.height;

    var graphic = {
        lon: lon,
        lat: lat,
        height: height
    };

    return graphic;
}

FdAnalysisArea.prototype.getData = function() {
    return {
        area: this._data
    };
}

FdAnalysisArea.prototype.setData = function(v) {
    for (var k in v) {
        this._currentType = v[k].type;
        this.addArea(v[k].pointsC);
    }
}

/*
 * 计算球面积
 * @param {Object[]} points 点组，Object.lon和Object.lat两个属性，分别表示经度纬度，单位都是度
 * @returns {Number} 面积，平方米
 */
FdAnalysisArea.prototype.SphericalPolygonAreaMeters = function(points) {
    var totalAngle = 0;
    for (var i = 0; i < points.length; i++) {
        var j = (i + 1) % points.length;
        var k = (i + 2) % points.length;
        totalAngle += this.Angle(points[i], points[j], points[k]);
    }
    var planarTotalAngle = (points.length - 2) * 180.0;
    var sphericalExcess = totalAngle - planarTotalAngle;
    if (sphericalExcess > 420.0) {
        sphericalExcess = totalAngle - planarTotalAngle;
    } else if (sphericalExcess > 300.0 && sphericalExcess < 420.0) {
        sphericalExcess = Math.abs(360.0 - sphericalExcess);
    }
    return sphericalExcess * RADIANS_PER_DEGREE * EARTH_RADIUSMETERS * EARTH_RADIUSMETERS;
}

/*
 * 计算矢量的夹角
 * @param {Object} p1 第一个点
 * @param {Object} p1.lon 第一个点经度，单位度
 * @param {Object} p1.lat 第一个点纬度，单位度
 * @param {Object} p2 第二个点
 * @param {Object} p2.lon 第一个点经度，单位度
 * @param {Object} p2.lat 第一个点纬度，单位度
 * @param {Object} p3 第三个点
 * @param {Object} p3.lon 第一个点经度，单位度
 * @param {Object} p3.lat 第一个点纬度，单位度
 * @returns {Number} 角度，单位度
 */
FdAnalysisArea.prototype.Angle = function(p1, p2, p3) {
    var bearing21 = this.Bearing(p2, p1);
    var bearing23 = this.Bearing(p2, p3);
    var angle = bearing21 - bearing23;
    if (angle < 0) {
        angle += 360;
    }
    return angle;
}

/*
 * 计算矢量的方向
 * @param {Object} from 起点
 * @param {Object} from.lon 起点经度，单位度
 * @param {Object} from.lat 起点纬度，单位度
 * @param {Object} to 终点
 * @param {Object} to.lon 终点经度，单位度
 * @param {Object} to.lat 终点纬度，单位度
 * @returns {Number} 角度，单位度
 */
FdAnalysisArea.prototype.Bearing = function(from, to) {
    var lat1 = from.lat * RADIANS_PER_DEGREE;
    var lon1 = from.lon * RADIANS_PER_DEGREE;
    var lat2 = to.lat * RADIANS_PER_DEGREE;
    var lon2 = to.lon * RADIANS_PER_DEGREE;
    var angle = -Math.atan2(Math.sin(lon1 - lon2) * Math.cos(lat2), Math.cos(lat1) * Math.sin(lat2) - Math.sin(lat1) * Math.cos(lat2) * Math.cos(lon1 - lon2));
    if (angle < 0) {
        angle += Math.PI * 2.0;
    }
    angle = angle * DEGREES_PER_RADIAN;
    return angle;
}

/*
 * 计算点组的中心点
 * @param {Cartesian3[]} positions 点组
 * @returns {Object} obj 中心点
 * @returns {Number} obj.lon 中心点，单位度
 * @returns {Number} obj.lat 中心点，单位度
 * @returns {Number} obj.height 中心点，单位米
 */
FdAnalysisArea.prototype.xyzCenter = function(positions) {
    var minX = Number.MAX_VALUE;
    var minY = Number.MAX_VALUE;
    var minZ = Number.MAX_VALUE;
    var maxX = -Number.MAX_VALUE;
    var maxY = -Number.MAX_VALUE;
    var maxZ = -Number.MAX_VALUE;

    if (positions.length == 0) {
        console.error('只有一个点，无法计算')
    } else if (positions.length == 1) {
        console.error('有两个点，无法计算')
    } else {
        for (var i = 0; i < positions.length; i++) {
            if (positions[i].x < minX) {
                minX = positions[i].x;
            }
            if (positions[i].y < minY) {
                minY = positions[i].y;
            }
            if (positions[i].z < minZ) {
                minZ = positions[i].z;
            }
            if (positions[i].x > maxX) {
                maxX = positions[i].x;
            }
            if (positions[i].y > maxY) {
                maxY = positions[i].y;
            }
            if (positions[i].z > maxZ) {
                maxZ = positions[i].z;
            }
        }
        var point = new Freedo.Cartesian3((maxX + minX) / 2, (maxY + minY) / 2, (maxZ + minZ) / 2);
        if (this._viewer.scene.globe && this._currentType != 'OnModel') {
            var ellipsoid = this._viewer.scene.globe.ellipsoid;
            var scartographic = ellipsoid.cartesianToCartographic(point);
            var lon = FreeDo.Math.toDegrees(scartographic.longitude);
            var lat = FreeDo.Math.toDegrees(scartographic.latitude);
            var height = scartographic.height;
            return {
                lon: lon,
                lat: lat,
                height: height
            };
        } else {
            return {
                lon: point.x,
                lat: point.y,
                height: point.z
            }
        }
    }
}

//===============================================================
// calcArea3D():computes the area of a 3D planar polygon
//    Input:  int n = the number of vertices in the polygon
//            Point* V = an array of n+2 vertices in a plane
//                       with V[n]=V[0] and V[n+1]=V[1]
//            Point N = unit normal vector of the polygon's plane
//    Return: the (float) area of the polygon</pre>

FdAnalysisArea.prototype.calcArea3D = function(pts) {
    var n, V, N;

    n = pts.length;
    V = Freedo.clone(pts, true);
    V.push(pts[0]);
    V.push(pts[1]);

    var r = new Freedo.Cartesian3();
    var v1 = Freedo.Cartesian3.subtract(pts[1], pts[0], new Freedo.Cartesian3());
    var v2 = Freedo.Cartesian3.subtract(pts[2], pts[1], new Freedo.Cartesian3());
    N = Freedo.Cartesian3.cross(v1, v2, new Freedo.Cartesian3());
    N = Freedo.Cartesian3.normalize(N, new Freedo.Cartesian3());
    var area = 0;
    var an, ax, ay, az; // abs value of normal and its coords
    var coord; // coord to ignore: 1=x, 2=y, 3=z
    var i, j, k; // loop indices

    // select largest abs coordinate to ignore for projection
    ax = (N.x > 0 ? N.x : -N.x); // abs x-coord
    ay = (N.y > 0 ? N.y : -N.y); // abs y-coord
    az = (N.z > 0 ? N.z : -N.z); // abs z-coord

    coord = 3; // ignore z-coord
    if (ax > ay) {
        if (ax > az) coord = 1; // ignore x-coord
    } else if (ay > az) coord = 2; // ignore y-coord

    // compute area of the 2D projection
    for (i = 1, j = 2, k = 0; i <= n; i++, j++, k++)
        switch (coord) {
            case 1:
                area += (V[i].y * (V[j].z - V[k].z));
                continue;
            case 2:
                area += (V[i].x * (V[j].z - V[k].z));
                continue;
            case 3:
                area += (V[i].x * (V[j].y - V[k].y));
                continue;
        }

    // scale to get area before projection
    an = Math.sqrt(ax * ax + ay * ay + az * az); // length of normal vector
    switch (coord) {
        case 1:
            area *= (an / (2 * ax));
            break;
        case 2:
            area *= (an / (2 * ay));
            break;
        case 3:
            area *= (an / (2 * az));
    }
    return area;
}

/**
 * 重置
 */
FdAnalysisArea.prototype.reset = function() {
    if (this._lsner)
        this._lsner();
    this._lsner = undefined;

    this._ple.cancel();
    this._event.raiseEvent('Cancel', {});
}

/**
 * 重置
 */
FdAnalysisArea.prototype.removeAll = function() {
    for (var key in this._polygons) {
        this._viewer.entities.remove(this._polygons[key]);
        this._viewer.scene.primitives.remove(this._polygons[key]);
    }
    for (var key in this._polygons3D) {
        this._viewer.scene.primitives.remove(this._polygons3D[key]);
    }
    for (var key in this._labels) {
        this._viewer.entities.remove(this._labels[key]);
    }

    this._polygons = [];
    this._polygons3D = [];
    this._labels = [];
    this._data = [];

    this._ple.removeAll();

    this._viewer.scene.requestRender();
}

/**
 * 销毁所有资源
 */
FdAnalysisArea.prototype.dispose = function() {
    this.reset();
    this.removeAll();
}

export {
    FdAnalysisArea
};