import { FdCommon } from './FdCommon';

/**
 * 地形服务
 * @namespace
 * @alias FdServer.FdTerrainServer
 */
class FdTerrainServer {}

/**
 * 处理获取到的地形的XML文件，返回当前地形的添加url
 * @param {XML} metadata 需要处理的XML文件
 * @private
 */
function _parseXmlMetadata(metadata) {
    let xmlMetadata = metadata;
    let xmlDom = xmlMetadata.documentElement;
    let layer = xmlDom.getElementsByTagName('Contents');
    let resourceUrls = layer[0].getElementsByTagName('ResourceURL');
    let len = resourceUrls.length;
    let imgUrl = resourceUrls[0].getAttribute('template');
    let url = imgUrl.replace('{TileMatrix}/{TileCol}/{TileRow}.bil', '');
    return url;
}

/**
 * 获取地形资源列表
 * @param {String} baseURL 域名
 * @param {String} token 用户权限token
 * @param {FdServer.FdTerrainServer~ResponseCallback} response 事件调用完成后回调函数 
 */
FdTerrainServer.getTerrains = async function(baseURL, token, callback) {
    let url = baseURL + `/cloudlink/freeserver-admin/admin/layer/get?token=${token}`;
    let response = [];
    try {
        response = await FdCommon.ajaxJson({
            url: url,
            data: {
                servicetype: 'WETS'
            },
            type: 'POST',
            dataType: 'json'
        })
    } catch (error) {
        console.log('无法正常获取列表');
    }
    callback(response);
}

/**
 * 获取地形列表
 * @param {String} url 请求地址url
 * @param {String} token 用户权限token
 * @param {FdServer.FdTerrainServer~ResponseCallback} response 事件调用完成后回调函数
 */
FdTerrainServer.getTerrain = async function(capaurl, token, callback) {
    let res = [];
    let pmtsUrl = capaurl + '?token=' + token;
    try {
        let response = await FdCommon.ajaxJson({
            url: pmtsUrl,
            type: 'GET',
            dataType: 'XML'
        })
        res = _parseXmlMetadata(response);
    } catch (error) {
        console.log('添加错误');
    }
    callback(res);
}

/**
 * FdTerrainServer相关的方法事件回调函数
 * @callback FdServer.FdTerrainServer~ResponseCallback
 * @param {Object} response 调用请求获取的返回值
 */

export {
    FdTerrainServer
}