var scope = typeof window !== 'undefined' ? window : typeof self !== 'undefined' ? self : {};
if (typeof scope.Freedo === 'undefined') {
    scope.Freedo = {};
    console.warn('You should include freedo.js first! 你需要首先引用freedo.js，再使用freedox.js库！');
}

window.FreedoX = scope.Freedo;

FreedoX.FdApp = FdApp;
