import {
	FdBezierCurveAlgorithm
} from './Analysis/FdBezierCurveAlgorithm';

import {
	FdMisc
} from '../FdMisc/FdMisc';

import {
	FdPolylineEditor
} from './Analysis/FdPolylineEditor';

/**
 * Polyline绘制
 * @alias FdMicroApp.FdDrawPolyline
 * @class
 *
 * @example
 * var viewer = Freedo.FdApp.createDefaultViewer('freedoContainer');
 * var dr = new FdDrawPolyline(viewer);
 * dr.start();
 */
class FdDrawPolyline {
	constructor(viewer) {
		this._viewer = viewer;

		this._polylines = [];
		this._data = [];

		this._lsner = undefined;

		this._ple = new FdPolylineEditor(viewer);
		this._ple.setAutoLoop(false);
		this._ple.setAutoDelLines(true);

		this._handler = undefined;

		this._event = new Freedo.Event();
		this._eventHelper = new Freedo.EventHelper();

		this._smoothRate = 0;
		
		this._currentIndex = 0;
	}
	
	get polylinesOptions(){
        return this._data;
    }
    
    set polylinesOptions(v){
        if(!Freedo.defined(v)){
            console.error('polyline数据错误');
            return;
        }
        
        this.clearPolylines();
        
        for(var k in v){
            this.addPolyline(v[k]['pts']);
        }
    }
}

/**
 * 激活Polyline绘制功能
 * 激活后，直接在地面选点即可
 */
FdDrawPolyline.prototype.start = function() {
	this._ple.run();
	this.addLsnr();
	this._event.raiseEvent('Start', {});
}

FdDrawPolyline.prototype.addLsnr = function() {
	if(this._lsner) {
        this._lsner();
        this._lsner = undefined;
    }

    var that = this;

    this._lsner = this._ple.on(function(eventType, param) {
        if(eventType == 'PLAdd') {
            that.onPLAdd(param);
        }
        
        if(eventType == 'EditEnd') {
            that.polylinesOptions[that._currentIndex].pts = Freedo.clone(param.pts);
            that.reDraw();
        }
    });
}

FdDrawPolyline.prototype.onPLAdd = function(param) {
	this.addPolyline(param.polyline.pts);
}

/**
 * 计算控制点
 * @private
 * @param {Cartesian3} p0p 第一个点
 * @param {Cartesian3} p1p 第二个点
 * @param {Cartesian3} p2p 第三个点
 * @returns {Array} 返回[Cartesian3,Cartesian3]格式的控制点
 */
FdDrawPolyline.prototype.calcCP = function(p0p, p1p, p2p) {
	var p0,
		p1,
		p2;
	p0 = p0p.position;
	p1 = p1p.position;
	p2 = p2p.position;

	var cp = [];
	var v1 = FreeDo.Cartesian3.subtract(p0, p1, new FreeDo.Cartesian3());
	var v2 = FreeDo.Cartesian3.subtract(p2, p1, new FreeDo.Cartesian3());

	var axis = FreeDo.Cartesian3.cross(v1, v2, new FreeDo.Cartesian3());

	var v1n = FreeDo.Cartesian3.normalize(v1, new FreeDo.Cartesian3());
	var v2n = FreeDo.Cartesian3.normalize(v2, new FreeDo.Cartesian3());
	var mv = FreeDo.Cartesian3.add(v1n, v2n, new FreeDo.Cartesian3());

	var quat1 = Freedo.Quaternion.fromAxisAngle(axis, Freedo.Math.PI_OVER_TWO, new Freedo.Quaternion());
	var quat2 = Freedo.Quaternion.fromAxisAngle(axis, -1 * Freedo.Math.PI_OVER_TWO, new Freedo.Quaternion());

	var m31 = Freedo.Matrix3.fromQuaternion(quat1, new Freedo.Matrix3());
	var m32 = Freedo.Matrix3.fromQuaternion(quat2, new Freedo.Matrix3());

	var mv1 = Freedo.Matrix3.multiplyByVector(m31, mv, new Freedo.Cartesian3());
	var mv2 = Freedo.Matrix3.multiplyByVector(m32, mv, new Freedo.Cartesian3());

	var mv1n = FreeDo.Cartesian3.normalize(mv1, new FreeDo.Cartesian3());
	var mv2n = FreeDo.Cartesian3.normalize(mv2, new FreeDo.Cartesian3());

	var len1 = FreeDo.Cartesian3.distance(p0, p1);
	var len2 = FreeDo.Cartesian3.distance(p2, p1);
	var len = 1;
	if(len1 < len2) {
		len = len1 / 4; //this._smoothRate;
	} else
		len = len2 / 4; // this._smoothRate;

	mv1n = FreeDo.Cartesian3.multiplyByScalar(mv1n, len, new FreeDo.Cartesian3());
	mv2n = FreeDo.Cartesian3.multiplyByScalar(mv2n, len, new FreeDo.Cartesian3());

	var cp0 = FreeDo.Cartesian3.add(p1, mv1n, new FreeDo.Cartesian3());
	var cp1 = FreeDo.Cartesian3.add(p1, mv2n, new FreeDo.Cartesian3());

	var dis1 = FreeDo.Cartesian3.distance(p0, cp0);
	var dis2 = FreeDo.Cartesian3.distance(p0, cp1);

	if(dis1 < dis2)
		return [cp0, cp1];
	else
		return [cp1, cp0];
}

/**
 * 平滑化
 * @private
 * @param {Object[]} pts 需要进行平滑化的点组
 * @returns 平滑后的点组，[Cartesian3]
 */
FdDrawPolyline.prototype.smoothPolyline = function(pts) {
	var aPt = [];
	if(pts.length < 3) {
		for(var i = 0; i < pts.length; i++) {
			aPt.push(pts[i].position);
		}
	} else {
		for(var i = 0; i <= pts.length - 2; i++) {
			var ca = {},
				cb = {},
				pa = {},
				pb = {};
			if(i == 0) {
				ca = pts[0];
				cb.position = this.calcCP(pts[0], pts[1], pts[2])[0];
				pa = pts[0];
				pb = pts[1];
			} else if(i == pts.length - 2) {
				ca.position = this.calcCP(pts[i - 1], pts[i], pts[i + 1])[1];
				cb = pts[i + 1];
				pa = pts[i];
				pb = pts[i + 1];
			} else {
				ca.position = this.calcCP(pts[i - 1], pts[i], pts[i + 1])[1];
				cb.position = this.calcCP(pts[i], pts[i + 1], pts[i + 2])[0];
				pa = pts[i];
				pb = pts[i + 1];
			}
			var bz = new FdBezierCurveAlgorithm(pa, pb, ca, cb);

			/*
			this._viewer.entities.add({
			position: ca.position,
			ellipsoid: {
			material: Freedo.Color.YELLOW.withAlpha(1),
			radii: new Freedo.Cartesian3(5, 5, 5)
			}
			});
			this._viewer.entities.add({
			position: cb.position,
			ellipsoid: {
			material: Freedo.Color.RED.withAlpha(1),
			radii: new Freedo.Cartesian3(5, 5, 5)
			}
			});
			 */

			var segPts = bz.getVertexForCurve(this._smoothRate);
			aPt = aPt.concat(segPts);
		}
	}

	//把重复的相邻点删除掉
	var newPts = [];
	for(var i = 0; i < aPt.length; i++) {
		var o = {};
		o = aPt[i];
		if(newPts.length > 0) {
			var len = FreeDo.Cartesian3.distance(newPts[newPts.length - 1], o);
			if(len > 0.0001)
				newPts.push(o);
		} else
			newPts.push(o);
	}
	return newPts;
}

/**
 * 创建多段线
 * @private
 * @param {Object} pts 点组
 * @returns 返回线段对象
 */
FdDrawPolyline.prototype.createLinePrimitive = function(pts) {
    return new Freedo.Primitive({
        geometryInstances: new Freedo.GeometryInstance({
            geometry: new Freedo.PolylineGeometry({
                positions: pts,
                width: 2.0,
                vertexFormat: Freedo.PolylineMaterialAppearance.VERTEX_FORMAT
            })
        }),
        appearance: new Freedo.PolylineMaterialAppearance({
            material: new Freedo.Material({
                fabric: {
                    type: 'Color',
                    uniforms: {
                        color: new Freedo.Color(1.0, 0.3, 0.3, 1.0)
                    }
                }
            }),
            renderState: new Freedo.RenderState({
                polygonOffset: {
                    enabled: true,
                    factor: -100.0,
                    units: -100.0,
                },
                depthTest: {
                    enabled: true,
                    func: Freedo.WebGLConstants.LEQUAL,
                }
            }),
        }),
        depthFailAppearance: new Freedo.PolylineMaterialAppearance({
            material: new Freedo.Material({
                fabric: {
                    type: 'PolylineDash',
                    uniforms: {
                        color: new Freedo.Color(1.0, 0.3, 0.3, 0.5)
                    }
                }
            }),
        }),
    });
}

/**
 * 设置路径点数组
 * @param {Object[]} points 经纬度高度格式的点组
 */
FdDrawPolyline.prototype.addPolyline = function(points) {
	var objLine = {};
	objLine.pts = points;

	var newPts = [];
	for(var k in points) {
		var newObj = {
			position: points[k]
		}
		newPts.push(newObj);
	}
	if(this._smoothRate == 0)
		objLine.smoothPts = points;
	else
		objLine.smoothPts = this.smoothPolyline(newPts);

	//console.log(objLine.smoothPts);

	var pl;
	//pl = this._viewer.scene.primitives.add(this.createLinePrimitive(objLine.smoothPts));
//	pl = this._viewer.entities.add({
//		name: 'Purple straight arrow at height',
//		polyline: {
//			positions: objLine.smoothPts,
//			width: 20, // 宽度
//			followSurface: true, // 是否贴地
//			material: new Freedo.PolylineArrowMaterialProperty(Freedo.Color.YELLOW),
//		}
//	});
    pl = this._viewer.scene.primitives.add(this.createLinePrimitive(objLine.smoothPts));
	objLine.polyline = pl;
	this._polylines.push(objLine);
	
    this._data.push({
        pts: points
    });
    
    this._event.raiseEvent('PolylineAdd', points);
    
    this._event.raiseEvent('DataChanged', {
        pts: points
    });
    this._viewer.scene.requestRender();
}

FdDrawPolyline.prototype.createLinePrimitive = function(pts) {
	return new Freedo.Primitive({
		geometryInstances: new Freedo.GeometryInstance({
			geometry: new Freedo.PolylineGeometry({
				positions: pts,
				width: 5,
				vertexFormat: Freedo.PolylineMaterialAppearance.VERTEX_FORMAT
			})
		}),
		appearance: new Freedo.PolylineMaterialAppearance({
			material: new Freedo.Material({
				fabric: {
					type: 'PolylineArrow',
					width: 5,
					uniforms: {
						color: new Freedo.Color(1.0, 1.0, 0, 1.0)
					}
				}
			}),
			renderState: new Freedo.RenderState({
				polygonOffset: {
					enabled: true,
					factor: -100.0,
					units: -100.0,
				},
				depthTest: {
					enabled: true,
					func: Freedo.WebGLConstants.LEQUAL,
				}
			}),
		}),
		depthFailAppearance: new Freedo.PolylineMaterialAppearance({
			material: new Freedo.Material({
				fabric: {
					type: 'PolylineArrow',
					width: 5,
					uniforms: {
						color: new Freedo.Color(1, 1, 0, 1.0)
					}
				}
			}),
		}),
	});
}

/**
 * 退出Polyline绘制功能
 */
FdDrawPolyline.prototype.cancel = function() {
	this._ple.cancel();
	this._event.raiseEvent('Cancel', {});
}

/**
 * 注册事件捕捉
 * @param {FdMicroApp.FdDrawPolyline~FdDrawPolylineCallback} listener 当有事件触发时被执行的函数。
 * @param {Object} [scope] listener函数执行时的绑定的对象。
 * @returns {Freedo.Event~RemoveCallback} 返回一个函数，调用该函数可以取消监听。
 */
FdDrawPolyline.prototype.on = function(listener, scope) {
	return this._eventHelper.add(this._event, listener, scope);
}

/**
 * @callback FdMicroApp.FdDrawPolyline~FdDrawPolylineCallback
 * @param {String} eventType 事件类型有以下几种：Start（绘制开始）/Cancel（绘制取消）
 * @param {Object} eventArg Start/Cancel事件返回空值
 */

/**
 * 设置路径平滑度
 * @param {Number} r 平滑度，建议1-11之间，数字越大越平滑
 */
FdDrawPolyline.prototype.setSmoothRate = function(r) {
	this._smoothRate = r;
	this.reDraw();
}

/**
 * 清空
 */
FdDrawPolyline.prototype.clearPolylines = function(r) {
    for(var key in this._polylines) {
        this._viewer.scene.primitives.remove(this._polylines[key]['polyline']);
    }
    this._polylines = [];
    this._viewer.scene.requestRender();
}

/**
 * 获取数据
 * @returns {Object[]} 返回值格式：
 * [{
 *     pts: [Cartesian3]
 * }...]
 */
FdDrawPolyline.prototype.getData = function() {
    return Freedo.clone(this._data, true);
}

/**
 * 开始进行节点编辑
 * @param {String} 组ID
 */
FdDrawPolyline.prototype.startEdit = function(index) {
    this.addLsnr();
    this._currentIndex = index;
    this._ple.startEdit(this.polylinesOptions[index].pts);
}

/**
 * 编辑上一个节点
 */
FdDrawPolyline.prototype.editPre = function() {
    this._ple.editPre();
}

/**
 * 编辑下一个节点
 */
FdDrawPolyline.prototype.editNext = function() {
    this._ple.editNext();
}

/**
 * 编辑下一个节点
 */
FdDrawPolyline.prototype.endEdit = function() {
    this._ple.endEdit();
}


/**
 * 删除
 */
FdDrawPolyline.prototype.removeAll = function() {
    this.clearPolylines();
    this._data = [];
}

/**
 * 重绘
 * @private
 */
FdDrawPolyline.prototype.reDraw = function(r) {
    this.clearPolylines();
    var data = Freedo.clone(this._data, true);
    this._data = [];
    for(var k in data){
        this.addPolyline(data[k]['pts']);
    }
}

/**
 * 设置高度偏移量
 * @param {Number} offset 高度偏移量
 */
FdDrawPolyline.prototype.setHeightOffset = function(offset) {
	this._ple.setHeightOffset(offset);
}

/**
 * 重置
 */
FdDrawPolyline.prototype.reset = function() {
	if(this._lsner)
		this._lsner();
	this._lsner = undefined;
	this.clearPolylines();
	this._data = [];
}

/**
 * 销毁所有资源
 */
FdDrawPolyline.prototype.dispose = function() {}

export {
	FdDrawPolyline
};