/**
 * 地形高程管理器
 *
 * @alias FdTools.FdTerrain
 * @param {Freedo.Viewer} viewer 视景器
 * @class
 *
 * @example
 * var container = document.getElementById('freedoContainer');
 *
 * var terrainManager = new Freedo.FdTools.FdTerrain(viewer);
 * 
 * //stk 地形
 * terrainManager.setTerrain('//terrain.gbim360.com/', 'STK');
 * 
 * //高度图地形（海南高精度）
 * terrainManager.setTerrain('//terrain.gbim360.com/HN', 'HM');
 * 
 * //自定义格式地形（全球低精度）
 * terrainManager.setTerrain('http://devmaps.gbim360.cn/services/wets?Service=WETS&Request=GetCapabilities&Version=1.0.0', 'WETS');
 * 
 * 
 * //多地形 ,新版本开始设置capabilitiesUrl ，而老版本设置url ，这里需要注意下；
 * terrainManager.setTerrain([{
 *      capabilitiesUrl: 'https://cfgateway.gbim360.cn/freeserver-wets/services/share/2cdc8c93-3ffb-404f-b851-22d2d6358b78/wets/1.0.0/WETSCapabilities.xml?accesskey=23a70df1-363a-4b51-96b0-d94bd2a901ac'
 * },{
 *     capabilitiesUrl: 'http://devmaps.gbim360.cn/services/wets?Service=WETS&Request=GetCapabilities&Version=1.0.0'
 * }] ) ;
 * 
 */
class FdTerrain {

    constructor(viewer) {
        this._viewer = viewer;
        this._scene = this._viewer.scene;

        this._terrainOption = {

        };

        this._showTerrain = false;

        this._terrain = new Freedo.CesiumTerrainProvider({
            url: '//terrain.gbim360.com',
            requestWaterMask: false
        });

        this._event = new FreeDo.Event();
        this._eventHelper = new FreeDo.EventHelper();

        this._defaultTerrain = new Freedo.EllipsoidTerrainProvider();
    }

    /**
     * 获取地形图层可见性
     * @type {Boolean}
     * 
     * @example
     * var terrainVisible = terrainManager.visible ;
     * 
     */
    get visible() {
        return this._showTerrain;
    }

    /**
     * 设置地形图层是否可见
     * @type {Boolean}
     * @param {Boolean} visible 是否可见
     * 
     * @example
     * terrainManager.visible = true;
     * 
     */
    set visible(visible) {
        if (this._showTerrain && visible === false) {
            this._viewer.terrainProvider = this._defaultTerrain;
            this._showTerrain = false;
        } else if (!this._showTerrain && visible === true) {
            this._viewer.terrainProvider = this._terrain;
            this._showTerrain = true;
        } else if (this._showTerrain && visible === true) {
            this._viewer.terrainProvider = this._terrain;
            this._showTerrain = true;
        }

        this._terrainOption.show = visible;

        this._event.raiseEvent('StatusChanged', {
            status: visible
        });
        this._viewer.scene.requestRender();
    }

    /**
     * 地形对象
     * @type {Freedo.CesiumTerrainProvider | Freedo.WETSTerrainProvider}
     * @readonly
     * 
     */
    get terrain() {
        return this._terrain;
    }

    /**
     * 地形图层选项
     * @type {Object}
     * @readonly
     * 
     * @example
     * var terrainOption = terrainManager.terrainOptions ;
     * var type = terrainOption.type ;
     * var show = terrainOption.show ;
     * var option = terrainOption.options;
     * 
     */
    get terrainOptions() {
        return this._terrainOption;
    };


    /**
     * 设置地形
     * @param {String|Object[]} terrainUri 地形服务地址或者参数
     * @param {String} type 地形服务类型，STK,HM,WETS
     * @param {constraintRegion[]} [constraintRegions] 限制区参数数组
     * @param {Number[]} [constraintRegions[i].constraintRegion ] 限制区范围,[minLongitude ,maxLongitude ,minLatitude ,maxLatitude](角度值)
     * @param {Number} [constraintRegions[i].height ] 限制区拉平高度
     * @example
     * 
     * terrainManager.setTerrain('//terrain.gbim360.com/', 'STK' ,[{
     *     constraintRegion: [109.64913096411063, 109.73642154758359, 19.11770556820389, 19.2042385831595],
     *     height: 0.0
     * }]) ;
     * 
     * //or
     * 
     * terrainManager.setTerrain([{
     *     capabilitiesUrl: 'https://cfgateway.gbim360.cn/freeserver-wets/services/share/2cdc8c93-3ffb-404f-b851-22d2d6358b78/wets/1.0.0/WETSCapabilities.xml?accesskey=23a70df1-363a-4b51-96b0-d94bd2a901ac'
     * },{
     *     capabilitiesUrl: 'http://devmaps.gbim360.cn/services/wets?Service=WETS&Request=GetCapabilities&Version=1.0.0'
     * }] ) ;
     * 
     */
    setTerrain(terrainUri, type, constraintRegions) {

        if (typeof terrainUri === 'string') {
            if (type === 'HM') {
                this._terrain = new Freedo.CesiumTerrainProvider({
                    url: terrainUri,
                    constraintRegions: constraintRegions,
                    requestWaterMask: false
                });

            } else if (type === 'STK') {
                this._terrain = new Freedo.CesiumTerrainProvider({
                    url: terrainUri,
                    constraintRegions: constraintRegions,
                    requestWaterMask: false
                });
            } else if (type === 'WETS') {
                this._terrain = new Freedo.WETSTerrainProvider({
                    sources: [{
                        capabilitiesUrl: terrainUri
                    }],
                    constraintRegions: constraintRegions
                });
            } else {
                throw new Freedo.RuntimeError('The Terrain Type:' + type + ' is not supported at present!');
                return;
            }

            this._terrainOption.type = type;
            this._terrainOption.show = true;
            this._terrainOption.options = terrainUri;

            this._event.raiseEvent('TypeChanged', {
                type: type
            });
            this.visible = true;
        } else {
            this.setWETSTerrain(terrainUri, constraintRegions);
        }

    }

    /**
     * 设置地形
     * @private
     * @param {Array} layerOptions WETS地形参数
     */
    setWETSTerrain(layerOptions, constraintRegions) {
        if (!(layerOptions instanceof Array)) {
            throw new Error('The parameter of setWETSTerrain must be an Array!');
            return;
        }

        if (layerOptions.length < 1) {
            return;
        }
        if (!constraintRegions instanceof Array) {
            constraintRegions = undefined;
        }

        this._terrain = new Freedo.WETSTerrainProvider({
            sources: layerOptions,
            constraintRegions: constraintRegions
        });

        this._terrainOption.type = 'WETS';
        this._terrainOption.show = true;
        this._terrainOption.options = layerOptions;

        this._event.raiseEvent('TypeChanged', {
            type: 'WETS'
        });
        this.visible = true;
    }


    /**
     * 开启WETS地形的反向裙边功能。漫游到地下后，将看不到地形的裙边。必须为自定义的地形（Freedo.WETSTerrainProvider）
     *
     */
    enableTerrainReverseSkirt() {
        var terrainProvider = this._terrain;
        if (!(terrainProvider instanceof Freedo.WETSTerrainProvider)) {
            throw new Error('The current Terrain is not The Freedo.WETSTerrainProvider Object!');
            return;
        }

        var viewer = this._viewer;
        viewer.scene.screenSpaceCameraController.enableCollisionDetection = false;

        var camera = viewer.camera;
        camera.showTerrainWhenUnderGround = true; // 相机进入地下后，强制显示地形（关闭背面剔除），优先级低于隐藏地形
        camera.reverseSkirtWhenUnderGround = true; // 相机进入地下后，翻转裙边（使用向上的裙边，需要addReverseSkirt为true），与showTerrainWhenUnderGround联合使用

        terrainProvider.addReverseSkirt = true; // 构造mesh时构造向上的裙边
        terrainProvider._hideSkirtIndexBufferWithContext = [];
        terrainProvider._reverseSkirtIndexBufferWithContext = [];
        terrainProvider._heightmapWidth1 = 65;
        terrainProvider._heightmapHeight1 = 65;

        terrainProvider.getHideSkirtIndexBuffer = function() {

            return function(context) {
                var indexBuffer = this._hideSkirtIndexBufferWithContext[context.id];
                if (!indexBuffer) {
                    indexBuffer = this._hideSkirtIndexBufferWithContext[context.id] = Freedo.Buffer.createIndexBuffer({
                        context: context,
                        typedArray: getHideSkirtIndices(this._heightmapWidth1, this._heightmapHeight1),
                        usage: Freedo.BufferUsage.STATIC_DRAW,
                        indexDatatype: Freedo.IndexDatatype.UNSIGNED_SHORT
                    });
                    indexBuffer.vertexArrayDestroyable = false;
                    indexBuffer.referenceCount = 1;
                }
                return indexBuffer;
            };

            function getHideSkirtIndices(width, height) {
                var indices = new Uint16Array((width - 1) * (height - 1) * 6);
                var index = 0;
                var indicesIndex = 0;
                for (var j = 0; j < height + 1; ++j) {
                    for (var i = 0; i < width + 1; ++i) {
                        if (j > 0 && j < height && i > 0 && i < width) {
                            var upperLeft = index;
                            var lowerLeft = upperLeft + width + 2;
                            var lowerRight = lowerLeft + 1;
                            var upperRight = upperLeft + 1;
                            indices[indicesIndex++] = upperLeft;
                            indices[indicesIndex++] = lowerLeft;
                            indices[indicesIndex++] = upperRight;
                            indices[indicesIndex++] = upperRight;
                            indices[indicesIndex++] = lowerLeft;
                            indices[indicesIndex++] = lowerRight;
                        }
                        ++index;
                    }
                    ++index;
                }
                return indices;
            }
        }();

        terrainProvider.getReverseSkirtIndexBuffer = function() {
            return function(context) {
                var indexBuffer = this._reverseSkirtIndexBufferWithContext[context.id];
                if (!indexBuffer) {
                    indexBuffer = this._reverseSkirtIndexBufferWithContext[context.id] = Freedo.Buffer.createIndexBuffer({
                        context: context,
                        typedArray: getReverseSkirtIndices(this._heightmapWidth1, this._heightmapHeight1),
                        usage: Freedo.BufferUsage.STATIC_DRAW,
                        indexDatatype: Freedo.IndexDatatype.UNSIGNED_SHORT
                    });
                    indexBuffer.vertexArrayDestroyable = false;
                    indexBuffer.referenceCount = 1;
                }
                return indexBuffer;
            };

            function getReverseSkirtIndices(width, height) {
                var indices = new Uint16Array((width + 1) * (height + 1) * 6);
                var index = 0;
                var indicesIndex = 0;
                var reverseSkirtIndexOffset = (width + 2) * (height + 2);
                for (var j = 0; j < height + 1; ++j) {
                    for (var i = 0; i < width + 1; ++i) {
                        var upperLeft = index;
                        var lowerLeft = upperLeft + width + 2;
                        var lowerRight = lowerLeft + 1;
                        var upperRight = upperLeft + 1;

                        if (j === 0) {
                            upperLeft = reverseSkirtIndexOffset + i;
                            upperRight = upperLeft + 1;
                            if (i === 0) {
                                lowerLeft = reverseSkirtIndexOffset + (width + 2);
                            } else if (i === width) {
                                lowerRight = reverseSkirtIndexOffset + (width + 2) + 1;
                            }
                        } else if (j === height) {
                            lowerLeft = reverseSkirtIndexOffset + (width + 2 + 2 * height) + i;
                            lowerRight = lowerLeft + 1;
                            if (i === 0) {
                                upperLeft = reverseSkirtIndexOffset + (width + 2 + 2 * (height - 1));
                            } else if (i === width) {
                                upperRight = reverseSkirtIndexOffset + (width + 2 + 2 * (height - 1)) + 1;
                            }
                        } else if (i === 0) {
                            upperLeft = reverseSkirtIndexOffset + (width + 2) + 2 * (j - 1) + i;
                            lowerLeft = upperLeft + 2;
                        } else if (i === width) {
                            upperRight = reverseSkirtIndexOffset + (width + 2) + 2 * (j - 1) + 1;
                            lowerRight = upperRight + 2;
                        }

                        indices[indicesIndex++] = upperLeft;
                        indices[indicesIndex++] = lowerLeft;
                        indices[indicesIndex++] = upperRight;
                        indices[indicesIndex++] = upperRight;
                        indices[indicesIndex++] = lowerLeft;
                        indices[indicesIndex++] = lowerRight;

                        ++index;
                    }
                    ++index;
                }
                return indices;
            }
        }();

    }

    /**
     * 设置是否进行深度检测，如果希望看到地面之下的物体，可以关闭深度深度测试来实现
     * @param {Boolean} depthTest true表示开启深度检测，fasle表示关闭
     * 
     * 
     */
    setDepthTest(depthTest) {
        this._viewer.scene.globe.depthTestAgainstTerrain = depthTest;
        this._viewer.scene.requestRender();
    }

    /**
     * 事件类型有：StatusChanged（地形可见性发生变化），TypeChanged（地形类型发生变化）
     * @param {FdTools.FdTerrain~FdTerrainCallback} listener 当有事件触发时被执行的函数。
     * @param {Object} [scope] listener函数执行时的绑定的对象。
     * @returns {Freedo.Event~RemoveCallback} 返回一个函数，调用该函数可以取消监听。
     */
    on(listener, scope) {
        return this._eventHelper.add(this._event, listener, scope);
    }

    /**
     * @callback FdTools.FdTerrain~FdTerrainCallback
     * @param {String} eventType StatusChanged（地形可见性发生变化），TypeChanged（地形类型发生变化）
     * @param {Object} eventArg StatusChanged事件参数为{status:visible},其中visible为是否可见(Boolean);TypeChanged事件参数为{type:terrainType} ,其中terrainType可能的值为：'HM','STK','WETS'
     * 
     */

    /**
     * @private
     */
    reset() {
        //this.showTerrain(false);
        this.visible = false;
    }

    /**
     * 销毁所有资源
     * 
     */
    dispose() {
        this.reset();
        this._terrainOption = {};
        this._terrain = null;
        this._eventHelper.removeAll();
    }
}

export {
    FdTerrain
};