import {
    FdBillboard
} from './FdBillboard';

import {
    FdCylinder
} from './FdCylinder';

import {
    FdLoft
} from './FdLoft';

import {
    FdTextBox2D
} from './FdTextBox2D';
/**
 * 参数化建模管理器
 * @alias FdModel.FdModelManager
 * @class
 *
 * @example
 * var viewer = Freedo.FdApp.createDefaultViewer('freedoContainer');
 * var mm = new FdModelManager(viewer);
 * mm.add('Billboard', {
 *     position: pos,
 *     width: width,
 *     height: height,
 *     rotate: rotate,
 *     div: div
 * });
 */
class FdModelManager {
    constructor(viewer) {
        this._viewer = viewer;
        this._scene = this._viewer.scene;

        this._entities = [];
        
        this._event = new Freedo.Event();
        this._eventHelper = new Freedo.EventHelper();
    }
}

FdModelManager.prototype.addLsnr = function() {
    if(this._lsner)
        this._lsner();
    var that = this;
}

/**
 * 增加实体，支持的类型：Billboard
 * @param {String} type 类型
 * @param {Object} option 参数选项
 * @param {String} id id，可选，若无则自动创建
 */
FdModelManager.prototype.add = function(type, option, id) {
    if(!Freedo.defined(type)){
        console.error('FdModelManager.add：type参数无效')   
    }

    var ent = undefined;
    if(type == 'Billboard'){
        ent = new FdBillboard(this._viewer, id, option);
    }
    if(type == 'Cylinder'){
        ent = new FdCylinder(this._viewer, id, option);
    }
    if(type == 'Loft'){
        ent = new FdLoft(this._viewer, id, option);
    }
    if(type == 'TextBox2D'){
        ent = new FdTextBox2D(this._viewer, id, option);
    }
    
    if(ent){
        ent.type == type;
        this._entities.push(ent);        
        this._viewer.scene.requestRender();
        return ent;
    }
}

/**
 * 注册事件捕捉
 * @param {FdModel.FdModelManager~FdModelManagerCallback} listener 当有事件触发时被执行的函数。
 * @param {Object} [scope] listener函数执行时的绑定的对象。
 * @returns {Freedo.Event~RemoveCallback} 返回一个函数，调用该函数可以取消监听。
 */
FdModelManager.prototype.on = function(listener, scope) {
    return this._eventHelper.add(this._event, listener, scope);
}

/**
 * @callback FdModel.FdModelManager~FdModelManagerCallback
 * @param {String} eventType 事件类型有：Start,Cancel
 * @param {Object} eventArg Start/Cancel无返回值
 */

/**
 * 清空实体
 * @private
 */
FdModelManager.prototype.clearEntities = function() {
    for(var key in this._entities) {
        this._entities[key].remove();
    }
    this._entities = [];
    this._viewer.scene.requestRender();
}

/**
 * 获取所管理的所有实体的数据
 * @returns {Object[]} 返回值格式：
 * [{
        id: GUID,
        type: 'Billboard',
        option: {
            
        }
   },...]
 */
FdModelManager.prototype.getData = function() {
    var datas = [];
    for(var k in this._entities){
        var d = this._entities[k].getData();
        if(d)
            datas.push(d);
    }
    return datas;
}

/**
 * 删除所有实体
 */
FdModelManager.prototype.removeAll = function() {
    this.clearEntities();
}

/**
 * 重置
 */
FdModelManager.prototype.reset = function() {
    if(this._lsner)
        this._lsner();
    this._lsner = undefined;
    this._ple.cancel();
}

/**
 * 销毁所有资源
 */
FdModelManager.prototype.dispose = function() {
    this.reset();
    this.clearEntities();
}

export {
    FdModelManager
};