function isSceneTypeValid(scene) {
    if (!(scene instanceof Freedo.Scene)) {
        throw new Freedo.DeveloperError('The parameter is not an instance of Freedo.Scene.');
    }
}

function isBoundingSphereValid(boundingSphere) {
    if (!Freedo.defined(boundingSphere)) {
        throw new Freedo.DeveloperError('boundingSphere is required.');
    }
}

function action(camera, boundingSphere, heading, pitch) {
    var pos = new Freedo.Cartographic.fromCartesian(boundingSphere.center);
    var height = pos.height;
    var doAction = function() {
        camera.flyToBoundingSphere(boundingSphere, {
            offset: new Freedo.HeadingPitchRange(heading, pitch, boundingSphere.radius * 3.0),
            maximumHeight: height,
            duration: 2.0
        });
    }

    var distance2bs = camera.distanceToBoundingSphere(boundingSphere);
    if (distance2bs > boundingSphere.radius * 5) {
        camera.flyToBoundingSphere(boundingSphere, {
            duration: 6.0,
            offset: new Freedo.HeadingPitchRange(camera.heading, camera.pitch, boundingSphere.radius * 5),
            complete: doAction
        });
    } else {
        doAction();
    }
}

/**
 * 相机飞到模型包围球南边，朝向模型
 *
 * @alias FdCamera.flyToSouth
 * @param {Freedo.Scene} scene 场景对象
 * @param {Freedo.BoundingSphere} boundingSphere 模型包围体
 *
 * @example
 * var boundingSphere = tileset.boundingSphere;
 * Freedo.FdCamera.flyToSouth(scene,boundingSphere);
 */
function flyToSouth(scene, boundingSphere) {
    isSceneTypeValid(scene);
    isBoundingSphereValid(boundingSphere);

    var camera = scene.camera;

    action(camera, boundingSphere, 0.0, 0.0);
}

/**
 * 相机飞到模型包围球西边，朝向模型
 *
 * @alias FdCamera.flyToWest
 * @param {Freedo.Scene} scene 场景对象
 * @param {Freedo.BoundingSphere} boundingSphere 模型包围体
 *
 * @example
 * var boundingSphere = tileset.boundingSphere;
 * Freedo.FdCamera.flyToWest(scene,boundingSphere);
 */
function flyToWest(scene, boundingSphere) {
    isSceneTypeValid(scene);
    isBoundingSphereValid(boundingSphere);

    var camera = scene.camera;

    action(camera, boundingSphere, FreeDo.Math.toRadians(90), 0.0);
}

