/**
 * 相机飞到模型包围球上边，俯视模型
 *
 * @alias FdCamera.flyToTop
 * @param {Freedo.Scene} scene 场景对象
 * @param {Freedo.BoundingSphere} boundingSphere 模型包围体
 *
 * @example
 * var boundingSphere = tileset.boundingSphere;
 * Freedo.FdCamera.flyToTop(scene,boundingSphere);
 */
function flyToTop(scene, boundingSphere) {
    isSceneTypeValid(scene);
    isBoundingSphereValid(boundingSphere);

    var camera = scene.camera;

    action(camera, boundingSphere, camera.heading, FreeDo.Math.toRadians(-90));
}

export { flyToSouth, flyToWest, flyToNorth, flyToEast, flyToTop };