import {
    FdMisc
} from '../../FdMisc/FdMisc';

/**
 * 点选高亮模型
 * @alias FdMicroApp.FdPickPMComponent
 * @class
 */
class FdPickPMComponent {
    constructor(viewer, pModelInfos, token) {
        this._viewer = viewer;
        this._bInSurvey = false;

        this._event = new Freedo.Event();
        this._eventHelper = new Freedo.EventHelper();

        this._currentTileSet = undefined;
        this._baseColor = [255, 255, 255, 1.0];
        this._highlightColor = [255, 0, 0, 1.0];

        var that = this;
        this.updatePModelStyleThrottle = _.throttle(that.updatePModelStyle, 100);
        this._mode = 'HIGH_LIGHT'; //SHOW_HIDE
        this._clickMode = Freedo.ScreenSpaceEventType.LEFT_CLICK;
        this._pModelSetMap = new Map();
        
        this._pModelInfos = pModelInfos;
        this._token = token;
    }
}

/*
 * 设置Id数组
 */
FdPickPMComponent.prototype.setPModelSets = function(aPModelSet) {
    this._aPModelSet = aPModelSet;
}

/*
 * 设置显示模式
 * @param {String} mode 模式，HIGH_LIGHT（高亮模式），SHOW_HIDE（隐藏模式），HALF_ALPHA（半透明模式）
 */
FdPickPMComponent.prototype.setMode = function(mode) {
    this._mode = mode;
}

/*
 * 设置激活模式
 * @param {Number} mode 参见ScreenSpaceEventType的取值
 */
FdPickPMComponent.prototype.setClickMode = function(mode) {
    this._clickMode = mode;
}

/*
 * 开始
 */
FdPickPMComponent.prototype.start = function() {
    this.cancel();
    this._hanlder = new Freedo.ScreenSpaceEventHandler(this._viewer.scene.canvas);
    this._hanlder.setInputAction(this.handleLeftClick.bind(this), this._clickMode); //默认Freedo.ScreenSpaceEventType.LEFT_CLICK
    this._bInSurvey = true;
}

/**
 * 注册事件捕捉
 * @param {FdMicroApp.FdPickPMComponent~FdPickPMComponentCallback} listener 当有事件触发时被执行的函数。
 * @param {Object} [scope] listener函数执行时的绑定的对象。
 * @returns {Freedo.Event~RemoveCallback} 返回一个函数，调用该函数可以取消监听。
 */
FdPickPMComponent.prototype.on = function(listener, scope) {
    return this._eventHelper.add(this._event, listener, scope);
}

/**
 * @callback FdMicroApp.FdPickPMComponent~FdPickPMComponentCallback
 * @param {String} eventType 事件类型有：DataChanged
 * @param {Object} eventArg DataChanged事件返回模型和部件ID
 */

FdPickPMComponent.prototype.updatePModelStyle = function() {
    var colorConditions = [];
    var showConditions = [];
    for(var [tileSet, value] of this._pModelSetMap) {
        colorConditions.splice(0, colorConditions.length);
        showConditions.splice(0, showConditions.length);

        var pModelStyle = value;
        if(typeof pModelStyle !== 'undefined') {
            for(var componentID in pModelStyle) {
                var componentStyle = pModelStyle[componentID];
                if(typeof componentStyle.color !== 'undefined') {
                    var c = componentStyle.color;
                    var colorStr = 'rgba(' + c[0] + ',' + c[1] + ',' + c[2] + ',' + c[3] + ')';
                    colorConditions.push(['${component} ~== \'' + componentID + '\'', colorStr]);
                }
                if(typeof componentStyle.show !== 'undefined') {
                    if(!componentStyle.show) {
                        showConditions.push(['${component} ~== \'' + componentID + '\'', 'false']);
                    }
                }
            }

            // colorConditions.push(['true', 'color('white')']);
            var bc = this._baseColor;
            var baseColorStr = 'rgba(' + bc[0] + ',' + bc[1] + ',' + bc[2] + ',' + bc[3] + ')';
            colorConditions.push(['true', baseColorStr]);
            showConditions.push(['true', 'true']);

            tileSet.style = new Freedo.FreedoPModelStyle({
                color: {
                    conditions: colorConditions,
                },
                show: {
                    conditions: showConditions,
                },
            });
        }
    }
    this._viewer.scene.requestRender();
};

FdPickPMComponent.prototype.findPModel = function(picked) {
    var ts = picked._content._tileset;
    for(var mk in this._aPModelSet) {
        if(this._aPModelSet[mk] === ts) {
            return mk;
        }
    }

    return undefined;
}

FdPickPMComponent.prototype.pickComponent = function(movement, selectedCallback, retAncestorComponents) {
    var picked = this._viewer.scene.pick(movement);
    if(Freedo.defined(picked)) {
        if(Freedo.defined(picked.tileset)) {
            if(!this._pModelSetMap.has(picked.tileset)) {
                this._pModelSetMap.set(picked.tileset, {});
            }

            var propertyNames = picked.getPropertyNames();
            console.log(picked.content.tile.computedTransform);
            console.log(picked.content.tile.boundingSphere.center);

            var componentid = picked.getProperty('component');
            if(Freedo.defined(componentid)) {
                var ancestorComponents = undefined;
                if(retAncestorComponents) {
                    ancestorComponents = picked.getAncestors('component');
                }
                selectedCallback(componentid, picked.tileset, ancestorComponents);
                this._event.raiseEvent('DataChanged', {
                    tilesetUrl: picked.tileset.basePath,
                    tileset: picked.tileset,
                    componentid: componentid
                });
                return;
            }
        }
    }

    selectedCallback(undefined, undefined);
};

/**
 * 获取数据
 * @returns {Object[]} 返回值格式：
 * [{
 *     componentid: 组件ID,
 *     tileset: tileset对象,
 *     tilesetURL: tileset地址
 * }...]
 */
FdPickPMComponent.prototype.getData = function() {
    var a = [];
    for(var [tileSet, value] of this._pModelSetMap) {
        for(var k in value) {
            a.push({
                tileset: tileSet,
                tilesetURL: tileSet.basePath,
                componentid: k
            });
        }
    }
    return a;
}

/**
 * 恢复所有高亮对象
 */
FdPickPMComponent.prototype.unhighlightAllComponents = function() {
    for(var [tileSet, value] of this._pModelSetMap) {
        var pModelStyle = value;
        for(var componentID in pModelStyle) {
            delete pModelStyle[componentID]['color'];
        }
    }

    this.updatePModelStyleThrottle();
}

/**
 * 显示某个场景对象中所有的组件
 * @param {String} sceneObjectID 场景对象ID
 */
FdPickPMComponent.prototype.showAllComponents = function(sceneObjectID) {
    for(var [tileSet, value] of this._pModelSetMap) {
        var pModelStyle = value;
        for(var componentID in pModelStyle) {
            delete pModelStyle[componentID]['show'];
        }
    }

    this.updatePModelStyleThrottle();
}

FdPickPMComponent.prototype.handleLeftClick = function(movement) {
    if(this._bInSurvey == false)
        return;

    var that = this;
    this.pickComponent(movement.position, function(componentID, tileSet) {
        if(that._mode == 'HIGH_LIGHT') {
            if(Freedo.defined(componentID)) {
                var r = that._highlightColor[0];
                var g = that._highlightColor[1];
                var b = that._highlightColor[2];
                var a = that._highlightColor[3];
                //that.unhighlightAllComponents();
                //sceneManager.showAllComponents();
                that.highlightComponent(componentID, tileSet, r, g, b, a);
                // sceneManager.setBaseColor(255, 255, 255, 0.1);
            } else {
                that.unhighlightAllComponents();
                //sceneManager.showAllComponents(); // 显示全部对象
                // sceneManager.setBaseColor(255, 255, 255, 1.0);
            }
        }
        if(that._mode == 'SHOW_HIDE') {
            if(Freedo.defined(componentID)) {
                var r = Math.random() * 255;
                var g = Math.random() * 255;
                var b = Math.random() * 255;
                var a = Math.random() * 0.5 + 0.5;
                that.showComponent(componentID, tileSet, false);
            } else {
                that.showAllComponents();
            }
        }

        if(that._mode == 'HALF_ALPHA') {
            if(Freedo.defined(componentID)) {
                var r = Math.random() * 255;
                var g = Math.random() * 255;
                var b = Math.random() * 255;
                var a = Math.random() * 0.5 + 0.5;
                that.highlightComponent(componentID, tileSet, 255, 255, 0, 0.1);
            } else {
                that.unhighlightAllComponents();
            }
        }
    }, false);
};

/**
 * 高亮某个部件
 *
 * @param {String} componentID 部件ID
 * @param {Object} tileSet 模型
 * @param {Number} r 颜色R分量，范围0~255
 * @param {Number} g 颜色G分量，范围0~255
 * @param {Number} b 颜色B分量，范围0~255
 * @param {Number} a 透明度，范围0~1
 * @returns {Boolean} 返回true表示设置成功，否则表示未成功
 */
FdPickPMComponent.prototype.highlightComponent = function(componentID, tileSet, r, g, b, a) {
    if(Freedo.defined(componentID)) {
        if(typeof this._pModelSetMap.get(tileSet) === 'undefined') {
            this._pModelSetMap.set(tileSet, {});
        }
        var pModelStyle = this._pModelSetMap.get(tileSet);
        if(typeof pModelStyle[componentID] === 'undefined') {
            pModelStyle[componentID] = {};
        }
        pModelStyle[componentID].color = [r, g, b, a];

        this.updatePModelStyleThrottle();

        return true;
    } else {
        return false;
    }
}

/**
 * 设置高亮颜色，默认黄色
 *
 * @param {Number} r 颜色R分量，范围0~255
 * @param {Number} g 颜色G分量，范围0~255
 * @param {Number} b 颜色B分量，范围0~255
 * @param {Number} a 透明度，范围0~1
 */
FdPickPMComponent.prototype.setHighlightColor = function(r, g, b, a) {
    this._highlightColor = [r, g, b, a];
}

/**
 * 显示或者隐藏某个部件
 * @param {Object} tileSet 模型
 * @param {String} sceneObjectID 场景对象ID
 * @param {Boolean} show 显示用true表示，隐藏用false表示
 */
FdPickPMComponent.prototype.showComponent = function(componentID, tileSet, show) {
    if(Freedo.defined(componentID)) {
        if(typeof this._pModelSetMap.get(tileSet) === 'undefined') {
            this._pModelSetMap.set(tileSet, {});
        }
        var pModelStyle = this._pModelSetMap.get(tileSet);
        if(typeof pModelStyle[componentID] === 'undefined') {
            pModelStyle[componentID] = {};
        }
        pModelStyle[componentID].show = show;
    }

    this.updatePModelStyleThrottle();
}

FdPickPMComponent.prototype.getUidBoundingSphere = function(pModel, componentID, callback) {
    var pModelInfo = this._pModelInfos.find(function(element) {
        return element.pModel === pModel;
    });
    Freedo.FdServer.FdPMTSParser.getUidBoundingSphere(pModelInfo.layer, pModelInfo.matrixSet,
        pModelInfo.matrix, componentID, token, undefined,
        function(bs, error) {
            if(typeof error !== 'undefined') {
                console.log('getUidBoundingSphere got error!');
                return;
            }
            callback(bs);
        });
}

/*
 * 取消
 */
FdPickPMComponent.prototype.cancel = function() {
    this.reset();
}

FdPickPMComponent.prototype.reset = function() {
    this._bInSurvey = false;
    this._hanlder = this._hanlder && this._hanlder.destroy();
    this.unhighlightAllComponents();
    this.showAllComponents();
};

export {
    FdPickPMComponent
};