import { flyToByCameraInfo, goToByCameraInfo } from '../../FdCamera/moveByCameraInfo.js';

var scratchTransformMatrix = new Freedo.Matrix4();

/**
 * FdViewPointsManager内部使用，用来控制从一个视点跳转到另外一个视点的飞行过程
 * @private
 */
class FdFlyToController {
    constructor(camera) {
        this._viewpoints = null;
        this._camera = camera;
        this._currentIndex = -1; // 如果为0则表示目前相机在0这个位置，播放时就跳过0，直接往1飞了
        this._status = 'Stopped';
        this._flyingToIndex = -1; // -1表示没有进行飞行，否则表示正在飞往某index
        this._loop = false;

        this._event = new Freedo.Event();
        this._eventHelper = new Freedo.EventHelper();
        this._timeoutID = undefined;
    }

    get currentIndex() {
        return this._currentIndex;
    }
    set currentIndex(index) {
        this._goto(index);
        this._setCurrentIndex(index);
    }

    get status() {
        return this._status;
    }

    get loop() {
        return this._loop;
    }
    set loop(value) {
        this._loop = value;
        this._event.raiseEvent('PropertyChanged');
    }

    startOrResume() {
        switch (this._status) {
            case 'Playing':
                // do nothing!
                break;
            case 'Stopped':
                this._play();
                break;
            case 'Paused':
                this._play();
                break;
            default:
                throw new Error('ViewpointsManager FdFlyToController error');
        }
    }

    pause() {
        switch (this._status) {
            case 'Playing':
                this._pause();
                break;
            case 'Stopped':
                // do nothing!
                break;
            case 'Paused':
                // do nothing!
                break;
            default:
                throw new Error('ViewpointsManager FdFlyToController error');
        }
    }

    stop() {
        switch (this._status) {
            case 'Playing':
                this._stop();
                break;
            case 'Stopped':
                // do nothing!
                break;
            case 'Paused':
                this._stop();
                break;
            default:
                throw new Error('ViewpointsManager FdFlyToController error');
        }
    }

    reset() {
        this._stop();
        this._viewpoints = null;
    }

    dispose() {
        this._viewpoints = null;
    }

    _setViewpoints(viewpoints) {
        this._stop();
        this._viewpoints = viewpoints;
    }

    _getViewpoints() {
        return this._viewpoints;
    }

    _play() {
        if (this._viewpoints === null) {
            throw new Error('FdFlyToController play error!');
        }
        this._flytoNext();
        this._setStatus('Playing');
    }

    _pause() {
        this._cancel();
        this._setStatus('Paused');
    }

    _stop() {
        this._cancel();
        this._setCurrentIndex(-1);
        this._setStatus('Stopped');
    }

    _completeCallback() {
        this._setCurrentIndex(this._flyingToIndex);
        if (this._currentIndex >= this._viewpoints.length) {
            this._setCurrentIndex(-1);
        }

        this._flyingToIndex = -1;

        var that = this;
        this._timeoutID = setTimeout(function() {
            that._flytoNext();
        }, 1000);
    }

    _flytoNext() {
        this._cancel();

        var cam;
        if (this._currentIndex >= this._viewpoints.length - 1) {
            if (this._loop && this._viewpoints.length !== 1) {
                this._flyingToIndex = 0;
            } else {
                this._stop();
            }
        } else {
            this._flyingToIndex = this._currentIndex + 1;
        }

        if (this._flyingToIndex !== -1) {
            cam = this._viewpoints[this._flyingToIndex].cameraInfo;

            if (typeof cam !== 'undefined') {
                var that = this;

                flyToByCameraInfo(that._camera, cam, {
                    complete: function() {
                        that._completeCallback();
                    }
                });

            }
        }
    }

    _cancel() {
        clearTimeout(this._timeoutID);
        this._timeoutID = undefined;
        //this._fdCamera.cancelFlight();
        this._camera.cancelFlight();
        this._flyingToIndex = -1;
    }

    _goto(index) {
        if (index >= this._viewpoints.length) {
            throw new Error('FdFlyToController inde error');
        }
        this._cancel();
        var option = this._viewpoints.list[index];
        var cam = option.cameraInfo;
        if (typeof cam === 'undefined') {
            throw new Error('FdFlyToController cameraInfo error');
        }

        goToByCameraInfo(this._camera, cam);
    }

    _setStatus(status) {
        this._status = status;
        this._event.raiseEvent('StatusChanged', status);
    }

    _setCurrentIndex(index) {
        this._currentIndex = index;
        this._event.raiseEvent('CurrentIndexChanged', index);
    }

    on(listener, scope) {
        // return this._event.addEventListener(listener, scope);
        return this._eventHelper.add(this._event, listener, scope);
    }

    reset() {
        this._setViewpoints(null);
        this._stop();
    }

    // 销毁所有资源
    dispose() {
        this.reset();
        this._eventHelper.removeAll();
    }
}


export {
    FdFlyToController
};